import { Component, OnInit, Renderer2 } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { FormService } from '../../modules/form/form-service.service';
import { UserNameFormGroup } from './models/household-wizard.models';

@Component({
  selector: 'app-household-wizard',
  templateUrl: './household-wizard.component.html',
  styleUrls: ['./household-wizard.component.scss']
})
export class HouseholdWizardComponent implements OnInit {
  formGroup: FormGroup;
  houseHoldFrom: FormGroup;
  houseHoldPeopleCategories: any[] = [
    { name: '1 people', value: 1 },
    { name: '1 people and child', value: 1_1 },
    { name: '2 people', value: 2 },
    { name: '3 people and 4 children', value: 3_4 },
    { name: '4 people', value: 4 }
  ];

  householdPeopleList = [];

  constructor(private formService: FormService, private renderer: Renderer2) {
    this.householdPeopleList = Array(2);
  }

  ngOnInit() {
    this.houseHoldFrom = new FormGroup({
      houseHoldPeople: new FormControl('')
    });

    this.formGroup = new FormGroup({
      test1: new FormControl('', [Validators.required]),
      test2: new FormControl('', [Validators.required])
    });
  }

  onChangeHouseHoldPeople(event) {
    this.householdPeopleList = Array(parseInt(event));
  }

  switchFromToEditMode(houseHoldStepperEl) {
    this.renderer.addClass(houseHoldStepperEl._elementRef.nativeElement, 'edit-mode-mat-stepper');
  }

  saveChangeAndCLoseEditMode(houseHoldStepperEl) {
    this.renderer.removeClass(
      houseHoldStepperEl._elementRef.nativeElement,
      'edit-mode-mat-stepper'
    );
  }
}
