import { DynamicScriptLoaderService } from './services/dynamic-script-loader.service';
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { ReactiveFormsModule } from '@angular/forms';
import { RouterModule } from '@angular/router';
import { MatStepperModule, MatIconModule } from '@angular/material';
import { CdkStepperModule } from '@angular/cdk/stepper';

import { SharedModule } from '../shared/shared.module';
import { FormModule } from './modules/form/form.module';
import { ScrollFormModule } from './modules/scroll-form/scroll-form.module';
import { PublicApiService } from './services/public-api.service';
import { ApiService } from './services/api.service';
import { IdentityApiService } from './services/identity-api.service';
import { OnlyAuthorizedGuard } from './guards/only-authorized.guard';
import { ConditionInputComponent } from './components/condition-input/condition-input.component';
import { HouseholdWizardComponent } from './components/household-wizard/household-wizard.component';
import { FooterComponent } from './components/footer/footer.component';
import { HeaderComponent } from './components/header/header.component';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';
import { BsDropdownModule } from 'ngx-bootstrap/dropdown';

const MaterialModules = [MatStepperModule, MatIconModule];

@NgModule({
  imports: [
    RouterModule,
    CommonModule,
    ReactiveFormsModule,
    FormModule,
    ScrollFormModule,
    ...MaterialModules,
    CdkStepperModule,
    SharedModule,
    NgbModule.forRoot(),
    BsDropdownModule.forRoot()
  ],
  declarations: [
    ConditionInputComponent,
    HouseholdWizardComponent,
    FooterComponent,
    HeaderComponent
  ],
  exports: [ConditionInputComponent, HouseholdWizardComponent, FooterComponent, HeaderComponent],
  providers: [
    OnlyAuthorizedGuard,
    ApiService, PublicApiService,
    IdentityApiService,
    DynamicScriptLoaderService,
  ]
})
export class CoreModule {}
