import {Injectable} from '@angular/core';

@Injectable()
export class DatapickerFormcontrolInputService {
  datapickersList = [];

  constructor() {
  }

  deleteDatapicker(id) {
    this.datapickersList.slice(this.datapickersList.indexOf(id), 1);
  }

  addDatapicker(pickerRef) {
    this.datapickersList.push(pickerRef);
  }

  closeALlDatePickers() {
    this.datapickersList.forEach(el => el.opened && el.close());
  }
}
