import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';

import {
  MatButtonModule,
  MatCardModule,
  MatCheckboxModule,
  MatChipsModule,
  MatDatepickerModule,
  MatExpansionModule,
  MatFormFieldModule,
  MatIconModule,
  MatInputModule,
  MatMenuModule,
  MatPaginatorModule,
  MatSelectModule,
  MatSortModule,
  MatStepperModule,
  MatTableModule,
  MatTabsModule
} from '@angular/material';

import { LoaderModule } from '../../shared/components/loader/loader.module';
import { CoreModule } from '../../core/core.module';
import { FaqService } from './services/faq.servises';
import { FaqRoutingModule } from './faq-routing.module';
import { FaqComponent } from './faq.component';

const MaterialModules = [
  MatButtonModule,
  MatPaginatorModule,
  MatTableModule,
  MatFormFieldModule,
  MatInputModule,
  MatSortModule,
  MatCheckboxModule,
  MatIconModule,
  MatMenuModule,
  MatCardModule,
  MatStepperModule,
  MatExpansionModule,
  MatDatepickerModule,
  MatChipsModule,
  MatTabsModule,
  MatSelectModule
];

@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    CoreModule,
    ReactiveFormsModule,
    ...MaterialModules,
    LoaderModule,
    FaqRoutingModule
  ],
  declarations: [FaqComponent],
  providers: [FaqService],
  entryComponents: []
})
export class FaqModule {}
