import { OnDestroy } from '@angular/core';
import { ChangeDetectorRef } from '@angular/core';
import { ConfigValueService } from './../../../../shared/services/config-value/config-value.service';
import { FormBuilder, FormArray, FormControl, ControlContainer, FormGroupDirective, Validators, FormGroup } from '@angular/forms';
import { Component, OnInit, Output, EventEmitter, AfterViewInit } from '@angular/core';
import { takeUntil } from 'rxjs/operators';
import { Subject } from 'rxjs';
import { flNameValidator } from 'projects/web/src/app/core/modules/form/validators/name.validator';

export class simpleUser {
  firstName: string;
  lastName: string;
}

@Component({
  selector: 'add-member-form',
  templateUrl: './add-member-form.component.html',
  styleUrls: ['./add-member-form.component.scss'],
  viewProviders: [{
    provide: ControlContainer,
    useExisting: FormGroupDirective
  }]
})
export class AddMemberFormComponent implements OnInit, AfterViewInit, OnDestroy {

  primaryUser: simpleUser;
  unsubscribe: Subject<void> = new Subject();

  ngAfterViewInit(): void {
    this.mainForm.get('initialUserGroup').valueChanges.pipe(takeUntil(this.unsubscribe)).subscribe((val) => {
      if (this.mainForm.get('initialUserGroup').valid) {
        this.primaryUser = {
          firstName: val.firstName,
          lastName: val.lastName
        };
      }

    })
  }

  @Output() nextStep: EventEmitter<any> = new EventEmitter();


  private mainForm: FormGroup;
  memberForm: FormGroup;
  relationshipTypes: any;

  constructor(private fb: FormBuilder, private parentForm: FormGroupDirective, private configSvc: ConfigValueService) { }

  ngOnInit() {
    this.configSvc.getAllConfigTypes('RelationshipType').subscribe(r => {

      this.relationshipTypes = r.RelationshipType.filter(option => (option.name as string).toLocaleLowerCase().indexOf('self') === -1);
    });

    this.mainForm = this.parentForm.form;

    let newForm = this.fb.group({
      membersArray: this.fb.array([])
    });

    this.memberForm = newForm;

    this.mainForm.addControl('membersInfoForm', this.memberForm);
  }

  //add new member controls
  addMember() {
    const arrayControl = this.memberForm.controls['membersArray'] as FormArray;

    let newGroup = this.fb.group({
      // id: new FormControl(0),
      firstName: new FormControl({ value: '', disabled: true }),
      lastName: new FormControl({ value: '', disabled: true }),
      relationship: new FormControl('', [Validators.required])
    });

    arrayControl.push(newGroup);
  }

  onUpdateRelationship(group: FormGroup) {
    console.log('this is the formarray group', group)

    let selectedRelation = group.get('relationship').value;
    let firstName = group.get('firstName');
    let lastName = group.get('lastName');
    if (selectedRelation === '') {
      firstName.disable();
      lastName.disable();
    } else {

      let option = this.relationshipTypes.find(r => r.configTypeId === parseInt(selectedRelation));
      if (option.name.trim() === 'Unborn Child') {
        firstName.setValidators([]);
        lastName.setValidators([]);
        firstName.disable();
        lastName.disable();
        //Need to mark as touched and dirty for the form footer counter
        firstName.markAsTouched();
        firstName.markAsDirty();
        lastName.markAsTouched();
        lastName.markAsDirty();
      } else {
        firstName.setValidators([Validators.required, flNameValidator]);
        lastName.setValidators([Validators.required, flNameValidator]);
      }

      group.updateValueAndValidity();
      firstName.enable();
      lastName.enable();
    }
  }

  isUnbornChildSelected(control) {

    let selectedValue = control.get('relationship').value;
    if (selectedValue) {
      let option = this.relationshipTypes.find(r => r.configTypeId === parseInt(selectedValue));
      return option.name.trim() === 'Unborn Child' || false;
    }

    return false;
  }

  //remove member from mainform
  removeMember(index: number) {
    const arrayControl = this.memberForm.controls['membersArray'] as FormArray;
    arrayControl.removeAt(index);
  }

  ngOnDestroy(): void {
    this.unsubscribe.next();
    this.unsubscribe.complete();
  }
}
