import { HouseholdMemberProfile } from '../../models/hh-member-profile.model';
import { FormControl, FormGroup, Validators, FormArray } from "@angular/forms";
import { flNameValidator } from 'projects/web/src/app/core/modules/form/validators/name.validator';
import { AssetTypeEnum } from 'projects/web/src/app/shared/enums/applicantEnums';

export class AssetHoldingFormConstructor {

  localFormGroup: FormGroup;

  assetType: FormControl;
  assetName: FormControl;
  //assetValue: FormControl;

  minRequirementList = [
    'assetType',
    'assetName',
    //'assetValue'
  ]

  constructor() {
    this.localFormGroup = new FormGroup({});
  }

  createLocalForm() {
    const assetType = this.assetType = new FormControl(null, [Validators.required]);
    const assetName = this.assetName = new FormControl(null, [Validators.required]);
    //const assetValue = this.assetValue = new FormControl(null, [Validators.required]);
    

    this.localFormGroup = new FormGroup({
      assetType,
      assetName,
      //assetValue
    });

  }

  createAssetBasedForm(targetType: number) {
    switch (targetType) {
      case AssetTypeEnum.SavingsAccount:
      case AssetTypeEnum.CheckingAccount:
      case AssetTypeEnum.OtherBankAccount:
      case AssetTypeEnum.RetirementAccount:
      case AssetTypeEnum.BrokerageAccount:
      case AssetTypeEnum.OtherInvestmentAccount:
      case AssetTypeEnum.ResidentialRealEstateProperty:
      case AssetTypeEnum.NonResidentialRealEstateProperty: {
        console.log('Create Asset Form');
        this.buildAssetForm();
        break;
      }
    }
  }

  buildAssetForm() {
    const assetType = this.assetType = new FormControl(null, [Validators.required]);
    const assetName = this.assetName = new FormControl(null, [Validators.required]);
    //const assetValue = this.assetValue = new FormControl(null, [Validators.required]);

    this.localFormGroup = new FormGroup({
      assetType,
      assetName,
      //assetValue
    });
  }
}
