import {Component, Input, Host, HostListener, OnInit} from '@angular/core';
import {DropdownSelectorComponent} from "./dropdown-selector.component";

@Component({
  selector: 'dropdown-selector-option',
  templateUrl: './dropdown-selector-option.component.html',
  styleUrls: ['dropdown-selector-option.component.scss']
})
export class DropdownSelectorOptionComponent implements OnInit{
  @Input() value: any;
  @Input() label: string;

  get isSelected() {
    if (this.value == undefined) {
      return false
    }

    return this.parentSelector.isChildSelected(this.value);
  }

  constructor(
    @Host() public parentSelector: DropdownSelectorComponent
  ) {
  }

  ngOnInit(): void {
    this.parentSelector.bootstrapChild({
      value: this.value,
      label: this.label,
    })
  }

  @HostListener('click', ['$event']) onClick() {
    this.parentSelector.toggleItem({
      value: this.value,
      label: this.label,
    });
  }
}
