import { Component, Input, OnInit, ChangeDetectorRef } from '@angular/core';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { FormControl, Validators } from "@angular/forms";
import { ActivatedRoute } from "@angular/router";
import { ApplicationsApiService } from '../../services/applications-api.service'
import { AppSettings } from 'projects/web/src/environments/appsettings';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'detail-appeal',
  templateUrl: './detail-appeal.component.html'
})
export class AppealDetailComponent implements OnInit {
  @Input() lottery: any = {};

  appealDetails: any;
  noteDocGroupId: string;
  disabledBtn: boolean = false;
  appealStatus: any;
  actionText: string;
  errorDescription: string = "Failed Background Check";
  capitalize: Function = (str) => str.charAt(0).toUpperCase() + str.slice(1).toLowerCase();

  appealReason: FormControl = new FormControl();
  complaintDetails: any;
  complaintReason: FormControl = new FormControl();
  complaintStatus: any;
  constructor(
    public activeModal: NgbActiveModal,
    private applicationsApiService: ApplicationsApiService,
    private toastr: ToastrService,
    private cdRef: ChangeDetectorRef
  ) { }

  ngOnInit() {
    this.noteDocGroupId = AppSettings.appealNoteGroupdId;

    this.applicationsApiService.getAppeal(this.lottery.details.lotteryApplication.lotteryApplicationId).subscribe(appealDetails => {
      this.appealDetails = appealDetails;
      console.log("Appeal Details", this.appealDetails);
      this.appealReason.setValue(this.appealDetails.reason);
      this.appealStatus = this.appealDetails.appealStatus.name;
      this.cdRef.detectChanges();
    })

    this.applicationsApiService.getComplaint(this.lottery.details.lotteryApplication.lotteryApplicationId).subscribe(complaintDetails => {
      this.complaintDetails = complaintDetails;
      console.log(this.complaintDetails);
      this.complaintReason.setValue(this.complaintDetails.reason);
      this.complaintStatus = this.complaintDetails.complaintStatus.name;
      this.cdRef.detectChanges();
      
    })
  }

  isReasonExists() {
    if (!this.appealReason.value) {
      return false;
    }
    return true;
  }

  postAppeal() {
    
    if (this.appealDetails.appealId != null) {
        this.toastr.success("Appeal Updated");
        this.onClose();
    }
    else {
      this.toastr.success("Appeal Saved");
      this.onClose();
    }
  }

  onUpload() {

    if (!this.appealDetails) {
      this.applicationsApiService.postAppeal({
        lotteryId: this.lottery.details.lotteryId,
        reason: this.appealReason.value
      }).subscribe(response => {
        console.log(`Save Appeal response`, response);
        this.toastr.success("Appeal Saved");
        this.onClose();
      });
    }
    else {
      this.applicationsApiService.postAppeal({
        lotteryId: this.lottery.details.lotteryId,
        reason: this.appealReason.value,
        appealId: this.appealDetails.appealId
      }).subscribe(response => {
        console.log(`Save Appeal response`, response);
        this.toastr.success("Appeal Updated");
        this.onClose();
      });
    }
  }

  onClose() {
    this.activeModal.dismiss('Cross click');
  }
}
