import { Component, OnInit, AfterContentInit, Output, EventEmitter } from '@angular/core';
import { FormControl, Validators, FormGroupDirective, FormGroup, ControlContainer } from '@angular/forms';

@Component({
  selector: 'mail-address-form',
  templateUrl: './mail-address-form.component.html',
  styleUrls: ['./mail-address-form.component.css'],
  viewProviders: [{
    provide: ControlContainer,
    useExisting: FormGroupDirective
  }]
})
export class MailAddressFormComponent implements OnInit, AfterContentInit {

  @Output() nextStep: EventEmitter<any> = new EventEmitter();

  mainForm: FormGroup;
  localForm: FormGroup;
  addressForm: FormGroup;

  // FormControls - Component References
  mailAddress: FormControl;
  mailCity: FormControl;
  mailState: FormControl;
  mailZip: FormControl;
  isSameAsCurrentAddress: FormControl;

  constructor(private parentFormDirective: FormGroupDirective) { }

  ngOnInit() {
    //attach to mainForm parent FormGroup directive
    this.mainForm = this.parentFormDirective.form;

    this.createLocalForm();
  }

  //builds out local form and attaches to parent mainForm
  createLocalForm(){
    const address = this.mailAddress = new FormControl({ value:"", disabled: true }, [Validators.required]);
    const city = this.mailCity = new FormControl({ value:"", disabled: true }, [Validators.required]);
    const state = this.mailState = new FormControl({ value:"", disabled: true }, [Validators.required]);
    const zip = this.mailZip = new FormControl({ value:"", disabled: true }, [Validators.required]);
    const isSameAsCurrentAddress = this.isSameAsCurrentAddress = new FormControl(true);

    this.localForm = new FormGroup({ address, city, state, zip, isSameAsCurrentAddress }, [Validators.required]);

    this.mainForm.addControl('mailAddressForm', this.localForm);

    this.isSameAsCurrentAddress.valueChanges.subscribe((isCurrentAddress) =>
    this.onIsSameAsCurrentAddressChange(isCurrentAddress));
  }


  private onIsSameAsCurrentAddressChange(isSameAsCurrentAddress){
    if(isSameAsCurrentAddress){
      Object.keys(this.localForm.controls).forEach(key => {
        if(key !== "isSameAsCurrentAddress") {
          let frmCtrl = this.localForm.get(key);
          frmCtrl.setValidators([]);
          frmCtrl.setValue('');
          frmCtrl.disable();
        }
      });
    } else {
      Object.keys(this.localForm.controls).forEach(key => {
        if(key !== "isSameAsCurrentAddress") {
          let frmCtrl = this.localForm.get(key);
          frmCtrl.setValidators([Validators.required]);
          frmCtrl.setValue('');
          frmCtrl.enable();
        }
      });

      this.localForm.updateValueAndValidity();
    }
  }

  ngAfterContentInit(): void {  };

}
