import { Component, ChangeDetectorRef, OnInit } from "@angular/core";
import { FormGroup } from "@angular/forms";
import { Router } from '@angular/router';
import {ToastrService} from 'ngx-toastr';

import {FormService} from "../../../../core/modules/form/form-service.service";
import { phoneMask } from '../../../../core/addons/textmask-directive-addons';
import {DashboardApiService} from "../../../../shared/services/api/dashboard-api.service";
import {AccountSettingsFormModel} from "../../models/account-settings-form.model";
import { OAuthService } from "angular-oauth2-oidc";

@Component({
  selector: 'app-account-settings',
  templateUrl: './account-settings.component.html',
  styleUrls: ['./account-settings.component.scss'],
})
export class AccountSettingsComponent implements OnInit {
  public accountSettingsForm: FormGroup;
  public passwordInputType: string = 'password';
  public newPasswordInputType = 'password';
  public phoneMask = phoneMask;
  public isChangePasswordActive: boolean = false;

  private mockData: any = {
    firstName: 'John',
    lastName: 'Doe',
    email: 'jdoe@example.com',
    username: 'john74',
    contactMethod: 'contact-phone',
    phoneNumber: '(123) 555-1234',
    language: 'en',
    emailPreference: 'no-email',
  }
  constructor (
    private cdRef: ChangeDetectorRef,
    private toastr: ToastrService,
    private oAuthService: OAuthService,
    private formService: FormService,
    private router: Router,
    private dashboardApiService: DashboardApiService,
  ) {}

  ngOnInit() {
    this.oAuthService.getIdentityProfile().subscribe(user => console.log('User: ', user));
    //this.dashboardApiService.getUserProfile().subscribe(user => console.log('User Data: ', user ))
    this.accountSettingsForm = this.formService.getFormByModel(new AccountSettingsFormModel(this.mockData));
    console.log(this.accountSettingsForm);
  }

  public updateAccountSettings() {
    this.dashboardApiService.updateAccount(this.accountSettingsForm.value);
  }

  public toggleInputType(fieldName) {
    if (fieldName === 'password') {
      this.passwordInputType = (this.passwordInputType === 'text') ? 'password' : 'text';
    }
    if (fieldName === 'newPassword') {
      this.newPasswordInputType = (this.newPasswordInputType === 'text') ? 'password' : 'text';
    }
  }

  public onInputChange(event, popOver): void {
    if (event.target.value && !popOver.isOpen()) {
      popOver.open();
      return;
    } else if (
        (!event.target.value && popOver.isOpen()) ||
        (
          event.target.value &&
          popOver.isOpen() &&
          !this.accountSettingsForm.get('newPassword').errors
        )
      ) {
      popOver.close();
      return;
    }
  }

  public isFieldValid(type): boolean {
    const fieldName = (type === 'minlength') ? type : `${type}Validator`;
    return (
      !(this.accountSettingsForm.get('newPassword').errors &&
        this.accountSettingsForm.get('newPassword').errors[fieldName])
    );
  }

  public togglePasswordChange(): void {
    if (this.isChangePasswordActive) {
      this.accountSettingsForm.get('password').reset();
      this.accountSettingsForm.get('newPassword').reset();
    }
    this.isChangePasswordActive = !this.isChangePasswordActive;
  }

  public cancelEdit() {
    this.router.navigate(['/dashboard']);
  }
}
