import { Injectable } from '@angular/core';
import { FormArray, FormBuilder, ValidatorFn, Validators } from '@angular/forms';

@Injectable()
export class FormService {
  constructor(private fb: FormBuilder) {}

  getFormByModel(model: any, validators: ValidatorFn[] = []) {
    return this.fb.group(this.generateForm(model), { validator: validators });
  }

  private generateForm(model) {
    const validationRules = model.validationRules;
    delete model.validationRules;
    const formModel = Object.assign({}, model);
    const formGroup = {};

    Object.keys(formModel).forEach(name => {
      const control = formModel[name];
      if (
        control &&
        control.hasOwnProperty('control_type') &&
        control.control_type === 'formArray'
      ) {
        formGroup[name] = this.fb.array([]);

        return true;
      }

      if (
        typeof control === 'object' &&
        control != null &&
        FormArray.prototype === control.__proto__
      ) {
        formGroup[name] = Array.isArray(control) ? [control] : control;
      } else {
        formGroup[name] = [control, this.getValidationByName(validationRules, name)];
      }
    });

    return formGroup;
  }

  private getValidationByName(rules: Object, name: string): ValidatorFn[] {
    const validation: ValidatorFn[] = [];

    if (typeof rules !== 'undefined' && typeof rules[name] !== 'undefined') {
      rules[name].forEach(rule => {
        validation.push(this.getValidationByRule(rule));
      });
    }
    return validation;
  }

  private getValidationByRule(rule: any) {
    let validationRule = rule;

    if (typeof rule === 'string') {
      // validation required, etc
      validationRule = Validators[rule];
    } else if (rule instanceof Array) {
      validationRule = Validators[rule[0]](rule[1]);
    }
    return validationRule;
  }
}
