import { Injectable } from "@angular/core";
import { AppSettings } from "projects/web/src/environments/appsettings";

interface Scripts {
  name: string;
  src: string;
}

export const ScriptStore: Scripts[] = [
  { name: 'convey', src: 'http://cdn.conveythis.com/javascript/30/conveythis.js' },
  { name: 'translate', src: 'http://cdn.conveythis.com/javascript/30/translate.js' },
  { name: 'googleApi', src: 'https://maps.google.com/maps/api/js?key=' + AppSettings.google_api_key + '&libraries=places&language=en-US'}
];

declare var document: HTMLDocument;

@Injectable({
  providedIn: 'root'
})
export class DynamicScriptLoaderService {

  private scripts: any = {};

  constructor() {
    ScriptStore.forEach((script: any) => {
      this.scripts[script.name] = {
        loaded: false,
        src: script.src
      };
    });
  }

  load(...scripts: string[]) {
    const promises: any[] = [];
    scripts.forEach((script) => promises.push(this.loadScript(script)));
    return Promise.all(promises);
  }

  loadScript(name: string) {
    return new Promise((resolve, reject) => {

      if(name === 'gooogleApi')
        this.removeGoogleMapsApiScript();

      if(this.scripts[name].loaded){
        let htmlScript = document.getElementById(name + 'Id');
        if(htmlScript) htmlScript.remove();
      }

      if (!this.scripts[name].loaded) {
        //load script
        let script = document.createElement('script') as any;
        script.id = name + 'Id'
        script.type = 'text/javascript';
        script.src = this.scripts[name].src;
        if (script.readyState) {  //IE
            script.onreadystatechange = () => {
                if (script.readyState === "loaded" || script.readyState === "complete") {
                    script.onreadystatechange = null;
                    this.scripts[name].loaded = true;
                    resolve({script: name, loaded: true, status: 'Loaded'});
                }
            };
        } else {  //Others
            script.onload = () => {
                this.scripts[name].loaded = true;
                resolve({script: name, loaded: true, status: 'Loaded'});
            };
        }
        script.onerror = (error: any) => resolve({script: name, loaded: false, status: 'Loaded'});
        document.getElementsByTagName('body')[0].appendChild(script);
      } else {
        resolve({ script: name, loaded: true, status: 'Already Loaded' });
      }
    });
  }

  removeGoogleMapsApiScript(){
    let scripts = Array.from(document.getElementsByTagName('script'));

    let targetItem = scripts.filter(r => r.getAttribute('src').includes(AppSettings.google_api_key));
    if(targetItem) targetItem.forEach(item => item.remove());
  }

}
