  hideIncomeAssestsTab: boolean;
  unsubscribed = new Subject<void>();
  filteredInsurance: Observable<string[]>;
  filteredLanguage: Observable<string[]>;
  insuranceControl = new FormControl();
  insuranceCheck = new FormControl(null);
  languageSwitch = new FormControl();
  languageControl = new FormControl();
  ccv = new FormControl();
  finalBGQ = new FormControl();

  @ViewChild('tabMember') tabMember: MatTabGroup;
  docsCompleted: boolean = false;

  constructor(private configSvc: ConfigValueService, private householdSvc: HouseholdService, private toastr: ToastrService, private cdRef: ChangeDetectorRef) { }

  ngOnInit() {

    this.configSvc.getAllConfigTypes('RelationshipType', 'Race', 'EnrollmentStatus', 'Pronoun', 'Ethnicity', 'Gender').subscribe(r => {
      this.configValueOptions = r;
    });

    this.filteredInsurance = this.insuranceControl.valueChanges
      .pipe(
        startWith(''),
        map(value => this._filterI(value))
      );

      this.filteredLanguage = this.languageControl.valueChanges
      .pipe(
        startWith(''),
        map(value => this._filterL(value))
      );

    this.mainForm = new FormGroup({});
    this.mainForm.valueChanges.pipe(debounceTime(250)).subscribe(values => {
      if (values.personalDetailsForm.minValidGroup.relationshipTypeId) {
        let relationshipId = parseInt(values.personalDetailsForm.minValidGroup.relationshipTypeId);
        this.hideIncomeAssestsTab = relationshipId == RelationshipTypeEnum.UnbornChild || relationshipId == RelationshipTypeEnum.LineInAide;
        this.cdRef.detectChanges();
      }
    });

    this.householdSvc.memberProfileStep$.subscribe(changedStep => this.onChangeStep(changedStep));
  }

  submit() {
    this.onGotoHousehold()
    this.toastr.success('Application Submitted');
  }


  private _filterI(value: string): string[] {
    const filterValue = value.toLowerCase();

    return this.insuranceList.filter(option => option.toLowerCase().includes(filterValue));
  }
 
  private _filterL(value: string): string[] {
    const filterValue = value.toLowerCase();

    return this.languages.filter(option => option.toLowerCase().includes(filterValue));
  }

