import { HouseholdMemberProfile } from '../../models/hh-member-profile.model';
import { Component, OnInit, Input, Output, EventEmitter, OnChanges, SimpleChanges } from '@angular/core';
import { IncomeTypeEnum } from '../../../../shared/enums/applicantEnums';
import { UtilityService } from '../../services/utility/utility.service';
import { MemberEtiquette } from '../../models/member-etiquette.model';

@Component({
  selector: 'member-summary-container',
  templateUrl: './member-summary-container.component.html',
  styleUrls: ['./member-summary-container.component.scss']
})
export class MemberSummaryContainerComponent implements OnInit {

  @Input() household:any; 
  @Input('householdMembers') viewModel: HouseholdMemberProfile[];
  @Input() relationshipOptions: any = [];
  @Output() EditMemberEvent: EventEmitter<HouseholdMemberProfile> = new EventEmitter();
  @Output() handleAddIncome: EventEmitter<any> = new EventEmitter<any>();
  memberEttiquette: MemberEtiquette;

  constructor(private utilsvc: UtilityService) { }

  ngOnInit() {
    console.log("Household Details", this.household);
    this.viewModel = this.viewModel.filter((listing: any) => listing.firstName != 'Jane' && listing.firstName != 'Richard');
  }

  //Communicate edit member to parent component
  transitionToMemberEdit(targetMember:HouseholdMemberProfile):void{
    this.EditMemberEvent.emit(targetMember);
  }

  onAddHouseholdMember() {
    const NewMember: HouseholdMemberProfile = new HouseholdMemberProfile();
    this.utilsvc.setCurrentMemberQuestionairreEtiquette(NewMember, this.relationshipOptions);
    this.EditMemberEvent.emit(NewMember);
  }

  onAddIncome(type) {
    this.handleAddIncome.emit(type);
    console.log(this.constructor.name);
  }

}
