import {Inject, Injectable} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import 'rxjs/add/operator/map';
import {DOCUMENT} from '@angular/common';


import {environment} from '../../../environments/environment';
import {environment as ENV_DEV} from '../../../environments/environment.dev';
import {environment as ENV_QA} from '../../../environments/environment.hpdqa';
import {environment as ENV_UAT} from '../../../environments/environment.hpduat';
import {environment as ENV_CON} from '../../../environments/environment.hpdcon';
import {environment as ENV_STG} from '../../../environments/environment.hpdstg';
import {environment as ENV_PROD} from '../../../environments/environment.prod';
import {shareReplay} from "rxjs/operators";

interface IAppConfigSetting {
  identityApiUri: string,
  publicApiUri: string
}

@Injectable()
export class ConfigurationService {
  private configuration: IServerConfiguration;
  url: string;
  appConfigSetting: IAppConfigSetting = <IAppConfigSetting>{};

  constructor(
    public http: HttpClient,
    @Inject(DOCUMENT) private document
  ) {
  }

  addAppConfigSetting(appConfigSetting: IAppConfigSetting) {
    this.appConfigSetting = appConfigSetting
  }

  loadConfig() {

    environment.publicApiUri = this.appConfigSetting.publicApiUri;
    environment.identityApiUri = this.appConfigSetting.identityApiUri;
  /*
    switch (window.location.hostname) {
      case 'msswvw-hpdhc2v2.csc.nycnet': {
        environment.adminApiUri = ENV_STG.adminApiUri;
        environment.identityApiUri = ENV_STG.identityApiUri;
        break;
      }
      case 'mspwvw-hpdhcc01': {
        environment.publicApiUri = ENV_CON.publicApiUri;
        environment.identityApiUri = ENV_CON.identityApiUri;
        break;
      }
      case 'mspwva-hpdhc202': {
        environment.publicApiUri = ENV_UAT.publicApiUri;
        environment.identityApiUri = ENV_UAT.identityApiUri;
        break;
      }
      case 'mspwva-hpdhca01': {
        environment.publicApiUri = ENV_QA.publicApiUri;
        environment.identityApiUri = ENV_QA.identityApiUri;
        break;
      }
      case 'uat.hpdweb.prutechlab.com': {
        environment.publicApiUri = ENV_DEV.publicApiUri;
        environment.identityApiUri = ENV_DEV.identityApiUri;
        break;
      }
      default:
      case "localhost": {
        environment.publicApiUri = this.appConfigSetting.publicApiUri;
        environment.identityApiUri = this.appConfigSetting.identityApiUri;
        break;
      }
    }
    */

    const CACHE_SIZE = 1;
    const getIServerConfiguration = this.http
      .get<IServerConfiguration>(environment.identityApiUri + 'Config/FetchPublicClientConfig')
      .pipe(shareReplay(CACHE_SIZE));
    getIServerConfiguration.subscribe(
      result => {
        this.configuration = <IServerConfiguration>result;
      },
      error => console.error(error)
    );
    return getIServerConfiguration;
  }

  get issuer() {
    return this.configuration.issuer;
  }

  get logoutUrl() {
    return this.configuration.logoutUrl;
  }

  get postLogoutRedirectUri() {
    return this.configuration.postLogoutRedirectUri;
  }

  get redirectUri() {
    return this.configuration.redirectUri;
  }

  get requireHttps() {
    return this.configuration.requireHttps == true;
  }

  get clientId() {
    return this.configuration.clientId;
  }

  get scope() {
    return this.configuration.scope;
  }

  get getCustomRolesUri() {
    return this.configuration.customrolesUri;
  }

  get getIdentityManagerUri() {
    return this.configuration.identityManagerUri;
  }

  //get adminApiUri() {
  //  return this.configuration.adminApiUri;
  //}

  //get identityApiUri() {
  //  return this.configuration.identityApiUri;
  //}
}

export interface IServerConfiguration {
  issuer: string;
  logoutUrl: string;
  postLogoutRedirectUri: string;
  redirectUri: string;
  requireHttps: boolean;
  clientId: string;
  scope: string;

  customrolesUri: string;
  identityManagerUri: string;

  //adminApiUri: string;
  //identityApiUri: string;
}

export function configurationFactory(config: ConfigurationService): () => Promise<any> {
  return (): Promise<any> => {
    return new Promise((resolve) => {

      config.http.get("/assets/app-config.json").subscribe((data: IAppConfigSetting) => {
        config.addAppConfigSetting(data);
        config.loadConfig().subscribe(data => {
          resolve()
        });
      })
    });
  };
}
