import { AfterViewInit, Component, HostListener, Input, OnInit, ViewChildren } from '@angular/core';
import { CdkSlider } from './core/cdk-slider';
import { sliderAnimations } from './slider-animations';

/*

// how to use
<app-slider>
  <app-slider-card>
    something
  </app-slider-card>
    <app-slider-card>
     -----------------
  </app-slider-card>
</app-slider>

// with navigation  button
<app-slider>
  <app-slider-card>
       <button sliderPrevious>pre</button>
       <button sliderNext>next</button>
  </app-slider-card>
</app-slider>


// navigation outside slider
<app-slider #sliderId>
  <app-slider-card>
      -------
  </app-slider-card>
</app-slider>

 <button (click)="sliderId.nextCard()">pre</button>
 <button (click)="sliderId.previousCard()">next</button>
*/

@Component({
  selector: 'app-slider',
  templateUrl: 'slider.component.html',
  styleUrls: ['slider.component.scss'],
  animations: [sliderAnimations.horizontalCardTransition]
})
export class SliderComponent extends CdkSlider implements OnInit, AfterViewInit {
  @ViewChildren('sliderWrapper') sliderWrapper;

  constructor() {
    super();
  }

  ngOnInit() {}

  ngAfterViewInit() {
    this.sliderWrapper.forEach(item => {
      // item.nativeElement.style.width = `${this._getSlideWidth()}%`;
      item.nativeElement.style.width = `${this._getSlideWidth()}%`;
    });

    this.onResize();
  }

  @HostListener('window:resize', ['$event'])
  onResize() {
    if (this.sourceDisplayContCardValue == 1) {
      return;
    }

    const width = window.innerWidth;
    if (width > 992) {
      this.displayContCard = this.sourceDisplayContCardValue;
    } else if (width > 576) {
      this.displayContCard = 2;
    } else {
      this.displayContCard = 1;
    }

    this.sliderWrapper.forEach(item => {
      item.nativeElement.style.width = `${this._getSlideWidth()}%`;
    });
  }

  getValue(value) {
    console.log(value);
    return value;
  }
}
