import { Component, OnInit, Input, ViewChild, ElementRef } from '@angular/core';
import { Router, ActivatedRoute, NavigationEnd, Params, PRIMARY_OUTLET } from '@angular/router';

@Component({
  selector: 'title-bar',
  templateUrl: 'title-bar.component.html',
  styleUrls: ["title-bar.component.scss"]
})
export class TitleBarComponent implements OnInit {
  @Input() title: string = "";

  @ViewChild('contextMenu') elMenu: ElementRef;
  @ViewChild('menuContainer') elMenuContainer: ElementRef;
  @ViewChild('breadcrumbs') elBreadcrumbs: ElementRef;
    
  constructor(private activatedRoute: ActivatedRoute, private router: Router) {
    
  }
 
  ngOnInit() {
   
  }

  ngAfterViewInit(): void {
    // hide context menu if no content
    //console.log("Context Menu: ", this.elMenu);
    // TODO: fix this logic
    if (this.elMenu && this.elMenu.nativeElement && this.elMenu.nativeElement.children.length > 0) {
      this.elMenuContainer.nativeElement.style.display = 'block';
    }



  }
}
