import { Component, OnInit, Input, EventEmitter, Output, OnChanges, ChangeDetectorRef } from '@angular/core';

@Component({
  selector: 'app-location-list',
  templateUrl: './location-list.component.html',
  styleUrls: ['./location-list.component.scss']
})
export class LocationListComponent implements OnInit {

  constructor(private cdRef: ChangeDetectorRef) { }

  @Input() locationList: any = [];
  @Input() availableLocations: any = [];
  @Input() selectedLocationId: number;
  @Output() locationSelected: EventEmitter<any> = new EventEmitter();

  

  ngOnInit() {
  }


  onClick(index) {
    this.locationSelected.emit(index);
    
  }
}
