import { UtilityService } from './../../services/utility/utility.service';
import { HouseholdMemberProfile } from '../../models/hh-member-profile.model';
import { FormControl, FormGroup, Validators, FormArray } from "@angular/forms";
import { IncomeTypeEnum } from 'projects/web/src/app/shared/enums/applicantEnums';
import { HouseholdMemberIncome, HouseholdMemberIncomeTaxFilingTypeModel } from '../../models';
import { isArray } from 'ngx-bootstrap';
import { CurrencyPipe } from '@angular/common';
import { CustomValidators } from 'projects/web/src/app/shared/custom-validation/custom-validation';

export class IncomeSourceFormConstructor {

  localFormGroup: FormGroup;

  incomeType: FormControl;
  employerName: FormControl;
  startDate: FormControl;
  isCityEmployee: FormControl;
  isLotteryAffiliated: FormControl;
  filedAnnualIncomeAmount: FormControl;
  currentIncomeAmount: FormControl;
  currentIncomeFreqType: FormControl;
  profession: FormControl;
  currentNetIncomeAmount: FormControl;
  taxFilingOptions: FormControl;
  taxFilingInfo: FormArray;
  subTypeId: FormControl;
  otherSubType:FormControl;
  isW2: FormControl;
  w2AmountsGroup: FormGroup;
  w2Box1: FormControl;
  w2Box4: FormControl;
  w2Box5: FormControl;
  hasIncomeChanged: FormControl;

  minDate: Date = new Date(1900, 0, 1);

  minRequirementList = [
    'incomeType',
    'employerName',
    'currentIncomeAmount',
    'currentIncomeFreqType',
    'startDate',
    'taxFilingInfo'
  ]

  mapFormToModel(profile: HouseholdMemberProfile, currentIncome?: HouseholdMemberIncome) {
    let model = new HouseholdMemberIncome();

    let values = this.localFormGroup.value;

    model.householdMmbrPrflId = profile.householdMemberProfileId;
    model.householdMemberIncomeId = currentIncome ? currentIncome.householdMemberIncomeId : 0;
    model.currentIncomeAmount = values.currentIncomeAmount ? parseFloat(values.currentIncomeAmount.replace(/[^0-9\.]+/g, "")) : null;
    model.currentIncomeFrequencyTypeId = values.currentIncomeFreqType ? parseInt(values.currentIncomeFreqType) : null;
    model.currentNetIncomeAmount = values.currentNetIncomeAmount || null;
    model.employerName = values.employerName || null;
    model.filedAnnualIncomeAmount = values.filedAnnualIncomeAmount || null;
    model.incomeSubTypeId = values.subTypeId || null;
    model.incomeTypeId = values.incomeType || null;
    model.isCityEmployee = values.isCityEmployee !== undefined ? values.isCityEmployee : null;
    model.isLotteryAffiliated = values.isLotteryAffiliated !== undefined ? values.isLotteryAffiliated : null;
    model.isW2 = values.isW2 !== undefined ? values.isW2 : null;
    model.otherSubType = values.otherSubType !== undefined ? values.otherSubType : null;
    model.profession = values.profession || null;
    model.startDate = values.startDate || null;
    model.isIncomeChanged = values.hasIncomeChanged !== undefined ? values.hasIncomeChanged : null;

    if (model.isW2) {
      model.w2Box1 = values.w2AmountsGroup.w2Box1 ? Number(values.w2AmountsGroup.w2Box1.replace(/[^0-9\.]+/g, "")) : null;
      model.w2Box4 = values.w2AmountsGroup.w2Box4 ? Number(values.w2AmountsGroup.w2Box4.replace(/[^0-9\.]+/g, "")) : null;
      model.w2Box5 = values.w2AmountsGroup.w2Box5 ? Number(values.w2AmountsGroup.w2Box5.replace(/[^0-9\.]+/g, "")) : null;
      let maxTotal = Math.max(model.w2Box1, model.w2Box4, model.w2Box5);
      model.filedAnnualIncomeAmount = isNaN(maxTotal) ? null : maxTotal;
      model.currentNetIncomeAmount = model.isIncomeChanged ? model.currentNetIncomeAmount : model.filedAnnualIncomeAmount;
    }

    model.householdMemberIncomeTaxFilingTypes = [];
    if (values.taxFilingInfo && isArray(values.taxFilingInfo)) {
      values.taxFilingInfo.forEach(filing => {
        let taxFiling = new HouseholdMemberIncomeTaxFilingTypeModel();
        let currentTaxFiling = currentIncome ? currentIncome.householdMemberIncomeTaxFilingTypes.find(r => r.taxFilingTypeId === filing.taxTypeId) : null;
        taxFiling.householdMmbrIncomeId = currentIncome ? currentIncome.householdMemberIncomeId : 0;
        taxFiling.hhMmbrIncomeTaxFilingTypeId = currentTaxFiling ? currentTaxFiling.hhMmbrIncomeTaxFilingTypeId : 0;
        taxFiling.amount = (!filing.amount) ? 0 : filing.amount.replace(/[^0-9\.]+/g, "");
        taxFiling.taxFilingTypeId = filing.taxTypeId;
        model.householdMemberIncomeTaxFilingTypes.push(taxFiling);
      });


      let amountsList = model.householdMemberIncomeTaxFilingTypes.map(r => parseFloat(r.amount.toString()));
      let sum = (values.taxFilingInfo.length > 0) ? this.utilSvc.calcTotalFromNumbers(amountsList) : 0;

      model.filedAnnualIncomeAmount = sum;
      model.currentNetIncomeAmount = model.isIncomeChanged ? model.currentNetIncomeAmount : sum;
    }

    //this would be the only selected tax filing type if selected
    if (values.taxFilingOptions && values.taxFilingOptions[0] === '4') {
      let notFiled = new HouseholdMemberIncomeTaxFilingTypeModel();
      let currentTaxFiling = currentIncome ? currentIncome.householdMemberIncomeTaxFilingTypes.find(r => r.taxFilingTypeId === 4) : null;

      if (currentIncome) {
        currentIncome.householdMemberIncomeTaxFilingTypes.forEach(income => {
          income.isActive = false;
        });
      }


      notFiled.amount = null;

      notFiled.hhMmbrIncomeTaxFilingTypeId = currentTaxFiling ? currentTaxFiling.hhMmbrIncomeTaxFilingTypeId : 0;
      notFiled.householdMmbrIncomeId = currentIncome ? currentIncome.householdMemberIncomeId : 0;
      notFiled.taxFilingTypeId = 4;

      if (currentIncome && currentIncome.householdMemberIncomeTaxFilingTypes.length > 0) {
        let currIndex = currentIncome.householdMemberIncomeTaxFilingTypes.findIndex(r => r.hhMmbrIncomeTaxFilingTypeId === 4);
        model.householdMemberIncomeTaxFilingTypes = [...currentIncome.householdMemberIncomeTaxFilingTypes];
        if (currIndex !== -1) {
          model.householdMemberIncomeTaxFilingTypes[currIndex] = notFiled;
        }
        else {
          model.householdMemberIncomeTaxFilingTypes.push(notFiled);
        }

      } else model.householdMemberIncomeTaxFilingTypes.push(notFiled)
    }

    if (model.currentIncomeAmount && model.currentIncomeFrequencyTypeId) {
      let total = this.utilSvc.calcNetIncomeByFrequency(model.currentIncomeFrequencyTypeId, model.currentIncomeAmount);
      model.filedAnnualIncomeAmount = total;
      model.currentNetIncomeAmount = model.isIncomeChanged ? model.currentNetIncomeAmount : total;
    }

    return model;
  }

  constructor(public cp: CurrencyPipe, public utilSvc: UtilityService) {
    this.localFormGroup = new FormGroup({});
  }

  createLocalForm() {
    const incomeType = this.incomeType = new FormControl(null, [Validators.required]);
    const employerName = this.employerName = new FormControl(null, [Validators.required, CustomValidators.noWhitespaceValidator]);
    const currentIncomeAmount = this.currentIncomeAmount = new FormControl(null, [Validators.required]);
    const currentIncomeFreqType = this.currentIncomeFreqType = new FormControl(null, [Validators.required]);
    const isCityEmployee = this.isCityEmployee = new FormControl(null);
    const isLotteryAffiliated = this.isLotteryAffiliated = new FormControl(null);
    const filedAnnualIncomeAmount = this.filedAnnualIncomeAmount = new FormControl(null);
    const profession = this.profession = new FormControl(null);
    const currentNetIncomeAmount = this.currentNetIncomeAmount = new FormControl(null);

    this.localFormGroup = new FormGroup({
      incomeType,
      employerName,
      isCityEmployee,
      isLotteryAffiliated,
      filedAnnualIncomeAmount,
      currentIncomeAmount,
      currentIncomeFreqType,
      profession,
      currentNetIncomeAmount
    });

  }

  createIncomeBasedForm(targetType: number) {
    switch (targetType) {
      case IncomeTypeEnum.Employment: {
        console.log('Create Employment Form');
        this.buildEmployerForm();
        //AddAnotherWorkForEmployer
        break;
      }
      case IncomeTypeEnum.SelfEmployment: {
        console.log('Create Self Employment Form');
        this.buildSelfEmploymentForm();
        //AddAnotherSelfEmployment
        break;
      }
      case IncomeTypeEnum.GovernmentBenefits:
      case IncomeTypeEnum.RetirementDisability:
      case IncomeTypeEnum.OtherPeople: {
        console.log('Create SubType Income Form');
        this.buildOtherEmlploymentForm();
        break;
      }
    }
  }

  buildEmployerForm() {
    const incomeType = this.incomeType = new FormControl(null);
    const employerName = this.employerName = new FormControl(null, [Validators.required, CustomValidators.noWhitespaceValidator]);
    const isCityEmployee = this.isCityEmployee = new FormControl(null, [Validators.required]);
    const startDate = this.startDate = new FormControl(null, [Validators.required]);
    const currentNetIncomeAmount = this.currentNetIncomeAmount = new FormControl(null, [Validators.required, CustomValidators.currency]);
    const filedAnnualIncomeAmount = this.filedAnnualIncomeAmount = new FormControl(null);
    const isLotteryAffiliated = this.isLotteryAffiliated = new FormControl(null);
    const isW2 = this.isW2 = new FormControl(null, [Validators.required]);
    const w2Box1 = this.w2Box1 = new FormControl(null, [CustomValidators.currency]);
    const w2Box4 = this.w2Box4 = new FormControl(null, [CustomValidators.currency]);
    const w2Box5 = this.w2Box5 = new FormControl(null, [CustomValidators.currency]);
    const hasIncomeChanged = this.hasIncomeChanged = new FormControl(null);
    const w2AmountsGroup = this.w2AmountsGroup = new FormGroup({ w2Box1, w2Box4, w2Box5 });

    this.localFormGroup = new FormGroup({
      incomeType,
      employerName,
      startDate,
      currentNetIncomeAmount,
      filedAnnualIncomeAmount,
      isCityEmployee,
      isLotteryAffiliated,
      isW2,
      w2AmountsGroup,
      hasIncomeChanged
    });
  }

  hasNoBusinessName:FormControl; //used for self employment only
  buildSelfEmploymentForm() {
    const incomeType = this.incomeType = new FormControl();
    const employerName = this.employerName = new FormControl(null, [Validators.required, CustomValidators.noWhitespaceValidator]);
    const profession = this.profession = new FormControl(null, [Validators.required, CustomValidators.noWhitespaceValidator]);
    const startDate = this.startDate = new FormControl(null, [Validators.required]);
    const taxFilingOptions = this.taxFilingOptions = new FormControl(null, [Validators.required]);
    const taxFilingInfo = this.taxFilingInfo = new FormArray([]);
    const filedAnnualIncomeAmount = this.filedAnnualIncomeAmount = new FormControl(null);
    const currentNetIncomeAmount = this.currentNetIncomeAmount = new FormControl(null, [Validators.required, CustomValidators.currency]);
    const hasIncomeChanged = this.hasIncomeChanged = new FormControl(null);
    const hasNoBusinessName = this.hasNoBusinessName = new FormControl(false);

    this.localFormGroup = new FormGroup({
      incomeType,
      employerName,
      profession,
      startDate,
      taxFilingOptions,
      taxFilingInfo,
      filedAnnualIncomeAmount,
      hasIncomeChanged,
      currentNetIncomeAmount,
      hasNoBusinessName
    });
  }

  buildOtherEmlploymentForm() {
    const incomeType = this.incomeType = new FormControl();
    const subTypeId = this.subTypeId = new FormControl(null, [Validators.required]);
    const otherSubType = this.otherSubType = new FormControl({ value: null, disabled: true }, [Validators.required]);
    const currentNetIncomeAmount = this.currentNetIncomeAmount = new FormControl(null);
    const currentIncomeFreqType = this.currentIncomeFreqType = new FormControl(null, [Validators.required]);
    const currentIncomeAmount = this.currentIncomeAmount = new FormControl(null, [Validators.required, CustomValidators.currency]);

    this.localFormGroup = new FormGroup({
      incomeType,
      subTypeId,
      otherSubType,
      currentNetIncomeAmount,
      currentIncomeAmount,
      currentIncomeFreqType
    });
  }

  removeMemberTaxFilingLineItem(targetTaxType: string) {
    if (targetTaxType !== '4') {
      if (this.taxFilingInfo.controls.length > 0) {
        let selectedTypes = this.taxFilingInfo.controls.map((group: FormGroup) => group.get('taxTypeId').value);
        let index = selectedTypes.indexOf(targetTaxType);
        this.taxFilingInfo.removeAt(index);
        this.taxFilingInfo.updateValueAndValidity();
      }
    }
    else {
      this.removeFormTaxFilings();
    }
  }

  addMemberTaxFilingLineItem(targetTaxFilingId: string) {
    // if there existing selections
    if (this.taxFilingInfo.controls.length > 0) {

      // type 4 was selected, remove all others
      if (targetTaxFilingId === "4") {
        this.taxFilingInfo.controls.splice(0, this.taxFilingInfo.controls.length);
      }
      // another type was selected
      else {
        // but type 4 was already pre-selected, remove it
        const index = this.taxFilingInfo.controls.findIndex(t => t.value.taxTypeId === "4");
        if (index !== -1) this.taxFilingInfo.removeAt(index);
      }

    }

    let taxTypeId = new FormControl(targetTaxFilingId, [Validators.required]);
    let amount = new FormControl(null, [Validators.required, CustomValidators.currency]);
    this.taxFilingInfo.push(new FormGroup({ taxTypeId: taxTypeId, amount: amount }));
    this.taxFilingInfo.updateValueAndValidity();
  }

  private removeFormTaxFilings() {
    if (this.taxFilingInfo.controls.length > 0) {
      this.taxFilingInfo.controls.splice(0, this.taxFilingInfo.controls.length);
      this.taxFilingInfo.updateValueAndValidity();
    }
  }

  anyMinInvalid() {
    if (!this.localFormGroup) return false;
    else {
      let isValid = Object.keys(this.localFormGroup.controls).some(
        controlName => this.minRequirementList.includes(controlName) && this.localFormGroup.controls[controlName].invalid
      );
      return isValid;
    }
  }

  resetForm(incomeType: number) {
    this.localFormGroup.reset();
    this.incomeType.setValue(incomeType);
    if (IncomeTypeEnum.SelfEmployment === incomeType) {
      this.removeFormTaxFilings();
      this.hasNoBusinessName.setValue(false);
    }
  }

  formatCurrency(amount: number) {
    if (!amount) return null;
    let formattedAmount = this.cp.transform(amount, '', '', '1.2');
    return formattedAmount;
  }

  setFormValues(incomeSource: HouseholdMemberIncome) {
    if (!incomeSource.householdMemberIncomeId || incomeSource.householdMemberIncomeId === 0) return;

    let model = incomeSource;

    if (this.taxFilingInfo) {
      let taxIds = model.householdMemberIncomeTaxFilingTypes.map(r => r.taxFilingTypeId.toString());
      this.taxFilingOptions.setValue([...taxIds] || null, { emitEvent: false });
      this.taxFilingOptions.updateValueAndValidity();

      //Add and set values for tax filing types
      model.householdMemberIncomeTaxFilingTypes.forEach(taxFiling => {
        if (taxFiling.taxFilingTypeId !== 4) {
          let taxTypeId = new FormControl(null, [Validators.required]);
          let amount = new FormControl(null, [Validators.required, CustomValidators.currency]);
          taxTypeId.setValue(taxFiling.taxFilingTypeId);
          amount.setValue(this.formatCurrency(taxFiling.amount));
          this.taxFilingInfo.push(new FormGroup({ taxTypeId: taxTypeId, amount: amount }));
        }
      });
    }

    this.currentIncomeAmount && this.currentIncomeAmount.setValue(this.formatCurrency(model.currentIncomeAmount) || null);
    this.currentIncomeFreqType && this.currentIncomeFreqType.setValue(model.currentIncomeFrequencyTypeId || null);
    this.hasIncomeChanged && this.hasIncomeChanged.setValue(model.isIncomeChanged);
    this.currentNetIncomeAmount && this.currentNetIncomeAmount.setValue(this.formatCurrency(model.currentNetIncomeAmount) || null);
    this.employerName && this.employerName.setValue(model.employerName || null);
    this.filedAnnualIncomeAmount && this.filedAnnualIncomeAmount.setValue(model.filedAnnualIncomeAmount || null);
    this.subTypeId && this.subTypeId.setValue(model.incomeSubTypeId || null);
    this.incomeType && this.incomeType.setValue(model.incomeTypeId || null);
    this.isCityEmployee && this.isCityEmployee.setValue(model.isCityEmployee);
    this.isLotteryAffiliated && this.isLotteryAffiliated.setValue(model.isLotteryAffiliated);
    this.isW2 && this.isW2.setValue(model.isW2);
    this.otherSubType && this.otherSubType.setValue(model.otherSubType || null);
    this.profession && this.profession.setValue(model.profession || null);
    this.startDate && this.startDate.setValue((model.startDate) ? new Date(model.startDate) : null);
    this.w2Box1 && this.w2Box1.setValue(this.formatCurrency(model.w2Box1) || null, { emitEvent: false });
    this.w2Box4 && this.w2Box4.setValue(this.formatCurrency(model.w2Box4) || null, { emitEvent: false });
    this.w2Box5 && this.w2Box5.setValue(this.formatCurrency(model.w2Box5) || null, { emitEvent: true });
  }
}
