import { Injectable } from '@angular/core';
import { FormGroup, AbstractControl, Validators, FormControlName, FormArray } from '@angular/forms';
import { createHostListener } from '@angular/compiler/src/core';

@Injectable({
  providedIn: 'root'
})
export class FormHelperService {

  constructor() { }

  clearFormValuesAndValidatorsExcept(formGroup: FormGroup | FormArray, [...exceptions]: string[]) {
    Object.keys(formGroup.controls).forEach(key => {
      if (!exceptions.includes(key)) {
        let frmCtrl = formGroup.get(key);
        if (frmCtrl instanceof FormGroup || frmCtrl instanceof FormArray) {
          this.clearFormValuesAndValidatorsExcept(frmCtrl, exceptions)
        }
        else this.clearControlValueAndValidator(frmCtrl);
      }
    });
    formGroup.updateValueAndValidity();
  }

  clearFormValuesExcept(formGroup: FormGroup | FormArray, [...exceptions]: string[]) {
    Object.keys(formGroup.controls).forEach(key => {
      if (!exceptions.includes(key)) {
        let frmCtrl = formGroup.get(key);
        if (frmCtrl instanceof FormGroup || frmCtrl instanceof FormArray) {
          this.clearFormValuesExcept(frmCtrl, exceptions)
        }
        else this.clearControlValue(frmCtrl);
      }
    });
    formGroup.updateValueAndValidity();
  }

  clearFormValidatorsExcept(formGroup: FormGroup | FormArray, [...exceptions]: string[]) {
    Object.keys(formGroup.controls).forEach(key => {
      if (!exceptions.includes(key)) {
        let frmCtrl = formGroup.get(key);
        if (frmCtrl instanceof FormGroup || frmCtrl instanceof FormArray) {
          this.clearFormValidatorsExcept(frmCtrl, exceptions)
        }
        else this.clearControlValidator(frmCtrl);
      }
    });
    formGroup.updateValueAndValidity();
  }

  clearFormValuesAndValidatorsTarget([...targetCtrls]: AbstractControl[]) {
    targetCtrls.forEach(frmCtrl => {
      if (frmCtrl.validator) {
        let validator = frmCtrl.validator({} as AbstractControl);

        if (validator && validator.required)
          this.clearControlValueAndValidator(frmCtrl);
      } else {
        this.clearControlValue(frmCtrl);
      }
    });
  }

  clearFormValuesTarget([...targetCtrls]: AbstractControl[]) {
    targetCtrls.forEach(frmCtrl => {
      this.clearControlValue(frmCtrl);
    });
  }

  markFormDirtyTouchedExcept(formGroup: FormGroup | FormArray, exceptions: string[] = []) {
    Object.keys(formGroup.controls).forEach(key => {
      if (!exceptions || !exceptions.includes(key)) {
        let frmCtrl = formGroup.get(key);
        if (frmCtrl instanceof FormGroup || frmCtrl instanceof FormArray) {
          this.markFormDirtyTouchedExcept(frmCtrl, exceptions)
        }
        else this.markDirtyTouched(frmCtrl);
      }
    });

    formGroup.updateValueAndValidity();
  }

  markControlsDirtyTouchedTarget(...targetCtrls: AbstractControl[]) {
    targetCtrls.forEach(frmCtrl => {
      this.markDirtyTouched(frmCtrl);
    });
  }

  setFormRequiredExcept(formGroup: FormGroup | FormArray, [...exceptions]: string[]) {
    Object.keys(formGroup.controls).forEach(key => {
      if (!exceptions.includes(key)) {
        let frmCtrl = formGroup.get(key);
        if (frmCtrl instanceof FormGroup || frmCtrl instanceof FormArray) {
          this.setFormRequiredExcept(frmCtrl, exceptions)
        }
        else this.setControlRequired(frmCtrl);
      }
    });
  }

  enableFormControlsTarget(formGroup: FormGroup | FormArray,[...targetCtrls]:string[]){
    Object.keys(formGroup.controls).forEach(key => {
      let frmCtrl = formGroup.get(key);
      if (frmCtrl instanceof FormGroup || frmCtrl instanceof FormArray) {
        this.enableFormControlsTarget(frmCtrl, targetCtrls);
      } else if (targetCtrls.includes(key)) {
        this.enableFormControls([frmCtrl]);
      }
    });
  }

  disableFormControlsExcept(formGroup: FormGroup | FormArray,[...exceptions]:string[]){
    Object.keys(formGroup.controls).forEach(key => {
      if (!exceptions.includes(key)) {
        let frmCtrl = formGroup.get(key);
        if (frmCtrl instanceof FormGroup || frmCtrl instanceof FormArray) {
          this.disableFormControlsExcept(frmCtrl, exceptions)
        }
        else this.disableFormControls([frmCtrl]);
      }
    });
  }

  disableFormControls([...targetCtrls]: AbstractControl[]|FormArray[]){
    targetCtrls.forEach(ctrl => {
      this.setControlDisabled(ctrl);
    })
  }

  enableFormControls([...targetCtrls]: AbstractControl[]){
    targetCtrls.forEach(ctrl => {
      this.setControlEnabled(ctrl);
    });
  }

  setFormDefaultRequired(formGroup: FormGroup | FormArray, [...defaultRequiredCtrls]) {
    Object.keys(formGroup.controls).forEach(key => {
      let frmCtrl = formGroup.controls[key];
      if (frmCtrl instanceof FormGroup || frmCtrl instanceof FormArray) {
        this.setFormDefaultRequired(frmCtrl, defaultRequiredCtrls)
      }
      else {
        if (defaultRequiredCtrls.includes(key)) {
          this.setControlRequired(frmCtrl);
        } else {
          this.clearControlValueAndValidator(frmCtrl);
        }
      }
    });
  }

  setControlsRequired([...targetCtrls]: AbstractControl[]) {
    targetCtrls.forEach(ctrl => this.setControlRequired(ctrl));
  }

  setControlsPristine(...targetCtrls: AbstractControl[]) {
    targetCtrls.forEach(ctrl => this.setPristine(ctrl));
  }

  removeFormControls(formGroup: FormGroup, [...targetCtrls]) {
    targetCtrls.forEach(ctrl => {
      formGroup.removeControl(ctrl);
    });
  }

  private setControlDisabled(ctrl:AbstractControl){
    if(ctrl.enabled){
      ctrl.disable();
      ctrl.updateValueAndValidity();
    }
  }

  private setControlEnabled(ctrl:AbstractControl){
    if(ctrl.disabled){
      ctrl.enable()
      ctrl.updateValueAndValidity();
    }
  }

  private setPristine(ctrl: AbstractControl) {
    ctrl.markAsPristine();
    ctrl.markAsUntouched();
    ctrl.updateValueAndValidity();
  }

  private setControlRequired(ctrl: AbstractControl) {
    if (ctrl.validator) {
      let validator = ctrl.validator({} as AbstractControl);
      if (validator && validator.required) return;
    }

    ctrl.setValidators(Validators.required);
    ctrl.updateValueAndValidity();
  }

  private clearControlValueAndValidator(ctrl: AbstractControl) {
    if (ctrl.validator) {
      let validator = ctrl.validator({} as AbstractControl);
      if (validator && validator.required)
        ctrl.clearValidators();
    }
    ctrl.setValue('');
    ctrl.updateValueAndValidity();
  }

  private clearControlValue(ctrl: AbstractControl) {
    if (ctrl.value) {
      ctrl.setValue('');
      ctrl.updateValueAndValidity();
    }
  }

  private clearControlValidator(ctrl: AbstractControl) {
    if (ctrl.validator) {
      let validator = ctrl.validator({} as AbstractControl);
      if (validator && validator.required) {
        ctrl.clearValidators();
        ctrl.updateValueAndValidity();
      }
    }
  }

  private markDirtyTouched(ctrl: AbstractControl) {
    ctrl.markAsDirty();
    ctrl.markAsTouched();
    ctrl.updateValueAndValidity();
  }
}
