
import { HouseholdService } from './../../services/household/household.service';
import { Component, OnInit, ChangeDetectorRef, ViewChild, Output, EventEmitter, Input } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { MatStepper } from '@angular/material';
import { ToastrService } from 'ngx-toastr';
import { ConfigValueService } from 'projects/web/src/app/shared/services/config-value/config-value.service';
import { Router } from '@angular/router';
import { forkJoin } from 'rxjs';
import { HouseholdRentalAssist } from '../../models/hh-rental-assist.model';
import { Household } from '../../models';
import { LotteryApiService } from '../../../../shared/services/api/lottery-api.service';
import { HouseholdPrefLayoutTypeModel } from '../../models/hh-pref-layout-type.model';
import { HouseholdPrefNeighborhoodModel } from '../../models/hh-pref-neighborhood.model';
import { HouseholdPrefBoroughModel } from '../../models/hh-pref-borough.model';
import { isNull } from 'util';

@Component({
  selector: 'app-household-details',
  templateUrl: './household-details.component.html',
  styleUrls: ['./household-details.component.scss']
})
export class HouseholdDetailsComponent implements OnInit {

  @Input() selectedStepIndex: number = 0;

  mainForm: FormGroup;
  relationShipOptions: any[];
  rentalAssistOptions: any[];
  neighborhoodOptions: any[];
  unitLayoutTypeOptions: any[];
  viewModel: Household;
  isLoading: boolean = true;
  isSaving: boolean = false;


  @Output() handleGotoHousehold: EventEmitter<any> = new EventEmitter<any>();

  @ViewChild('mattySteps') private mattySteps: MatStepper;

  constructor(private householdSvc: HouseholdService,
    private toastr: ToastrService,
    private configValSvc: ConfigValueService,
    private route: Router,
    private cd: ChangeDetectorRef,
    private lotteryApiService: LotteryApiService
  ) { }

  ngOnInit() {
    this.mainForm = new FormGroup({});

    let householdReq = this.householdSvc.getHousehold();

    let configValuesReq = this.configValSvc.getAllConfigTypes('RelationshipType', 'RentalAssistType')

    forkJoin(householdReq,
      this.lotteryApiService.getUnitLayoutTypes(),
      this.lotteryApiService.getNeighborhoods(),
      configValuesReq,
    ).subscribe(([
      householdVm,
      unitLayoutTypesResult,
      neighborhoodResult,
      configVals
    ]) => {
      this.relationShipOptions = configVals.RelationshipType;
      this.rentalAssistOptions = configVals.RentalAssistType;

      this.unitLayoutTypeOptions = unitLayoutTypesResult;
      this.neighborhoodOptions = this.createNeighborhoodsDataModel(neighborhoodResult);

      console.log("relationShipOptions", this.relationShipOptions);
      console.log("rentalAssistOptions", this.rentalAssistOptions);
      console.log("unitLayoutTypeOptions", this.unitLayoutTypeOptions);
      console.log("neighborhoodOptions", this.neighborhoodOptions);
      this.viewModel = householdVm;
      setTimeout(() => {
        this.isLoading = false;
        if(!this.cd['destroyed'])this.cd.detectChanges();
      }, 200);
    });

  }

  createNeighborhoodsDataModel(neighborhoodApiData) {
    return [
      { title: 'Manhattan' },
      { title: 'Bronx' },
      { title: 'Brooklyn' },
      { title: 'Queens' },
      { title: 'Staten Island' },
    ].map((borough: any, index) => {
      borough.id = index + 1;
      borough.subList = neighborhoodApiData
        .filter(n => n.boroughId === borough.id)
        .map(n => {
          n.parentId = borough.id;
          n.title = n.name;
          return n;
        });
      return borough;
    });
  }



  onSaveHousehold(form: FormGroup, goBackToSummary: boolean = true) {
    let updatedHousehold = this.mapFormToHousehold(form);
    updatedHousehold = this.mapHousingChoicesFormToHousehold(<FormGroup>form.get("housingChoicesForm"));
    this.isSaving = true;
    this.householdSvc.updateHousingChoices(updatedHousehold).subscribe(result => {
      this.householdSvc.updateHousehold(updatedHousehold).subscribe(result => {
        //something happens here after successful save of household
        //put in redirect if save and exit
        this.toastr.success("Updated Household Information", "Saved Household");
        this.isSaving = false;
        if (goBackToSummary) {
          this.handleGotoHousehold.emit();
        }
      });
    });

    console.log('this is the new updated householdModel', updatedHousehold);
  }

  onSaveHousingChoices(event) {
    console.log('Housing Choices submission', event.form);

    let updatedHousehold = this.mapHousingChoicesFormToHousehold(event.form);
    this.isSaving = true;

    this.householdSvc.updateHousingChoices(updatedHousehold).subscribe(result => {

      this.householdSvc.updateHousehold(updatedHousehold).subscribe(result => {
        //something happens here after successful save of household
        //put in redirect if save and exit
        this.toastr.success("Updated Household Information", "Saved Household");
        this.isSaving = false;
        if (event.goBackToSummary) {
          this.handleGotoHousehold.emit();
        }
      });
    });

    console.log('this is the new updated householdModel', updatedHousehold);
  }

  private mapFormToHousehold(targetForm: FormGroup) {
    let household = { ...this.viewModel };

    //map address
    let addrForm = targetForm.get("addressForm").value;
    household.noCurrentAddress = addrForm.noCurrentAddress;
    household.address = addrForm.address || null;
    household.apartmentNumber = addrForm.unitNumber || null;
    household.city = addrForm.city || null;
    household.state = addrForm.state || null;
    household.zip = addrForm.zip || null;

    //map permanent address
    if (targetForm.get("permanentAddressForm")) {
      let permanentAddrForm = targetForm.get("permanentAddressForm").value;
      household.permanentAddress = permanentAddrForm.address || null;
      household.permanentApartmentNumber = permanentAddrForm.unitNumber || null;
      household.permanentCity = permanentAddrForm.city || null;
      household.permanentState = permanentAddrForm.state || null;
      household.permanentZip = permanentAddrForm.zip || null;
    }

    let livingSituation = this.mainForm.get('currentLivingSituation').value;

    household.isNychaProperty = livingSituation === 'isNychaProperty';
    household.isNameOnNychaForm = this.mainForm.get('isNameOnNycha').value || null;
    household.isHomeless = livingSituation === 'isHomelessShelter';

    //Add Rental Assist Info
    household.isRentalAssisted = targetForm.value.isRentalAssisted === "true";
    household.householdRentalAssist = [];
    let selectedRentalAssists = targetForm.value.rentalAssistTypes || null;
    if (selectedRentalAssists) {
      let hhRentalAssistTypes = this.rentalAssistOptions
        .filter(r => selectedRentalAssists.includes(r.configTypeId.toString()))
        .map(r => {
          let existing = this.viewModel.householdRentalAssist.find(current => current.rentalAssistTypeId.toString() == r.configTypeId);
          let rat = new HouseholdRentalAssist();
          rat.rentalAssistTypeId = r.configTypeId;
          rat.householdId = existing ? existing.householdId : null;
          rat.householdRentalAssistId = existing ? existing.householdRentalAssistId : null;
          //if other was one of the selected options add the required description
          if (r.name === 'Other')
            rat.otherDescription = targetForm.value.rentalAssistOtherDescription;
          return rat;
        });

      household.householdRentalAssist = hhRentalAssistTypes;
    }

    return household;
  }

  private mapHousingChoicesFormToHousehold(targetForm: FormGroup) {
    let household = { ...this.viewModel };

    if (isNull(targetForm)) return household;

    let housingChoicesFormValues = targetForm.value;

    household.isRerentalInterest = housingChoicesFormValues.isInterestedRerentalsControl === "true" ? true : (housingChoicesFormValues.isInterestedRerentalsControl === "false" ? false : null);
    //household.householdPrefLayoutType = housingChoicesFormValues.preferredLayoutTypesControl;
    //household.householdPrefNeighborhood = housingChoicesFormValues.preferredNeighborhoodsControl;
    household.isPrefUnitAccessible = housingChoicesFormValues.isPreferredElevatorControl === "true" ? true : (housingChoicesFormValues.isPreferredElevatorControl === "false" ? false : null);
    household.isPrefEntranceAccessible = housingChoicesFormValues.isPreferredAccessibleEntranceControl === "true" ? true : (housingChoicesFormValues.isPreferredAccessibleEntranceControl === "false" ? false : null);
    household.isPrefPetFriendly = housingChoicesFormValues.isPreferredPetPolicyControl === "true" ? true : (housingChoicesFormValues.isPreferredPetPolicyControl === "false" ? false : null);

    let selectedLayoutTypes = housingChoicesFormValues.preferredLayoutTypesControl || null;
    if (selectedLayoutTypes) {
      //let hhPrefLayoutTypes = this.unitLayoutTypeOptions
      //  .filter(l => selectedLayoutTypes.includes(l.configTypeId.toString()))
      //  .map(l => {
      //    //let existing = this.viewModel.householdPrefLayoutType.find(current => current.unitLayoutTypeId.toString() == r.configTypeId);
      //    let lt = new HouseholdPrefLayoutTypeModel();
      //    lt.unitLayoutTypeId = l.configTypeId;
      //    lt.householdId = this.viewModel.householdId;
      //    //lt.householdRentalAssistId = existing ? existing.householdRentalAssistId : null;

      //    return lt;
      //  });

      let hhPrefLayoutTypes = selectedLayoutTypes.map(l => {
        //let existing = this.viewModel.householdPrefLayoutType.find(current => current.unitLayoutTypeId.toString() == r.configTypeId);
        let lt = new HouseholdPrefLayoutTypeModel();
        lt.unitLayoutTypeId = l;
        lt.householdId = this.viewModel.householdId;
        //lt.householdRentalAssistId = existing ? existing.householdRentalAssistId : null;

        return lt;
      });

      household.householdPrefLayoutType = hhPrefLayoutTypes;
    }

    let selectedNeighborhoods = housingChoicesFormValues.preferredNeighborhoodsControl || null;
    if (selectedNeighborhoods) {
      let hhPrefNeighborhoods = selectedNeighborhoods.map(n => {
        let pn = new HouseholdPrefNeighborhoodModel();
        pn.neighborhoodId = n.neighborhoodId;
        pn.householdId = this.viewModel.householdId;

        return pn;
      });

      household.householdPrefNeighborhood = hhPrefNeighborhoods;
    }

    let selectedBoroughs = housingChoicesFormValues.preferredBoroughsControl || null;
    if (selectedBoroughs) {
      let hhPrefBoroughs = selectedBoroughs.map(b => {
        let bo = new HouseholdPrefBoroughModel();
        bo.boroughId = b.id;
        bo.householdId = this.viewModel.householdId;

        return bo;
      });

      household.householdPrefBorough = hhPrefBoroughs;
    }

    return household;
  }

  onGotoHousehold() {
    this.handleGotoHousehold.emit();
  }

}
