import { Pipe, PipeTransform } from '@angular/core';

@Pipe({name: 'appSort'})
export class ApplicationSort implements PipeTransform {
  transform(applications: any[], sort: string): any[] {
    switch (true) {
      case sort === 'closingDateAsc':
        return applications.sort(this.sortByClosingDateAsc);
      case sort === 'closingDateDesc':
        return applications.sort(this.sortByClosingDateDesc);
      case sort === 'status':
        return applications.sort(this.sortByStatus);
      default:
        return applications;
    }
  }

  private sortByClosingDateAsc(a, b): number {
    return (b.details.endIn > a.details.endIn)
            ? -1
            : 1;
  };

  private sortByClosingDateDesc(a, b): number {
    return (b.details.endIn > a.details.endIn)
            ? 1
            : -1;
  };

  private sortByStatus(a,b): number {
    const statusMap: any = {
      inProgress: 0,
      actionNeeded: 1,
      accepted: 2,
      rejected: 3,
      inReview: 4,
    };
    return statusMap[a.status] - statusMap[b.status];
  }
}