import { Component, OnInit, ChangeDetectorRef } from '@angular/core';
import { ApplicationsApiService } from '../../../modules/dashboard/services/applications-api.service';

@Component({
  selector: 'app-alerts-display',
  templateUrl: './alerts-display.component.html',
  styleUrls: ['./alerts-display.component.scss']
})
export class AlertsDisplayComponent implements OnInit {

  alerts: any[];

  constructor(
    private applicationsApiService: ApplicationsApiService,
    private cdRef: ChangeDetectorRef,) { }

  ngOnInit() {
    this.getAlerts();
  }

  groupBy = key => array =>
  array.reduce((objectsByKeyValue, obj) => {
    const value = obj[key];
    objectsByKeyValue[value] = (objectsByKeyValue[value] || []).concat(obj);
    return objectsByKeyValue;
  }, {});

  getAlerts() {
    this.applicationsApiService.getUnreadComments().subscribe(result => console.log(result));
    this.applicationsApiService.getMyAlerts().subscribe(result => {
      const groupByLotteryId = this.groupBy('lotteryId');
      const groupByNoteType = this.groupBy('noteType');
      this.alerts = Object.values(groupByLotteryId(result)).map(lottery => Object.values(groupByNoteType(lottery)));
      console.log(this.alerts);
      if (!this.cdRef['destroyed']) this.cdRef.detectChanges();
    })
  }
}
