import { HouseholdMemberProfile } from '../../models/hh-member-profile.model';
import { FormControl, FormGroup, Validators } from "@angular/forms";
import { flNameValidator } from 'projects/web/src/app/core/modules/form/validators/name.validator';
import { CustomValidators } from '../../../../shared/custom-validation/custom-validation';

export class PersonalDetailsFormConstructor {

  localFormGroup: FormGroup;
  minValidGroup: FormGroup;

  firstName: FormControl;
  lastName: FormControl;
  relationshipTypeId: FormControl;
  otherRelationship: FormControl;
  dateOfBirth: FormControl;
  expectedDueDate: FormControl;
  isCurrentStudent: FormControl;
  enrollmentStatus: FormControl;
  expectedGraduationDate: FormControl;
  gender: FormControl;
  otherGender: FormControl;
  ssn: FormControl;
  tin: FormControl;
  userRace: FormControl;
  userRaceOther: FormControl;
  ethnicity: FormControl;
  isDisabled: FormControl;
  isVeteran: FormControl;
  isEmancipatedMinor: FormControl;
  prefPronounId: FormControl;
  isMobilityDisabled: FormControl;
  isVisionHearingDisabled: FormControl;
  isAccommodationNeeded: FormControl;
  isExtraBedroomNeeded: FormControl;
  otherAccommodation: FormControl;
  otherPrefPronoun: FormControl;

  minRequirementList = [
    'firstName',
    'lastName',
    'relationshipTypeId',
    'isCurrentStudent',
    'gender',
    'userRace',
    'ethnicity',
    'isVeteran',
    'dateOfBirth',
    'prefPronounId',
    'isDisabled'
  ]

  constructor() {
    this.createLocalForm();
  }

  createLocalForm() {

    const firstName = this.firstName = new FormControl('', [flNameValidator, CustomValidators.noWhitespaceValidator]);
    const lastName = this.lastName = new FormControl(null, [flNameValidator, CustomValidators.noWhitespaceValidator]);
    const relationshipTypeId = this.relationshipTypeId = new FormControl(null, []);
    const isCurrentStudent = this.isCurrentStudent = new FormControl(null, []);
    const gender = this.gender = new FormControl({ value: null, disabled: true }, []);
    const userRace = this.userRace = new FormControl('');
    const userRaceOther = this.userRaceOther = new FormControl({ value: '', disabled: true }, [CustomValidators.noWhitespaceValidator]);
    const ethnicity = this.ethnicity = new FormControl('', []);
    const isVeteran = this.isVeteran = new FormControl(null, []);
    const dateOfBirth = this.dateOfBirth = new FormControl(null, []);    
    const prefPronounId = this.prefPronounId = new FormControl(null, []);
    const isDisabled = this.isDisabled = new FormControl(null, []);
    const otherRelationship = this.otherRelationship = new FormControl({ value: '', disabled: true }, [CustomValidators.noWhitespaceValidator]);
    const expectedDueDate = this.expectedDueDate = new FormControl({ value: null, disabled: true }, []);
    const enrollmentStatus = this.enrollmentStatus = new FormControl({ value: null, disabled: true }, []);
    const expectedGraduationDate = this.expectedGraduationDate = new FormControl({ value: null, disabled: true }, []);
    const otherGender = this.otherGender = new FormControl({ value: '', disabled: true }, [CustomValidators.noWhitespaceValidator]);
    const ssn = this.ssn = new FormControl(null, [Validators.minLength(11)]);
    const tin = this.tin = new FormControl(null, [Validators.minLength(10)]);
    const isEmancipatedMinor = this.isEmancipatedMinor = new FormControl({ value: null, disabled: true }, []);
    const isMobilityDisabled = this.isMobilityDisabled = new FormControl({ value: null, disabled: true }, []);
    const isVisionHearingDisabled = this.isVisionHearingDisabled = new FormControl({ value: null, disabled: true }, []);
    const isAccommodationNeeded = this.isAccommodationNeeded = new FormControl({ value: null, disabled: true }, []);
    const isExtraBedroomNeeded = this.isExtraBedroomNeeded = new FormControl({ value: null, disabled: true }, []);
    const otherPrefPronoun = this.otherPrefPronoun = new FormControl({ value: null, disabled: true }, [CustomValidators.noWhitespaceValidator]);
    const otherAccommodation = this.otherAccommodation = new FormControl({ value: '', disabled: true }, [CustomValidators.noWhitespaceValidator]);

    const minValidGroup = this.minValidGroup = new FormGroup({
      firstName,
      lastName,
      relationshipTypeId,
      otherRelationship,
      ssn,
      tin,
      userRace
    })

    this.localFormGroup = new FormGroup({
      minValidGroup,
      // dateOfBirth,
      isCurrentStudent,
      enrollmentStatus,
      // expectedGraduationDate,
      gender,
      userRace,
      userRaceOther,
      ethnicity,
      isVeteran,
      isEmancipatedMinor,
      otherGender,
      prefPronounId,
      isDisabled,
      isMobilityDisabled,
      isVisionHearingDisabled,
      isAccommodationNeeded,
      isExtraBedroomNeeded,
      otherPrefPronoun,
      otherAccommodation
    });

  }

  setDefaultValidations() {
    this.setFirstLastNameValidation(true);
    this.relationshipTypeId.enable();
    this.isCurrentStudent.enable();
    this.gender.enable();
    this.userRace.enable();
    this.ethnicity.enable();
    this.isVeteran.enable();
    this.dateOfBirth.enable();
    this.prefPronounId.enable();
    this.isDisabled.enable();
    this.ssn.enable();
    this.tin.enable();
  }

  setFirstLastNameValidation(isRequired: boolean = true) {
    let setValidators = isRequired ? [flNameValidator] : [flNameValidator]
    this.firstName.setValidators(setValidators);
    this.firstName.updateValueAndValidity();
    this.lastName.setValidators(setValidators);
    this.lastName.updateValueAndValidity();
  }
}
