import {Validators} from '@angular/forms';
import {CommonFormConstructor} from '../../../core/modules/form/common-form-constructor';
import {uppercaseValidator} from '../../../core/modules/form/validators/uppercase.validator';
import {lowercaseValidator} from '../../../core/modules/form/validators/lowercase.validator';
import {digitValidator} from '../../../core/modules/form/validators/digit.validator';
import {specialValidator} from '../../../core/modules/form/validators/special.validator';
import { emailValidator } from '../../../core/modules/form/validators/email.validator';
import { phoneValidator } from '../../../core/modules/form/validators/phone.validator';

const AccountSettingsFormValidation = {
  firstName: [Validators.required],
  lastName: [Validators.required],
  email: [Validators.required, emailValidator],
  username: [Validators.required],
  password: [Validators.required],
  newPassword: [
    Validators.required,
    Validators.minLength(8),
    uppercaseValidator,
    lowercaseValidator,
    digitValidator,
    specialValidator
  ],
  phoneNumber: [phoneValidator],
  alternateEmail: [emailValidator],
  emailPreference: [Validators.required],
};

const AccountSettingsFormValidationMessages = {
  'username.duplicate': 'That username is taken. Please try another one.',
  'email.duplicate': 'This email is already registered',
  'phoneNumber.duplicate': 'This phone is already registered',
  'alternateEmail.duplicate': 'This email is already registered',
};

export class AccountSettingsFormModel extends CommonFormConstructor {

  firstName: string = '';
  lastName: string = '';
  email: string = '';
  username: string = '';
  password: string = '';
  newPassword: string = '';
  contactMethod: string = '';
  phoneNumber: string = '';
  alternateEmail: string = '';
  language: string = '';
  emailPreference: string = '';
  deactivateAccount: boolean = false;

  constructor(data?: any) {
    super();
    this.setData(data);
  }

  get validationRules() {
    return AccountSettingsFormValidation;
  }

  get validationMessages() {
    return AccountSettingsFormValidationMessages;
  }
}
