import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { ApiService } from './api.service';
import { environment } from '../../../environments/environment';

@Injectable()
export class IdentityApiService {
  constructor(private apiService: ApiService) {}

  public get(path: string): Observable<any> {
    return this.apiService.get(`${environment.identityApiUri}${path}`);
  }

  public post(path: string, model: any, options?: any): Observable<any> {
    return this.apiService.post(`${environment.identityApiUri}${path}`, model, options);
  }

  public getAnonymous(path: string): Observable<any> {
    return this.apiService.getAnonymous(`${environment.identityApiUri}${path}`);
  }

  public postAnonymous(path: string, model: any, options?: any): Observable<any> {
    return this.apiService.postAnonymous(`${environment.identityApiUri}${path}`, model, options);
  }

  public put(path: string, model: any): Observable<any> {
    return this.apiService.put(`${environment.identityApiUri}${path}`, model);
  }

  public delete(path: string): Observable<any> {
    return this.apiService.delete(`${environment.identityApiUri}${path}`);
  }
}
