import { Component, OnInit, Input, EventEmitter, Output } from '@angular/core';
import { Router } from '@angular/router';
import { ApplicationsApiService } from '../../services/applications-api.service';
import { addDays } from 'date-fns';
import { FormControl } from '@angular/forms';
import { NoteModel } from 'projects/web/src/app/shared/components/note/note.model';
import { DocService } from 'projects/web/src/app/shared/components/doc/doc.service';

@Component({
  selector: 'todo-list',
  templateUrl: './todo-list.component.html',
  styleUrls: ['./todo-list.component.scss']
})
export class TodoListComponent implements OnInit {

  constructor(private router: Router, private applicationsApiService: ApplicationsApiService, private docService: DocService) { }


  todos: any;
  todoInput: FormControl = new FormControl([""]);
  showCompletedTasks: boolean = false;

  ngOnInit() {
    this.todos = [
      { message: "Finish setting up profile", isCompleted: false },
      { message: "Renew License for 'MY DELI FOOD MARKET INC'", isCompleted: false },
      { message: "Settle inspection violation INS-000553899", isCompleted: false }
    ]
  }

  addNote() {
    let text = this.todoInput.value;
    if (text) {
      let newtodo = {
        message: text,
        isCompleted: false
      };
      this.todos.push(newtodo);
      this.todoInput.setValue("");
    }
  }

  checkTodo(index, event) {
    this.todos[index].isCompleted = event;
  }

  toggleCompletedTasks() {
    this.showCompletedTasks = !this.showCompletedTasks;
  }

}
