import { Injectable } from '@angular/core';
import { IdentityApiService } from '../../../core/services/identity-api.service';
import { RegisterViewModel } from '../models/register-view.model';
import { AccountRecoveryViewModel } from '../models/account-recovery-view.model';
import { ForgotPasswordViewModel } from '../models/forgot-password-view.model';
// import { ResetPasswordComponent } from '../pages/reset-password/reset-password.component';
import { ResetPasswordViewModel } from '../models/reset-password-view.model';
import { ForgotUsernameViewModel } from '../models/forgot-username-view.model';
import { TokenRequestViewModel } from '../models/token-request-view.model';
//import {RegisterViewModel} from "../../user-management/models/register-view.model";

@Injectable()
export class AuthService {
  constructor(private identityApiService: IdentityApiService) { }

  getUserRegister() {
    return this.identityApiService.get('Account/Register');
  }

  userExists(uname: string, email: string, phone: string, email2: string) {
    return this.identityApiService.getAnonymous(
      'Manage/UserExists?uname=' +
      uname +
      '&email=' +
      email +
      '&phone=' +
      phone +
      '&email2=' +
      email2
    );
  }

  saveUser(data: RegisterViewModel) {
    return this.identityApiService.postAnonymous('Account/Register', data);
  }

  confirmRegistration(id: string, code: string) {
    return this.identityApiService.getAnonymous(
      `Account/ConfirmEmail?id=${id}&code=${decodeURIComponent(code)}`
    );
  }

  confirmActivationToken(id: string, token: string) {
    return this.identityApiService.postAnonymous('Account/ConfirmActivationToken', { id: id, token: token });
  }

  confirmTwoFactorAuthentication(id: string, token: string) {
    return this.identityApiService.postAnonymous('Account/ConfirmTwoFactorAuthentication', {
      id: id,
      token: token
    });
  }

  retryActivation(id: string) {
    return this.identityApiService.postAnonymous('Account/RetryActivation?id=' + id, {});
  }

  sendToken(model: TokenRequestViewModel) {
    return this.identityApiService.postAnonymous('Account/SendToken', model);
  }

  recoveryOptions(model: AccountRecoveryViewModel) {
    return this.identityApiService.postAnonymous('Account/RecoveryOptions', model);
  }

  forgotUsername(model: ForgotUsernameViewModel) {
    return this.identityApiService.postAnonymous('Account/ForgotUsername', model);
  }

  forgotPassword(model: ForgotPasswordViewModel) {
    return this.identityApiService.postAnonymous('Account/ForgotPassword', model);
  }

  resetPassword(model: ResetPasswordViewModel, code: string) {
    return this.identityApiService.postAnonymous(`Account/ResetPassword?code=${code}`, model);
  }

  validateResetPasswordLink(userId: string, code: string) {
    return this.identityApiService.getAnonymous(`Account/ValidateResetPasswordLink?userId=${userId}&code=${code}`);
  }
}
