import { Injectable } from '@angular/core';
import { of } from 'rxjs';
// @ts-ignore
import * as faqMockData from './../../../shared/mock-data/faq.mock.json';
import { PublicApiService } from '../../../core/services/public-api.service';

const categories = (<any>faqMockData).categories;
const faqs = (<any>faqMockData).faqs;

@Injectable()
export class FaqService {
  constructor(private publicApiService: PublicApiService) {}

  getCategories() {
    return this.publicApiService.getAnonymous('/Lottery/GetFAQCategories');
  }

  getAllFaqs() {
    return this.publicApiService.getAnonymous('Lottery/GetFaqs');
  }
}
