import { CommonProperties } from "../../../entities/common-fields.model";
import { HouseholdMemberIncome, HouseholdMemberAsset } from "./hh-member-income.model";

export class HouseholdMemberProfile extends CommonProperties{
  householdMemberProfileId:   number  = null;
  firstName:                  string  = null;
  lastName:                   string  = null;
  middleName:                 string  = null;
  dateOfBirth:                string  = null;
  householdId:                number  = null;
  enrollmentStatusId:         string  = null;
  expectedGraduationDate:     string  = null;
  ssn:                        string  = null;
  tin:                        string  = null;
  relationshipTypeId:         number  = null;
  expectedDueDate:            string  = null;
  lotteryApplicationId:       number  = null;
  otherRelationship:          string  = null;
  logNumber:                  number  = null;
  householdSize:              number  = null;
  genderId:                   number  = null;
  otherGender:                string  = null;
  ethnicityId:                number  = null;
  prefPronounId:              number  = null;
  otherPrefPronoun:           string  = null;
  otherAccommodation:          string  = null;


  //bit fields
  isCurrentStudent:         boolean  = null;
  isDisabled:               boolean  = null;
  isVeteran:                boolean  = null;
  isHispanic:               boolean  = null;
  isEmancipatedMinor:       boolean  = null;
  isMobilityDisabled:       boolean  = null;
  isVisionHearingDisabled:  boolean  = null;
  isAccommodationNeeded:    boolean  = null;
  isExtraBedroomNeeded:     boolean  = null;
  isNoIncome:               boolean  = null;
  isNoAssets:               boolean  = null;

  householdMemberRaces:HouseholdMemberRace [] = [];
  householdMemberIncomes: HouseholdMemberIncome[] = [];
  householdMemberAssets: HouseholdMemberAsset[] = [];

  isProfileComplete: boolean = false;
}


export class HouseholdMemberRace extends CommonProperties{
  householdMemberRaceId:number;
  raceId:number;
  householdMemberProfileId:number;
}
