import { MemberEtiquette } from './../../models/member-etiquette.model';
import { HouseholdMemberProfile } from 'projects/web/src/app/entities';
import { Injectable } from '@angular/core';
import { ReplaySubject } from 'rxjs';
import { IncomeFrequencyTypeEnum } from 'projects/web/src/app/shared/enums/applicantEnums';

@Injectable({
  providedIn: 'root'
})
export class UtilityService {

  constructor() {  }

  processGeoAutoCompleteResult(result){
    if(result.response){
      let addressData = result.data.address_components as any [];
      let geometryData = result.data.geometry;

      let streetNumber = addressData.find(r => r.types.includes("street_number"));
      let streetName = addressData.find(r => r.types.includes("route"));
      let city = addressData.find(r => r.types.includes("sublocality_level_1"));
      let state = addressData.find(r => r.types.includes("administrative_area_level_1"));
      let postalCode = addressData.find(r => r.types.includes("postal_code"));
      let country = addressData.find(r => r.types.includes("country"));
      let locality = addressData.find(r => r.types.includes("locality"));
      let countyAdmin = addressData.find(r => r.types.includes("administrative_area_level_2"));
      let latitude = geometryData.location.lat;
      let longitude = geometryData.location.lng;

      return {
        "mappedAddress": {
          address: `${streetNumber? streetNumber.long_name: ''} ${streetName? streetName.long_name: ''}`,
          city: city? city.long_name : locality ? locality.long_name : countyAdmin? countyAdmin.long_name : '',
          state: state ? state.short_name : '',
          zip: postalCode ? postalCode.long_name : '',
          country: country.short_name,
          isUSAddress: country.short_name === 'US'
        },
        "rawAddress":{
          streetNumber,
          streetName,
          city,
          state,
          postalCode,
          country,
          latitude,
          longitude
        },
        'formatted_address': result.data.formatted_address
      }
    }
    return null;
  }

  private currentMemberEtiquette: ReplaySubject<MemberEtiquette> = new ReplaySubject(null);
  currentMemberEtiquette$ = this.currentMemberEtiquette.asObservable();

  setCurrentMemberQuestionairreEtiquette(member:HouseholdMemberProfile, relationShipOptions:any[]):void{
    if(!relationShipOptions) return;
    let memberEtiquette = this.getQuestionairreEtiquette(member,relationShipOptions);
    this.currentMemberEtiquette.next(memberEtiquette);
  }

  getQuestionairreEtiquette(member:HouseholdMemberProfile, relationShipOptions:any[]):MemberEtiquette{
    let memberEtiquette = new MemberEtiquette();

    if (!member.householdMemberProfileId || member.householdMemberProfileId === 0) {
      memberEtiquette.possessive = "member's";
      memberEtiquette.properAddressNoun = "member";
      memberEtiquette.fullName = 'Member';
      return memberEtiquette;
    }

    let firstName = member.firstName;
    let lastName = member.lastName;
    memberEtiquette.fullName = `${firstName ? firstName : ''} ${lastName ? lastName : ''}`;

    let relation = relationShipOptions.find(r => r.configTypeId == member.relationshipTypeId);

    if (relation.name.toLowerCase() === 'myself') {
      memberEtiquette.possessive = 'your';
      memberEtiquette.properAddressNoun = 'you';
      memberEtiquette.isSelf = true;
    } else if (relation.name.toLowerCase() === 'unborn child') {
      memberEtiquette.possessive = firstName ? `${firstName}'s` : "your unborn child's";
      memberEtiquette.properAddressNoun = firstName ? firstName : 'unborn child';
      memberEtiquette.fullName = firstName ? memberEtiquette.fullName : 'Unborn Child';
    } else {
      memberEtiquette.possessive = `${firstName}'s`;
      memberEtiquette.properAddressNoun = firstName;
    }

    return memberEtiquette;
  }

  calcTotalFromNumbers(items:number[]){
    let sum = items.reduce((acc: number, x: number) => {
      let xValue = Math.sign(x) === -1 ? 0 : x;//Treat negative numbers as 0 required
      return acc + xValue;
    }, 0);

    return sum;
  }

  calcNetIncomeByFrequency(freq:number, amount:number){
    let netIncome = 0.0;
    switch(freq){
      case IncomeFrequencyTypeEnum.Daily:{
        netIncome = amount * 365;
        break;
      }
      case IncomeFrequencyTypeEnum.Every2Weeks:{
        netIncome = amount * 26;
        break;
      }
      case IncomeFrequencyTypeEnum.OnceMonththly:{
        netIncome = amount * 12;
        break;
      }
      case IncomeFrequencyTypeEnum.TwiceMonthly:{
        netIncome = amount * 24;
        break;
      }
      case IncomeFrequencyTypeEnum.Weekly : {
        netIncome = amount * 52;
        break;
      }
      case IncomeFrequencyTypeEnum.OnceYearly:
      default:{
        netIncome = amount;
      }
    }

    return netIncome;
  }
}
