import { Component, OnInit, EventEmitter, Output, Input, SimpleChanges } from '@angular/core';
import { NgbDateStruct } from '@ng-bootstrap/ng-bootstrap/datepicker/ngb-date-struct';
import { NgbDateStructAdapter } from '@ng-bootstrap/ng-bootstrap/datepicker/adapters/ngb-date-adapter';
import { addWeeks, compareAsc } from 'date-fns';


@Component({
  selector: 'app-date-selector',
  templateUrl: './date-selector.component.html',
  styleUrls: ['./date-selector.component.scss']
})
export class DateSelectorComponent implements OnInit {

  @Input() availableDates: Date[] = [];
  @Output() dateSelected: EventEmitter<any> = new EventEmitter();
  @Input() dateChoice: NgbDateStruct = null;
  @Input() startDate: Date;

  minDate: NgbDateStruct;
  maxDate: NgbDateStruct;
  navigationType: string;


  bind = (func) => func.bind(this);

  ngOnInit() {
    this.minDate = this.convertToNgbDate(new Date());
    this.maxDate = this.convertToNgbDate(addWeeks(new Date(), 2));
    let lastDay = this.availableDates.length? this.convertToNgbDate(this.availableDates.slice(-1)[0]) : this.maxDate;
    this.navigationType = (this.convertToNgbDate(this.startDate).month !== lastDay.month) ? 'arrows' : 'none';
  }

  ngOnChanges(changes: SimpleChanges): void {
    console.log(this.availableDates);
  }

  setDate(selectedDate) {
    this.dateSelected.emit(selectedDate);
  }

  convertToDate(date: NgbDateStruct) {
    if (date) return new Date(date.year, date.month - 1, date.day);
  }
  convertToNgbDate(date: Date) {
    if (date) return { day: date.getDate(), month: date.getMonth() + 1, year: date.getFullYear() }
  }

  isDisabled(date: NgbDateStruct) {
    const d = new Date(date.year, date.month - 1, date.day);
    let found = false;
    if (this.availableDates.length > 0) {
      this.availableDates.forEach(availableDate => {
        if (availableDate.getDate() == d.getDate() &&
          availableDate.getMonth() == d.getMonth() &&
          availableDate.getFullYear() == d.getFullYear() && (compareAsc(d, this.convertToDate(this.maxDate)) < 1) ) {
          found = true;
        }
      });
    }
    return !found;
  }

  isSelected(date: NgbDateStruct) {
    if (this.dateChoice)
      return (date.day == this.dateChoice.day &&
        date.month == this.dateChoice.month &&
        date.year == this.dateChoice.year);
  }
}
