import { Injectable } from '@angular/core';
import { PublicApiService } from '../../../core/services/public-api.service';
import { of } from 'rxjs';

@Injectable()
export class HomeService {
  constructor(private publicApiService: PublicApiService) {}

  getMyProjectActivities() {
    return this.publicApiService.get('Development/GetMyProjectActivities');
  }

  getMyGetMyTasks() {
    return this.publicApiService.get('Development/GetMyTasks');
  }

  getOpenLotteriesByBorough() {
    return this.publicApiService.getAnonymous('Lottery/GetOpenLotteriesByBorough');
  }
}
