import { Component, OnInit } from '@angular/core';
import { FormGroup, Validators } from '@angular/forms';
import { FormService } from '../../../core/modules/form/form-service.service';
import { ContactUsService } from './services/contact-us.service';
import { ContactUsFormModel } from './models/contact-us-form.model';

@Component({
  selector: 'app-contact-us',
  templateUrl: './contact-us.component.html',
  styleUrls: ['./contact-us.component.scss']
})
export class ContactUsComponent implements OnInit {
  contactUsForm: FormGroup;

  constructor(private contactUsService: ContactUsService, private formService: FormService) {}

  ngOnInit() {
    this.contactUsForm = this.formService.getFormByModel(new ContactUsFormModel());
  }

  postMessage() {
    this.contactUsService.postMessage(this.contactUsForm.value);
  }
}
