import createNumberMask from 'text-mask-addons/dist/createNumberMask';

export const percentageMask = createNumberMask({
  prefix: '',
  integerLimit: 3,
  maxValue: 100,
  suffix: '%'
});

export const percentagePipe = (conformedValue, config) => {
  const inputValue = config.rawValue;

  const isPercentageExist = inputValue.indexOf('%') >= 0;

  if (isPercentageExist) {
    inputValue.replace('%', '');
  }

  const alphabeticValue = inputValue.replace(/[^0-9]*/g, '');

  if (alphabeticValue > 100) {
    return config.previousConformedValue;
  }
  return conformedValue;
};

