  ngOnChanges(changes: SimpleChanges): void {
    if (!this.unsubscribed) this.unsubscribed = new Subject();

    if (changes.selectedAssetType && changes.selectedAssetType.currentValue) {
      console.log("Selected Asset Type:", this.asset.assetTypeId);
      this.createAssetBasedForm(this.asset.assetTypeId);
      this.setAssetFormValueAndContext(this.asset.assetTypeId);
      this.cdRef.detectChanges();
    }
  }

  setAssetFormValueAndContext(assetType) {
    switch (assetType) {
      case AssetTypeEnum.SavingsAccount: {
        this.assetTypeTitle = 'Savings Account';
        this.assetTypeDescription = `
            Add savings account description`;
        this.setSavingsAccountSubscriptions();
        break;
      }
      case AssetTypeEnum.CheckingAccount: {
        this.assetTypeTitle = 'Checking Account';
        this.assetTypeDescription = `
            Add checking account description`;
        this.setCheckingAccountSubscriptions();
        break;
      }
      case AssetTypeEnum.OtherBankAccount: {
        this.assetTypeTitle = 'Other Bank Account';
        this.assetTypeDescription = `
            Add bank account account description`;
        this.setOtherBankAccountSubscriptions();
        break;
      }
      case AssetTypeEnum.RetirementAccount: {
        this.assetTypeTitle = 'Retirement Account';
        this.assetTypeDescription = `
            Add retirement account description`;
        this.setRetirementAccountSubscriptions();
        break;
      }
      case AssetTypeEnum.BrokerageAccount: {
        this.assetTypeTitle = 'Brokerage Account';
        this.assetTypeDescription = `
            Add brokerage account description`;
        this.setBrokerageAccountSubscriptions();
        break;
      }
      case AssetTypeEnum.OtherInvestmentAccount: {
        this.assetTypeTitle = 'Other Investment Account';
        this.assetTypeDescription = `
            Add other investment account description`;
        this.setOtherInvestmentAccountSubscriptions();
        break;
      }
      case AssetTypeEnum.ResidentialRealEstateProperty: {
        this.assetTypeTitle = 'Residential Real Estate Property';
        this.assetTypeDescription = `
            Add residential real estate property description`;
        this.setResidentialRealEstateSubscriptions();
        break;
      }
      case AssetTypeEnum.NonResidentialRealEstateProperty: {
        this.assetTypeTitle = 'Non-residential Real Estate Property';
        this.assetTypeDescription = `
            Add non-residential real estate property description`;
        this.setNonResidentialRealEstateSubscriptions();
        break;
      }
      default: {
        this.assetTypeTitle = 'Something went wrong';
        this.assetTypeDescription = `
            Please go back and try to add another asset type`;
        break;
      }
        
    }

  }

