import { Component, ChangeDetectorRef, OnInit, Input } from "@angular/core";
import { FormControl, Validators } from "@angular/forms";
import { LotteryApiService } from '../../services/api/lottery-api.service';
import { Router, ActivatedRoute } from '@angular/router';
import { MarkerModel } from 'projects/web/src/app/shared/models/marker.model';
import { PropertyTypeEnum, OwnerTypeEnum } from 'projects/web/src/app/shared/enums/developmentEnums';
import { Observable } from 'rxjs';

@Component({
  selector: 'application-details',
  templateUrl: 'application-details.component.html',
  styleUrls: ['application-details.component.scss']
})
export class ApplicationDetailsComponent {
  //@Input() details: any = {};
  @Input() lotteryApplicationId: number;
  lottery : any;
  details : any;
  lotteryId: string;
  cardViewType: string = 'summary';

  constructor(
    private lotteryService: LotteryApiService, private cdRef: ChangeDetectorRef) {
  }

  ngOnInit() {
    this.getApplicationInfo();
  }

  getApplicationInfo() {

    this.lotteryService.getApplicationDetails(this.lotteryApplicationId).subscribe(data => {
      this.details = data;
      this.lotteryId = this.details.lotteryId.toString();
      this.lotteryService.getCardView(this.lotteryId).subscribe(details => {
          this.lottery = this.aggregateApplicationData(details);        
          this.cdRef.detectChanges();
        });

      this.cdRef.detectChanges();
    });
  }

  private aggregateApplicationData(item) {
    const subwayServices = item.trains
      ? Array.from(new Set(item.trains.split(','))).sort()
      : [];

    const markers: Array<MarkerModel> = (item.markers)
      ? item.markers.map((item, index) => {
        return {
          address: item.address,
          city: item.city,
          lat: item.lat,
          lng: item.lng,
          label: item.name,
          markerLabel: item.name,
          state: item.state,
          zip: item.zip,
          fullAddress: `$(item.address), $(item.city), $(item.state) $(item.zip)`,
          id: 0,
          draggable: false,
          index: index,
          iconUrl: '/assets/images/map-marker.svg',
          type: 'building',
          color: null,
          trainName: null
        } as MarkerModel;
      })
      : [];

    return {
      mainImage: (item.defaultPhotoStream)
        ? (item.defaultPhotoStream)
        : '/assets/images/demo-lottery-house.png',
      markers,
      details: {
        amenities: item.amenities,
        area: (
          (item.neighborhood)
            ? item.neighborhood
            : 'Neighborhood'
        ) + ' | ' + ((item.borough) ? item.borough : 'Borough'),
        borough: item.borough,
        description: item.lotteryDescription,
        endIn: item.endIn,
        lotteryEndDate: item.lotteryEndDate,
        lotteryId: item.lotteryId,
        lotteryName: item.lotteryName,
        street: item.lotteryName,
        subwayServices,
        images: [
          (item.defaultPhotoStream) ? (item.defaultPhotoStream) : '/assets/images/demo-lottery-house.png'
        ],
        isAddedToFavorite: false,
        expirationDate: new Date(),
        expirationDateText: (item.endIn > 0)
          ? `Lottery closing in ${item.endIn} days`
          : (item.endIn === 0)
            ? 'Lottery ends today'
            : 'Lottery closed',
        minIncome: item.minIncome,
        maxIncome: item.maxIncome,
        minHousehold: item.minHouseholdSize,
        maxHousehold: item.maxHouseholdSize,
        mobilityPref: item.mobility,
        units: item.units,
        studios: item.studios,
        oneBR: item.oneBR,
        twoBR: item.twoBR,
        threeBR: item.threeBR,
        fourBR: item.fourBR,
        unitLayouts: item.unitLayouts,
        propertyTypeId: item.propertyTypeId,
        propertyType: (item.propertyTypeId == PropertyTypeEnum.Rental ? "Rental" : (item.propertyTypeId == PropertyTypeEnum.Owner ? "Sales" : "")),
        ownerTypeId: item.ownerTypeId,
        ownerType: (item.ownerTypeId == OwnerTypeEnum.SmallHome ? "Small Home" : (item.ownerTypeId == OwnerTypeEnum.CondoCoop ? "Condo/Co-op" : "")),
        visionHearingPref: item.visionHearing,
        lotteryApplication: item.lotteryApplication,
      }
    }
  }


  getStatusClass(statusId: number) {
    switch (statusId) {
      case 1: //Default
      case 6: //BC Passed
      case 7: //BC Failed
      case 16://In Batch
      case 5: //Appointment Scheduled
        return 'status-in-progress';
      //Action Needed (Document / Appointment)
      case 3://Request for Document
        return 'status-action-needed';
      //Rejected
      case 2://Ineligible
      case 4://Rejected(Appeal?)
      case 11://Appeal Rejected (Complain?)
      case 14://Complaint Rejected
      case 17://Duplicate
        return 'status-rejected';
      //In Review (Appeal/Complaint)
      case 9: //Appeal Filed
      case 10://Appeal Accepted
      case 12://Complaint Filed
      case 13://Complaint Accepted
        return 'status-in-review';
      //Accepted
      case 8://Priority Waitlist
      case 15://Lease Signed
        return 'status-accepted';
    case 0:
      return 'status-in-progress';
      default:
        return 'status-in-progress';
    }
  }


}
