import { HouseholdMemberProfile } from 'projects/web/src/app/entities';
import { ConfigValueService } from 'projects/web/src/app/shared/services/config-value/config-value.service';
import { ScrollStepper } from './../../../../core/modules/scroll-form/scroll-form.component';
import { HouseholdService } from './../../services/household/household.service';
import { MemberEtiquette } from './../../models/member-etiquette.model';
import { UtilityService } from './../../services/utility/utility.service';
import { HouseholdMemberIncome } from './../../models/hh-member-income.model';
import { Component, OnInit, AfterContentInit, Input, ChangeDetectorRef, EventEmitter, Output, OnDestroy, OnChanges, SimpleChanges } from '@angular/core';
import { FormGroupDirective, FormGroup, ControlContainer, FormArray, FormControl } from '@angular/forms';
import { IncomeTypeEnum } from '../../../../shared/enums/applicantEnums';
import { Subject } from 'rxjs';

@Component({
  selector: 'income-details-form',
  templateUrl: './income-details-form.component.html',
  styleUrls: ['./income-details-form.component.scss'],
  viewProviders: [{
    provide: ControlContainer,
    useExisting: FormGroupDirective
  }]
})
export class IncomeDetailsFormComponent implements OnInit, AfterContentInit, OnDestroy, OnChanges {

  mainForm: FormGroup;
  incomeDetailsForm: FormGroup;
  paychecksInputs: FormArray;
  @Input() memberProfile: HouseholdMemberProfile;
  @Input() memberIncome: HouseholdMemberIncome[] = [];
  @Output() handleAddIncome = new EventEmitter<HouseholdMemberIncome>();
  @Output() handleEditIncome = new EventEmitter<HouseholdMemberIncome>();
  @Output() nextStep: EventEmitter<any> = new EventEmitter();
  memberNoIncome: FormGroup;
  hasNoIncomeCtrl: FormControl;

  incomeTypes: any[];
  incomeSubTypes: any[];

  IncomeTypeEnum = IncomeTypeEnum;

  constructor(private parentFormDirective: FormGroupDirective,
    private utilSvc: UtilityService,
    private householdSvc: HouseholdService,
    private cdRef: ChangeDetectorRef,
    private configValSvc: ConfigValueService) {
  }

  unsubscribe = new Subject();
  memberEtiquette: MemberEtiquette;

  ngOnInit() {
    this.mainForm = this.parentFormDirective.form;

    this.configValSvc.getIncomeSubTypes().subscribe(r => this.incomeSubTypes = r);

    this.utilSvc.currentMemberEtiquette$.subscribe(memberEtiquette => this.memberEtiquette = memberEtiquette);
    this.householdSvc.memberIncomeAdded$.subscribe(updatedIncome => this.onAddNewIncome(updatedIncome));
    this.householdSvc.memberIncomeUpdated$.subscribe(updatedIncome => this.onUpdateIncome(updatedIncome));
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.memberIncome && changes.memberIncome.currentValue) {
      this.calcTotalMemberIncome();
    }

    //Create has no income form and control
    if (changes.memberProfile && changes.memberProfile.currentValue) {
      this.hasNoIncomeCtrl = new FormControl(this.memberProfile.isNoIncome);
      this.memberNoIncome = new FormGroup({ 'hasNoIncomeCtrl': this.hasNoIncomeCtrl });
    }
  }

  getIncomeTypeName(id: number) {
    if (id === IncomeTypeEnum.Employment) return 'Employment Income';
    if (id === IncomeTypeEnum.SelfEmployment) return 'Self-Employment Income';
    if (id === IncomeTypeEnum.GovernmentBenefits) return "Government Benefit Income";
    if (id === IncomeTypeEnum.RetirementDisability) return "Retirement/Disability Income";
    if (id === IncomeTypeEnum.OtherPeople) return "Other Sources Income";
  }

  getSubTypesIncome(id: number) {
    if (!this.incomeSubTypes) return '';
    let subtype = this.incomeSubTypes.find(r => r.incomeSubTypeId === id);
    return subtype ? subtype.name : ''
  }

  onUpdateIncome(updatedIncome: HouseholdMemberIncome) {
    let index = this.memberIncome.findIndex(r => r.householdMemberIncomeId === updatedIncome.householdMemberIncomeId);
    this.memberIncome[index] = { ...updatedIncome };
    this.calcTotalMemberIncome();
  }

  onAddNewIncome(newIncome: HouseholdMemberIncome) {
    this.memberIncome.push({ ...newIncome });
    this.calcTotalMemberIncome();
  }

  onUpdateNoIncome(scrollStepper: ScrollStepper) {

    if (this.hasNoIncomeCtrl.value) {
      this.householdSvc.updateMemberHasNoIncome(this.memberProfile.householdMemberProfileId).subscribe(result => {
        if (result.success) {
          console.log('member profile updated');
          this.memberProfile.isNoIncome = true;
        }
        else
          console.log('Please remove all income before declaring member has no income');
      });
    } else {
      scrollStepper.scrollPrevious();
    }

  }

  ngAfterContentInit(): void { };

  onAddIncome(type) {
    let model = new HouseholdMemberIncome()
    model.incomeTypeId = type;
    model.householdMemberIncomeId = 0;
    this.handleAddIncome.emit(model);
  }

  onEditIncome(model) {
    this.handleAddIncome.emit({ ...model });
  }

  totalIncome: number = 0;
  calcTotalMemberIncome() {
    let totalIncome = this.memberIncome.reduce((a, b) => {
      return a + b.currentNetIncomeAmount
    }, 0);

    this.totalIncome = totalIncome;
    console.log('Members total income', totalIncome);
  }

  onRemoveIncome(income: HouseholdMemberIncome) {
    this.householdSvc.removeMemberIncome(income.householdMmbrPrflId, income.householdMemberIncomeId).subscribe(() => {
      this.memberIncome = this.memberIncome.filter(r => r.householdMemberIncomeId !== income.householdMemberIncomeId);
      this.calcTotalMemberIncome();
      this.cdRef.detectChanges();
    });
  }

  ngOnDestroy(): void {
    this.unsubscribe.next();
    this.unsubscribe.complete();
  }
}
