import { ContentChildren, Directive, Input, QueryList } from '@angular/core';
import { SliderCardComponent } from '../components/app-slider-card.component';

@Directive({
  selector: '[cdkSlider]',
  exportAs: 'CdkSlider'
})
export class CdkSlider {
  @ContentChildren(SliderCardComponent) cards: QueryList<SliderCardComponent>;

  constructor() {}

  sourceDisplayContCardValue: number = 1;
  displayContCard: number = 1;

  @Input('display-cont-card')
  set _displayContCard(value) {
    this.sourceDisplayContCardValue = value;
    this.displayContCard = value;
  }

  @Input()
  get selectedIndex() {
    return this._selectedIndex;
  }

  set selectedIndex(index: number) {
    if (this.cards) {
      // Ensure that the index can't be out of bounds.
      if (index < 0 || index > this.cards.length - 1) {
        throw Error('cdkStepper: Cannot assign out-of-bounds value to `selectedIndex`.');
      }

      // if (this._selectedIndex != index) {
      //   this._updateSelectedItemIndex(index);
      // }

      this._selectedIndex = index;
    } else {
      this._selectedIndex = index;
    }
  }

  private _selectedIndex = 0;
  _getAnimationDirection() {
    return this._selectedIndex > this.cards.length - 1 ? 'previous' : 'next';
  }

  nextCard() {
    if (this.displayContCard != 1) {
      const nextSlideIndex = (this._selectedIndex + 1) * this.displayContCard;
      const isNextSlideAvailable = nextSlideIndex > this.cards.length - 1;

      if (isNextSlideAvailable) {
        this._selectedIndex = 0;
      }
    }

    this.selectedIndex = Math.min(this._selectedIndex + 1, this.cards.length - 1);
  }

  previousCard() {
    this.selectedIndex = Math.max(this._selectedIndex - 1, 0);
  }
  _getSlideWidth() {
    return 100 / this.displayContCard;
  }
}
