import { Component, OnDestroy, OnInit, ChangeDetectorRef } from '@angular/core';
import { HomeService } from './services/home.service';
import { Router } from '@angular/router';
import { OAuthService } from 'angular-oauth2-oidc';

@Component({
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss']
})
export class HomeComponent implements OnInit, OnDestroy {
  private homepageVideoId: string = '163721649';

  openLotteriesByBorough: {};

  constructor(private homeService: HomeService, private router: Router, private cdRef: ChangeDetectorRef, private oauthService: OAuthService) {}

  public ngOnInit() {
    this.homeService.getOpenLotteriesByBorough().subscribe(data => {
      const boroughData = [
        {
          imgPath: '/assets/images/homepage-lotteries-borough-001-bronx.jpg',
          title: 'The Bronx',
          boroughId: 2
        },
        {
          imgPath: '/assets/images/homepage-lotteries-borough-002-brooklyn.jpg',
          title: 'Brooklyn',
          boroughId: 3
        },
        {
          imgPath: '/assets/images/homepage-lotteries-borough-003-manhattan.jpg',
          title: 'Manhattan',
          boroughId: 1
        },
        {
          imgPath: '/assets/images/homepage-lotteries-borough-004-queens.jpg',
          title: 'Queens',
          boroughId: 4
        },
        {
          imgPath: '/assets/images/homepage-lotteries-borough-005-staten-island.jpg',
          title: 'Staten Island',
          boroughId: 5
        }
      ]
      this.openLotteriesByBorough = {
        desktopTitle: 'Explore open lotteries by borough',
        mobileTitle: 'Lotteries by borough',
        boroughList: boroughData.map(borough => {
          return {
            imgPath: borough.imgPath,
            title: borough.title,
            boroughId: borough.boroughId,
            lotteryCount: data.find(boroughResult => boroughResult.boroughId == borough.boroughId).lotteryCount
          }
        })
      };
      if(!this.cdRef["destroyed"]) this.cdRef.detectChanges();
    });
  }

  navigateToSearch() {
    this.router.navigate(['/search-businesses']
      //, {
      //  queryParams: {
      //    householdSize: 1
      //  }
      //}
    );
  }

  navigateToSignup() {
    this.router.navigate(['/auth/sign-up']);
  }

  public ngOnDestroy() { }

  isUserLoggedIn() {
    return ((this.oauthService.hasValidIdToken() && this.oauthService.hasValidAccessToken()));
  }
}
