import {Component, OnInit, ChangeDetectorRef} from '@angular/core';
import {Router} from '@angular/router';
import {dollarAmount} from '../../../../core/addons/textmask-directive-addons';
import {LotteryApiService} from "../../../../shared/services/api/lottery-api.service";
import {HouseholdTypeEnum} from "../../../../shared/enums/developmentEnums";
import {FormControl} from "@angular/forms";

@Component({
  selector: 'app-calculate-income',
  templateUrl: './calculate-income.component.html',
  styleUrls: ['./calculate-income.component.scss']
})
export class CalculateIncomeComponent implements OnInit {
  income: string;
  householdSizes: any[];
  householdSizeFormControl = new FormControl();

  constructor(
    private router: Router,
    private lotteryApiService: LotteryApiService,
    private cdRef: ChangeDetectorRef
  ) {
  }

  ngOnInit() {
    this.lotteryApiService.getHouseholdSizeOptions().subscribe(result => {
      this.householdSizes = result;
      if (!this.cdRef["destroyed"]) this.cdRef.detectChanges();
    });
  }

  onChangeHouseIncome(event) {
    this.income = event;
  }

  navigateToSearch() {
    this.router.navigate(
      ['/search-businesses'],
      {
        queryParams: {
          householdSize: this.householdSizeFormControl.value,
          householdIncome: this.income,
          householdType: HouseholdTypeEnum.Rentals,
        },
      },
    );
  }
}
