import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { NgbDate, NgbActiveModal, NgbDateStruct } from '@ng-bootstrap/ng-bootstrap';
import { FormControl } from '@angular/forms';
import { AppointmentModel } from '../../../models/appointment-model';
import { ApplicationsApiService } from '../../../services/applications-api.service';
import { ToastrService } from 'ngx-toastr';
import { Router } from '@angular/router';
import { MarkerModel } from 'projects/web/src/app/shared/models/marker.model';

@Component({
  selector: 'app-appointment-details',
  templateUrl: './appointment-details.component.html',
  styleUrls: ['./appointment-details.component.scss']
})
export class AppointmentDetailsComponent implements OnInit {
  mappedEndTime: string;
  displayConfirmation: boolean = false;
  appointmentModel: AppointmentModel;
  locationMarker: MarkerModel;

  constructor(public activeModal: NgbActiveModal,
    private applicationApiService: ApplicationsApiService,
    private toastr: ToastrService,
    private router: Router
  ) { }

  @Input() selectedLocation;
  @Input() selectedDate: NgbDate;
  @Input() selectedTime: string = "";

  @Input() translation: boolean = false;
  @Input() accessibility: boolean = false;

  @Input() applicationId: number;

  @Output() clearFilters: EventEmitter<any> = new EventEmitter();
  @Output() onPost: EventEmitter<any> = new EventEmitter();

  mappedTime: string = "";
  finished: boolean = false;

  translationControl: FormControl = new FormControl;
  accessibilityControl: FormControl = new FormControl;



  mapTime(timeString: string) {
    let newString = ':' + timeString.split(':')[1];
    let hour = Number(timeString.split(':')[0]);
    switch (true) {
      case (hour < 12):
        return (String(Number(hour)) + newString + " AM");
      case (hour > 12):
        return (String(Number(hour) - 12) + newString + " PM");
      case (hour == 12):
        return (String(Number(hour) + newString + " PM"))
    }
  }

  ngOnInit() {
    let [startHour, startMins] = this.selectedTime.split(':').map(x => parseInt(x));
    let endMins = (startMins + 30) % 60;
    let endHours = (startMins + 30 >= 60) ? ((startHour + 1) % 24) : startHour;
    this.mappedTime = this.mapTime(this.selectedTime);
    this.mappedEndTime = this.mapTime(endHours + ':' + endMins.toString().padStart(2, '0'));
    this.translationControl.setValue(this.translation);
    this.accessibilityControl.setValue(this.accessibilityControl)
    
    if (this.selectedLocation) {

      let m = new MarkerModel();
      m.id = this.selectedLocation.locationId;
      m.index = 0;
      m.lat = this.selectedLocation.latitude;
      m.lng = this.selectedLocation.longitude;
      m.label = this.selectedLocation.label;
      m.address = this.selectedLocation.address;
      this.locationMarker = m;

    }
  }

  ngOnChanges() {
    let [startHour, startMins] = this.selectedTime.split(':').map(x => parseInt(x));
    let endMins = (startMins + 30) % 60;
    let endHours = (startMins + 30 >= 60) ? ((startHour + 1) % 24) : startHour;
    this.mappedTime = this.mapTime(this.selectedTime);
    this.mappedEndTime = this.mapTime(endHours + ':' + endMins.toString().padStart(2, '0'));
    if (!this.selectedDate || !this.selectedLocation || this.selectedTime == "")
      this.finished = false;
  }

  confirmWindow() {
    this.finished = true;
  }

  postAppointment() {
    console.log("POST GOES HERE");
    this.appointmentModel = new AppointmentModel();
    let [startHour, startMins] = this.selectedTime.split(':').map(x => parseInt(x));
    let endMins = (startMins + 30) % 60;
    let endHours = (startMins + 30 >= 60) ? ((startHour + 1) % 24) : startHour;

    let appointmentStartDate = this.convertToDate(this.selectedDate);
    let appointmentEndDate = this.convertToDate(this.selectedDate);
    appointmentStartDate.setHours(startHour, startMins);
    appointmentEndDate.setHours(endHours, endMins);
    this.appointmentModel.startTime = appointmentStartDate;
    this.appointmentModel.endTime = appointmentEndDate;
    this.appointmentModel.lotteryApplicationId = this.applicationId;
    this.appointmentModel.meetingLocationId = this.selectedLocation.locationId;
    console.log(this.appointmentModel);
    this.applicationApiService.postAppointment(this.appointmentModel).subscribe(result => {
      console.log(result);
      if (result.success) {
        this.toastr.success(result.message);
        //Navigate to confirmation view
        this.displayConfirmation = true;
        this.onPost.emit(true);
      }
      else {
        this.toastr.error(result.message);
      }
    });
    //this.activeModal.close();
  }

  goToAppointments(){
    this.activeModal.close();
  }

  convertToDate(date: NgbDateStruct) {
    if (date) return new Date(date.year, date.month - 1, date.day);
  }

  openMapsLink(address) {
    let addressArray = encodeURIComponent(address);
    console.log(addressArray);
    window.open('https://www.google.com/maps/search/?api=1&query=' + addressArray)
  }

}
