import { Component, Input, Output, OnInit, EventEmitter, ChangeDetectorRef } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { OAuthService } from 'angular-oauth2-oidc';
import { ToastrService } from 'ngx-toastr';
import { NoteModel } from "./note.model";
import { NoteService } from "./note.service";
import { AppSettings } from "../../../../environments/appsettings";

@Component({
  selector: 'app-notes',
  templateUrl: './note.component.html'
})
export class NoteComponent implements OnInit {
  @Input() lotteryId: string;
  @Input() inputnotegroupid: any;
  @Input() lotteryapplicationid: string;

  isLoading: boolean = false;

  @Output() onAdd: EventEmitter<any> = new EventEmitter<any>();

  complexForm: FormGroup;
  note = new NoteModel();
  notes: Array<NoteModel> = [];

  constructor(
    private toastr: ToastrService,
    private oauthService: OAuthService,
    private fb: FormBuilder,
    private noteService: NoteService,
    private cdRef: ChangeDetectorRef

  ) {

    this.complexForm = this.fb.group({
      'comment': ["", Validators.required]
    })
  }

  ngOnInit() {
    //if (!this.inputnotegroupid) {
    //  this.inputnotegroupid = AppSettings.appealNoteGroupdId;
    //}
    this.note = new NoteModel();
    this.note.lotteryId = this.lotteryId;
    this.note.noteDocGroupId = this.inputnotegroupid;
    this.note.lotteryApplicationId = this.lotteryapplicationid;

    const isAuthenticated = this.oauthService.hasValidAccessToken();
    if (isAuthenticated) {
      this.getNotes();
    }

  }

  getNotes(isNewCommentAdded: boolean = false) {
    this.isLoading = true;
    if (this.inputnotegroupid) {
      this.noteService.getNotes(this.note).subscribe(data => {
        this.notes = data;
        this.isLoading = false;
        console.log("Comments", this.notes);               

        this.cdRef.detectChanges();
        if (isNewCommentAdded) this.highlightNewComment();
      });
    } else {
      this.noteService.getCommentList(this.lotteryapplicationid).subscribe(data => {
        this.notes = data;
        this.isLoading = false;
        console.log("Comments", this.notes);       

        this.cdRef.detectChanges();
        if (isNewCommentAdded) this.highlightNewComment();
      });
    }
  }

  highlightNewComment() {
    document.querySelector('#comments-container').scrollIntoView({ behavior: "smooth", block: "start", inline: "nearest" });

    setTimeout(function () {
      let newCommentEl = (document.querySelector('#comments-container') as HTMLElement);
      document.querySelector('#comments-container .list-group .list-item:first-of-type').classList.add('pulse-3');
    }, 500);

    
  }

  submitForm() {
    this.note.noteText = this.complexForm.controls["comment"].value;

    this.noteService.saveNote(this.note).subscribe(data => {
      if (data.success == true) {
        //(document.querySelector('#comments-container .list-group .list-item:first-of-type') as HTMLElement)..classList.remove('pulse-1');
        this.toastr.success(data.message);
        this.complexForm.reset();
        this.getNotes();
        this.onAdd.emit(this.note);
      } else
        this.toastr.error(data.message);
    });
  }
}
