import { Component, OnInit } from '@angular/core';
import { ToggleLoginService } from '../../../shared/services/toggle-login.service';

@Component({
  selector: 'app-footer',
  templateUrl: './footer.component.html',
  styleUrls: ['./footer.component.scss']
})
export class FooterComponent implements OnInit {
  isLoggedIn: any;

  constructor(private toggleSvc: ToggleLoginService) {}

  ngOnInit() {
    this.toggleSvc.isLoggedIn.subscribe(value => {
      this.isLoggedIn = value;
    });
  }
}
