import { Component, OnInit, Input, Output, EventEmitter, ViewChild, ChangeDetectorRef } from '@angular/core';
import { MatTabChangeEvent, MatTabGroup } from '@angular/material';

@Component({
  selector: 'app-lottery-grid-card',
  templateUrl: './lottery-grid-card.component.html',
  styleUrls: ['./lottery-grid-card.component.scss']
})
export class LotteryGridCardComponent implements OnInit {
  @Input() lottery: any;
  @Input() enteredLotteryId: string;
  //  viewMode options: 'full', 'summary', 'condensed'
  //  full:       tabbed detail view
  //  summary:    untabbed custom summary view
  //  condensed:  untabbed title, graphic, location, status view
  @Input() viewMode: string = 'full';
  @Input() isClickable: boolean = true;
  @Input() onClickTitle: string = 'View Details';

  @ViewChild('tabGroup') tabGroup: MatTabGroup;

  @Output() onClickEvent: EventEmitter<any> = new EventEmitter(); //Outputs the handling of the click event to the parent
  @Output() onStatusClickEvent: EventEmitter<any> = new EventEmitter(); //Outputs the handling of the status click event to the parent
  @Output() selectedTabChange: EventEmitter<MatTabChangeEvent>;

  isTransitioning: boolean = false;

  constructor(private cdr: ChangeDetectorRef) { }

  ngOnInit() {
  }

  onClick() {
    if (this.isClickable) {
      console.log("On lottery click:", this.lottery);
      this.onClickEvent.emit(this.lottery);
    }
  }

  onStatusClick() {
    console.log("On application status click:", this.lottery);
    if (this.isClickable) {      
      this.onStatusClickEvent.emit(this.lottery);
    }
  }

  log(e) {
    console.log(e);
  }

  handleSelectedTabChange(event) {
    //fix for duplicate tab-body-content rendering when clicking too fast
    this.isTransitioning = true;
    setTimeout(() => { this.isTransitioning = false; this.cdr.detectChanges(); }, 500);


  }
}
