import { Component, OnInit, Input, EventEmitter, Output } from '@angular/core';

export class SelectModel {
  id: string;
  title: string;
}

@Component({
  selector: 'app-dropdown-menu-select',
  templateUrl: './dropdown-menu-select.component.html',
  styleUrls: ['./dropdown-menu-select.component.scss']
})
export class DropdownMenuSelectComponent implements OnInit {
  @Input() selectedItems: string[] = [];

  @Input() isMultiselect: boolean = true;
  @Input() autoClose: string = 'outside'; //true, false, 'inside', 'outside';
  @Input() unselectedLabel: string = "Any";

  @Output() onDeleteItem: EventEmitter<any> = new EventEmitter();

  get uiLabel():string {
    return (this.selectedItems.length ? this.selectedItems.join(', ') : this.unselectedLabel);
  }

  constructor() { }

  ngOnInit() {
  }

}
