import { Component, OnInit, ChangeDetectorRef } from '@angular/core';
import { AppSettings } from "../../../../../environments/appsettings";
import { Router, ActivatedRoute } from '@angular/router';
import { ApplicationsApiService } from '../../services/applications-api.service';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-alerts',
  templateUrl: './alerts.component.html',
  styleUrls: ['./alerts.component.scss'],
})
export class AlertsComponent {  
  
  alerts: any[];
  isLoadingData: boolean = true;

  constructor(private activatedRoute: ActivatedRoute, private router: Router,
              private applicationApiService: ApplicationsApiService, private toastr: ToastrService,
              private cdRef: ChangeDetectorRef) {}  

  ngOnInit() {
    this.getAlerts();
  }

  getAlerts() {
    this.applicationApiService.getMyAlerts().subscribe(data => {
      this.alerts = data;
      this.isLoadingData = false;
      if (!this.cdRef['destroyed']) this.cdRef.detectChanges();
    });
  }  
}
