import { Household } from 'projects/web/src/app/entities';
import { Validators } from '@angular/forms';
import { Component, OnInit, ViewChild, ElementRef, Input, OnDestroy, OnChanges, SimpleChanges } from '@angular/core';
import { FormControl, FormGroup, ControlContainer, FormGroupDirective } from '@angular/forms';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';

@Component({
  selector: 'household-details-form',
  templateUrl: './household-details-form.component.html',
  styleUrls: ['./household-details-form.component.scss'],
  viewProviders: [{
    provide: ControlContainer,
    useExisting: FormGroupDirective
  }]
})
export class HouseholdDetailsFormComponent implements OnInit, OnDestroy, OnChanges {

  @Input() rentalAssistOptions:any[];
  @Input() household:Household = null;
  @Input() disabled:boolean = false;
  currentRentalAssistOtherValue:string = null;

  mainForm: FormGroup;
  currentLivingSituation: FormControl;
  isRentalAssisted: FormControl;
  isNameOnNycha: FormControl;
  rentalAssistTypes: FormControl;
  rentalAssistOtherDescription:FormControl;

  // show/hide controls
  showHomelessShelterFollowUp:boolean = false;
  showNychaFollowUp:boolean = false;
  showRentalAssistanceFollowUp: boolean = false;
  showRentalAssistOther:boolean = false;

  unsubscribe:Subject<void> = new Subject();

  constructor(private parentFormGroupDirective:FormGroupDirective) { }

  ngOnInit() {
    this.mainForm = this.parentFormGroupDirective.form;

    const currentLivingSitaution = this.currentLivingSituation = new FormControl('', [Validators.required]);
    const isNameOnNycha = this.isNameOnNycha = new FormControl(null);
    const isRentalAssisted = this.isRentalAssisted = new FormControl(null, [Validators.required]);
    const rentalAssistTypes = this.rentalAssistTypes = new FormControl(null);

    this.mainForm.addControl('currentLivingSituation', this.currentLivingSituation);
    this.mainForm.addControl('isNameOnNycha', this.isNameOnNycha);
    this.mainForm.addControl('isRentalAssisted', this.isRentalAssisted);
    this.mainForm.addControl('rentalAssistTypes', this.rentalAssistTypes);

    this.currentLivingSituation.valueChanges.pipe(takeUntil(this.unsubscribe)).subscribe(newValue => this.onChangeLivingSituation(newValue));
    this.isRentalAssisted.valueChanges.pipe(takeUntil(this.unsubscribe)).subscribe(newValue => this.onChangeIsRentalAssisted(newValue));
    this.rentalAssistTypes.valueChanges.pipe(takeUntil(this.unsubscribe)).subscribe(newValue => this.onChangeRentalAssistTypes(newValue));

    if(this.household)
      this.setFormValues();
  }

  ngOnChanges(changes: SimpleChanges): void {
    if(changes.household){
      if(!changes.household.firstChange && changes.household.currentValue){
       this.setFormValues();
      }
    }

  }


  setFormValues() {

      let livingSituation = this.currentLivingSituationOptions.find(r => {
        if(this.household.isHomeless) return r.value === "isHomelessShelter";
        if(this.household.isNameOnNychaForm) return r.value === "isNychaProperty";
        return r.value === "noneApply";
      });

      this.currentLivingSituation.setValue(livingSituation.value);
      this.isNameOnNycha.setValue(this.household.isNameOnNychaForm !== null ? this.household.isNameOnNychaForm.toString() : null);

      this.isRentalAssisted.setValue(this.household.isRentalAssisted.toString());

      if(this.household.isRentalAssisted){
        let currentRentalAssistValues = this.household.householdRentalAssist.filter(s => s.isActive).map(r => r.rentalAssistTypeId.toString());

        let isOtherSelected = this.isRentalAssistOther(currentRentalAssistValues);
        if(isOtherSelected)  this.currentRentalAssistOtherValue = this.household.householdRentalAssist.find(r => r.otherDescription !== null).otherDescription;

        this.rentalAssistTypes.setValue(currentRentalAssistValues);
        this.rentalAssistTypes.setValidators(Validators.required);
      }

  }

  currentLivingSituationOptions = [
    {
      value:'isHomelessShelter',
      description: 'This is a homeless shelter overseen by the city of New York'
    },
    {
      value:'isNychaProperty',
      description: 'This is a NYCHA property'
    },
    {
      value:'noneApply',
      description: 'None of the situations apply to me'
    }
]

  isRentalAssistOther(selectedValues: string[]) {
    const otherOption = this.rentalAssistOptions.find(r => r.name === 'Other');
    if (otherOption) {
      return selectedValues.includes(otherOption.configTypeId.toString());
    } else return false;
  }


onChangeRentalAssistTypes(selectedValues:string []){
  // let otherOptionId = this.rentalAssistOptions.find(r => r.name === 'Other').configTypeId;

  this.showRentalAssistOther = this.isRentalAssistOther(selectedValues)

  if(this.showRentalAssistOther){
    this.rentalAssistOtherDescription = new FormControl(this.currentRentalAssistOtherValue, [Validators.required]);
    this.mainForm.addControl( 'rentalAssistOtherDescription', this.rentalAssistOtherDescription);

    // setTimeout(()=>{
    //   document.getElementById('otherDescription').focus();
    // },100);

  } else {
    this.mainForm.removeControl('rentalAssistOtherDescription');
    this.currentRentalAssistOtherValue = '';
  }

}

  onChangeLivingSituation(selectedValue){
    //This should make the permanent address show on the form
    this.showHomelessShelterFollowUp = selectedValue === 'isHomelessShelter';

    //Show and upate validation for NYCHA follow up question
    this.showNychaFollowUp = selectedValue === 'isNychaProperty'

    if(this.showNychaFollowUp){
      this.isNameOnNycha.setValidators(Validators.required);
    } else {
      this.isNameOnNycha.setValue(null);
      this.isNameOnNycha.setValidators([]);
      this.isNameOnNycha.markAsDirty();
      this.isNameOnNycha.markAsTouched();
    }

    this.mainForm.updateValueAndValidity();
  }

  onChangeIsRentalAssisted(selectedValue){
      this.showRentalAssistanceFollowUp = selectedValue === "true";

      if(this.showRentalAssistanceFollowUp){
        this.rentalAssistTypes.setValidators(Validators.required);
      } else {
        this.rentalAssistTypes.setValidators([]);
        this.rentalAssistTypes.markAsDirty();
        this.rentalAssistTypes.markAsTouched();
        this.rentalAssistTypes.setValue([]);
        this.rentalAssistTypes.updateValueAndValidity();
      }

      this.mainForm.updateValueAndValidity;
  }

  ngOnDestroy(): void {
    this.unsubscribe.next();
    this.unsubscribe.complete();
  }
}
