import { AbstractControl } from "@angular/forms";

const validCharsRegEx: RegExp = /[a-zA-Z\-\.\' ]/;
const notNotValidCharsRegEx: RegExp = /[^a-zA-Z\-\.\' ]/;
const antiPatternRegEx: RegExp = /([^a-zA-Z][\-\.])|(\-[^a-zA-Z])|(\.[^ ])|([\-]{2,}|[\.]{2,})|([^a-zA-Z]\.)|\-$/;
const startsWithRegEx: RegExp = /^[a-zA-Z]+/;
const noDblSpaceBetweenRegEx: RegExp = /[a-zA-Z]+(\s{2,})[a-zA-Z]+/;


export function flNameValidator(control: AbstractControl): { [key: string]: boolean } | null {

  if (control.value !== undefined && control.value !== '') {
    let hasValidStart = startsWithRegEx.test(control.value);
    let hasValidCharacters = validCharsRegEx.test(control.value);
    let hasInvalidCharacters = notNotValidCharsRegEx.test(control.value);
    let hasInvalidPatterns = antiPatternRegEx.test(control.value);
    let hasInvalidDblSpace = noDblSpaceBetweenRegEx.test(control.value);

    if(!hasValidStart || !hasValidCharacters || hasInvalidCharacters || hasInvalidPatterns || hasInvalidDblSpace)
      return { 'name': true };
  }
  return null;
}
