  constructor(private cd: ChangeDetectorRef, private householdSvc: HouseholdService, private router: Router) { }

  ngOnInit() {
    //subscribe to any household changes in order to update details
    this.householdSvc.householdUpdated$.pipe(takeUntil(this.unSubscribe)).subscribe(household => {
      this.viewModel = household;
    });
  }

  ngOnDestroy() {
    this.unSubscribe.next();
    this.unSubscribe.complete();
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.viewModel.currentValue) {
      //calc how many household members
      this.calcTotalHouseholdMembers();

      //calc sum income and assets from household members
      this.calcTotalIncomeAndAssets();
    }
  }

  private calcTotalHouseholdMembers() {
    let vmMemberProfiles = this.viewModel.householdMemberProfile;

    this.totalMemberCount = vmMemberProfiles ?
      (vmMemberProfiles.length) : 0;

    console.log('total members:', this.totalMemberCount)
  }

  private calcTotalIncomeAndAssets() {
    let vmMemberProfiles = this.viewModel.householdMemberProfile;

    if (vmMemberProfiles && vmMemberProfiles.length > 0) {
      this.totalIncome = 0;
      this.totalAssets = 0;
      //foreach through members and sum totals for income and assets
      vmMemberProfiles.forEach(member => {
         let memberIncomes = member.householdMemberIncomes;
         let memberAssets = member.householdMemberAssets;
         memberIncomes.forEach(income => {
          this.totalIncome += income.currentNetIncomeAmount;
         })
         memberAssets.forEach(asset => {
          this.totalAssets++;
         })
      });
    } else {
      this.totalIncome = 0;
      this.totalAssets = 0;
    }
  }

  onEditHouseholdDetails() {
    this.handleEditHouseholdDetails.emit();
  }

  getLanguageLabel(languageId) {
    let optionValue = this.languageOptions.find(r => r.configTypeId === languageId);
    return optionValue ? optionValue.name : 'English';
  }

  getStateLabel(stateId) {
    let optionValue = this.stateOptions.find(r => r.configTypeId === stateId);
    return optionValue ? optionValue.name : '';
  }

  getHouseholdCompletionStatus() {
    if (this.viewModel){
      let householdValid = true;
      if (!this.viewModel.householdMemberProfile.length) return false;
      this.viewModel.householdMemberProfile.forEach(profile => {
        if (!profile.isProfileComplete) householdValid = false;
      })
      return householdValid;
    } else return false;
  }

  public goToBusinessDetails() {
    this.router.navigate(['/business/business-details']);
  }

