  replaceStatuses(x) {
    if (x.name == "Action Required") {
      x.name = "Consent Required";
      return x;
    }
    else if (x.name == "In Process") {
      x.name = "IEP In Progress";
      return x;
    }
    return x;
  }

  loadAlerts(message) {
    this.toastr.success(message);
    this.applicationApiService.getMyAlerts().subscribe(
      messages => {
        messages = messages.sort((a, b) => (Number(new Date(a['createdDate'])) - Number(new Date(b['createdDate']))));

        const groupByLotteryId = this.groupBy('lotteryId');

        const unsortedMessages = Object.values(groupByLotteryId(messages));

        this.messages = unsortedMessages.sort((a, b) => (Number(new Date(a['createdDate'])) - Number(new Date(b['createdDate']))));

        if (!this.cdRef["destroyed"]) this.cdRef.detectChanges();
      }
    )
  }

