import { AppSettings } from "../../../environments/appsettings";

export function loadGoogleScript() {
  var scripts = document.getElementsByTagName('script');
  for (var i = 0; i < scripts.length; ++i) {
    if (scripts[i].getAttribute('src') != null && scripts[i].getAttribute('src').includes('/maps/api/js')) {

      if (window['google'])
        delete window['google'];
      if (scripts[i].src.includes(AppSettings.google_api_key))
        scripts[i].remove();
    }
  }

  const dynamicScript = `https://maps.googleapis.com/maps/api/js?key=${AppSettings.google_api_key}&callback=initGoogleMap`;
  const node = document.createElement('script');
  node.src = dynamicScript;
  node.type = 'text/javascript';
  node.async = false;
  node.charset = 'utf-8';
  document.getElementsByTagName('head')[0].appendChild(node);
}
