import {Component, AfterViewInit, Input} from '@angular/core';
import {tabsAnimations} from './neighborhoods-animations';
import * as _ from 'lodash';

@Component({
  selector: 'neighborhoods',
  templateUrl: './neighborhoods.component.html',
  styleUrls: ['./neighborhoods.component.scss'],
  animations: [tabsAnimations.translateTab]
})
export class NeighborhoodsComponent implements AfterViewInit {
  filteredList: any[] = [];
  activeSubListIndex: number | null = null;
  subList: any[] = [];
  isActiveMainList = true;
  isActiveTreeList = false;
  inputValue;

  @Input() placeholderText: string = "Any";
  @Input('sourceData')
  set sourceData(data) {
    if (!data) {
      return;
    }

    this.filteredList = data;
  }

  @Input() selectedNeighborhoods: any[] = [];

  get selected(): string[] {
    let selectedItems: string[] = [];
    if (this.selectedNeighborhoods) {
      this.selectedNeighborhoods.forEach(item => {
        if (item.hasOwnProperty('title')) {
          selectedItems.push(item.title);
        } else if (item.hasOwnProperty('name')) {
          selectedItems.push(item.name);
        }
      });
    }
    return selectedItems;
  }

  constructor() {
  }

  ngOnInit() {

  }


  ngAfterViewInit() {
  }

  //updateSelected() {
  //  this.selectedItems = [];
  //  this.selectedNeighborhoods.forEach(item => {
  //    if (item.hasOwnProperty('title')){
  //      this.selectedItems.push(item.title);
  //    } else if (item.hasOwnProperty('name')) {
  //      this.selectedItems.push(item.name);
  //    }      
  //  });
  //}

  onDeleteFilterTag(item) {
    this.deleteSelectedNeighborhoodFromList(item);
  }

  deleteSelectedNeighborhoodFromList(item) {
    const deletedItemId = this.selectedNeighborhoods.findIndex(selectedItem => {
        if (item.hasOwnProperty('id')) {
          return selectedItem.id === item.id
        }
        if (item.hasOwnProperty('neighborhoodId')) {
          return selectedItem.neighborhoodId === item.neighborhoodId
        }
    });

    return this.selectedNeighborhoods.splice(deletedItemId, 1)[0];
  }

  applySearchInputValue(list) {
    return list.filter(item => this.applyFilerToProps(item, this.inputValue));
  }

  onChangeFilterTagInput(value) {
    this.inputValue = value.toLowerCase();
    this.isActiveTreeList = !!value;
    this.switchToMainList();
  }

  applyFilerToProps(lotteryObj, inputValue) {
    if (!inputValue) {
      return true;
    }

    return (
      lotteryObj.title.toLowerCase().indexOf(inputValue) >= 0 ||
      (lotteryObj.id + '').indexOf(inputValue) >= 0
    );
  }

  onChangeNeighborhoodsItem(item) {
  
    if (this.isItemSelected(item)) {
      this.deleteSelectedNeighborhoodFromList(item);
      //this.updateSelected();
      return;
    }

    // add sub item
    this.selectedNeighborhoods.push(item);

    const parentId = item.parentId;

    if(parentId){
      // remove parent selected item
      this.selectedNeighborhoods
        .filter(item => this.isItemSelected(item) && item.id == parentId)
        .forEach(item => {
          this.deleteSelectedNeighborhoodFromList(item);
        });
    } else {
      item.subList
        .filter(subItem => this.isItemSelected(subItem))
        .forEach(subItem => {
          this.deleteSelectedNeighborhoodFromList(subItem);
        });
    }
    //this.updateSelected();
  }

  isItemSelected(item) {
    const isElementExist = this.selectedNeighborhoods.find(selectedItem => {
      if (item.hasOwnProperty('id')) {
        return selectedItem.id === item.id
      }

      if (item.hasOwnProperty('neighborhoodId')) {
        return selectedItem.neighborhoodId === item.neighborhoodId
      }
    });


    return !!isElementExist;
  }

  switchToSubList(listId) {
    this.isActiveMainList = false;
    this.isActiveTreeList = false;
    this.activeSubListIndex = listId;
    this.remapSubList();
  }

  remapSubList() {
    if (!this.filteredList.length) {
      this.subList = [];
      return;
    }

    this.subList =
      _.chain(this.filteredList)
        .filter(i => i.id === this.activeSubListIndex)
        .first()
        .get('subList')
        .value() || [];
  }

  switchToMainList() {
    this.isActiveMainList = true;
    this.activeSubListIndex = null;
  }

  getData() {
    return this.selectedNeighborhoods;
  }

  log(e) {
    console.log(e);
  }
}
