import { Injectable} from '@angular/core';
import 'rxjs/add/operator/map';
import { PublicApiService} from "../../../core/services/public-api.service";
import { NoteModel} from "../note/note.model";
import { DocModel } from "./doc.model";
import { ResponseContentType } from '@angular/http';

@Injectable()
export class DocService {
  response: string;

  constructor(
    private publicApiService: PublicApiService
  ) {
  }

  getSubDocTypes() {
    return this.publicApiService.get('Applicant/GetDocTypes');
  }

  getFile(file) {
    const url = `Applicant/Download?id=${file.fileid || file.docId || file.hpdDocId}&contenttype=${file.mediatype || file.fileType}`;
    const options = {
      responseType: ResponseContentType.Blob,
    };

    return this.publicApiService.get(url, options).subscribe(response => {
      const blob = new Blob([response], {type: file.mediatype || file.fileType});
      const url = window.URL.createObjectURL(blob);

      let a = document.createElement("a");

      //a.target = "_blank";
      //a.rel = "noopener noreferrer";
      a.href = url;
      a.download = file.fileName;
      a.click();

      //window.open(url);
      window.URL.revokeObjectURL(url);

    });
  }

  //deleteDoc(doc) {
  //  return this.publicApiService.get('Applicant/DeleteDocument', { params: doc, responseType: ResponseContentType.Json });
  //}

  deleteDoc(hpddocid) {
    return this.publicApiService.get('Applicant/DeleteDocumentById?id=' + hpddocid, {responseType: ResponseContentType.Json});
  }

  getDoctypes(notedocgrpid) {
    return this.publicApiService.get('Comment/GetDocTypes?notedocgrpid=' + notedocgrpid);
  }

  getHPDRegulatoryAgreementDocuments(note, iscached) {
    return this.publicApiService.get('Comment/GetHPDRegulatoryAgreementDocuments?cached=' + iscached, {params: note});
  }

  getAssociatedProjects(developmentId) {
    return this.publicApiService.get('Comment/GetDevelopmentHPDProjects?developmentId=' + developmentId);
  }

  getDocs(note, iscached) {
    return this.publicApiService.get('Applicant/GetDocumentList?cached=' + iscached, {params: note});
  }

  getDocsTest() {
    return this.publicApiService.get('Applicant/GetDocumentList');
  }

  saveNote(note: NoteModel) {
    // this is wrong implementation.. is should be the note component
    return this.publicApiService.post('Applicant/AddComment', note);
  }

  assignApplication(notes: Array<DocModel>)
  {
    return this.publicApiService.post('Applicant/AssignApplicationDocument', notes);

  }
}
