import { HouseholdService } from './../../modules/household/services/household/household.service';
import { Injectable } from '@angular/core';
import { CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot, Router } from '@angular/router';
import { Observable, of } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class HouseholdApplicationGuard implements CanActivate {
  constructor(private router: Router, private householdSvc: HouseholdService) { }

  canActivate(
    next: ActivatedRouteSnapshot,
    state: RouterStateSnapshot): Observable<boolean> | Promise<boolean> | boolean {
    if (!next.paramMap.has('lotteryApplicationId')) return false;

    const lotteryApplicationId = parseInt(next.paramMap.get('lotteryApplicationId'));
    return this.householdSvc.doesHouseholdOwnApplication(lotteryApplicationId).map(householdOwns => {
      if (!householdOwns) {
        this.router.navigate(['/business/summary']);
      }

      return householdOwns;

    }).catch((err) => {
        this.router.navigate(['/']);
        return of(false);
    });
  }
}
