import { HouseholdMemberIncome, HouseholdMemberAsset } from './../../models/hh-member-income.model';
import { ApiService } from './../../../../core/services/api.service';
import { tap } from 'rxjs/operators';
import { Observable, Subject, ReplaySubject, BehaviorSubject } from 'rxjs';
import { ToastrService } from 'ngx-toastr';
import { environment } from '../../../../../environments/environment';
import { HouseholdMemberProfile, Household } from '../../models';
import { Injectable } from '@angular/core';


const applicantApiCtrl = 'Applicant'
@Injectable({
  providedIn: 'root'
})
export class HouseholdService {

  private householdAdded = new Subject<Household>();
  householdAdded$ = this.householdAdded.asObservable();

  private householdUpdated = new Subject<Household>();
  householdUpdated$ = this.householdUpdated.asObservable();

  private memberUpdated = new Subject<HouseholdMemberProfile>();
  memberUpdated$ = this.memberUpdated.asObservable();

  private memberIncomeUpdated = new Subject<HouseholdMemberIncome>();
  memberIncomeUpdated$ = this.memberIncomeUpdated.asObservable();

  private memberIncomeAdded = new Subject<HouseholdMemberIncome>();
  memberIncomeAdded$ = this.memberIncomeAdded.asObservable();

  private memberIncomeRemoved = new Subject<any>();
  memberIncomeRemoved$ = this.memberIncomeRemoved.asObservable();

  private memberAssetUpdated = new Subject<HouseholdMemberAsset>();
  memberAssetUpdated$ = this.memberAssetUpdated.asObservable();

  private memberAssetAdded = new Subject<HouseholdMemberAsset>();
  memberAssetAdded$ = this.memberAssetAdded.asObservable();

  private memberAssetRemoved = new Subject<any>();
  memberAssetRemoved$ = this.memberAssetRemoved.asObservable();

  private memberRemoved = new Subject<number>();
  memberRemoved$ = this.memberRemoved.asObservable();

  private currentHousehold = new ReplaySubject<Household>();
  currentHousehold$ = this.currentHousehold.asObservable(); 

  private memberHasNoIncome = new Subject<number>();
  memberHasNoIncome$ = this.memberHasNoIncome.asObservable();

  private memberHasNoAssets = new Subject<number>();
  memberHasNoAssets$ = this.memberHasNoIncome.asObservable();

  private memberProfileStep = new BehaviorSubject<number>(0);
  memberProfileStep$ = this.memberProfileStep.asObservable();

  //TODO: Add toastr success messages here in service
  constructor(private apiSvc:ApiService, private toastr: ToastrService) { }

  //getHousehold
  getHousehold(): Observable<Household>{
    return this.apiSvc.get(`${environment.publicApiUri}${applicantApiCtrl}/GetHousehold`).pipe(tap(resp => {
      console.log("Current household:", resp);
      this.currentHousehold.next(resp);
    }));
  }

  //getMemberIncome
  getHouseholdMembersIncome(): Observable<HouseholdMemberIncome[]>{
    return this.apiSvc.get(`${environment.publicApiUri}${applicantApiCtrl}/GetHouseholdMemberIncome`);
  }

  //addHousehold
  addHousehold(hhmodel:Household):Observable<Household>{
    return this.apiSvc.post(`${environment.publicApiUri}${applicantApiCtrl}/SaveHousehold`,hhmodel).pipe(tap(resp => {
      this.householdAdded.next(resp);
    }));
  }

  //updateHousehold
  updateHousehold(hhmodel:Household):Observable<any>{
    return this.apiSvc.post(`${environment.publicApiUri}${applicantApiCtrl}/SaveHousehold`, hhmodel)
    .pipe(tap(resp => {
      this.householdUpdated.next(resp);
      this.currentHousehold.next(resp);
    }));
  }

  //updateHousingChoices
  updateHousingChoices(hhmodel: Household): Observable<any> {
    return this.apiSvc.post(`${environment.publicApiUri}${applicantApiCtrl}/SaveHouseholdPreferences`, hhmodel)
      .pipe(tap(resp => {
        if (resp.Success) {
          this.getHousehold();
        }
      }));
  }

  doesHouseholdExist(){
      var url = environment.publicApiUri + 'Applicant/HouseholdExists';
      return this.apiSvc.get(url);
  }

  //updateMember
  updateHouseholdMember(member: HouseholdMemberProfile):Observable<any>{
    return this.apiSvc.post(`${environment.publicApiUri}${applicantApiCtrl}/SaveHouseholdMemberProfile`, member).pipe(tap(resp => {
      console.log('this is the response from updated member post req:', resp);
      this.memberUpdated.next(resp);
    }));
  }

  //removeMember
  removeHouseholdMember(id:number):Observable<any>{
    return this.apiSvc.get(`${environment.publicApiUri}${applicantApiCtrl}/RemoveHouseholdMember?householdMemberId=${id}`)
    .pipe(
      tap(resp => this.memberRemoved.next(id)) //push out Id of removed member onto stream
      );
  }

  // member income
  addHouseholdMemberIncome(member: HouseholdMemberIncome):Observable<any>{
    return this.apiSvc.post(`${environment.publicApiUri}${applicantApiCtrl}/SaveHouseholdMemberIncome`, member).pipe(tap(resp => {
      console.log('this is the response from updated member post req:', resp);
      this.memberIncomeAdded.next(resp);
    }));
  }

  removeMemberIncome(memberId:number, incomeId:number){
    return this.apiSvc.get(`${environment.publicApiUri}${applicantApiCtrl}/RemoveHouseholdMemberIncome?householdMemberId=${memberId}&incomeId=${incomeId}`).pipe(tap(resp => {
      console.log('this is the response from updated member post req:', resp);
      this.memberIncomeRemoved.next({memberId:memberId,incomeId:incomeId});
    }));
  }

  updateMemberHasNoIncome(memberId:number){
    return this.apiSvc.get(`${environment.publicApiUri}${applicantApiCtrl}/UpdateMemberHasNoIncome?memberid=${memberId}`).pipe(tap(resp => {
      console.log('this is the response from no income:', resp);
      this.memberHasNoIncome.next(memberId);
    }));
  }

  updateMemberHasNoAssets(memberId:number){
    return this.apiSvc.get(`${environment.publicApiUri}${applicantApiCtrl}/UpdateMemberHasNoAssets?memberid=${memberId}`).pipe(tap(resp => {
      console.log('this is the response from no income:', resp);
      this.memberHasNoAssets.next(memberId);
    }));
  }

  updateHouseholdMemberIncome(member: HouseholdMemberIncome):Observable<any>{
    return this.apiSvc.post(`${environment.publicApiUri}${applicantApiCtrl}/SaveHouseholdMemberIncome`, member).pipe(tap(resp => {
      console.log('this is the response from updated member post req:', resp);
      this.memberIncomeUpdated.next(resp);
    }));
  }

  // member asset
  addHouseholdMemberAsset(member: HouseholdMemberAsset): Observable<any> {
    return this.apiSvc.post(`${environment.publicApiUri}${applicantApiCtrl}/SaveHouseholdMemberAsset`, member).pipe(tap(resp => {
      console.log('this is the response from updated member post req:', resp);
      this.memberAssetAdded.next(resp);
    }));
  }

  removeMemberAsset(memberId: number, assetId: number) {
    return this.apiSvc.get(`${environment.publicApiUri}${applicantApiCtrl}/RemoveHouseholdMemberAsset?householdMemberId=${memberId}&AssetId=${assetId}`).pipe(tap(resp => {
      console.log('this is the response from updated member post req:', resp);
      this.memberAssetRemoved.next({ memberId: memberId, assetId: assetId });
    }));
  }

  updateHouseholdMemberAsset(member: HouseholdMemberAsset): Observable<any> {
    return this.apiSvc.post(`${environment.publicApiUri}${applicantApiCtrl}/SaveHouseholdMemberAsset`, member).pipe(tap(resp => {
      console.log('this is the response from updated member post req:', resp);
      this.memberAssetUpdated.next(resp);
    }));
  }

  doesHouseholdOwnApplication(lotteryApplicationId: number): Observable<any> {
    return this.apiSvc.get(`${environment.publicApiUri}${applicantApiCtrl}/HouseholdOwns?lotteryApplicationId=${lotteryApplicationId}`);
  }

  //Member Profile
  updateMemberProfileStep(step: number){
    this.memberProfileStep.next(step);
  }

  getIEPData(iepId: number) {
    return this.apiSvc.get(`${environment.publicApiUri}${applicantApiCtrl}/GetIepData?iepId=${iepId}`);
  }

  getSchoolPlacementsData(iepId,locationId,services) {
     return this.apiSvc.get(`${environment.publicApiUri}${applicantApiCtrl}/GetSchoolPlacements?iepId=${iepId}&schoolLocationId=${locationId}&bGetServices=${services}`);
  }

  getAssignedProvider(serviceId) {
     return this.apiSvc.get(`${environment.publicApiUri}${applicantApiCtrl}/GetAssignedProvider?serviceId=${serviceId}`);
  }

}
