import { ChangeDetectorRef, Component, Input, OnInit, ViewChild } from '@angular/core';
import { AgmMap } from '@agm/core';
import * as _ from 'lodash';
import { NearbyOptionsModel, NEARBY_PLACE_TYPES, NearbyMarkerType, NearbyTransit, TRANSIT_LINES } from '../../models/nearbyoptions.model';
import { Marker } from '@agm/core/services/google-maps-types';
import { MarkerModel } from '../../models/marker.model';

@Component({
  selector: 'app-nearby',
  templateUrl: './nearby.component.html',
  styleUrls: ['./nearby.component.scss']
})
export class NearByComponent implements OnInit {

  @Input() nearbyOptions: NearbyOptionsModel;
  @Input() mapdata: MarkerModel[] = [];
  @Input() nearby: MarkerModel[] = [];
  @Input() viewMode: string = "detail"; // map-only, detail

  // This is the data that will be displayed
  mapDataFiltered: MarkerModel[] = [];
  nearbyFiltered: MarkerModel[] = [];
  transit: MarkerModel[] = [];
  nearbyTransit: NearbyTransit[] = [];
  
  noBuildings: number;

  @ViewChild('map') map: AgmMap;

  selectedBuilding: number;
  showNearby: boolean = true;
  showNearByFilters: boolean = true;

  nearbyPlaceTypes: NearbyMarkerType[] = NEARBY_PLACE_TYPES;

  _options: NearbyOptionsModel;

  // initial center position for the map
  lat: number;
  lng: number;

  // google maps zoom level
  zoom: number = 15;

  markerLabels = "ABCDEFGHIJKLMNOPQRSTUVWXYZ";

  latArray: number[] = [];
  lngArray: number[] = [];

  constructor(private cdRef: ChangeDetectorRef) {

  }

  ngOnInit() {
    if (!this.nearbyOptions)
      this.nearbyOptions = new NearbyOptionsModel();
    this._options = JSON.parse(JSON.stringify(this.nearbyOptions));

    this.initMapStartLocation();

    this.getNearbyTransit();

    this.filterLocations();

    console.log('Nearby component - Map Data', this.mapdata);
    this.noBuildings = this.mapdata.length;

  }

  // Calculates the average of all the buildings as the center point for the map 
  initMapStartLocation() {
    if (this.mapdata.length) {
      const sumLat = this.mapdata.reduce(function (sum, elem) {
        return sum + parseFloat((elem.lat)? elem.lat.toString() : '0');
      }, 0);
      const avgLat = sumLat / this.mapdata.length;

      const sumLng = this.mapdata.reduce(function (sum, elem) {
        return sum + parseFloat((elem.lng)? elem.lng.toString() : '0');
      }, 0);
      const avgLng = sumLng / this.mapdata.length;

      this.lat = +avgLat;
      this.lng = +avgLng;
    }
  }

  selectPinIcon(type: string) {
    switch (type) {
      //School
      case 'School':
        return 'http://maps.google.com/mapfiles/ms/icons/orange-dot.png';
      //Subway Station
      case 'SubwayStation':
        return 'http://maps.google.com/mapfiles/ms/icons/purple-dot.png';
      //Train Station
      case 'TrainStation':
        return 'http://maps.google.com/mapfiles/ms/icons/yellow-dot.png';
      //Police
      case 'Police':
        return 'http://maps.google.com/mapfiles/ms/icons/blue-dot.png';
      //Park
      case 'Park':
        return 'http://maps.google.com/mapfiles/ms/icons/green-dot.png';
      //Hospital
      case 'Hospital':
        return 'http://maps.google.com/mapfiles/ms/icons/pink-dot.png';

      default:
        return 'http://maps.google.com/mapfiles/ms/icons/red-dot.png';
    }
  }

  filterLocations() {
    if (this.mapdata.length > 0) {
      //for (const [index, obj] of this.mapdata.entries()) {
      this.mapDataFiltered = [];
      for (let i = 0; i < this.mapdata.length; i++) {
        const obj = this.mapdata[i];

        const id = obj['id'] || (obj['buildingId'] || '').toString();
        const lat = obj['lat'] || obj['latitude'];
        const lng = obj['lng'] || obj['longitude'];
        const label = obj['label'] || obj['name'] || '';
        const address = obj['address'] || '';
        const city = obj['city'] || '';
        const state = obj['state'] || '';
        const zip = obj['zip'] || '';
        const fullAddress = obj['fullAddress'] || `${address}, ${city}, ${state} ${zip}`;
        const markerLabel = this.markerLabels[i % this.mapdata.length];
        const iconUrl = obj['iconUrl'] || `http://chart.apis.google.com/chart?chst=d_map_spin&chld=0.6|0|36a3f7|12|_|${markerLabel}`;

        let markerToInsert: MarkerModel = {
          id: Number(id),
          index: i + 1,
          markerLabel: markerLabel,
          lat: +lat,
          lng: +lng,
          label: label,
          address: address,
          city: city,
          state: state,
          zip: zip,
          fullAddress: fullAddress,
          iconUrl: iconUrl,
          draggable: false
        };

        this.mapDataFiltered.push(markerToInsert);
      }
    }

    if (this.nearby) {
      //this.nearbylocations = this.nearby.filter(l => ((this.selectedBuilding && l.buildingId == this.selectedBuilding) || !this.selectMarker) && this.nearbyOptions[l.placeType]);
      this.nearbyFiltered = this.nearby.filter(l => this.nearbyOptions[l.type]);
      this.nearbyFiltered.forEach((n, i) => {
        n.color = this.nearbyPlaceTypes.find(p => p.name === n.type).color;
        n.iconUrl = `http://chart.apis.google.com/chart?chst=d_map_spin&chld=0.3|0|${n.color.substring(1, 7)}|12|_|`;
      });
    }
  }

  getNearbyTransit() {    

    this.transit = this.nearby.filter(n => n.trainName);
    
    // How do I maintain 'this' and also the original marker in this nested forEach loop?
    //this.transit.forEach(item => item.trainName.split(',').forEach(function (name) {
    //    const transitLine = new NearbyTransit();
    //    transitLine.marker = 
    //    this.nearbyTransit.push
    //  }, this), this);

    //);

    // Going back to basics
    for (let i = 0; i < this.transit.length; i++) {
      const lines: string[] = this.transit[i].trainName.split(',');
      for (let j = 0; j < lines.length; j++) {
        let line = new NearbyTransit();
        line.name = lines[j].trim();
        line.marker = this.transit[i];
        line.transitLine = TRANSIT_LINES[line.name];

        if (!this.nearbyTransit.find(l => l.name == line.name)) {
          this.nearbyTransit.push(line);
        }        
      }
    }

    //this.nearbyTransit = _.orderBy(this.transit, 'trainName');
      
  }

  selectMarker(m: MarkerModel, $event) {
    //console.log('selectMarker');
    //const bId = this.selectedBuilding;
    this.selectedBuilding = m.id;
    // fix error if lat/lng has changed
    // this.filterLocations();

    // if current building is different previous, display nearby locations automatically
    //if (bId !== m.id) {
    //  this.showNearby = false;
    //}
    //this.toggleNearby();

    this.lat = +m.lat;
    this.lng = +m.lng;
    this.zoom = 17;
    

    this.cdRef.detectChanges();
  }

  //selectMarker(marker: MarkerModel) {
  //  console.log("Marker selected : ", marker);
  //}

  toggleNearby() {
    this.showNearby = !this.showNearby;
    //this.showNearByFilters = !this.showNearByFilters;

    if (!this.showNearby) {
      this._options = JSON.parse(JSON.stringify(this.nearbyOptions));
      for (let place in this.nearbyOptions) {
        this.nearbyOptions[place] = false;
      }
    }
    else {
      for (let place in this.nearbyOptions) {
        this.nearbyOptions[place] = this._options[place];
      }
    }
    this.filterLocations();
  }

  toggleLocations(placeType: string) {
    if (!(placeType in this.nearbyOptions))
      return;

    this.nearbyOptions[placeType] = !this.nearbyOptions[placeType];

    //if (!this.showNearby) {
    //  this.toggleNearby();
    //}

    this.filterLocations();
  }

  clickedMarker(label: string, index: number) {
    //console.log(`clicked the marker: ${label || index}`);
  }

  initMap(event) {
  }

  onZoomChange(event) {
    this.zoom = event;
  }

  onMapClick($event) {
    //this.markers.push({
    //  lat: $event.coords.lat,
    //  lng: $event.coords.lng,

    //  draggable: true
    //});
  }
}
