import { NgModule} from '@angular/core';
import { RouterModule, Routes} from '@angular/router';
import { DashboardComponent} from "./dashboard.component";
import { ApplicationsComponent} from "./pages/applications/applications.component";
import { AccountSettingsComponent} from "./pages/account-settings/account-settings.component";
import { AppointmentsComponent } from './pages/appointments/appointments.component';
import { ApplicationDocsComponent} from './pages/application-docs/application-docs.component'
import { AppointmentSetupComponent } from './pages/appointment-setup/appointment-setup.component';
import { AlertsComponent } from './pages/alerts/alerts.component';
import { AuthGuard } from '../../shared/auth/auth.guard';
import { HouseholdApplicationGuard } from '../../core/guards/household-application.guard';

const routes: Routes = [
  {
    path: '',
    component: DashboardComponent,
  },
  {
    path: 'applications',
    component: ApplicationsComponent,
  },
  {
    path: 'account-settings',
    component: AccountSettingsComponent,
  },
  {
    path: 'application-documents',
    component: ApplicationDocsComponent,
  },
  {
    path: 'appointments',
    component: AppointmentsComponent,
  },
  {
    path: 'appointment-setup/:lotteryApplicationId',
    component: AppointmentSetupComponent,
    canActivate: [AuthGuard, HouseholdApplicationGuard]
  },
  {
    path: 'alerts',
    component: AlertsComponent,
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class DashboardRoutingModule {}
