import { Injectable } from '@angular/core';
import { PublicApiService } from '../../../core/services/public-api.service';

@Injectable()
export class ApplicationsApiService {
  constructor(private publicApiService: PublicApiService) {}

  getApplications() {
    return this.publicApiService.get('Applicant/GetApplicationSummary');
  }
 
  getAttendance() {
    return this.publicApiService.get('Applicant/GetStudentAttendance');
  }
 
  getServices() {
    return this.publicApiService.get('Applicant/GetStudentIEPServices');
  }

  getMeetingLocations(lotteryId) {
    return this.publicApiService.get('Applicant/GetMeetingLocations?lotteryId=' + lotteryId);
  }

  getAppointmentSchedules(viewModel) {
    return this.publicApiService.post('Applicant/GetAppointmentSlots', viewModel);
  }

  getAppeal(lotteryApplicationId) {
    return this.publicApiService.get(`Applicant/GetAppeal?lotteryApplicationId=${lotteryApplicationId}`);
  }

  postAppeal(data) {
    return this.publicApiService.post('Applicant/SaveAppeal', data, { params: data });
  }

  getDocumentation(applicationId, additionaldocs)
  {
    return this.publicApiService.get('Applicant/GetDocumentation?applicationId='+applicationId + '&bAdditional=' + additionaldocs);
  }

  getMyAlerts() {
    return this.publicApiService.get('Applicant/GetMyAlerts');
  }

  getUnreadComments() {
    return this.publicApiService.get('Applicant/GetUnReadComments');
  }

  getComplaint(lotteryApplicationId) {
    return this.publicApiService.get(`Applicant/GetComplaint?lotteryApplicationId=${lotteryApplicationId}`);
  }

  getRequestedAppointments() {
    return this.publicApiService.get('Applicant/GetRequestedAppointments');
  }

  getScheduledAppointments() {
    return this.publicApiService.get('Applicant/GetAppointmentsByHousehold')
  }

  getApplicationAppointments(lotteryApplicationId) {
    return this.publicApiService.get(`Applicant/GetApplicationAppointments?lotteryApplicationId=${lotteryApplicationId}`)
  }

  checkLotteryApplication(lotteryApplicationId) {
    return this.publicApiService.get(`Applicant/HouseholdOwns?lotteryApplicationId=${lotteryApplicationId}`)
  }

  getApplicationDetails(lotteryApplicationId) {
    return this.publicApiService.get(`Applicant/GetApplicationDetails?lotteryApplicationId=${lotteryApplicationId}`)
  }

  getRecommendedLotteries() {
    return this.publicApiService.get('Applicant/RecommendedLotteries')
  }

  postComplaint(data) {
    return this.publicApiService.post('Applicant/SaveComplaint', data, { params: data });
  }

  postAppointment(appointmentModel) {
    return this.publicApiService.post('Applicant/MakeAppointment', appointmentModel);
  }

  saveConsent(data) {
    return this.publicApiService.post('Applicant/SaveConsent', data, { params: data });
  }

  checkIsConsent(id) {
    return this.publicApiService.get(`Applicant/GetConsent?lotteryApplicationId=${id}`);
  }

  submitInterest(data) {
    return this.publicApiService.post('Applicant/SaveInterest', data);
  }

  canAppeal(id) {
    return this.publicApiService.get(`Applicant/CanAppeal?lotteryId=${id}`);
  }

  canComplaint(id) {
    return this.publicApiService.get(`Applicant/CanComplaint?lotteryId=${id}`);
  }
}
