import { Component, Input, OnInit, ChangeDetectorRef } from '@angular/core';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { FormControl, Validators } from "@angular/forms";
import { ActivatedRoute } from "@angular/router";
import { ApplicationsApiService } from '../../services/applications-api.service'
import { AppSettings } from 'projects/web/src/environments/appsettings';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'detail-complaint',
  templateUrl: './detail-complaint.component.html'
})
export class AppealComplaintComponent implements OnInit {
  @Input() lottery: any = {};

  complaintDetails: any;
  noteDocGroupId: string;
  disabledBtn: boolean = false;
  complaintStatus: any;

  actionText: string = "Complain to HPD";
  capitalize: Function = (str) => str.charAt(0).toUpperCase() + str.slice(1).toLowerCase();

  complaintReason: FormControl = new FormControl();
  constructor(
    public activeModal: NgbActiveModal,
    private applicationsApiService: ApplicationsApiService,
    private toastr: ToastrService,
    private cdRef: ChangeDetectorRef
  ) { }

  ngOnInit() {
    this.noteDocGroupId = AppSettings.complaintNoteGroupID;

    this.applicationsApiService.getComplaint(this.lottery.details.lotteryApplication.lotteryApplicationId).subscribe(complaintDetails => {
      this.complaintDetails = complaintDetails;
      console.log(this.complaintDetails);
      this.complaintReason.setValue(this.complaintDetails.reason);
      this.complaintStatus = this.complaintDetails.complaintStatus.name;
      this.cdRef.detectChanges();
      
    })
  }

  isReasonExists() {
    if (!this.complaintReason.value) {
      return false;
    }
    return true;
  }

  onUploadComplaint() {
    console.log(this.complaintDetails);
    console.log("Complaint Reason", this.complaintReason);

    if (!this.complaintDetails) {
      this.applicationsApiService.postComplaint({
        lotteryId: this.lottery.details.lotteryId,
        reason: this.complaintReason.value
      }).subscribe(response => {
        console.log(`Save complaint response`, response);
        this.toastr.success("Complaint Saved");
        this.onClose();
      });
    }
    else {
      this.applicationsApiService.postComplaint({
        lotteryId: this.lottery.details.lotteryId,
        reason: this.complaintReason.value,
        complaintId: this.complaintDetails.complaintId
      }).subscribe(response => {
        console.log(`Save complaint response`, response);
        this.toastr.success("Complaint Saved");
        this.onClose();
      });
    }



  }

  postComplaint() {
    console.log(this.complaintDetails);
    console.log("Complaint Reason", this.complaintReason);
    if (this.complaintDetails != null) {
      this.toastr.success("complaint Updated");
      this.onClose();
    }
    else {
      this.toastr.error("Document attachment required");
      
    }
  
  }

  onClose() {
    this.activeModal.dismiss('Cross click');
  }
}
