/// <reference types='@types/googlemaps' />
import {Component, OnInit, ViewChild, Input, OnDestroy, ElementRef, Output, EventEmitter, ChangeDetectorRef,} from '@angular/core';
import {MapMarker} from './mapMarker.class';
import {Observable, Subscription} from 'rxjs';

import { loadGoogleScript } from '../../helpers/load-google-script';
import { MarkerModel } from '../../models/marker.model';

@Component({
  selector: 'app-map',
  templateUrl: './map.component.html',
  styleUrls: ['./map.component.scss'],
})
export class MapComponent implements OnInit, OnDestroy {
  @Input() markers: Array<MarkerModel>;
  @Input() events?: Observable<any>;
  @Input() view?: string = "MapOnly"; // MapOnly, Details
  @Input() selectedMarker?: number = -1;
  @Input() address?: string;
  @Output() clickedMarker: EventEmitter<any> = new EventEmitter();
  @Output() fileComplaint: EventEmitter<any> = new EventEmitter();

  @ViewChild('gmap') gmapElement: ElementRef;

  eventsSubscription: Subscription;

  mapInstance: google.maps.Map;
  mapListeners: google.maps.MapsEventListener[] = [];
  mapMarkers: Array<any> = [];
  mapBounds: google.maps.LatLngBounds = new google.maps.LatLngBounds();
  transitLayer: google.maps.TransitLayer;
  poiMap: google.maps.StyledMapType;
  addressResult: any;

  constructor(private cdRef: ChangeDetectorRef) {
  }

  ngOnInit() {
    //if (this.events) {
    //  this.eventsSubscription = this.events.subscribe({
    //    next: (arg) => {
    //      console.log('fromchild', arg);
    //      this.applyPoiStyles(this.mapInstance, arg);
    //    },
    //    error: () => {
    //    },
    //    complete: () => {
    //    },
    //  })
    //}

    const mapConfig = {
      center: new google.maps.LatLng(40.782248, -73.965651),
      zoom: 15,
      mapTypeId: google.maps.MapTypeId.TERRAIN,
      styles: [
        {
          featureType: 'administrative.land_parcel',
          elementType: 'labels',
          stylers: [
            {
              visibility: 'off'
            }
          ]
        },
        {
          featureType: 'poi',
          elementType: 'labels.text',
          stylers: [
            {
              visibility: 'off'
            }
          ]
        },
        {
          featureType: 'poi.business',
          stylers: [
            {
              visibility: 'off'
            }
          ]
        },
        {
          featureType: 'road',
          elementType: 'labels.icon',
          stylers: [
            {
              visibility: 'off'
            }
          ]
        },
      ].map(styleItem => styleItem as google.maps.MapTypeStyle),
      zoomControl: true,
      mapTypeControl: false,
      scaleControl: false,
      streetViewControl: true,
      rotateControl: false,
      fullscreenControl: false,
    };

    this.mapInstance = new google.maps.Map(this.gmapElement.nativeElement, mapConfig);
    
    if (this.markers) {
     this.addMarkers(this.markers);
     this.showMarkers(this.mapInstance);

     if (this.markers.length > 1) {
       this.fitMapToBounds(this.mapInstance, this.mapBounds);
      //  if (this.mapInstance.getZoom() > 16) {
        this.mapInstance.setZoom(14);
      //  }
     } else if (this.markers.length && this.markers[0].lat && this.markers[0].lng){
       this.mapInstance.setCenter(new google.maps.LatLng(
         this.markers[0].lat || this.markers[0].latlng.lat(),
         this.markers[0].lng || this.markers[0].latlng.lng()));
       this.mapInstance.setZoom(14);
     }
    }
    else if (this.address) {
      let geocoder = new google.maps.Geocoder();
      geocoder.geocode({address: this.address}, (callback) => {
        console.log(callback);
        this.addressResult = callback[0];
        let latlng = this.addressResult.geometry.location;
        let marker = new MarkerModel();
        marker.latlng = latlng;
        this.addMarkers([latlng]);
        this.showMarkers(this.mapInstance);
        this.mapInstance.setCenter(latlng);
        this.mapInstance.setZoom(14);
        this.cdRef.detectChanges();
      })
    }
    

    //const addToFavsListener = google.maps.event.addListener(this.mapInstance, 'addToFavorites', lotteryId => {
    //  console.log('adding to favorites in component', lotteryId);
    //  // some magic will happen here
    //  google.maps.event.trigger(this.mapInstance, 'lotteryToggleComplete', {id: lotteryId});
    //});

    //const closePopupListener = google.maps.event.addDomListener(this.mapInstance, 'click', () => {
    //  google.maps.event.trigger(this.mapInstance, 'openPopup', null);
    //});

    //this.mapListeners = [addToFavsListener, closePopupListener];

  }

  ngOnDestroy() {
    this.mapListeners.forEach(google.maps.event.removeListener);
  }

  ngOnChanges() {
    if (this.markers && this.mapInstance) {
      this.resetMarkersAndBounds();
      this.addMarkers(this.markers);
      this.showMarkers(this.mapInstance);
 
      if (this.markers.length > 1) {
        this.fitMapToBounds(this.mapInstance, this.mapBounds);
         if (this.mapInstance.getZoom() > 16) {
         this.mapInstance.setZoom(14);
         }
      } else if (this.markers.length && this.markers[0].lat && this.markers[0].lng){
        this.mapInstance.setCenter(new google.maps.LatLng(
          this.markers[0].lat || this.markers[0].latlng.lat(),
          this.markers[0].lng || this.markers[0].latlng.lng()));
        this.mapInstance.setZoom(14);
      }
     }
  }

  public onGridItemHover(markers) {
    this.resetMarkersAndBounds();
    this.addMarkers(markers);
    this.showMarkers(this.mapInstance);
    this.fitMapToBounds(this.mapInstance, this.mapBounds);
    if (this.mapInstance.getZoom() > 14) {
      this.mapInstance.setZoom(14);
    }
  }

  private addMarkers(markers:Array<any>): void {
    markers
      //.sort((a, b) => b.lat - a.lat)
      .forEach(marker => {
        if (!marker.lat || !marker.lng) {
           console.log('missing required map coordinates');
           return;
        }

        const LatLng = new google.maps.LatLng(
          marker.lat || marker.latlng.lat(),
          marker.lng || marker.latlng.lng());

        let args = {
          name: marker.BUSINESS_NAME,
          location: marker.BUILDING + ' ' + marker.STREET,
          daysToLive: marker.LICENSE_NBR,
          images: [
            marker.imgUrl
          ],
          iconUrl: (marker.SWC_TYPE == 'Unenclosed')?'/assets/images/map-marker-green.svg':((marker.SWC_TYPE == 'Enclosed')?'/assets/images/map-marker-orange.svg':'/assets/images/map-marker-red.svg')
        }
        // const selected = {'selected': (this.selectedMarker)? this.selectedMarker:-1, 'id':marker.id};
        const mapMarker = new MapMarker(LatLng, args, this.mapInstance, false);
        //  marker.latlng,
        //  marker.details,
        //  this.mapInstance,
        //  false
        //);
        //mapMarker.addListener('click', function() {
        //  this.mapInstance.setZoom(8);
        //  this.mapInstance.setCenter(mapMarker.getPosition());
        //});
        mapMarker.emitter.subscribe(data => {
          console.log('MAP MARKER CLICKED', data);

          this.clickedMarker.emit(data);
        })
        mapMarker.complaintEmitter.subscribe(data => {
          this.fileComplaint.emit(data);
        })
        

        this.mapMarkers.push(mapMarker);
        if (LatLng.lat() > 40.756) this.mapBounds.extend(LatLng);

      })

    //console.log(markers.length);
  }


  private setMarkersToMap(map: google.maps.Map): void {
    this.mapMarkers.forEach((marker) => {
      marker.setMap(map);
    });
  }

  private showMarkers(map: google.maps.Map): void {
    this.setMarkersToMap(map);
  }

  private hideMarkers(): void {
    this.setMarkersToMap(null);
  }

  private addTransitLayer(map): void {
    this.transitLayer = new google.maps.TransitLayer();
    this.transitLayer.setMap(map);
  }

  private fitMapToBounds(map, bounds): void {
    map.fitBounds(bounds);
  }

  private applyPoiStyles(map, formValues) {
    const resultingStyles = [
      {
        featureType: 'administrative.land_parcel',
        elementType: 'labels',
        stylers: [
          {
            visibility: 'off'
          }
        ]
      },
      {
        featureType: 'poi',

        stylers: [
          {
            visibility: 'off'
          }
        ]
      },
      {
        featureType: 'poi.business',
        stylers: [
          {
            visibility: 'off'
          }
        ]
      },
      {
        featureType: 'road',
        elementType: 'labels.icon',
        stylers: [
          {
            visibility: 'off'
          }
        ]
      },
    ].map(styleItem => styleItem as google.maps.MapTypeStyle);
    // Types of poi 
    // https://developers.google.com/places/supported_types
    const styles = {
      park: {
        "featureType": "poi.park",
        "stylers": [
          {
            "visibility": "on",
          },
        ],
      },
      hospital: {
        "featureType": "poi.medical",
        "stylers": [
          {
            "visibility": "on",
          },
        ],
      },
      school: {
        "featureType": "poi.school",
        "stylers": [
          {
            "visibility": "on",
          },
        ],
      },
      police: {
        "featureType": "poi.police",
        "stylers": [
          {
            "visibility": "on",
          }
        ],
      },
    };

    Object.keys(formValues).forEach(key => {
      if (formValues[key]) {
        resultingStyles.push(styles[key] as google.maps.MapTypeStyle);
      }
    });
    //console.log(resultingStyles);

    if (resultingStyles.length) {
      const styledMapType = new google.maps.StyledMapType(resultingStyles);
      map.mapTypes.set('poiMap', styledMapType);
      map.setMapTypeId('poiMap');
    }
  }

  private resetMarkersAndBounds(): void {
    this.hideMarkers();
    this.mapMarkers = [];
    this.mapBounds = new google.maps.LatLngBounds();
  }
}
