import { CurrencyPipe } from '@angular/common';
import { HouseholdMemberIncome } from './../../models/hh-member-income.model';
import { HouseholdService } from './../../services/household/household.service';
import { UtilityService } from './../../services/utility/utility.service';
import { ScrollStepper } from './../../../../core/modules/scroll-form/scroll-form.component';
import { FormHelperService } from './../../services/form-helper/form-helper.service';
import { Component, OnInit, AfterContentInit, Input, ChangeDetectorRef, EventEmitter, Output, OnChanges, SimpleChanges, ViewChild } from '@angular/core';
import { FormGroupDirective, FormGroup, ControlContainer } from '@angular/forms';
import { HouseholdMemberProfile } from '../../../../entities';
import { IncomeTypeEnum, IncomeFrequencyTypeEnum } from '../../../../shared/enums/applicantEnums';
import { IncomeSourceFormConstructor } from '../helpers/income-source-form-constructor';
import { Subject, forkJoin } from 'rxjs';
import { takeUntil, debounceTime, distinctUntilChanged } from 'rxjs/operators';
import { ConfigValueService } from 'projects/web/src/app/shared/services/config-value/config-value.service';
import { MatSelectionListChange } from '@angular/material';
import { IncomeSubType } from 'projects/web/src/app/shared/models';
import { MemberEtiquette } from '../../models/member-etiquette.model';
import { BsDatepickerConfig } from 'ngx-bootstrap';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'income-source-form',
  templateUrl: './income-source-form.component.html',
  styleUrls: ['./income-source-form.component.scss'],
  viewProviders: [{
    provide: ControlContainer,
    useExisting: FormGroupDirective
  }]
})
export class IncomeSourceFormComponent extends IncomeSourceFormConstructor implements OnInit, AfterContentInit, OnChanges {

  @Input() memberProfile: HouseholdMemberProfile;
  @Input() incomeSource: HouseholdMemberIncome = null;
  @Output() handleGotoMemberProfile = new EventEmitter<any>();
  @Output() handleIncomeSourceUpdate = new EventEmitter<any>();
  @ViewChild(ScrollStepper) scrollStepper:ScrollStepper;

  dpConfig: Partial<BsDatepickerConfig> = {
    containerClass: 'theme-dark-blue',
    dateInputFormat: 'MM/DD/YYYY',
    showWeekNumbers: false
  }
  maxDate:Date = new Date();
  minDateEntry: Date = new Date(1900, 0, 1);

  IncomeTypeEnum = IncomeTypeEnum;
  IncomeFrequencyTypeEnum = IncomeFrequencyTypeEnum;

  selectedIncomeType:number;
  mainForm: FormGroup;
  unsubscribed: Subject<void>;
  employmentTypeTitle: string;
  employmentTypeDescription: string;
  otherEmploymentDescription: string;
  selectedIncomeSource:string = 'income source';
  currentEmployer:string = 'employer';
  subTypeDdlDefaultText:string = 'Benefit Type';

  taxFilingTypeOptions: any[];
  didNotFileTaxFilingType: any;
  taxFilingTypesDisabled: boolean = false;

  incomeSubTypeOptions: IncomeSubType[];
  filteredIncomeSubTypes: IncomeSubType[];
  memberEtiquette:MemberEtiquette;
  isSaving:boolean = false;
  selectedIndex:number;
  isBenefitsOther: boolean;

  constructor(
    private formHelpSvc: FormHelperService,
    private configValSvc: ConfigValueService,
    public utilSvc: UtilityService,
    private cdRef: ChangeDetectorRef,
    private householdSvc:HouseholdService,
    private toastr:ToastrService,
    public cp:CurrencyPipe
  ) {
    super(cp,utilSvc)
  }

  ngOnInit() {
    this.utilSvc.currentMemberEtiquette$.subscribe(memberEtiquette => this.memberEtiquette = memberEtiquette);

    this.localFormGroup = new FormGroup({});
    let configvalSub = this.configValSvc.getAllConfigTypes('TaxFilingType');
    //get all the different income sub Types here
    let subTypesSub = this.configValSvc.getIncomeSubTypes();

    forkJoin(configvalSub, subTypesSub).subscribe(([configValOptions, subTypes]) => {
      this.setTaxTypesConfig(configValOptions);
      this.incomeSubTypeOptions = subTypes;
    })
  }

  showEditIncome:boolean = false;
  ngOnChanges(changes: SimpleChanges): void {
    if (!this.unsubscribed) this.unsubscribed = new Subject();

    if (changes.incomeSource && changes.incomeSource.currentValue) {

      this.showEditIncome = this.incomeSource.householdMemberIncomeId > 0;
      this.selectedIncomeType = this.incomeSource.incomeTypeId;
      this.createIncomeBasedForm(this.selectedIncomeType);
      this.setIncomeFormValueAndContext(this.selectedIncomeType);
      if(!this.showEditIncome && this.scrollStepper) this.scrollStepper.reset(); //Need to reset stepper going from update to add or will throw error
      this.incomeType.setValue(this.selectedIncomeType);
      this.cdRef.detectChanges();
    }
  }

  setIncomeFormValueAndContext(incomeType) {
    switch (incomeType) {
      case IncomeTypeEnum.Employment: {
        this.employmentTypeTitle = 'Employment Income';
        this.otherEmploymentDescription = null;
        this.employmentTypeDescription = `
        You selected that ${this.memberEtiquette.properAddressNoun} works for an employer.
        Income from employment is calculated differently than income from self-employment. In this case,
        income from employment means that ${this.memberEtiquette.properAddressNoun} ${this.memberEtiquette.isSelf? 'receive':'receives'} income from a company or another person.
        ${this.memberEtiquette.properAddressNoun} may receive a W2 from the employer,
        for example. If this description of employment does not apply to ${this.memberEtiquette.properAddressNoun}, please go back.`;
        this.setEmploymentSubscriptions();
        break;
      }
      case IncomeTypeEnum.SelfEmployment: {
        this.employmentTypeTitle = 'Self-Employment Income';
        this.otherEmploymentDescription = null;
        this.employmentTypeDescription = `
        Income from self-employment is calculated differently than other types of income.
        In this case, income from self-employment means that you have your own business,
        do freelance work, work as an independent consultant, etc.
        You may receive a 1099 tax form for this work, for example.
        If this description of self-employment does not apply to ${this.memberEtiquette.properAddressNoun}, please go back.`;
        this.setSelfEmploymentSubscriptions();
        break;
      }
      case IncomeTypeEnum.GovernmentBenefits: {
        this.employmentTypeTitle = 'Government Benefits Income';
        this.otherEmploymentDescription = `Income from government benefits: (unemployment, public assistance, TANF, Armed Forces Reserve, etc.)`
        this.setupFormForOtherEmployment(IncomeTypeEnum.GovernmentBenefits);
        break;
      }
      case IncomeTypeEnum.RetirementDisability: {
        this.employmentTypeTitle = 'Retirement/Disablility Income';
        this.otherEmploymentDescription = `Income from retirement or disability: (pension, annuity, social security, SSI/SSD, worker’s compensation, etc.)`
        this.setupFormForOtherEmployment(IncomeTypeEnum.RetirementDisability);
        break;
      }
      case IncomeTypeEnum.OtherPeople: {
        this.employmentTypeTitle = 'Other Sources Income';
        this.otherEmploymentDescription = `Income from other sources: (child support, gift income, alimony, rental income, etc)`
        this.setupFormForOtherEmployment(IncomeTypeEnum.OtherPeople);
        break;
      }
      default: {
        this.employmentTypeTitle = 'Something Went Wrong';
        this.otherEmploymentDescription = null;
        this.employmentTypeDescription = 'Something went wrong here. Please go back and try reselect an income type.'
        break;
      }
    }

    this.setFormValues(this.incomeSource);
  }

  otherEmploymentConfig = {
    benefitTypesDefaultText : 'Benefit Types',
    question1Noun: 'benefit type',
    addAnotherNoun: 'Benefit Type'
  }

  updateOtherEmploymentConfig(){
    switch(this.selectedIncomeType){
      case IncomeTypeEnum.GovernmentBenefits: {
        this.otherEmploymentConfig.benefitTypesDefaultText = "Government Benefit Type";
        this.otherEmploymentConfig.question1Noun = 'government benefit type';
        this.otherEmploymentConfig.addAnotherNoun = "'Government Income'";
        break;
      }
      case IncomeTypeEnum.RetirementDisability: {
        this.otherEmploymentConfig.benefitTypesDefaultText = "Retirement/Disability Type";
        this.otherEmploymentConfig.question1Noun = 'retirement or disability benefit type';
        this.otherEmploymentConfig.addAnotherNoun = "'Retirement/Disability Income'";
        break;
      }
      case IncomeTypeEnum.OtherPeople: {
        this.otherEmploymentConfig.benefitTypesDefaultText = "Other Source Benefit Type";
        this.otherEmploymentConfig.question1Noun = 'other source of income benefit type';
        this.otherEmploymentConfig.addAnotherNoun = "'Other Source of Income'";
        break;
      }
    }
  }

  setupFormForOtherEmployment(subTypeId:number){
    this.employmentTypeDescription = `You selected that ${this.memberEtiquette.properAddressNoun} receive income from sources other than employment or self-employment.
        If this description of employment does not apply to ${this.memberEtiquette.properAddressNoun}, please go back.`;
    this.updateOtherEmploymentConfig();
    this.filteredIncomeSubTypes = this.incomeSubTypeOptions.filter(opt => opt.incomeTypeId === subTypeId || opt.incomeTypeId === 0);
    this.subTypeId.valueChanges.pipe(takeUntil(this.unsubscribed)).subscribe(value => this.onSubTypeChanged(value));
    this.currentIncomeAmount.valueChanges.pipe(takeUntil(this.unsubscribed),debounceTime(500),distinctUntilChanged()).subscribe(value => this.onCurrentIncomeChanged(value));
  }

  setEmploymentSubscriptions() {
    this.employerName.valueChanges.pipe(takeUntil(this.unsubscribed), debounceTime(500),distinctUntilChanged()).subscribe(empName => this.onEmployerNameChanged(empName));
    this.isCityEmployee.valueChanges.pipe(takeUntil(this.unsubscribed)).subscribe(value => this.onCityEmployeeChanged());
    this.isW2.valueChanges.pipe(takeUntil(this.unsubscribed)).subscribe(value => this.onIsW2Changed());
    this.w2AmountsGroup.valueChanges.pipe(takeUntil(this.unsubscribed), debounceTime(500)).subscribe(values => this.onW2AmountsChanged(values));
    this.hasIncomeChanged.valueChanges.pipe(takeUntil(this.unsubscribed)).subscribe(() => this.onHasIncomeChanged());
  }

  setSelfEmploymentSubscriptions() {
    this.hasIncomeChanged.valueChanges.pipe(takeUntil(this.unsubscribed)).subscribe(() => this.onHasIncomeChanged());
    this.employerName.valueChanges.pipe(takeUntil(this.unsubscribed), debounceTime(500)).subscribe(empName => this.onEmployerNameChanged(empName));
    this.taxFilingInfo.valueChanges.pipe(takeUntil(this.unsubscribed), debounceTime(200)).subscribe(formValues => this.onTaxFilingInfoChanged(formValues));
    this.hasNoBusinessName.valueChanges.pipe(takeUntil(this.unsubscribed)).subscribe((checked:boolean) => this.onHasNoBusinessName(checked));
  }

  onEmployerNameChanged(empName){
    this.currentEmployer = empName;
  }

  onCityEmployeeChanged() {
    let showCityEmployeeFollowUp = this.isCityEmployee.value;
    if (showCityEmployeeFollowUp)
      this.formHelpSvc.setControlsRequired([this.isLotteryAffiliated]);
    else
      this.formHelpSvc.clearFormValuesAndValidatorsTarget([this.isLotteryAffiliated]);
  }

  onIsW2Changed() {
    let showW2FollowUp = this.isW2.value;
    let targetControls = [this.w2Box1, this.w2Box4, this.w2Box5];

    if (!showW2FollowUp) {
      this.formHelpSvc.clearFormValuesTarget(targetControls);
      this.formHelpSvc.clearFormValuesAndValidatorsTarget([this.hasIncomeChanged]);
      this.formHelpSvc.setControlsRequired([this.currentNetIncomeAmount]);
    }

  }

  onW2AmountsChanged(values) {
    console.log('w2 Amounts:', values);
    if (this.w2AmountsGroup.valid) {
      let maxAmount = Math.max(parseFloat(values.w2Box1), parseFloat(values.w2Box4), parseFloat(values.w2Box5));
      this.filedAnnualIncomeAmount.setValue(maxAmount);
    }
  }

  onHasIncomeChanged(){
    let showIncomeChangeFollowUp = this.hasIncomeChanged.value;
    if(showIncomeChangeFollowUp){
      this.formHelpSvc.setControlsRequired([this.currentNetIncomeAmount]);
    } else {
      this.formHelpSvc.clearFormValuesAndValidatorsTarget([this.currentNetIncomeAmount]);
    }
  }

  onTaxFilingTypesChanged(matOptions: MatSelectionListChange) {
    let didNotFile = matOptions.option.selected && matOptions.option.value === this.didNotFileTaxFilingType.configTypeId.toString();

    if (matOptions.option.selected)
      this.addMemberTaxFilingLineItem(matOptions.option.value);
    else
      this.removeMemberTaxFilingLineItem(matOptions.option.value);

    if (didNotFile && matOptions.option.selected) {
      this.taxFilingOptions.setValue(['4']);
      this.taxFilingOptions.updateValueAndValidity();
      this.formHelpSvc.disableFormControls(this.taxFilingInfo.controls);
      this.formHelpSvc.setControlsRequired([this.currentNetIncomeAmount]);
      this.currentNetIncomeAmount.setValue(null);
      this.cdRef.detectChanges();
    } else {
      this.taxFilingOptions.setValue(
        (matOptions.option.selected) ?
          this.taxFilingOptions.value.filter(r => r != '4') : this.taxFilingOptions.value.filter(r => r != matOptions.option.value.toString())
      );
      this.taxFilingOptions.updateValueAndValidity();
      this.formHelpSvc.setControlsRequired([this.hasIncomeChanged]);
    }

    this.taxFilingTypesDisabled = didNotFile;
    // if(this.taxFilingTypesDisabled){
    //   this.formHelpSvc.setControlsRequired([this.currentNetIncomeAmount]);
    // if(this.currentNetIncomeAmount.value === this.filedAnnualIncomeAmount.value)
    // this.currentNetIncomeAmount.setValue(null);
    // }
  }

  onTaxFilingInfoChanged(formValues) {
    console.log('here are taxinfo form values:', formValues);
    if (this.taxFilingInfo.valid) {
      let amountsList = formValues.map(r => parseFloat(r.amount.replace(/[^0-9\.]+/g,"")));
      let sum = amountsList.reduce((acc: number, x: number) => {
        let xValue = Math.sign(x) === -1 ? 0 : x;
        return acc + xValue;//Treat negative numbers as 0
      }, 0);
      this.filedAnnualIncomeAmount.setValue(sum);
      this.filedAnnualIncomeAmount.updateValueAndValidity();
    }
  }

  onSubTypeChanged(value: string) {
    if (value) {
      this.selectedIncomeSource = `'${this.filteredIncomeSubTypes.find(r => r.incomeSubTypeId === parseInt(value)).name}'`;
      this.isBenefitsOther = (value == '16') ? true : false;

      // ENABLE/DISABLE OTHERSUBTYPE
      if(this.isBenefitsOther)
        this.formHelpSvc.enableFormControls([this.otherSubType]);
      else {
        this.formHelpSvc.disableFormControls([this.otherSubType]);
        this.otherSubType.setValue(null);
      }

    }
  }

  onCurrentIncomeChanged(value){
    if(this.currentIncomeFreqType.valid){
      let calcNetIncome = this.utilSvc.calcNetIncomeByFrequency(parseInt(this.currentIncomeFreqType.value), parseFloat(value.replace(/[^0-9\.]+/g, "")));
      this.currentNetIncomeAmount.setValue(calcNetIncome);
    }
  }

  onHasNoBusinessName(isChecked){
    if(isChecked)
      this.employerName.setValue('Self Employed');
    else
      this.employerName.setValue('');
  }

  //Need to provide special treatment for not filed
  setTaxTypesConfig(configOptions) {
    this.taxFilingTypeOptions = configOptions.TaxFilingType.filter(r => r.configTypeId !== 4);//taxFilingType 4 = did not file
    this.didNotFileTaxFilingType = configOptions.TaxFilingType.find(r => r.configTypeId === 4);
  }

  ngAfterContentInit(): void { }

  onGotoMemberProfile(event) {
    this.handleGotoMemberProfile.emit(event);
    this.scrollStepper.ngOnDestroy();
    this.unsubscribed.next();
    this.unsubscribed.complete();
  }

  getToolTip(taxTypeId) {
    let context = ''
    switch (taxTypeId) {
      case '1': {
        context = 'Schedule C Net Profit';
        break;
      }
      case '2': {
        context = 'Schedule E Total Income';
        break;
      }
      default: {
        context = 'W2 Box 1 Amount';
        break;
      }
    }

    return context;
  }

  getName(taxTypeId) {
    let context = ''
    switch (taxTypeId) {
      case '1': {
        context = 'scheduleCnetProfit';
        break;
      }
      case '2': {
        context = 'scheduleETotalIncome';
        break;
      }
      default: {
        context = 'w2Box1Amount';
        break;
      }
    }

    return context;
  }

  incomeInvalid() {
    if (this.w2AmountsGroup) return this.w2AmountsGroup.invalid;
  }

  onAddIncome(addAnotherIncome:boolean = false){
    this.isSaving = true;
    let memberIncomeModel = this.mapFormToModel(this.memberProfile);

    this.householdSvc.addHouseholdMemberIncome(memberIncomeModel).subscribe(() => {
      this.toastr.success('Member Income Added');
      if(addAnotherIncome)
        this.addAnotherEmployment();
      else
        this.onGotoMemberProfile(null);
    },() => this.isSaving = false, () =>this.isSaving = false);
  }

  onUpdateIncome(){
    this.isSaving = true;
    let memberIncomeModel = this.mapFormToModel(this.memberProfile, this.incomeSource);
    this.householdSvc.updateHouseholdMemberIncome(memberIncomeModel).subscribe(incomeSourceModel => {
      this.toastr.success('Member Income Updated');
      this.incomeSource = incomeSourceModel;
      this.handleIncomeSourceUpdate.emit(this.incomeSource);
      this.onGotoMemberProfile(null);
    },() => this.isSaving = false, () =>this.isSaving = false);
  }

  addAnotherEmployment() {
    this.scrollStepper.scrollToSection(0, null);

    setTimeout(() => {
      this.resetForm(this.selectedIncomeType);
    }, 500)
  }
}
