import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'schoolLocation'
})
export class SchoolCategoryPipe implements PipeTransform {
  transform(items: Array<any> = [], id: any): Array<any> {
      return items.filter(itemOrArray => (!Array.isArray(itemOrArray))? itemOrArray.schooL_LOCATION_ID === id : itemOrArray.some(x => x.schooL_LOCATION_ID === id));
  }
}
