import { Component, OnInit, Input, ElementRef, ViewChild, TemplateRef } from '@angular/core';

@Component({
  selector: 'app-section-header',
  templateUrl: './section-header.component.html',
  styleUrls: ['./section-header.component.scss']
})
export class SectionHeaderComponent implements OnInit {
  isUnderlined: boolean = false;

  @Input() underline: string;

  @Input() title: TemplateRef<any>;
  @Input() subtitle: TemplateRef<any>;
  @Input() description: TemplateRef<any>;
  @Input() actions: TemplateRef<any>;
  

  @ViewChild('subtitle') elSubtitle: ElementRef; // Because ng-content only allows 1 instance and need 2 instances, we will duplicate the HTML content of the rendered instance

  constructor() { }

  ngOnInit() {
    this.isUnderlined = this.underline == "";
  }

}
