import {NgModule} from '@angular/core';
import {ReactiveFormsModule} from "@angular/forms";
import {CommonModule} from '@angular/common';

import {MAT_DATE_LOCALE, MatDatepickerModule} from "@angular/material";
import {DatapickerFormcontrolInputService} from "./datapicker-formcontrol-input.service";
import {DatapickerFormcontrolInputComponent} from "./datapicker-formcontrol-input.component";

@NgModule({
  imports: [
    CommonModule,
    MatDatepickerModule,
    ReactiveFormsModule,
  ],
  declarations: [DatapickerFormcontrolInputComponent],
  exports: [DatapickerFormcontrolInputComponent],
  providers: [
    DatapickerFormcontrolInputService,
    {provide: MAT_DATE_LOCALE, useValue: 'en-GB'}
  ]
})
export class DatapickerFormcontrolInputModule {
}
