import { Component, OnInit, ChangeDetectorRef } from '@angular/core';

import { Router } from '@angular/router';
import {LotteryApiService} from "../../../../shared/services/api/lottery-api.service";

@Component({
  selector: 'app-homepage-carousel',
  templateUrl: './carousel.component.html',
  styleUrls: ['./carousel.component.scss']
})
export class HomepageCarouselComponent implements OnInit {
  constructor(private lotteryApiService: LotteryApiService,
    private cdRef: ChangeDetectorRef,
    private router: Router
  ) { }

  carouselData: any[];
  displayedColumns = ['houseHold', 'combinedIncome', 'monthlyRent'];
  getFacilitiesIcon(value) {
    switch (value) {
      case 'animal':
        return 'fa-paw';
      case 'gym':
        return 'fa-dumbbell';
      default:
        return 'love';
    }
  };

  openLottery(lotteryId) {
    this.router.navigate(['/details/' + lotteryId]);
  }

  ngOnInit() {
    const facilities = ['animal', 'gym', 'washMachine', 'animal', 'animal'];
    const facilitiesInIcon = facilities.map(this.getFacilitiesIcon);

    const tableData = [
      { houseHold: '1 Person', combinedIncome: '$45,678 - $95,567', monthlyRent: '$8,765' },
      { houseHold: '2 Person', combinedIncome: '$45,678 - $95,567', monthlyRent: '$8,765' },
      { houseHold: '3 Person', combinedIncome: '$45,678 - $95,567', monthlyRent: '$8,765' },
      { houseHold: '4 Person', combinedIncome: '$45,678 - $95,567', monthlyRent: '$8,765' }
    ];

    const availableUnit = [
      { title: 'Studios', data: tableData },
      { title: '1 BR', data: tableData },
      { title: '2 Br', data: tableData },
      { title: '3 Br', data: tableData }
    ];

    this.lotteryApiService.getLatestLotteries().subscribe((result) => {
      let lotteryResults = result.map((lottery) => {
        let subwaysArray = (lottery.trains) ? lottery.trains.split(',').sort() : [];
        let subwayArray = [];
        if (subwaysArray.length - 1 >= 0) {
          for (var i = 0; i < subwaysArray.length - 1; i++) {
            if (subwaysArray[i] !== subwaysArray[i + 1]) {
              subwayArray.push(subwaysArray[i]);
            }
          }
          subwayArray.push(subwaysArray[subwaysArray.length - 1]);
        }
        return {
          mainImage: (lottery.defaultPhotoStream) ? (lottery.defaultPhotoStream) : '/assets/images/demo-lottery-house.png',
          details: {
            street: lottery.lotteryName,
            area: ((lottery.neighborhood) ? lottery.neighborhood : 'Neighborhood') + ' | ' + ((lottery.borough) ? lottery.borough : 'Borough'),
            expirationDateText: (lottery.endIn) ? ('Lottery closing in ' + lottery.endIn + ' days') : "",
            subwayServices: subwayArray,
            lotteryId: lottery.lotteryId,
          }
        }
      })
      if (lotteryResults) this.carouselData = lotteryResults;
      if(!this.cdRef["destroyed"])this.cdRef.detectChanges();
    })
  }
}
