import {Component, Input, Host, HostListener, OnInit, TemplateRef, ViewChild} from '@angular/core';
import {DropdownSelectorComponent} from "./dropdown-selector.component";

@Component({
  selector: 'dropdown-selector-option-checkbox',
  template: `
      <div class="dropdown-item-container pr-sm pl-sm" [class.selected]="isSelected" >
        <mat-checkbox [checked]="isSelected" [disabled]="true">
          <ng-content></ng-content>
        </mat-checkbox>
      </div>`,
  styleUrls: ['./dropdown-selector-option-checkbox.component.scss']
})
export class DropdownSelectorOptionCheckboxComponent implements OnInit {
  @Input() value: any;
  @Input() label: string;

  get isSelected() {
    if (this.value == undefined) {
      return false
    }
    return this.parentSelector.isChildSelected(this.value);
  }

  constructor(
    @Host() public parentSelector: DropdownSelectorComponent
  ) {

    if (this.parentSelector.isSelectorMultiple == undefined) {
      this.parentSelector.setTypeOfOption({type: 'checkbox'});
    }
  }

  ngOnInit(): void {
    this.parentSelector.bootstrapChild({
      value: this.value,
      label: this.label,
    });
  }

  @HostListener('click', ['$event']) onClick() {
    this.parentSelector.toggleItem({
      value: this.value,
      label: this.label,
    });
  }
}
