import { Component, ChangeDetectorRef, OnInit, OnDestroy } from '@angular/core';
import { Router, NavigationEnd } from '@angular/router';
import { OAuthService } from 'angular-oauth2-oidc';
import { UtilityService } from '../../../household/services/utility/utility.service';

@Component({
  selector: 'app-homepage-greeter',
  templateUrl: './greeter.component.html',
  styleUrls: ['./greeter.component.scss']
})
export class GreeterComponent implements OnInit, OnDestroy {
  userLoggedIn: boolean = false;
  navigationSubscription;
  userSettings = {
    'showCurrentLocation': false,
    'showSearchButton': false,
    'inputPlaceholderText': 'Search for a business by name, address, neighborhood, or ZIP code'
  };
  
  searchValue: { address: string; city: any; state: any; zip: any; country: any; isUSAddress: boolean; };
  
  autoCompleteCallback(result, index) {
    console.log(result);
    let data = this.utilSvc.processGeoAutoCompleteResult(result);
    
    if (data) {
      let addr = data.mappedAddress;
      this.searchValue = addr;
    }
    this.cdRef.detectChanges();
  }
  constructor(private router: Router, private oauthService: OAuthService, private cdRef: ChangeDetectorRef, private utilSvc: UtilityService) {
    this.navigationSubscription = this.router.events.subscribe((e: any) => {
      // If it is a NavigationEnd event re-initalise the component
      if (e instanceof NavigationEnd) {
        this.initialiseInvites();
      }
    });
  }

  initialiseInvites() {
    this.userLoggedIn = false;
    if (this.oauthService.hasValidAccessToken()) {
      this.userLoggedIn = true;
      if (!this.cdRef['destroyed']) this.cdRef.detectChanges();
    }
  }
  

  ngOnInit() {
    this.initialiseInvites();
  }

  navigateToSearch() {
    this.router.navigate(['/search-businesses']);
  }

  navigateToLogin() {
    this.oauthService.initImplicitFlow('/some-state;p1=1;p2=2');
  }

  ngOnDestroy() {
    // avoid memory leaks here by cleaning up after ourselves. If we
    // don't then we will continue to run our initialiseInvites()
    // method on every navigationEnd event.
    if (this.navigationSubscription) {
      this.navigationSubscription.unsubscribe();
    }
  }
}
