import { Pipe, PipeTransform } from '@angular/core';
import { isString } from 'util';

@Pipe({
  name: 'orderBy'
})
export class OrderByPipe implements PipeTransform {

  transform(array, orderBy, asc = true) {

    if(!array) return array;

    if (!orderBy || orderBy.trim() == "") {
    //  return  Array.from(array).sort((a, b) =>
    //   this.orderByComparator(a, b)
    //   );
    return array;
    }

    var sortedArray = Array.from(array).sort((a, b) =>
      this.orderByComparator(a[orderBy], b[orderBy])
    );

    return asc ? sortedArray : sortedArray.reverse();

  }

  orderByComparator(alpha: any, bravo: any): number {

    if ((isNaN(parseFloat(alpha)) || !isFinite(alpha)) || (isNaN(parseFloat(bravo)) || !isFinite(bravo))) {

      if (isString(alpha)) {
        alpha = alpha.trim().toLocaleUpperCase();
        bravo = bravo.trim().toLocaleUpperCase();
      }

      return alpha < bravo ? -1 : alpha > bravo ? 1 : 0;
    }
    else {

      return parseFloat(alpha) - parseFloat(bravo);
    }
  }
}
