import {
  animate,
  state,
  style,
  transition,
  trigger,
  AnimationTriggerMetadata
} from '@angular/animations';

export const scrollFormStepAnimations: {
  readonly stepTransition: AnimationTriggerMetadata;
} = {
    stepTransition: trigger('stepTransition', [
      state('previous', style({ opacity: '0.2' })),
      state('next', style({ opacity: '0.2' })),
      state('current', style({ opacity: '1' })),
      transition('* <=> current', animate('0.3s'))
    ])

    // stepTransition: trigger('stepTransition', [
    //   state('previous', style({ color: 'black' })),
    //   state('next', style({ color: 'black' })),
    //   state('current', style({ color: 'black' })),
    //   transition('* <=> current', animate('0s'))
    // ])

  };
