import { Injectable } from '@angular/core';
import { MarkerModel } from '../../../shared/models/marker.model';
import { PropertyTypeEnum, OwnerTypeEnum } from '../../../shared/enums/developmentEnums';

interface StatusGroupColor {
  name: string; // Status group name
  color: string; // Color hex code
  colorClass: string; // CSS class name
  description: string; //tooltip description
  orderByPriority: number; //order by priority
}

@Injectable({
  providedIn: 'root'
})
export class ApplicationService {  

  public statusGroupColors: StatusGroupColor[] = [
    {
      name: 'Pending',
      color: '#e6e6e6',
      colorClass: 'grey-90',
      description: 'Application is awaiting to be selected from a list.',
      orderByPriority: 3
    },
    {
      name: 'IEP In Progress',
      color: '#ffb822',
      colorClass: 'warning',
      description: 'Application is undergoing some internal review.',
      orderByPriority: 2
    },    
    {
      name: 'Consent Required',
      color: '#00e6ab',
      colorClass: 'success',
      description: 'Application requires applicant\'s input to proceed.',
      orderByPriority: 0
    },
    {
      name: 'Completed',
      color: '#fd397a',
      colorClass: 'danger',
      description: 'Application review has reached a resolution.',
      orderByPriority: 1
    },    
  ];  

  constructor() { }

  aggregateApplicationData(item) {
    const subwayServices = item.trains
    ? Array.from(new Set(item.trains.split(','))).sort()
    : [];

    const markers: Array<MarkerModel> = (item.markers)
    ? item.markers.map((item, index) => {
      return {
        address: item.address,
        city: item.city,
        lat: item.lat,
        lng: item.lng,
        label: item.name,
        markerLabel: item.name,
        state: item.state,
        zip: item.zip,
        fullAddress: `$(item.address), $(item.city), $(item.state) $(item.zip)`,
        id: 0,
        draggable: false,
        index: index,
        iconUrl: '/assets/images/map-marker.svg',
        type: 'building',
        color: null,
        trainName: null
      } as MarkerModel;
  })
  : [];

    return {
      mainImage: (item.defaultPhotoStream)
        ? (item.defaultPhotoStream)
        : '/assets/images/demo-lottery-house.png',
      markers,
      details: {
        amenities: item.amenities,
        area: (
          (item.neighborhood)
            ? item.neighborhood
            : 'Neighborhood'
        ) + ' | ' + ((item.borough) ? item.borough : 'Borough'),
        borough: item.borough,
        description: item.lotteryDescription,
        endIn: item.endIn,
        lotteryEndDate: item.lotteryEndDate,
        lotteryId: item.lotteryId,
        lotteryName: item.lotteryName,
        street: item.lotteryName,
        subwayServices,
        images: [
          (item.defaultPhotoStream) ? (item.defaultPhotoStream) : '/assets/images/demo-lottery-house.png'
        ],
        isAddedToFavorite: false,
        expirationDate: new Date(),
        expirationDateText: (item.endIn > 0)
          ? `Lottery closing in ${item.endIn} days`
          : (item.endIn === 0)
            ? 'Lottery ends today'
            : 'Lottery closed',
        minIncome: item.minIncome,
        maxIncome: item.maxIncome,
        minHousehold: item.minHouseholdSize,
        maxHousehold: item.maxHouseholdSize,
        mobilityPref: item.mobility,
        units: item.units,
        studios: item.studios,
        oneBR: item.oneBR,
        twoBR: item.twoBR,
        threeBR: item.threeBR,
        fourBR: item.fourBR,
        unitLayouts: item.unitLayouts,
        propertyTypeId: item.propertyTypeId,
        propertyType: (item.propertyTypeId == PropertyTypeEnum.Rental ? "Rental" : (item.propertyTypeId == PropertyTypeEnum.Owner ? "Sales" : "")),
        ownerTypeId: item.ownerTypeId,
        ownerType: (item.ownerTypeId == OwnerTypeEnum.SmallHome ? "Small Home" : (item.ownerTypeId == OwnerTypeEnum.CondoCoop ? "Condo/Co-op" : "")),
        visionHearingPref: item.visionHearing,
        lotteryApplication: item.lotteryApplication,
      }
    }
  }

  getStatusGroupColorbyName(name: string): StatusGroupColor {
    return this.statusGroupColors.find(x => x.name === name);
  }
}
