import { Component} from '@angular/core';
import { FormControl, Validators } from "@angular/forms";
import { AppSettings} from "../../../../../environments/appsettings";
import { Router, ActivatedRoute } from '@angular/router';
import { ApplicationsApiService} from '../../services/applications-api.service';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-application-docs',
  templateUrl: './application-docs.component.html',
  styleUrls: ['./application-docs.component.scss'],
})
export class ApplicationDocsComponent {
  public inputnotegroupid: string = AppSettings.profileApplicationNoteGroupdId;
  public displayProfileDocs : boolean = false;  //
  constructor(private activatedRoute: ActivatedRoute, private router: Router,
              private applicationApiService: ApplicationsApiService, private toastr: ToastrService) {}

  lotteryapplicationid: number;
  lotteryId : number;
  title : string = "Profile";  
  isAgreeWithTermsOfUse: FormControl = new FormControl(null, [Validators.required]);
  isConsent : boolean = false;

  ngOnInit() {
    this.activatedRoute.queryParams.subscribe(params => {
      this.lotteryapplicationid = params['id'];
      if (this.lotteryapplicationid) {
        
        this.displayProfileDocs = true;
        this.title = "Application";

        this.applicationApiService.checkIsConsent(this.lotteryapplicationid).subscribe(data => {
          var application = data;
          this.isAgreeWithTermsOfUse.setValue(application.isConsent);
          this.lotteryId = application.lotteryId;
          this.isConsent = application.isConsent;
        })
      }
    });
  }

  submit() {     
      this.applicationApiService.saveConsent(this.lotteryapplicationid)
        .subscribe(result => {
          if (result) {
            if (result.success) {
              this.toastr.success(result.message);              
              this.router.navigate([`/dashboard/application-documents?id=` + this.lotteryapplicationid]);
            }
            else {
              this.toastr.error(result.message);
            }            
          }
          else {
            this.toastr.error("Could not process the request");
          }
      });     
    }
}
