import { MemberEtiquette } from './../../models/member-etiquette.model';
import { UtilityService } from './../../services/utility/utility.service';
import { HouseholdMemberIncome, HouseholdMemberAsset } from './../../models/hh-member-income.model';
import { Component, OnInit, AfterContentInit, Input, ChangeDetectorRef, EventEmitter, Output, OnDestroy, ViewChild } from '@angular/core';
import { FormGroupDirective, FormGroup, ControlContainer, FormArray, FormBuilder, FormControl, Validators } from '@angular/forms';
import { AssetTypeEnum } from '../../../../shared/enums/applicantEnums';
import { Subject, forkJoin, Observable } from 'rxjs';
import { ConfigValueService } from '../../../../shared/services/config-value/config-value.service';
import { AssetType } from '../../../../shared/models';
import { HouseholdMemberProfile, Household } from '../../models';
import { HouseholdService } from '../../services/household/household.service';
import { ToastrService } from 'ngx-toastr';
import { Ng4GeoautocompleteModule } from 'ng4-geoautocomplete';
import { CustomValidators } from 'projects/web/src/app/shared/custom-validation/custom-validation';
import { ScrollStepper } from 'projects/web/src/app/core/modules/scroll-form/scroll-form.component';


@Component({
  selector: 'asset-details-form',
  templateUrl: './asset-details-form.component.html',
  styleUrls: ['./asset-details-form.component.scss'],
  viewProviders: [{
    provide: ControlContainer,
    useExisting: FormGroupDirective
  }]
})
export class AssetDetailsFormComponent implements OnInit, AfterContentInit, OnDestroy {

  mainForm: FormGroup;
  assetsForm: FormGroup;

  newAccountAssetForm: FormGroup;
  newPropertyAssetForm: FormGroup;
  assetDetailsForm: FormGroup;
  //paychecksInputs: FormArray;
  private _memberProfile;
  get memberProfile(): HouseholdMemberProfile {
    return this._memberProfile;
  } 
  @Input() set memberProfile(profile: HouseholdMemberProfile) {
    this._memberProfile = profile;
    this.filterMemberAssetsByType();
  }
  memberAssetAccounts: HouseholdMemberAsset[] = [];
  memberAssetProperties: HouseholdMemberAsset[] = [];
  @Output() handleAddAsset = new EventEmitter<HouseholdMemberAsset>();
  @Output() handleEditAsset = new EventEmitter<HouseholdMemberAsset>();
  @Output() nextStep: EventEmitter<any> = new EventEmitter();
  household: Household;

  @ViewChild('addressValidator') geoValidator: any;

  isAddPropertyMode: boolean = false;
  isAddAccountMode: boolean = false;
  addPropertyAssetTypeId: string = '';
  addAccountAssetTypeId: string = '';
  newAccountName: string = '';
  isSaving: boolean = false;
  deletingAssetId: number;
  isNewPropertyAddressSameAsLivingAddress: boolean = false;
  isCurrentLivingAddressAvailable: boolean = false;
  isNewPropertyAddressValidated: boolean = false;
  newPropertyValidatedAddress: any = null;

  //salaryPerMonth = 0;
  //calculationAlgorithmSwitcher = false;

  memberNoAssets: FormGroup;
  hasNoAssetsCtrl: FormControl;

  AssetTypeEnum = AssetTypeEnum;
  assetAccountTypeOptions: any[];
  assetPropertyTypeOptions: any[];
  stateOptions: any[];

  constructor(
    private parentFormDirective: FormGroupDirective,
    private configValSvc: ConfigValueService,
    private utilSvc: UtilityService,
    private fb: FormBuilder,
    private cd: ChangeDetectorRef,
    private householdSvc: HouseholdService,
    private toastr: ToastrService) {
  }

  unsubscribe = new Subject();
  memberEtiquette: MemberEtiquette = new MemberEtiquette();

  //Settings for ng4geovalidation
  userSettings = {
    'showCurrentLocation': false,
    'showSearchButton': false,
    'inputPlaceholderText': 'Enter address here'
  };

  ngOnInit() {


    this.mainForm = this.parentFormDirective.form;

    

    this.assetsForm = this.fb.group({
      assetAccountsArray: this.fb.array([]),
      assetRealEstatePropertiesArray: this.fb.array([])
    });

    this.newAccountAssetForm = this.fb.group({
      assetType: this.fb.control('', [Validators.required]),
      assetName: this.fb.control('', [Validators.required, CustomValidators.noWhitespaceValidator])
    });

    this.newPropertyAssetForm = this.fb.group({
      assetType: this.fb.control('', [Validators.required]),
      address: this.fb.control(''),
      apartmentNumber: this.fb.control('')
    });

    this.mainForm.addControl('assetsForm', this.assetsForm);

    let configvalSub = this.configValSvc.getAllConfigTypes('AssetType', 'State');

    forkJoin(configvalSub).subscribe(([configValOptions]) => {
      this.assetAccountTypeOptions = configValOptions.AssetType.filter(option => (option.name as string).toLocaleLowerCase().indexOf('property') === -1);
      this.assetPropertyTypeOptions = configValOptions.AssetType.filter(option => (option.name as string).toLocaleLowerCase().indexOf('property') != -1);
      this.stateOptions = configValOptions.State;

      this.filterMemberAssetsByType();
    })

    this.householdSvc.currentHousehold$.subscribe(hh => {
      this.household = hh;

      this.isCurrentLivingAddressAvailable = this.household.address ? true : false;

    });

    console.log(this.memberProfile.householdMemberAssets);
    console.log("filtered properties", this.memberAssetProperties);

    this.utilSvc.currentMemberEtiquette$.subscribe(memberEtiquette => this.memberEtiquette = memberEtiquette);
    this.householdSvc.memberAssetAdded$.subscribe(updatedAsset => this.onAddNewAsset(updatedAsset));
    this.householdSvc.memberAssetUpdated$.subscribe(updatedAsset => this.onUpdateAsset(updatedAsset));
  }

  filterMemberAssetsByType() {
    if (this.assetPropertyTypeOptions && this.assetAccountTypeOptions) {
      this.memberAssetProperties = this._memberProfile.householdMemberAssets.filter(asset => this.assetPropertyTypeOptions.some(el => el.configTypeId == asset.assetTypeId));
      this.memberAssetAccounts = this._memberProfile.householdMemberAssets.filter(asset => this.assetAccountTypeOptions.some(el => el.configTypeId == asset.assetTypeId));    
    }    
  }

  onUpdateAsset(updatedAsset: HouseholdMemberAsset) {
    let index = this.memberProfile.householdMemberAssets.findIndex(r => r.householdMemberAssetId === updatedAsset.householdMemberAssetId);
    this.memberProfile.householdMemberAssets[index] = { ...updatedAsset };
    this.filterMemberAssetsByType();
  }

  onAddNewAsset(newAsset: HouseholdMemberAsset) {
    this.memberProfile.householdMemberAssets.push({ ...newAsset });
    this.filterMemberAssetsByType();
  }

  onUpdateNoAssets(scrollStepper: ScrollStepper) {

    if (!this.memberAssetProperties.length) {
      this.householdSvc.updateMemberHasNoAssets(this.memberProfile.householdMemberProfileId).subscribe(result => {
        if (result.success) {
          console.log('member profile updated');
          this.memberProfile.isNoAssets = true;
          scrollStepper.scrollNext();
        }
        else
          console.log('Please remove all assets before declaring member has no income');
      });
    } else {
      scrollStepper.scrollPrevious();
    }

  }

  ngAfterContentInit(): void { };

  addAccountAsset(assetTypeId) {
    const arrayControl = this.assetsForm.controls['assetAccountsArray'] as FormArray;

    let newGroup = this.fb.group({
      //id: new FormControl(0),
      assetName: new FormControl('', [Validators.required]),
      assetType: new FormControl(assetTypeId, [Validators.required]),
      assetValue: new FormControl('', [Validators.required])
    });

    arrayControl.push(newGroup);
  }

  addAccountAssetType(type) {

    this.newAccountAssetForm.get('assetType').setValue(type);
    this.newAccountAssetForm.get('assetName').setValue('');
    this.addAccountAssetTypeId = type;
    this.isAddAccountMode = true;
  }

  addPropertyAssetType(type) {

    this.addPropertyAssetTypeId = type;
    this.isAddPropertyMode = true;
    this.isNewPropertyAddressValidated = false;
    this.newPropertyValidatedAddress = null;

    this.newPropertyAssetForm.get('assetType').setValue(type);
    this.newPropertyAssetForm.get('address').setValue('');
    this.newPropertyAssetForm.get('apartmentNumber').setValue('');
  }

  cancelAddNewAccountType() {
    this.isAddAccountMode = false;
  }

  cancelAddNewPropertyType() {
    this.isAddPropertyMode = false;
  }

  getAccountAssetTypeName(type) {
    return this.assetAccountTypeOptions.find(option => option.configTypeId === type).name;
  }

  getPropertyAssetTypeName(type) {
    return this.assetPropertyTypeOptions.find(option => option.configTypeId === type).name;
  }

  addPropertyAsset(type) {
    //let model = new HouseholdMemberAsset()
    //model.assetTypeId = type;
    //model.householdMemberAssetId = 0;
    //this.handleAddAsset.emit(model);

    //const arrayControl = this.assetsForm.controls['assetRealEstatePropertiesArray'] as FormArray;

    //let propertyGroup = this.fb.group({
    //  //id: new FormControl(0),
    //  //assetName: new FormControl('', [Validators.required]),
    //  assetType: new FormControl(type, [Validators.required]),
    //  assetStreetAddress: new FormControl('', [Validators.required]),
    //  propertyAddress: new FormControl(''),
    //  propertyUnitNumber: new FormControl(''),
    //  propertyCity: new FormControl(''),
    //  propertyState: new FormControl(''),
    //  propertyZip: new FormControl(''),
    //  propertyCountry: new FormControl(''),
    //  noPropertyAddress: new FormControl('')
    //});

    //arrayControl.push(propertyGroup);

  }

  //removeAsset(index: number) {
  //  const arrayControl = this.assetsForm.controls['assetAccountsArray'] as FormArray;
  //  arrayControl.removeAt(index);
  //}  

  onUpdateAssetType(group: FormGroup) {
    console.log('this is the formarray group', group)
    
  }

  saveAccountAsset() {

    let form = this.newAccountAssetForm;

    console.log(form);

    let newAsset: HouseholdMemberAsset = new HouseholdMemberAsset();

    newAsset.householdMemberAssetId = 0;
    newAsset.householdMmbrPrflId = this.memberProfile.householdMemberProfileId;
    newAsset.assetTypeId = this.newAccountAssetForm.get('assetType').value;
    newAsset.name = this.newAccountAssetForm.get('assetName').value;
    newAsset.address = null;
    newAsset.city = null;
    newAsset.state = null;
    newAsset.zip = null;
    newAsset.value = null;
    newAsset.apartmentNumber = null;
    newAsset.country = null;
    newAsset.isActive = true;

    this.saveAsset(newAsset);
  }

  autoCompleteCallback(result) {
    let data = this.utilSvc.processGeoAutoCompleteResult(result);
    if (data) {
      this.isNewPropertyAddressValidated = true;
      this.newPropertyValidatedAddress = data;
      this.newPropertyAssetForm.get('address').setValue(this.geoValidator.locationInput);
    }
  }

  savePropertyAsset() {

    let newAsset: HouseholdMemberAsset = new HouseholdMemberAsset();

      newAsset.householdMemberAssetId = 0;
      newAsset.householdMmbrPrflId = this.memberProfile.householdMemberProfileId;
      newAsset.assetTypeId = parseInt(this.addPropertyAssetTypeId);
      newAsset.name = this.getPropertyAssetTypeName(this.addPropertyAssetTypeId);      
      newAsset.value = null;      
      newAsset.isActive = true;

    if (this.isNewPropertyAddressValidated) {
      this.newPropertyAssetForm.get('address').setValue('');

      newAsset.address = this.newPropertyValidatedAddress.mappedAddress.address;
      newAsset.city = this.newPropertyValidatedAddress.mappedAddress.city;
      newAsset.state = this.newPropertyValidatedAddress.mappedAddress.state;
      newAsset.zip = this.newPropertyValidatedAddress.mappedAddress.zip;
      newAsset.apartmentNumber = this.newPropertyAssetForm.get('apartmentNumber').value;
      newAsset.country = this.newPropertyValidatedAddress.mappedAddress.country;
    } else {
      newAsset.address = this.geoValidator.locationInput ? this.geoValidator.locationInput : 'Address not available';
      newAsset.city = null;
      newAsset.state = null;
      newAsset.zip = null;
      newAsset.apartmentNumber = this.newPropertyAssetForm.get('apartmentNumber').value;
      newAsset.country = null;
    }

    this.saveAsset(newAsset);    
  }

  addPropertyWithNoAddress() {
    let newAsset: HouseholdMemberAsset = new HouseholdMemberAsset();

    newAsset.householdMemberAssetId = 0;
    newAsset.householdMmbrPrflId = this.memberProfile.householdMemberProfileId;
    newAsset.assetTypeId = parseInt(this.addPropertyAssetTypeId);
    newAsset.name = this.getPropertyAssetTypeName(this.addPropertyAssetTypeId);
    newAsset.address = 'Address not available';
    newAsset.city = null;
    newAsset.state = null;
    newAsset.zip = null;
    newAsset.value = null;
    newAsset.apartmentNumber = null;
    newAsset.country = null;
    newAsset.isActive = true;

    this.saveAsset(newAsset);
  }

  saveAsset(memberAssetModel: HouseholdMemberAsset) {
    this.isSaving = true;

    this.householdSvc.addHouseholdMemberAsset(memberAssetModel).subscribe(r => {
      this.toastr.success('Asset has been added');
      this.isAddPropertyMode = false;
      this.isAddAccountMode = false;
      this.cd.detectChanges();

    }, () => { this.isSaving = false }, () => { this.isSaving = false; this.cd.detectChanges(); });
  }

  removeAsset(asset: HouseholdMemberAsset) {
    this.deletingAssetId = asset.householdMemberAssetId;

    this.householdSvc.removeMemberAsset(asset.householdMmbrPrflId, asset.householdMemberAssetId).subscribe(() => {
      this.memberProfile.householdMemberAssets = this.memberProfile.householdMemberAssets.filter(r => r.householdMemberAssetId !== asset.householdMemberAssetId);
      this.toastr.success('Asset removed');
      this.deletingAssetId = null;
      this.filterMemberAssetsByType();
      this.cd.detectChanges();
    });
  }

  onAddAsset(type) {
    let model = new HouseholdMemberAsset()
    model.assetTypeId = type;
    model.householdMemberAssetId = 0;
    this.handleAddAsset.emit(model);
  }

  onEditAsset(model) {
    this.handleAddAsset.emit({ ...model });
  }

  getTotalAssetAccountsTotal() {
    const assetsArray = this.assetsForm.controls['assetAccountsArray'] as FormArray;
    if (assetsArray) {
      let total = assetsArray.controls.reduce((sum, elem: FormGroup) => {
        return sum + (parseInt(elem.get('assetValue').value) || 0)
      }, 0);
      return total;
    } else return 0;
  }

  getAssetTypeName(assetTypeId) {

  }

  onChangeIsCurrentLivingAddress(value) {
    this.isNewPropertyAddressSameAsLivingAddress = value.checked;
    if (this.isNewPropertyAddressSameAsLivingAddress) {

      let fullAddress = this.household.address ? `${this.household.address}${this.household.city ? ', ' + this.household.city : ''}${this.household.state ? ', '+this.getStateLabel(this.household.state) : ''}${this.household.zip ? ' '+this.household.zip: ''}`: '';
      this.geoValidator.locationInput = fullAddress;
      this.newPropertyAssetForm.get('apartmentNumber').setValue(this.household.apartmentNumber);
    }
  }

  getStateLabel(stateId) {
    let optionValue = this.stateOptions.find(r => r.configTypeId === stateId);
    return optionValue ? optionValue.name : '';
  }

  ngOnDestroy(): void {
    this.unsubscribe.next();
    this.unsubscribe.complete();
  }

  log(e) {
    console.log(e);
  }

  trimValues(form: FormGroup) {
    form.valueChanges
      .map((value) => {
        // Here you can manipulate your value
        value.firstName = value.firstName.trim();
        return value;
      })
      .filter((value) => form.valid)
      .subscribe((value) => {
        console.log("Model Driven Form valid value: vm = ", JSON.stringify(value));
      });
  }
}
