  horizontalCardTransition: trigger('cardTransition', [
    state('previous', style({ transform: 'translate3d(-{{distance}}%, 0px, 0px)' }), {
      params: { distance: '0' }
    }),
    state('current', style({ transform: 'translate3d(-{{distance}}%, 0px, 0px)' }), {
      params: { distance: '0' }
    }),
    state('next', style({ transform: 'translate3d(-{{distance}}%, 0px, 0px)' }), {
      params: { distance: '0' }
    }),
    transition('* => *', animate('1s cubic-bezier(0.35, 0, 0.25, 1)'))
  ])

