import { Directive } from '@angular/core';
import { SliderComponent } from '../slider.component';
import { CdkSlider } from '../core/cdk-slider';

@Directive({
  selector: 'button[sliderNext]',
  host: {
    '(click)': '_slider.nextCard()'
  },
  providers: [{ provide: CdkSlider, useExisting: SliderComponent }]
})
export class SliderNext {
  constructor(public _slider: SliderComponent) {}
}

@Directive({
  selector: 'button[sliderPrevious]',
  host: {
    '(click)': '_slider.previousCard()'
  },
  providers: [{ provide: CdkSlider, useExisting: SliderComponent }]
})
export class SliderPrevious {
  constructor(public _slider: SliderComponent) {}
}
