import { Component, OnInit } from '@angular/core';
import { FaqService } from '../../services/faq.servises';
import * as _ from 'lodash';
import { OAuthService } from 'angular-oauth2-oidc';

interface FAQInterface {
  isActive: boolean;
  answer: string;
  categoryId: number;
  displayOrder: number;
  faqId: number;
  question: string;
}

@Component({
  selector: 'app-faq',
  templateUrl: './faq.component.html',
  styleUrls: ['./faq.component.scss']
})
export class FAQComponent implements OnInit {
  sticky: boolean = false;
  categoryIndexMap: Map<any, any>;
  faqs = {
    data: []
  };
  categories: any[] = [];
  activeCategoryId: number;
  isLoadingData: boolean = true;

  constructor(private faqService: FaqService, private oauthService: OAuthService) {}

  ngOnInit() {
    this.getFaqCategories();
    this.getFaqsSource();
  }

  getFaqCategories() {
    this.faqService.getCategories().subscribe(data => {
      this.categories = data;
      this.activeCategoryId = this.categories[0].id;
    });
  }

  getFaqsSource() {
    this.faqService.getAllFaqs().subscribe(data => {
      const faqsData = this.mapFAQDataByCategory(data);
      this.faqs['data'] = this.sortingFAQDataByDisplayOrder(faqsData);

      this.isLoadingData = false;
    });
  }

  mapFAQDataByCategory(data) {
    // ToDo need Refactor this piece
    this.categoryIndexMap = new Map();

    return data.reduce((source, item) => {
      let arrayIndex = this.categoryIndexMap.get(item.categoryId);

      if (arrayIndex === undefined) {
        arrayIndex = source.length;
        source[arrayIndex] = { categoryId: item.categoryId, list: [] };
        this.categoryIndexMap.set(item.categoryId, arrayIndex);
      }

      source[arrayIndex].list.push(item);
      return source;
    }, []);
  }

  sortingFAQDataByDisplayOrder(data) {
    _.forEach(data, FAQGroup => {
      FAQGroup.list = _.sortBy(FAQGroup.list, item => item.displayOrder);
    });
    return data;
  }

  switchToCategory(id): void {
    this.activeCategoryId = id;
  }

  get isUserLogin() {
    return this.oauthService.getAccessToken();
  }
}
