import { EventEmitter } from "@angular/core";

export function MapPopup(latlng, args, map) {
  if (!window['google']) {
    return;
  }
  this.emitter = new EventEmitter<any>();
  // constructor(latlng, args, map) {
  google.maps.OverlayView.call(this);
  this.latlng = latlng;
  this.args = args;
  this.map = map;
  this.div = HTMLDivElement;
  this.elementsMap = {};
  this.listenerMap = {};
  // }

  this.draw = function () {
    this.imageIndex = 0;

    let bubble = this.elementsMap.popUpBubble;
    if (!bubble) {
      this.buildDOM();

      bubble = this.elementsMap.popUpBubble;

      this.addListeners();

      let panes = this.getPanes();
      panes.overlayImage.appendChild(bubble);
    }

    let point = this.getProjection().fromLatLngToDivPixel(this.latlng);

    if (point) {
      bubble.style.left = point.x - 163 + 'px';
      bubble.style.top = point.y - 400 + 'px';
      bubble.style.left = '-630px';
      bubble.style.top = '-100px';
    }
  }

  this.onRemove = function () {
    this.removeListeners();
    if (this.elementsMap.popUpBubble && this.elementsMap.popUpBubble.parentNode) {
      this.elementsMap.popUpBubble.parentNode.removeChild(this.elementsMap.popUpBubble);
      this.elementsMap.popUpBubble = null;
    }
  }

  this.getPosition = function () {
    return this.latlng;
  }

  this.nextImage = function () {
    const prevIdx = this.imageIndex;
    this.imageIndex = (this.imageIndex + 1) % this.args.images.length;
    this.setImage(this.imageIndex);
    this.setActiveSliderDot(this.imageIndex, prevIdx);
  }

  this.previousImage = function () {
    const prevIdx = this.imageIndex;
    this.imageIndex = (this.imageIndex + this.args.images.length - 1) % this.args.images.length;
    this.setImage(this.imageIndex);
    this.setActiveSliderDot(this.imageIndex, prevIdx);
  }

  this.addToFavorites = function () {
    google.maps.event.trigger(this.map, 'addToFavorites', this.args.id);
  }

  this.setImage = function (idx) {
    this.elementsMap.popUpImage.src = this.args.images[idx];
  }

  this.setActiveSliderDot = function (idx, prevIdx) {
    this.elementsMap.popUpSliderDots[prevIdx].className = 'popup-slider-dot-item';
    this.elementsMap.popUpSliderDots[idx].className = 'popup-slider-dot-item active';
  }

  this.toggleFavorite = function () {
    if (this.args.isFavorite) {
      this.args.isFavorite = false;
      this.elementsMap.popUpStar.className = 'la la-star-o';
    } else {
      this.args.isFavorite = true;
      this.elementsMap.popUpStar.className = 'la la-star';
    }
  }

  this.buildDOM = function () {
    this.elementsMap.popUpBubble = document.createElement('div');
    this.elementsMap.popUpBubble.className = 'popup-bubble';

    this.elementsMap.mapPopUpContainer = document.createElement('div');
    this.elementsMap.mapPopUpContainer.className = 'map-popup-container';

    this.elementsMap.mapPopupImageContainer = document.createElement('div');
    this.elementsMap.mapPopupImageContainer.className = 'map-popup-image-container';

    this.elementsMap.popUpStarContainer = document.createElement('p');
    this.elementsMap.popUpStarContainer.className = 'popup-star';

    this.elementsMap.popUpStar = document.createElement('i');
    this.elementsMap.popUpStar.className = this.args.isFavorite ? 'la la-star' : 'la la-star-o';

    this.elementsMap.popUpArrowLeft = document.createElement('p');
    this.elementsMap.popUpArrowLeft.className = 'popup-arrow-left';
    this.elementsMap.popUpArrowLeft.innerHTML = '<i class="la la-angle-left"></i>';

    this.elementsMap.popUpArrowRight = document.createElement('p');
    this.elementsMap.popUpArrowRight.className = 'popup-arrow-right';
    this.elementsMap.popUpArrowRight.innerHTML = '<i class="la la-angle-right"></i>';

    this.elementsMap.popUpSliderDotsContainer = document.createElement('div');
    this.elementsMap.popUpSliderDotsContainer.className = 'popup-slider-dots';

    this.elementsMap.popUpSliderDots = [];
    if (this.args.images) this.args.images.forEach((val, idx) => {
      const dot = document.createElement('div');
      dot.className = idx === 0 ? 'popup-slider-dot-item active' : 'popup-slider-dot-item';
      this.elementsMap.popUpSliderDots.push(dot);
      this.elementsMap.popUpSliderDotsContainer.appendChild(dot);
    });

    this.elementsMap.infoContainer = document.createElement('div');
    this.elementsMap.infoContainer.className = 'popup-info-container';
    this.elementsMap.infoContainer.innerHTML = `
      <p class="lottery-time-to-live"> ${this.args.daysToLive}</p>
      <p class="lottery-name">${this.args.name}</p>
      <p class="lottery-location fw-xl">
        <i class="la la-map-marker"></i> ${this.args.location}
      </p>
      `;

    this.elementsMap.actionContainer = document.createElement('div');
    this.elementsMap.actionContainer.className = 'popup-action-container';
    this.elementsMap.actionContainer.innerHTML = `
      <div class="popup-action-details p-2 mt-10">
      <h6 class="m-0">Licenses</h6>
      <table class="table table-sm" style="line-height:1.2;">
      <tbody>
      <tr>
      <td class="pl-0">
      <button class="btn btn-link p-0 emptyDash" container="body" outsideclick="true" placement="top" type="button">
       Electronic Cigarette Dealer License
      </button>
      </td>
      <td class="emptyDash">Active</td>
      <td class="emptyDash text-right pr-0">8/1/2018</td>
      </tr>
      <tr>
      <td class="pl-0">
      <button class="btn btn-link p-0 emptyDash" container="body" outsideclick="true" placement="top" type="button">
       Tobacco Retail Dealer License 
       </button>
       </td>
       <td class="emptyDash">Active</td>
       <td class="emptyDash text-right pr-0">
       8/6/2018
       </td>
       </tr>
       </tbody>
       </table>
       <h6 class="m-0">Inspections</h6>
       <table class="table table-sm mb-0" style="line-height:1.2;"><tbody><tr><td class="pl-0"><button class="btn btn-link p-0 emptyDash" container="body" outsideclick="true" placement="top" type="button"> Tobacco Retail Dealer </button><div _ngcontent-ays-c1="" class="small font-italic text-muted">Tobacco - T21</div></td><td _ngcontent-ays-c1="" class="emptyDash">1/18/2019</td><td _ngcontent-ays-c1="" class="emptyDash text-right pr-0">No Evidence of Activity</td></tr></tbody></table>
       </div>
      <div class="d-flex justify-content-center bg-base p-10">
      <button class="btn col-10 btn-accent btn-sm m-btn--pill">File a Complaint</button>
      </div>
      `;

    this.elementsMap.bubbleTail = document.createElement('div');
    this.elementsMap.bubbleTail.className = 'map-popup-arrow-down';

    this.goToComplaint = function() {
      this.emitter.emit(this.args);
    };

    this.elementsMap.popUpImage = document.createElement('img');
    this.elementsMap.popUpImage.src = (this.args.images) ? this.args.images[this.imageIndex] : 'http://localhost:7202/b-cafe.png';

    this.elementsMap.styleElement = document.createElement('style');
    this.elementsMap.styleElement.innerHTML = `
    .popup-bubble {
      position: absolute;
      cursor: default;
      width: 400px;
      height: 437px;
      background: #ffffff;
    }
    div.map-popup-container {
      display: flex;
      flex-direction: column;
      height: 437px;
      box-shadow: 0px 0px 5px 3px rgba(150, 150, 150, 0.44);
    }
    div.map-popup-image-container {
    }
    div.map-popup-image-container img {
      width: 400px;
      height: 192px;
    }
    div.popup-info-container > p {
      margin: 10px 0 0 10px;
    }
    div.popup-action-details {
    }
    div.popup-info-container {
      
      display: block;
    position: absolute;
    bottom: 245px;
    left: 0;
    width: 100%;
    background: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.5) 30%, rgba(0, 0, 0, 0.8) 90%) no-repeat;
    color: white;
    padding-top: 2.667rem;
    padding-bottom: 1rem;
    }
    .popup-arrow-left, .popup-arrow-right {
      position: absolute;
      top: 107px;
      color: #fff;
      cursor: pointer;
    }
    .popup-arrow-left i.la, .popup-arrow-right i.la {
      font-size: 24px;
    }
    .popup-arrow-right {
     right: 15px;
    }
    .popup-arrow-left {
      left: 15px;
    }
    .popup-star {
      position: absolute;
      top: 15px;
      right: 15px;
      color: #fff;
      cursor: pointer;
    }
    .popup-star i.la {
      font-size: 24px;
    }
    .popup-slider-dots {
      position: absolute;
      width: 100%;
      top: 172px;
      display: flex;
      flex-direction: row;
      justify-content: center;
    }
    .popup-slider-dot-item {
      height: 8px;
      width: 8px;
      border-radius: 3px;
      border: 1px solid #ffffff;
      margin: 5px;
    }
    .popup-slider-dot-item.active {
      background-color: #ffffff;
    }
    .lottery-time-to-live {
      margin-top: 15px;
      font-size: 12px;
      font-weight: 500;
      color: #889aa9;
    }
    .lottery-name {
      font-size: 20px;
      font-weight: bold;
    }
    .lottery-location {
      font-size: 14px;
    }
    // .map-popup-arrow-down {
    //   width: 0; 
    //   height: 0; 
    //   border-left: 10px solid transparent;
    //   border-right: 10px solid transparent;
      
    //   border-top: 10px solid #fff;
    //   margin-left: 151px;
    // }
    `;

    this.elementsMap.popUpStarContainer.appendChild(this.elementsMap.popUpStar);
    this.elementsMap.mapPopupImageContainer.appendChild(this.elementsMap.popUpStarContainer);
    // this.elementsMap.mapPopupImageContainer.appendChild(this.elementsMap.popUpArrowLeft);
    // this.elementsMap.mapPopupImageContainer.appendChild(this.elementsMap.popUpArrowRight);
    this.elementsMap.mapPopupImageContainer.appendChild(this.elementsMap.popUpImage);
    this.elementsMap.mapPopupImageContainer.appendChild(this.elementsMap.popUpSliderDotsContainer);

    this.elementsMap.mapPopUpContainer.appendChild(this.elementsMap.mapPopupImageContainer);
    this.elementsMap.mapPopUpContainer.appendChild(this.elementsMap.infoContainer);
    this.elementsMap.mapPopUpContainer.appendChild(this.elementsMap.actionContainer);

    this.elementsMap.popUpBubble.appendChild(this.elementsMap.styleElement);
    this.elementsMap.popUpBubble.appendChild(this.elementsMap.mapPopUpContainer);
    this.elementsMap.popUpBubble.appendChild(this.elementsMap.bubbleTail);
  }

  this.addListeners = function () {
    const self = this;
    self.listenerMap.nextClick = google.maps.event.addDomListener(
      self.elementsMap.popUpArrowRight,
      'click',
      () => self.nextImage()
    );

    self.listenerMap.prevClick = google.maps.event.addDomListener(
      self.elementsMap.popUpArrowLeft,
      'click',
      () => self.previousImage()
    );

    self.listenerMap.favClick = google.maps.event.addDomListener(
      self.elementsMap.popUpStar,
      'click',
      () => self.addToFavorites()
    );

    self.listenerMap.lotteryToggleComplete = google.maps.event.addListener(
      self.map,
      'lotteryToggleComplete',
      args => {
        if (args.id === self.args.id) {
          self.toggleFavorite();
        }
      }
    );

    self.listenerMap.dblClick = google.maps.event.addDomListener(
      self.elementsMap.popUpBubble,
      'dblclick',
      event => event.stopPropagation()
    );

    self.listenerMap.click = google.maps.event.addDomListener(
      self.elementsMap.popUpBubble,
      'click',
      event => event.stopPropagation()
    );
    
    self.listenerMap.click = google.maps.event.addDomListener(
      self.elementsMap.actionContainer,
      'click',
      () => self.goToComplaint()
    );
  }

  this.removeListeners = function () {
    Object.values(this.listenerMap).forEach(google.maps.event.removeListener);
  }
}

const intervalCounter = setInterval(() => {
  if (window['google']) {
    MapPopup.prototype = Object.create(google.maps.OverlayView.prototype);
    clearInterval(intervalCounter);
  }
}, 1000);
