import { Component, OnInit, Input, EventEmitter, Output } from '@angular/core';

@Component({
  selector: 'app-time-selector',
  templateUrl: './time-selector.component.html',
  styleUrls: ['./time-selector.component.scss']
})
export class TimeSelectorComponent implements OnInit {

  constructor() { }

  @Input() availableTimes: string[];
  @Output() timeSelected: EventEmitter<any> = new EventEmitter();

  @Input() timeChoice: any;

  ngOnInit() {
    console.log(this.availableTimes);
  }

  setTime(selectedTime) {
    this.timeSelected.emit(selectedTime);
    //this.timeChoice = (this.timeChoice == selectedTime)? '' : selectedTime;
  }

  mapTime(timeString: string) {
    let newString = ':' + timeString.split(':')[1];
    let hour = Number(timeString.split(':')[0]);
    switch(true) {
      case (hour < 12):
        return (String (Number(hour)) + newString + " AM");
      case (hour > 12):
        return (String (Number(hour)-12) + newString + " PM");
      case (hour == 12):
        return (String (Number(hour) + newString + " PM"))
    }
  }
}
