import { Component, OnInit, ChangeDetectorRef } from '@angular/core';
import { OAuthService } from 'angular-oauth2-oidc';
import { ToggleLoginService } from '../../../shared/services/toggle-login.service';
import { ActivatedRoute, NavigationEnd, Router } from '@angular/router';
import { ApplicationsApiService } from '../../../modules/dashboard/services/applications-api.service';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss']
})
export class HeaderComponent implements OnInit {
  loggedIn: any;
  isMobileMenuOpen = false;
  loginFailed: boolean = false;
  userProfile: object;
  userDetails: any;
  navigationSubscription;

  constructor(
    private toggleSvc: ToggleLoginService,
    private oauthService: OAuthService,
    private router: Router, 
    private cdRef: ChangeDetectorRef,
    private applicationsApiService: ApplicationsApiService,
  ) {
    this.navigationSubscription = this.router.events.subscribe((e: any) => {
      // If it is a NavigationEnd event re-initalise the component
      if (e instanceof NavigationEnd) {
        this.initialiseInvites();
      }
    });
  }

  initialiseInvites() {
    // Set default values and re-fetch any data you need.
    this.loggedIn = true;
    if (this.oauthService.hasValidAccessToken()) {
      this.loadUserProfile();
      this.loggedIn = true;
      if (!this.cdRef['destroyed']) this.cdRef.detectChanges();
    }
  }

  ngOnInit() {
    this.initialiseInvites();
  }

  ngOnDestroy() {
    // avoid memory leaks here by cleaning up after ourselves. If we
    // don't then we will continue to run our initialiseInvites()
    // method on every navigationEnd event.
    if (this.navigationSubscription) {
      this.navigationSubscription.unsubscribe();
    }
  }

  loadUserProfile(): void {
    if (this.oauthService.userinfoEndpoint) {
      this.oauthService.loadUserProfile().then(up => (this.userProfile = up));
      this.getUserDetails();
    }    
  }

  getUserDetails(): void {
    if (this.oauthService.userinfoEndpoint) {
      this.userDetails = JSON.parse(this.oauthService.getUserProfile());
      if (!this.cdRef['destroyed']) this.cdRef.detectChanges();
    }    
  }

  toggleLogin() {
    this.oauthService.initImplicitFlow('/some-state;p1=1;p2=2');

    //this.toggleSvc.toggleLogin();
    //this.login();
  }

  register() {
    this.router.navigateByUrl('/', { skipLocationChange: true }).then(() =>
      this.router.navigate(['/auth/sign-up']));
  }

  login() {
    if (this.oauthService.hasValidAccessToken()) {
      this.logout();
    } else {
      this.oauthService.initImplicitFlow('/some-state;p1=1;p2=2');
    }
  }

  logout() {

    if (localStorage["lotteryFiltersRentals"])
      localStorage.removeItem('lotteryFiltersRentals');

    if (localStorage["lotteryFiltersSales"])
      localStorage.removeItem('lotteryFiltersSales');
 
    this.oauthService.logOut(false);

  }
}
