import { Component, Input } from '@angular/core';
import { LotteryApiService } from '../../services/api/lottery-api.service';
import { first } from 'rxjs/operators';

@Component({
  selector: 'app-select-favorite-lottery',
  templateUrl: './select-favorite-lotteries.component.html',
  styleUrls: ['./select-favorite-lotteries.component.scss']
})
export class SelectFavoriteLotteryComponent {
  @Input() isSelected: boolean;
  @Input() lotteryId: number;

  constructor(private lotteryApiService: LotteryApiService) {}

  toggleStar() {
    this.lotteryApiService
      .updateFavoriteLottery(this.lotteryId, !this.isSelected)
      .pipe(first())
      .subscribe(data => {
        if (data.error) {
          return;
        }

        this.isSelected = !this.isSelected;
      });
  }
}
