import { PublicApiService } from './../../../core/services/public-api.service';
import { Injectable } from '@angular/core';
import { map } from 'rxjs/operators';
import { ConfigValue, IncomeSubType, ApplicationStatus } from '../../models';
import { BehaviorSubject, Subject, Observable } from 'rxjs';

const configUrl = 'LotteryConfig'
@Injectable({
  providedIn: 'root'
})
export class ConfigValueService {

  constructor(private publicApiSvc:PublicApiService) {
    this.getStates();
  }

  getAllConfigTypes(...targetTypes:string[]){
    return this.publicApiSvc.getAnonymous(`${configUrl}/GetAllConfigTypes`).pipe(
      map(results =>
      {
        if(!targetTypes)
          return results;

        let targetResults = results.filter(r => targetTypes.includes(r.configType));
        let filteredResults:any={}
        if(targetResults){

        targetTypes.forEach(tType => {
          let filterValTypeOptions = targetResults.filter(r=>tType===r.configType);
          if(filterValTypeOptions)
            filteredResults[tType] = filterValTypeOptions;
        });

        }
        return filteredResults || results;
      }));
  }

  private stateOptions:BehaviorSubject<any> = new BehaviorSubject([]);
  stateOptions$ = this.stateOptions.asObservable();

  private getStates(){
    this.publicApiSvc.getAnonymous(`${configUrl}/GetStates`).subscribe(stateConfigResults => {
        let targetValues = stateConfigResults.map(option => {

          let configVal:ConfigValue = {
            name: option.name,
            value: option.stateId
          };

          return configVal;
        }) as ConfigValue [];

        this.stateOptions.next(targetValues);
      });
  }

  //Will need to change this to get proper config values from the database
  getLanguage():Subject<ConfigValue[]>{
    let langOptions$ = new Subject<ConfigValue[]>();

    let options:ConfigValue[] = [
      { value:'1', name:'English'},
      { value:'2', name:'Spanish'}
    ]

    setTimeout(() => {
      langOptions$.next(options);
    }, 1000);

    return langOptions$;
  }

  getIncomeSubTypes():Observable<IncomeSubType[]>{
    return this.publicApiSvc.getAnonymous(`${configUrl}/GetIncomeSubTypes`);
  }

  getApplicationStatuses(): Observable<ApplicationStatus[]> {
    return this.publicApiSvc.getAnonymous(`${configUrl}/GetApplicationStatusConfig`);
  }

}
