import { Component, OnInit, ViewChild, ElementRef, Input, Attribute, ChangeDetectorRef, AfterViewChecked, EventEmitter, Output } from '@angular/core';
import { Observable } from 'rxjs';
import { LoadingBarService } from '@ngx-loading-bar/core';
import { PortletComponent } from '../../../../assets/metronic/app/content/partials/content/general/portlet/portlet.component';

@Component({
  selector: 'portlet',
  templateUrl: './portlet2.component.html',
  styleUrls: ['./portlet2.component.scss']
})
export class Portlet2Component extends PortletComponent implements OnInit, AfterViewChecked {
  // Optional attributes
  // Add any of these attributes for extended functionality
  @Input('collapsible') collapsible;                    // is the portlet collapsible?
  @Input('collapsed') collapsed;                        // does the portlet in the collapsed state?
  @Input('expandable') expandable;                      // is the body of the portlet expandable to show more details?
  @Input('expanded') expanded;                          // is the portlet body expanded?
  @Input('showHeader') showHeader = true;               // is portle header visible. TODO: Have this calculate based on whether [mPortletHeadTitle] or [mPortletHeadTools] are provided
  @Input() expandShowLabel = "Show more"; 
  @Input() expandHideLabel = "Show less";
  @Input() isSeperatorVisible: boolean = true;          //  is the divider visible betwen the body and view more expandable section

  @Output() onOpen = new EventEmitter<any>();           // triggered with user opens the portlet
  @Output() onClose = new EventEmitter<any>();          // triggered with user closes/collapses the portlet
  @Output() onExpand = new EventEmitter<any>();         // triggered with user clicks 'Show more'
  @Output() onCompress = new EventEmitter<any>();       // triggered with user clicks 'Show less'
  
  @Input() isCollapsible: boolean = false; 
  @Input() isCollapsed: boolean = false;   
  @Input() isExpandable: boolean = false;  
  @Input() isExpanded: boolean = false;

  @ViewChild('mPortletViewMore') elViewMore: ElementRef;

  bodyHeight: any;

  constructor(public loader: LoadingBarService, private cd: ChangeDetectorRef) {
    super(loader);
  }

  ngOnInit() {
    this.isCollapsible = this.collapsible != null ? true : this.isCollapsible;
    this.isCollapsed = this.collapsed != null ? true : this.isCollapsed;
    this.isExpandable = this.expandable != null ? true : this.isExpandable;
    this.isExpanded = this.expanded != null ? true : this.isExpanded;
  }

  getBodyHeight() {
    let height = 0;
    if ((this.isCollapsible && !this.isCollapsed) || !this.isCollapsible) {
      height = (this.elBody.nativeElement.scrollHeight + (16 * 4));
      if (this.isExpanded) {
        height += this.elViewMore.nativeElement.scrollHeight;
      }
    }
    this.bodyHeight = height;
    this.cd.detectChanges();
  }

  ngAfterViewChecked() {
    this.getBodyHeight();
  }

  toggleCollapse() {
    if (this.isCollapsible) {      
      this.isCollapsed = !this.isCollapsed;
      if (this.isCollapsed) this.onClose.emit();
      else this.onOpen.emit();

      this.getBodyHeight();
    }
  }

  toggleExpand() {
    if (this.isExpandable) {      
      this.isExpanded = !this.isExpanded;
      if (this.isExpanded) this.onExpand.emit();
      else this.onCompress.emit();

      this.getBodyHeight();          
    }
    
  }

}
