import { Component, OnInit, Input, OnChanges } from '@angular/core';
import { Form, FormControl } from '@angular/forms';
import { ValidationMessagesModel } from '../models/validation-messages.model';


@Component({
  selector: 'form-control-validation',
  templateUrl: './form-control-validation.component.html'
})
export class FormControlValidationComponent implements OnInit {
  @Input('vControlName') constolName: string;
  @Input('VMessages') customMessages: any;
  @Input('vFromGroup') form: any;
  @Input('vHideErrorMessages') hideErrorMessages: boolean;
  validationMessages = new ValidationMessagesModel();
  control: FormControl;

  constructor() {
  }

  ngOnInit() {
    this.control = this.form.get(this.constolName);

    if (this.customMessages) {
      const controlMessages = this.getMessagesAdditionalAndChanged();
      const messagesObj = this.pathGeneralValidationMessages(this.validationMessages, controlMessages);

      Object.assign(this.validationMessages, messagesObj);
    }
  }

  pathGeneralValidationMessages(generalValidationMessage, controlMessages) {
    return controlMessages.reduce((newCustomObject, customType) => {
      const baseType = customType.split('.')[1];
      const changeType = baseType || customType;

      newCustomObject[changeType] = this.customMessages[customType];
      return newCustomObject;
    }, {});
  }

  getMessagesAdditionalAndChanged() {
    return Object.keys(this.customMessages)
      .filter(name =>
        name.indexOf(this.constolName) >= 0 || name.indexOf('.') === -1
      );
  }

  get errorMessages() {
    return this.control.errors && Object.keys(this.control.errors);
  }
}
