const carouselUnit = {
  mainImage: '/assets/images/demo-lottery-house.png',
  details: {
    lotteryId: 0, //set in loop,
    street: '34 Berry Street',
    area: 'Williamsburg | brooklyn',
    apartmentTypes: 'Studios, 1 Beds, 2 Beds, 3Beds',
    subwayServices: ['A', 'B', 'C', 'Q', 'R'],
    images: [
      '/assets/images/demo-lottery-house.png',
      '/assets/images/demo-lottery-house2.jpg',
      '/assets/images/demo-lottery-house3.jpg',
      '/assets/images/demo-lottery-house4.jpg',
      '/assets/images/demo-lottery-house5.jpg',
      '/assets/images/demo-lottery-house6.jpg'
    ],
    isAddedToFavorite: false,
    expirationDate: new Date(),
    expirationDateText: 'Lottery closing in 15 days'
  }
};

const cloneObject = o => JSON.parse(JSON.stringify(o));

// let lotteryResultData = [...Array(5)].map((_,i) => console.log(i));
// @ts-ignore
let lotteryResultData = <any>Array(100).fill('');

lotteryResultData = lotteryResultData.map((item, index) => {
  const entity = cloneObject(carouselUnit);
  entity.details.lotteryId = index;
  entity.details.area += `__${index}`;
  entity.details.street = `${index}__${entity.details.street}`;

  return entity;
});

// @ts-ignore
lotteryResultData[1].details.isAddedToFavorite = true;
// @ts-ignore
lotteryResultData[3].details.isAddedToFavorite = true;

const lotteryData = {
  getAllLotteriesData: () => lotteryResultData
};

export default lotteryData;
