import { ConfigValueService } from './../../../../shared/services/config-value/config-value.service';
import { HouseholdService } from './../../services/household/household.service';
import { FormGroup } from '@angular/forms';
import { Component, OnInit, ViewChild, ViewChildren, ElementRef } from '@angular/core';
import { ToastrService } from 'ngx-toastr';
import { MatStepper } from '@angular/material';
import { HouseholdRentalAssist } from 'projects/web/src/app/modules/household/models/hh-rental-assist.model';
import { StepperSelectionEvent } from '@angular/cdk/stepper';
import { Router } from '@angular/router';
import { Household, HouseholdMemberProfile } from '../../models';

@Component({
  selector: 'app-household-setup',
  templateUrl: './household-setup.component.html',
  styleUrls: ['./household-setup.component.scss']
})
export class HouseholdSetupComponent implements OnInit {

  mainForm: FormGroup;
  relationShipOptions: any [];
  languageOptions: any [];
  rentalAssistOptions: any [];
  isSaving:boolean = false;

  @ViewChild('mattySteps') private mattySteps: MatStepper;
  isLoading:boolean = true;


  constructor(private householdSvc: HouseholdService,
              private toastr: ToastrService,
              private configValSvc:ConfigValueService,
              private route:Router) { }

  ngOnInit() {
    this.mainForm = new FormGroup({});

    // this.configureMainForm();
    this.configValSvc.getAllConfigTypes('RelationshipType', 'Language', 'RentalAssistType').subscribe(result => {
      this.relationShipOptions = result.RelationshipType;
      this.languageOptions = result.Language;
      this.rentalAssistOptions = result.RentalAssistType;
      console.log('RelationshipOptions', result);
    });

  }

  onSaveHousehold(householdForm:FormGroup){
    let householdModel = this.mapFormToHousehold(householdForm);

    this.isSaving = true;
    console.log('Here is my Household Model:', householdModel)
    this.householdSvc.addHousehold(householdModel).subscribe(resp => {

      this.toastr.success('Initial Household Setup Complete', 'Saved Household Information');
      this.isSaving = false;
      //lets transition to the houshold-details page now. Thank You.
      this.route.navigate(["/household/summary"]);
    });
  }

  onStepsterNext(){
      this.mattySteps.next();
  }

  @ViewChildren('mattStep') mattStep;

  onNextStepChanged(stepEvent:StepperSelectionEvent){

    const stepId = this.mattySteps._getStepLabelId(stepEvent.selectedIndex !== 0? stepEvent.selectedIndex - 1 : 0);
    const stepElement = document.getElementById(stepId);

    //temp solution to keep the current tab selected into view
    setTimeout(() => {
      if(stepEvent.selectedIndex === 0){
        stepElement.scrollTo({ top: 0, behavior: 'smooth' });
      } else
      stepElement.scrollIntoView({ block: 'start', behavior: 'smooth'});
    }, 200);

  }

  private mapFormToHousehold(targetForm: FormGroup){
    let household = new Household();

    //map address
    let addrForm = targetForm.get("addressForm").value;
    household.noCurrentAddress = addrForm.noCurrentAddress;
    household.address = addrForm.address || null;
    household.city = addrForm.city || null;
    household.state = addrForm.state || null;
    household.zip = addrForm.zip || null;
    household.apartmentNumber = addrForm.unitNumber || null;

    //map permanent address
    if(targetForm.get("permanentAddressForm")){
      let permanentAddrForm = targetForm.get("permanentAddressForm").value;
      household.permanentAddress = permanentAddrForm.address || null;
      household.permanentApartmentNumber = permanentAddrForm.unitNumber || null;
      household.permanentCity = permanentAddrForm.city || null;
      household.permanentState = permanentAddrForm.state || null;
      household.permanentZip = permanentAddrForm.zip || null;
    }

    //map each member to the household model;
    household.householdMemberProfile = [];

    //add primary user to household
    let primaryUserForm = this.mainForm.get('initialUserGroup').value;
    let user = new HouseholdMemberProfile ();
    user.firstName = primaryUserForm.firstName;
    user.lastName = primaryUserForm.lastName;
    user.relationshipTypeId = 1; // TODO change to get from config table value - self relationship
    household.householdMemberProfile.push(user)

    let memberInfoForms = targetForm.get("membersInfoForm.membersArray").value as Array<any>;
    memberInfoForms.forEach(item => {
      let member = new HouseholdMemberProfile();
      member.householdMemberProfileId = item.id;
      member.firstName = item.firstName;
      member.lastName = item.lastName;
      member.relationshipTypeId = parseInt(item.relationship);

      household.householdMemberProfile.push(member);
    });

    //TODO: add language to household
    household.language = parseInt(primaryUserForm.language);

    let livingSituation = this.mainForm.get('currentLivingSituation').value;

    household.isNychaProperty = livingSituation === 'isNychaProperty';
    household.isNameOnNychaForm = this.mainForm.get('isNameOnNycha').value || null;
    household.isHomeless = livingSituation === 'isHomelessShelter';

    //Add Rental Assist Info
    household.isRentalAssisted = targetForm.value.isRentalAssisted === "true";
    household.householdRentalAssist = []
    let selectedRentalAssists = targetForm.value.rentalAssistTypes || null;
    if(selectedRentalAssists) {
      let hhRentalAssistTypes = this.rentalAssistOptions
                          .filter(r => selectedRentalAssists.includes(r.configTypeId.toString()))
                          .map(r => {
                            let rat = new HouseholdRentalAssist();
                            rat.rentalAssistTypeId = r.configTypeId;
                            rat.householdId = null;
                            rat.householdRentalAssistId = null;
                            //if other was one of the selected options add the required description
                            if(r.name === 'Other')
                              rat.otherDescription = targetForm.value.rentalAssistOtherDescription;
                            return rat;
                           });

      household.householdRentalAssist = hhRentalAssistTypes;
    }

    return household;
  }
}
