import {
  AfterContentInit,
  ChangeDetectorRef,
  Component,
  OnDestroy,
  OnInit,
  QueryList,
  ViewChildren,
  ElementRef
} from '@angular/core';
import { CdkStep, CdkStepper, StepContentPositionState } from '@angular/cdk/stepper';
import { Directionality } from '@angular/cdk/bidi';
import { scrollFormStepAnimations } from './scroll-form-step-animations';
import { Subject } from 'rxjs';
import { debounceTime, distinctUntilChanged, take, takeUntil } from 'rxjs/operators';

enum Scroll_Direction_value {
  DOWN = 'DOWN',
  UP = 'UP'
}

type ScrollDirection = Scroll_Direction_value.DOWN | Scroll_Direction_value.UP;

@Component({
  selector: 'scroll-stepper',
  templateUrl: './scroll-form.component.html',
  styleUrls: ['./scroll-form.component.scss'],
  animations: [scrollFormStepAnimations.stepTransition],
  providers: [{ provide: CdkStepper, useExisting: ScrollStepper }]
})
export class ScrollStepper extends CdkStepper implements OnInit, OnDestroy, AfterContentInit {
  @ViewChildren('scrollStep') scrollSteps;
  @ViewChildren('scrollStepper') scrollStepper;

  private ngUnsubscribe: Subject<void> = new Subject<void>();

  onScrollEven$: Subject<any> = new Subject();
  userFriendlyScrollDiff = 50;
  linear: boolean;
  selectedIndex: number;
  deltaHeight: any;
  deltaBottomHeight: any;
  stepAnimationStarted: boolean;

  constructor(dir: Directionality, changeDetectorRef: ChangeDetectorRef) {
    super(dir, changeDetectorRef);
  }

  ngAfterContentInit() {}

  ngOnInit() {
    window.addEventListener('scroll', this.onScroll.bind(this), true);
    window.addEventListener('keydown', this.onKeydown.bind(this), true);

    this.deltaHeight = document.body.clientHeight / 3;
    this.deltaBottomHeight = document.body.clientHeight / 4 + this.userFriendlyScrollDiff;

    this.onScrollEven$
      .pipe(takeUntil(this.ngUnsubscribe))
      // .pipe(debounceTime(100))
      .subscribe(event => {
        const bodyRect = event.target.ownerDocument.body.getBoundingClientRect();
        const height = bodyRect.height;
        const scrollDirection = this.getScrollDirection(document.body.scrollTop);
        const centerOfScreen = height / 2 - this.getDiffByScrollDirection(scrollDirection);
        const stepsLength = this.scrollSteps.length - 1;
        const matchedEl = this.scrollSteps.find((el, idx) => {
          const elRect = el.nativeElement.getBoundingClientRect();
          const topBorder = elRect.y;
          const bottomBorder = topBorder + elRect.height;

          return stepsLength == idx
            ? topBorder <= centerOfScreen
            : topBorder <= centerOfScreen && bottomBorder >= centerOfScreen;
        });

        const indexMatchedEl = this.scrollSteps._results.indexOf(matchedEl);

        if (indexMatchedEl < 0 || this.selectedIndex == indexMatchedEl) {
          return;
        }

        this.stepAnimationStarted = true;

        if(this.selected.completed && scrollDirection === Scroll_Direction_value.DOWN && indexMatchedEl >= this.selectedIndex)
          this.selectedIndex = indexMatchedEl;

        if(Scroll_Direction_value.UP === scrollDirection && indexMatchedEl <= this.selectedIndex){
          this.selectedIndex = indexMatchedEl;
        }
      });
  }

  onScroll(event) {
    this.onScrollEven$.next(event);
  }

  ngOnDestroy() {
    window.removeEventListener('scroll', this.onScroll.bind(this), true);
    window.removeEventListener('keydown', this.onKeydown.bind(this), true);
    this.ngUnsubscribe.next();
    this.ngUnsubscribe.complete();

  }

  // scroll to the next available indexed step
  scrollNext(){
    let newIndex = this.selectedIndex + 1;
    if(this._steps['_results'][newIndex]){
      this.scrollToSection(newIndex,null);
    }
  }

  scrollPrevious(){
    let newIndex = this.selectedIndex - 1;
    if(this._steps['_results'][newIndex]){
      this.scrollToSection(newIndex,null);
    }
  }

  scrollToSection(idx, event) {
    if(event) event.stopPropagation();
    const currentStepEl = this.scrollSteps._results[idx].nativeElement;

    currentStepEl.scrollIntoView({ behavior: 'smooth', block: "center" });

    setTimeout(() => this.selectedIndex = idx, 200);


  }

  _preBodyTopScrollValue: 0;
  _preScrollDirection: ScrollDirection = Scroll_Direction_value.DOWN;
  getScrollDirection(bodyTopScrollValue) {
    if (this._preBodyTopScrollValue == bodyTopScrollValue) {
      return this._preScrollDirection;
    }

    this._preScrollDirection =
      this._preBodyTopScrollValue < bodyTopScrollValue
        ? Scroll_Direction_value.DOWN
        : Scroll_Direction_value.UP;

    this._preBodyTopScrollValue = bodyTopScrollValue;
    return this._preScrollDirection;
  }

  getDiffByScrollDirection(
    scrollDirection: Scroll_Direction_value.DOWN | Scroll_Direction_value.UP
  ) {
    return scrollDirection == Scroll_Direction_value.DOWN ? this.userFriendlyScrollDiff : 0;
  }

  onKeydown(event: KeyboardEvent): void {
    if (event.key === 'Enter') {
      this.scrollNext();
    }
  }
}
