import {
  animate,
  state,
  style,
  transition,
  trigger,
  AnimationTriggerMetadata
} from '@angular/animations';

export const tabsAnimations: {
  readonly translateTab: AnimationTriggerMetadata;
} = {
  /** Animation translates a tab along the X axis. */
  translateTab: trigger('translateTab', [
    // Note: transitions to `none` instead of 0, because some browsers might blur the content.
    state('mainListIsVisible', style({ transform: 'translate3d(0%, 0, 0)' })),
    state('mainListIsHidden', style({ transform: 'translate3d(-100%, 0, 0)', display: 'none' })),
    state('subListIsVisible', style({ transform: 'translate3d(0%, 0, 0)' })),
    state('subListIsHidden', style({ transform: 'translate3d(100%, 0, 0)', display: 'none' })),
    transition('* => *', animate('500ms cubic-bezier(0.35, 0, 0.25, 1)')),
  ])
};
