import { Component, OnInit, ChangeDetectorRef } from '@angular/core';
import { NgbModal } from "@ng-bootstrap/ng-bootstrap";
import { ApplicationsApiService } from '../../services/applications-api.service';
import { Router } from '@angular/router';
import { AppointmentSetupComponent } from '../appointment-setup/appointment-setup.component';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import { forkJoin } from 'rxjs';

@Component({
  selector: 'app-appointments',
  templateUrl: './appointments.component.html',
  styleUrls: ['./appointments.component.scss']
})
export class AppointmentsComponent implements OnInit {

  bsModalRef: BsModalRef;
  applications: any[];
  appointments: any[];
  activeAppointments: any[];

  constructor(
    private modalService: BsModalService,
    private applicationApiService: ApplicationsApiService,
    private cdRef: ChangeDetectorRef,
    private router: Router,
  ) { }

  ngOnInit() {
    this.getAppointments();
  }

  getAppointments() {
    let applications = this.applicationApiService.getRequestedAppointments();
    let appointments = this.applicationApiService.getScheduledAppointments();
    forkJoin([applications, appointments]).subscribe(([lotteries, appointments]) => {
      this.activeAppointments = appointments.filter(appointment => !appointment.isCancelled && (new Date(appointment.startTime) >= new Date()));
      let activeAppointmentIds = this.activeAppointments.map(a => a.lotteryApplicationId);
      this.appointments = appointments;
      this.applications = lotteries.filter(lottery => !activeAppointmentIds.includes(lottery.lotteryApplicationId));
      this.cdRef.detectChanges();
    })
  }

  scheduleAppointment(id) {
    this.router.navigate(['/dashboard/appointment-setup/' + id]);
  }

  getDate(app) {
    return new Date(app);
  }

  getAvailableDates() {
    return [new Date('2019-07-09')];
  }

  openMapsLink(address) {
    let addressArray = encodeURIComponent(address);
    console.log(addressArray);
    window.open('https://www.google.com/maps/search/?api=1&query=' + addressArray)
  }

}
