import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { ApiService } from './api.service';
import { environment } from '../../../environments/environment';

@Injectable()
export class PublicApiService {
  constructor(private apiService: ApiService) { }

  public get(path: string, requestParams?: any): Observable<any> {
    return this.apiService.get(`${environment.publicApiUri}${path}`, requestParams);
  }

  public post(path: string, model: any, requestParams?: any): Observable<any> {
    return this.apiService.post(`${environment.publicApiUri}${path}`, model, requestParams);
  }

  public put(path: string, model: any): Observable<any> {
    return this.apiService.put(`${environment.publicApiUri}${path}`, model);
  }

  public delete(path: string): Observable<any> {
    return this.apiService.delete(`${environment.publicApiUri}${path}`);
  }

  public getAnonymous(path: string): Observable<any> {
    return this.apiService.getAnonymous(`${environment.publicApiUri}${path}`);
  }

  public postAnonymous(path: string, model: any, requestParams?: any): Observable<any> {
    return this.apiService.postAnonymous(`${environment.publicApiUri}${path}`, model, requestParams);
  }
}
