import { AbstractControl, FormControl } from "@angular/forms";

export class CustomValidators {

  static minDate(min: Date, originalDate:Date): any {
    return (control: AbstractControl): { [key: string]: any } => {
      let v: string = control.value;
      if (!control.value) return null;
      let vDate = new Date(v);
      vDate.setHours(0, 0, 0, 0);
      if (originalDate) {
        originalDate.setHours(0, 0, 0, 0);
        if (vDate.getTime() == originalDate.getTime()) return null;
      }      
      min.setHours(0, 0, 0, 0);
      return vDate < min ?
        { "minDate": true } :
        null;
    };
  }

  static minNumber(minControl: AbstractControl): any {
    return (control: AbstractControl): { [key: string]: any } => {
      let v: number = +control.value;
     
      return (minControl.value && minControl.value !== 0 && v < minControl.value)?
        { "minNumber": true } :
        null;
    };
  }

  static maxNumber(maxControl: AbstractControl): any {
    return (control: AbstractControl): { [key: string]: any } => {
      let v: number = +control.value;
     
      return (maxControl.value && maxControl.value !== 0 && v > maxControl.value) ?
        { "maxNumber": true } :
        null;
    };
  }

  static noWhitespaceValidator(control: FormControl) {
    const isWhitespace = (control.value || '').trim().length === 0;
    const isValid = !isWhitespace;
    return isValid ? null : { 'required': true };
  }

  static currency(control: FormControl) {
    if (!control.value) return null;
    const currencyRegEx: RegExp = /^(\d{1,7}(\.\d\d?)?|(\d{1,3}(\,\d\d\d)?|\d(\,\d\d\d){2})\.\d\d)$/;
    const isValid = !!control.value.toString().match(currencyRegEx);
    return isValid ? null : { 'currency': true };
  }

}
