import { Validators } from '@angular/forms';
import { CommonFormConstructor } from '../../../../core/modules/form/common-form-constructor';
import { emailValidator } from '../../../../core/modules/form/validators/email.validator';

const contactUsFormValidation = {
  firstName: [Validators.required],
  lastName: [Validators.required],
  email: [Validators.required, emailValidator],
  phone: [Validators.required],
  message: [Validators.required]
};

export class ContactUsFormModel extends CommonFormConstructor {
  firstName = '';
  lastName = '';
  email = '';
  phone = '';
  message = '';

  constructor(data?: any) {
    super();
    this.setData(data);
  }

  get validationRules() {
    return contactUsFormValidation;
  }
}
