import {AfterContentInit, Component, Input, OnDestroy, OnInit, ViewChild} from '@angular/core';
import {DatapickerFormcontrolInputService} from "./datapicker-formcontrol-input.service";
import {MatDatepicker} from "@angular/material";


// if need use this data picker with datapicker  go to this link and need to add additional functionality
// https://github.com/angular/material2/issues/5282
@Component({
  selector: 'datapicker-formcontrol-input',
  template: `
    <div class="input-group">
      <input class="form-control" placeholder="mm/dd/yyyy" type="text"
             [formControl]="formControlRef"
             [matDatepicker]="datapickerId"
             (click)="onToggleDatapicker($event)"
             (keydown)="false">
      <div class="input-group-append">
        <button class="btn btn-secondary" (click)="onToggleDatapicker($event)" type="button">
          <i class="la la-calendar"></i>
        </button>
      </div>
      <mat-datepicker #datapickerId (opened)="onChangeDate(true)" (closed)="onChangeDate(false)"></mat-datepicker>
    </div>
  `,
  styles: [`
    :host {
      display: block;
    }
  `]
})
export class DatapickerFormcontrolInputComponent implements OnInit, AfterContentInit, OnDestroy {
  @Input() formControlRef;
  @ViewChild(MatDatepicker) datepicker;
  datapickerId: string;

  constructor(private service: DatapickerFormcontrolInputService) {
  }

  ngOnInit(): void {
    this.datapickerId = '__' + Math.random().toString(36).substr(2, 9);
  }

  onChangeDate() {
  }

  ngAfterContentInit() {
    this.service.addDatapicker(this.datepicker)
  }

  ngOnDestroy() {
    this.service.deleteDatapicker(this.datepicker)
  }

  onToggleDatapicker($event) {
    $event.stopPropagation();

    if (this.datepicker.opened) {
      this.datepicker.close();
      return;
    }

    this.service.closeALlDatePickers();
    this.datepicker.open();
  }
}
