import { Injectable } from '@angular/core';
import { Http, Headers, RequestOptions } from '@angular/http';
import 'rxjs/add/operator/share';
import 'rxjs/add/operator/catch';
import { Observable } from 'rxjs/Observable';
import { throwError } from 'rxjs';
import { OAuthService } from 'angular-oauth2-oidc';
import { ToastrService } from 'ngx-toastr';
import {ConfigurationService} from "./configuration.service";

@Injectable()
export class ApiService {
  private headers: Headers;
  private options: RequestOptions;
  private headers2: Headers;
  private options2: RequestOptions;

  constructor(
    private http: Http,
    private oauthService: OAuthService,
    private config: ConfigurationService,
    private toastr: ToastrService
  ) {
    this.headers = new Headers({ 'Content-Type': 'application/json' });
    this.headers.set('Authorization', 'Bearer ' + this.oauthService.getAccessToken());
    this.options = new RequestOptions({ headers: this.headers });
    this.oauthService.events.subscribe(e => {
      if (e.type === 'token_received') {
        this.headers.set('Authorization', 'Bearer ' + this.oauthService.getAccessToken());
        this.options = new RequestOptions({ headers: this.headers });
      }
    });

    this.headers2 = new Headers({ 'Content-Type': 'application/json' });
    this.options2 = new RequestOptions({ headers: this.headers2 });
  }

  public get(path: string, requestParams?: any): Observable<any> {
    const options = Object.assign(this.options, requestParams);

    return this.http
      .get(path, options)
      .map(response => response.json())
      .share()
      .catch((error: any) => {
        return this.handleError(error);
      });
  }

  public post(path: string, model: any, options?: any): Observable<any> {
    const requestParams = Object.assign(this.options, options);
    return this.http
      .post(path, model, requestParams)
      .map(response => response.json())
      .share()
      .catch((error: any) => {
        return this.handleError(error);
      });
  }

  public put(path: string, model: any): Observable<any> {
    //const requestParams = Object.assign({}, params);
    return this.http
      .put(path, model, this.options)
      .map(response => response.json())
      .share()
      .catch((error: any) => {
        return this.handleError(error);
      });
  }

  public getAnonymous(path: string): Observable<any> {
    return this.http
      .get(path, this.options2)
      .map(response => response.json())
      .share()
      .catch((error: any) => {
        return this.handleError(error);
      });
  }

  public postAnonymous(path: string, model: any, options?: any): Observable<any> {
    const requestParams = Object.assign(this.options2, options);
    return this.http
      .post(path, model, requestParams)
      .map(response => response.json())
      .share()
      .catch((error: any) => {
        return this.handleError(error);
      });
  }

  public delete(path: string): Observable<any> {
    return this.http
      .delete(path, this.options)
      .map(response => response.json())
      .share()
      .catch((error: any) => {
        return this.handleError(error);
      });
  }

  private handleError(error: any) {
    if (!error || error.status !== 401) {
      console.log(error);
    }
    // this.loaderService.hideLoadingStatus();
    return throwError(error);
  }
}
