import { Component, OnInit, ChangeDetectorRef, Input } from '@angular/core';
import { MarkerModel } from 'projects/web/src/app/shared/models/marker.model';
import { NgbDate, NgbModal, NgbDateStruct } from '@ng-bootstrap/ng-bootstrap';
import { ApplicationsApiService } from '../../services/applications-api.service';
import { AppointmentDetailsComponent } from '../../components/appointments/appointment-details/appointment-details.component';
import { Observable, forkJoin } from 'rxjs';
import { addWeeks } from 'date-fns';
import { ActivatedRoute, ActivatedRouteSnapshot, Router } from '@angular/router';
import { AppointmentModel } from '../../models/appointment-model';

@Component({
  selector: 'app-appointment-setup',
  templateUrl: './appointment-setup.component.html',
  styleUrls: ['./appointment-setup.component.scss']
})
export class AppointmentSetupComponent implements OnInit {

  @Input() lotteryIdInput: number;
  @Input() applicationIdInput: number;

  locations: any[];
  dates: Date[];
  hours: any[];

  appointmentSlots: any[] = [];

  filteredDates: Date[];

  locationMarkers: MarkerModel[] = [];
  selectedLocationMarkers: MarkerModel[] = [];

  selectedLocation: any = {};
  selectedDate: NgbDate;
  selectedTime: string = "";

  selectedAccessibility: boolean = false;
  selectedTranslation: boolean = false;

  selectedLocationId = -1;
  locationsLoaded: boolean = false;


  lotteryId: number;
  applicationId: number;
  inApplicationModel: boolean = false;

  startDate: Date;

  lotteryApplication: any;

  constructor(
    private applicationApiService: ApplicationsApiService,
    private modalService: NgbModal,
    private cdRef: ChangeDetectorRef,
    private routeSnapshot: ActivatedRoute,
    private router: Router,
  ) { }

  ngOnInit() {
    this.startDate = new Date();
    if (!this.applicationIdInput) {
      this.routeSnapshot.params.subscribe(routeParams => {
        this.loadResources(routeParams.lotteryApplicationId);
      });
    } else {
      this.loadResources(this.applicationIdInput);
      this.inApplicationModel = true;
    }
  }

  loadResources(applicationId) {
    this.applicationId = applicationId;
    this.getApplication(applicationId).subscribe(app => {
      this.lotteryId = app.lotteryId;
      forkJoin([
        this.getLocations(this.lotteryId),
        this.getTimeSlots(this.lotteryId)
      ]).subscribe(([locations, slots]) => {
        // console.log('Loaded! Locations: ' + JSON.stringify(locations) + '\nSlots: ' + JSON.stringify(slots));
        this.cdRef.detectChanges();
      });
    });
  }

  getApplication(lotteryApplicationId): Observable<any> {
    return this.applicationApiService.getApplicationDetails(lotteryApplicationId);
  }

  getLocations(lotteryId) {
    //Call Appointments service
    return Observable.create(subscriber => {
      this.applicationApiService.getMeetingLocations(lotteryId).subscribe(locations => {
        this.locations = locations;
        this.locationsLoaded = true;
        // this.cdRef.detectChanges();
        subscriber.next(locations);
        subscriber.complete();
      });
    });
  }

  getTimeSlots(lotteryId) {
    //Call Appointments Service
    //Select date range - One month by default
    return Observable.create(subscriber => {
      const today = new Date();
      const startDay = new Date(today.getFullYear(), today.getMonth(), today.getDate());
      const endDay = addWeeks(startDay, 2);
      endDay
      const view = {
        start: startDay,
        end: endDay, //new Date().setDate(today.getDate() + 1)
        lotteryId: lotteryId
      };
      console.log(view);
      this.applicationApiService.getAppointmentSchedules(view).subscribe(slots => {
        this.appointmentSlots = slots;
        console.log(this.appointmentSlots);
        this.dates = this.appointmentSlots.map(timeslot => { return new Date(timeslot.date) });
        this.startDate = (this.dates.length)? new Date(this.dates[0]) : new Date();
        subscriber.next(slots);
        subscriber.complete();
      });
    });
  }


  convertToDate(date: NgbDateStruct) {
    if (date) return new Date(date.year, date.month - 1, date.day);
  }


  clearFilters() {
    this.selectedLocation = {};
    this.selectedDate = null;
    this.selectedTime = "";
    this.dates = this.appointmentSlots.map(timeslot => { return new Date(timeslot.date) });
    this.selectedLocationId = -1;
  }

  availableLocations() {
    if (this.locations) {
      if(this.locations.length == 1) {
        this.selectedLocationId = this.locations[0].locationId;
        this.selectedLocation = this.locations.find(location => location.locationId === this.selectedLocationId);
      }
      if (!this.selectedDate && this.selectedTime == "") {
        return this.appointmentSlots.map(timeslot => {
          return timeslot.location;
        }).filter((e, i, a) => a.indexOf(e) === i).map(locationId => { return this.locations.find(location => location.locationId === locationId) });
      }
      else {
        return this.appointmentSlots.map(timeslot => {
          if (this.selectedDate || this.selectedTime !== "") {
            if (!this.selectedDate) {
              if (timeslot.slots.includes(this.selectedTime))
                return timeslot.location;
            }
            if (this.selectedTime === "") {
              if (new Date(timeslot.date).getDate() === this.selectedDate.day)
                return timeslot.location;
            }
            if (timeslot.slots.includes(this.selectedTime) && new Date(timeslot.date).getDate() === this.selectedDate.day)
              return timeslot.location;
          }
        }).map(locationId => { return this.locations.find(location => location.locationId === locationId) }).filter((e, i, a) => a.indexOf(e) === i && e != null);
      }
    }
    return;
  }

  availableDates() {
    if (this.selectedLocationId === -1 && this.selectedTime == "") {
      return this.appointmentSlots.map(timeslot => { return new Date(timeslot.date) });
    }
    else {
      return this.appointmentSlots.filter(timeslot => {
          return ((this.selectedLocationId !== -1 || this.selectedTime !== "") && this.selectedLocationId === -1 && (timeslot.slots.includes(this.selectedTime)) || (this.selectedTime === "" && (timeslot.location === this.selectedLocationId)) || (timeslot.slots.includes(this.selectedTime) && timeslot.location === this.selectedLocationId));
        }).map(timeslot => { return new Date(timeslot.date) });
    }
  }

  availableTimes() {
    let times = Array.from(new Set(this.appointmentSlots.filter(timeslot => {
      return (this.selectedLocationId !== -1) ? (this.selectedLocationId === timeslot.location) : (true);
    }).filter(timeslot => {
      return (this.selectedDate) ? (this.selectedDate.day == new Date(timeslot.date).getDate()) : (true);
    }).map(timeslot => timeslot.slots).reduce((acc, val) => acc.concat(val), [])).values()).sort(
      (a: string, b: string) => {
        const [hoursA, minA] = a.split(':').map(x => parseInt(x));
        const [hoursB, minB] = a.split(':').map(x => parseInt(x));

        return hoursA - hoursB + (minA - minB)/60;
    })
    return times;
  }

  setLocation(id) {
    if (this.selectedLocation.locationId == id) {
      this.selectedLocation = {};
      this.selectedLocationId = -1;
    } else {
      this.selectedLocationMarkers = this.locationMarkers.filter(mark => mark.id == id);
      this.selectedLocation = this.locations.find(location => location.locationId == id);
      this.selectedLocationId = this.selectedLocation.locationId;
    }
  }

  setDate(event: NgbDate) {
    this.selectedDate = (event.equals(this.selectedDate)) ? null : event;
    console.log(this.selectedDate);
  }

  setTime(event: string) {
    this.selectedTime = (this.selectedTime == event) ? "" : event;
  }
  openConfirmation() {
    const modalRef = this.modalService.open(AppointmentDetailsComponent, { centered: true, size: "lg" });
    modalRef.componentInstance.selectedDate = this.selectedDate;
    modalRef.componentInstance.selectedLocation = this.selectedLocation;
    modalRef.componentInstance.selectedTime = this.selectedTime;
    modalRef.componentInstance.applicationId = this.applicationId;
    modalRef.componentInstance.onPost.subscribe(app => this.postAppointment(app));
    modalRef.componentInstance.accessibility = this.selectedAccessibility;
    modalRef.componentInstance.translation = this.selectedTranslation;
  }

  postAppointment(value) {
    console.log("POST RETURN", value);
    //ROUTE BACK TO APPOINTMENTS PAGE
    this.router.navigate(['/dashboard/appointments']);
  }

  goToAppointments() {
    this.router.navigate(['/dashboard/appointments']);
  }

  mapTime(timeString: string) {
    let newString = ':' + timeString.split(':')[1];
    let hour = Number(timeString.split(':')[0]);
    switch (true) {
      case (hour < 12):
        return (String(Number(hour)) + newString + " AM");
      case (hour > 12):
        return (String(Number(hour) - 12) + newString + " PM");
      case (hour == 12):
        return (String(Number(hour) + newString + " PM"))
    }
  }

}
