import { Directive, ElementRef, HostListener, Input } from '@angular/core';
import { CurrencyPipe } from '@angular/common';

@Directive({
  selector: 'input[digitsAndDecimals]'
})
export class DigitsAndDecimals {
  @Input('digits') num: number = 7;
  @Input('decimals') dec: number = 2;
  //private regex: RegExp = new RegExp(/^(?!\.?$)\d{0,5}(\.\d{0,2})?$/g);

  // Allow key codes for special events. Reflect :
  // Backspace, tab, end, home
  private specialKeys: Array<string> = ['Backspace', 'Delete', 'ArrowLeft', 'ArrowRight', 'Tab', 'End', 'Home'];

  private currencyRegEx: RegExp = /^(\d{1,7}(\.\d{1,2})?|(\d{1,3}(\,\d\d\d)?|\d(\,\d\d\d){2})(\.\d{1,2})?)$/;
  cp = new CurrencyPipe('en-US');
  constructor(private el: ElementRef) {
  }
  @HostListener('focus', ['$event']) onFocus(event: KeyboardEvent) {
    if (this.el.nativeElement.value) {
      let parsed = this.el.nativeElement.value.toString().replace(/,/g, '');
      this.el.nativeElement.value = parsed;
    }
  }
  @HostListener('keydown', ['$event'])
  onKeyDown(event: KeyboardEvent) {
    //console.log(this.el.nativeElement.value);
    // Allow Backspace, tab, end, and home keys
    if (this.specialKeys.indexOf(event.key) !== -1) {
      return;
    }
    let regex: RegExp = new RegExp('^(?!\\.?$)\\d{0,' + this.num + '}(\\.\\d{0,' + this.dec + '})?$');
    //remove commas from formatted numbers 1,000.00
    let current: string = this.el.nativeElement.value.toString().replace(/,/g, '');
    let position = this.el.nativeElement.selectionStart;
    //let next: string = current.concat(event.key);
    let next: string = current.substr(0, position) + event.key + current.substr(position);
    if (next && !String(next).match(regex)) {
      event.preventDefault();
    }

  }
  @HostListener('blur', ['$event']) onBlur(event: KeyboardEvent) {
    //comma separated thousands
    if (this.el.nativeElement.value) {
      const value = this.el.nativeElement.value.toString();
      if (!value.match(this.currencyRegEx)) return;
      let parsed = this.cp.transform(value,'','',`1.${this.dec}`);
        this.el.nativeElement.value = parsed;
      }
  }

}
