import { UtilityService } from './../../services/utility/utility.service';
import { MemberEtiquette } from './../../models/member-etiquette.model';
import { FormHelperService } from './../../services/form-helper/form-helper.service';
import { ChangeDetectorRef, Output, EventEmitter, QueryList, ViewChildren, ViewChild, ApplicationRef } from '@angular/core';
import { debounceTime } from 'rxjs/operators';
import { PersonalDetailsFormConstructor } from './../helpers/personal-details-form-constructor';
import { Component, OnInit, AfterContentInit, Input, OnChanges, SimpleChanges, OnDestroy } from '@angular/core';
import { FormGroupDirective, FormGroup, ControlContainer, Validators } from '@angular/forms';
import { HouseholdMemberProfile } from 'projects/web/src/app/entities';
import { Subject } from 'rxjs';
import { takeUntil, distinctUntilChanged } from 'rxjs/operators';
import { BsDatepickerConfig, BsDatepickerDirective } from 'ngx-bootstrap/datepicker';
import { ScrollStepper } from 'projects/web/src/app/core/modules/scroll-form/scroll-form.component';
import { t } from '@angular/core/src/render3';

const now = new Date();
@Component({
  selector: 'personal-details-form',
  templateUrl: './personal-details-form.component.html',
  styleUrls: ['./personal-details-form.component.scss'],
  viewProviders: [{
    provide: ControlContainer,
    useExisting: FormGroupDirective
  }]
})
export class PersonalDetailsFormComponent extends PersonalDetailsFormConstructor implements OnInit, OnChanges, OnDestroy {

  @ViewChildren(BsDatepickerDirective) datepickers: QueryList<BsDatepickerDirective>;
  @ViewChild(ScrollStepper) scrollStepper: ScrollStepper;
  household: any;

  onScrollClosePicker() {
    this.datepickers.forEach(picker => {
      picker.hide();
    });
  }

  @Input('memberInfo') viewModel: HouseholdMemberProfile;
  @Input() configValueOptions: any;
  @Input() selectedLicense: any;
  @Input() prefillAddress: any;
  @Output() nextStep: EventEmitter<any> = new EventEmitter();
  unsubscribed: Subject<void> = new Subject();

  mainForm: FormGroup;
  personalDetailsForm: FormGroup;
  memberEtiquette: MemberEtiquette = new MemberEtiquette();

  memberFirstName: string;
  memberLastName: string;
  isSelf: boolean;
  isAide: boolean;
  isUnbornChild: boolean;
  isRelationshipOther: boolean;
  showEnrollmentStatusFollowUp: boolean = false;
  isUnder18: boolean;

  //Select Options
  relationshipTypeList: any;
  allRelationshipTypes: any;
  ethnicityList: any;
  raceList: any;
  cnaOptionRace: any; //Choose not to anwser for race
  pronounList: any;
  genderList: any;
  enrollmentStatusList: any;

  genderOtherValue: any;
  pronounOtherValue: any;
  fulltimeStudentValue: any;

  /** transitioning:boolean - work around for stepper throwing errors about undefined steps
   * when transitioning from and to personal details for different member profiles
   * where the steps no longer match previous template **/
  transitioning: boolean = true;


  dpConfig: Partial<BsDatepickerConfig> = {
    containerClass: 'theme-dark-blue',
    dateInputFormat: 'MM/DD/YYYY',
    showWeekNumbers: false
  }

  minDate: Date;
  maxDate: Date;
  minDob: Date = new Date(1900, 0, 1);

  public ssnMask = [/\d/, /\d/, /\d/, '-', /\d/, /\d/, '-', /\d/, /\d/, /\d/, /\d/];
  public tinMask = [/\d/, /\d/, '-', /\d/, /\d/, /\d/, /\d/, /\d/, /\d/, /\d/];

  applicationNature = [
    'Modification to Existing Sidewalk Cafe',
    'Application for a new Sidewalk Cafe',
    'Assignment of existing SWC Consent'
  ]
  stoopApplicationNature = [
    'Modification to Existing Stoop Line Stand',
    'Application for a new Stoop Line Stand',
    'Assignment of existing Stoop Line Stand consent'
  ]

  swcType = [
    'Unenclosed',
    'Small Unenclosed',
    'Regular Unenclosed/Small Unenclosed',
    'Enclosed'
  ]

  standType = [
    'Fruits, Vegatables, Soft Drinks, or Flowers',
    'Confectionary, Cigars, Cigarettes, or Tobacco',
    'Ice Cream'
  ]

 
  businessTypeList = [
    'Amusement Arcade',
    'Amusement Device - Permanent',
    'Amusement Device - Portable',
    'Amusement Device - Temporary',
    'Auction House',
    'Auctioneer',
    'Bingo Game Operator',
    'Booting Company',
    'Car Wash',
    'Commercial Lessor',
    'Dealer in Products for the Disabled',
    'Debt Collection Agency',
    'Electronic & Home Appliance Service Dealer',
    'Electronic Cigarette Retail Dealer',
    'Electronics Store',
    'Employment Agency',
    'Games of Chance',
    'Gaming Café',
    'Garage',
    'Garage and Parking Lot',
    'General Vendor',
    'General Vendor Distributor',
    'Home Improvement Contractor',
    'Home Improvement Salesperson',
    'Horse Drawn Cab Driver',
    'Horse Drawn Cab Owner',
    'Industrial Laundry',
    'Industrial Laundry Delivery',
    'Locksmith',
    'Locksmith Apprentice',
    'Newsstand',
    'Parking Lot',
    'Pawnbroker',
    'Pedicab Business',
    'Pedicab Driver',
    'Pool or Billiard Room',
    'Process Server Individual',
    'Process Serving Agency',
    'Retail Laundry',
    'Scale Dealer/Repairer',
    'Scrap Metal Processor',
    'Secondhand Dealer Auto',
    'Secondhand Dealer General',
    'Sidewalk Café',
    'Sightseeing Bus',
    'Sightseeing Guide',
    'Special Sale (e.g., Going Out of Business, Liquidation, etc.)',
    'Stoop Line Stand',
    'Storage Warehouse',
    'Temporary Street Fair Vendor Permit',
    'Ticket Seller Business',
    'Ticket Seller Individual',
    'Tobacco Retail Dealer',
    'Tow Truck Company',
    'Tow Truck Driver',
    'Tow Truck Exemption'
  ]

  inputString = null;

  constructor(private parentFormDirective: FormGroupDirective,
    private formHelpSvc: FormHelperService,
    private cdRef: ChangeDetectorRef,
    private utilSvc: UtilityService, private appRef: ApplicationRef) {
    super();
    this.minDate = new Date();
    this.maxDate = new Date();

    this.isCurrentStudent.valueChanges.pipe(takeUntil(this.unsubscribed), distinctUntilChanged()).subscribe(r => this.onCurrentStudentChanged(r));

    this.relationshipTypeId.valueChanges.pipe(takeUntil(this.unsubscribed), debounceTime(250)).subscribe(r => {
      if (r) {
        this.setMemberRelationship(parseInt(r));
        this.setRelationshipType(parseInt(r));
      }

    });

    this.isDisabled.valueChanges.pipe(takeUntil(this.unsubscribed), distinctUntilChanged()).subscribe(r => this.onDisabledMemberChange(r));

    this.isAccommodationNeeded.valueChanges.pipe(takeUntil(this.unsubscribed), distinctUntilChanged()).subscribe(r => this.onAccomodationChange(r));

    this.gender.valueChanges.pipe(takeUntil(this.unsubscribed), distinctUntilChanged()).subscribe(r => this.onGenderChange(r));

    this.prefPronounId.valueChanges.pipe(takeUntil(this.unsubscribed), distinctUntilChanged()).subscribe(r => this.onPrefPronounChange(r));

    this.isExtraBedroomNeeded.valueChanges.pipe(takeUntil(this.unsubscribed), distinctUntilChanged()).subscribe(r => this.onExtraBedroomNeededChange(r));

    this.enrollmentStatus.valueChanges.pipe(takeUntil(this.unsubscribed), distinctUntilChanged()).subscribe(r => this.onEnrollmentStatusChange(r));

    this.minValidGroup.valueChanges.pipe(takeUntil(this.unsubscribed), debounceTime(500), distinctUntilChanged()).subscribe(values => this.onUpdateMemberEtiquette(values));

    this.dateOfBirth.valueChanges.pipe(takeUntil(this.unsubscribed), distinctUntilChanged()).subscribe(values => this.onDateOfBirthChange(values));
  }

  onDateOfBirthChange(value) {
    if (value) {
      var today = new Date();
      var birthDate = new Date(value);
      var age = today.getFullYear() - birthDate.getFullYear();
      var m = today.getMonth() - birthDate.getMonth();
      if (m < 0 || (m === 0 && today.getDate() < birthDate.getDate())) {
        age = age - 1;
      }

      this.isUnder18 = age < 18;
      if (!this.isAide && !this.isUnbornChild && this.isUnder18)
        this.formHelpSvc.enableFormControls([this.isEmancipatedMinor]);
      else
        this.formHelpSvc.disableFormControls([this.isEmancipatedMinor]);
    }
  }

  ngOnInit() {
    this.mainForm = this.parentFormDirective.form;

    this.utilSvc.currentMemberEtiquette$.pipe(takeUntil(this.unsubscribed)).subscribe(memberEtiquette => {
      this.memberEtiquette = memberEtiquette;
      this.cdRef.detectChanges();
    });

    window.addEventListener('scroll', this.onScrollClosePicker.bind(this), true);
    this.unsubscribed.subscribe(() => window.removeEventListener('scroll', this.onScrollClosePicker.bind(this), true));
    this.mainForm.addControl('personalDetailsForm', this.localFormGroup);
    if (this.prefillAddress){
      this.firstName.setValue('MY DELI FOOD MARKET INC.')
      this.inputString = '109 PARK AVE';
    }
  }

  onUpdateMemberEtiquette(values) {
    this.viewModel.firstName = values.firstName;
    this.viewModel.lastName = values.lastName;
    this.viewModel.relationshipTypeId = values.relationshipTypeId ? values.relationshipTypeId : this.viewModel.relationshipTypeId;
    if (values.firstName || values.lastName || values.relationshipTypeId)
      this.utilSvc.setCurrentMemberQuestionairreEtiquette(this.viewModel, this.allRelationshipTypes);
  }

  fillBusiness(bName, bAddress) {
    this.firstName.setValue(bName);
    this.inputString = bAddress;
    // this.nextStep.emit();

  }

  setFormValues(): void {
    let vm = this.viewModel;

    //First Last Relationship
    this.firstName.setValue(vm.firstName || '');
    this.lastName.setValue(vm.lastName || '');

    // hardcoded self
    this.relationshipTypeId.setValue(1);
    //this.otherRelationship.setValue(vm.otherRelationship || '');

    this.isEmancipatedMinor.setValue(vm.isEmancipatedMinor !== null ? vm.isEmancipatedMinor : null);
    if (vm.dateOfBirth) {
      let dob = new Date(vm.dateOfBirth);
      this.dateOfBirth.setValue(dob);
    }
    else this.dateOfBirth.setValue(null);

    if (vm.expectedDueDate) {
      let expectDate = new Date(vm.expectedDueDate);
      this.expectedDueDate.setValue(expectDate);
    } else this.expectedDueDate.setValue(null);

    //Student Info
    this.isCurrentStudent.setValue(vm.isCurrentStudent !== null ? vm.isCurrentStudent : null);
    this.enrollmentStatus.setValue(vm.enrollmentStatusId || '');
    if (vm.expectedGraduationDate) {
      let gradDate = new Date(vm.expectedGraduationDate);
      this.expectedGraduationDate.setValue(gradDate);
    } else this.expectedGraduationDate.setValue(null);

    this.minValidGroup.get('ssn').setValue(vm.ssn);
    this.minValidGroup.get('tin').setValue(vm.tin);

    this.isVeteran.setValue(vm.isVeteran !== null ? vm.isVeteran : null);

    //Disability info
    this.isDisabled.setValue(vm.isDisabled !== null ? vm.isDisabled : null);
    this.isMobilityDisabled.setValue(vm.isMobilityDisabled !== null ? vm.isMobilityDisabled : null);
    this.isVisionHearingDisabled.setValue(vm.isVisionHearingDisabled !== null ? vm.isVisionHearingDisabled : null);
    this.isAccommodationNeeded.setValue(vm.isAccommodationNeeded !== null ? vm.isAccommodationNeeded : null);
    this.isExtraBedroomNeeded.setValue(vm.isExtraBedroomNeeded !== null ? vm.isExtraBedroomNeeded : null);
    this.otherAccommodation.setValue(vm.otherAccommodation || '');

    //Gender Race Ethnicity Info
    this.gender.setValue(vm.genderId ? vm.genderId.toString() : '');
    this.otherGender.setValue(vm.otherGender || '');
    this.ethnicity.setValue(vm.ethnicityId || '');
    this.userRace.setValue(vm.householdMemberRaces ? vm.householdMemberRaces.map(r => r.raceId) : null);
    this.prefPronounId.setValue(vm.prefPronounId ? vm.prefPronounId.toString() : '');
    this.otherPrefPronoun.setValue(vm.otherPrefPronoun || '');

    this.formHelpSvc.disableFormControls([this.isVeteran, this.isCurrentStudent, this.enrollmentStatus, this.isEmancipatedMinor, this.prefPronounId, this.isExtraBedroomNeeded]);

    this.cdRef.detectChanges();
  }

  ngOnChanges(changes: SimpleChanges): void {
    this.transitioning = true;
    if (changes.configValueOptions && changes.configValueOptions.currentValue) {
      let r = this.configValueOptions;
      this.relationshipTypeList = r.RelationshipType.filter(rt => rt.name.toLowerCase().indexOf('myself') === -1);
      this.allRelationshipTypes = r.RelationshipType;
      this.raceList = r.Race;
      this.enrollmentStatusList = r.EnrollmentStatus;
      this.pronounList = r.Pronoun;
      this.ethnicityList = r.Ethnicity;
      this.genderList = r.Gender;
      this.genderOtherValue = this.genderList.find(r => r.name === 'Other').configTypeId.toString();
      this.pronounOtherValue = this.pronounList.find(r => r.name === 'Other').configTypeId.toString();
      this.fulltimeStudentValue = this.enrollmentStatusList.find(r => r.name.toLowerCase() === 'full time').configTypeId.toString();
    }

    if (changes.viewModel && changes.viewModel.currentValue) {
      this.setMemberRelationship(this.viewModel.relationshipTypeId);
      this.setFormValues();
      this.cdRef.detectChanges();
      this.transitioning = false;

      if (!this.viewModel.householdMemberProfileId || this.viewModel.householdMemberProfileId === 0) {
        this.localFormGroup.markAsPristine();
        this.localFormGroup.markAsUntouched();
      } else {
        this.formHelpSvc.markFormDirtyTouchedExcept(this.localFormGroup);
      }
    }
  }

  setMemberRelationship(id: number) {
    let relationshipOption = this.allRelationshipTypes.find(r => r.configTypeId === id);

    let relationName = relationshipOption ? relationshipOption.name.toLowerCase() : '';
    this.isSelf = relationName.toLowerCase() === 'myself';
    this.isAide = relationName.toLowerCase() === 'live-in aide';
    this.isUnbornChild = relationName.toLowerCase() === 'unborn child';
    this.isRelationshipOther = relationName.toLowerCase() === 'other';
  }

  setRelationshipType(id: number) {


    if (this.isRelationshipOther) {
      this.formHelpSvc.enableFormControls([this.otherRelationship]);
      this.formHelpSvc.markControlsDirtyTouchedTarget(this.otherRelationship);
      return;
    } else this.formHelpSvc.disableFormControls([this.otherRelationship]);

    this.setFirstLastNameValidation(true);
    if (this.isUnbornChild) {
      this.formHelpSvc.disableFormControlsExcept(this.localFormGroup, ['relationshipTypeId', 'ssn', 'tin', 'firstName', 'lastName']);
      this.formHelpSvc.enableFormControls([this.expectedDueDate]);

      if (!this.firstName.value && !this.lastName.value)
        this.formHelpSvc.setControlsPristine(this.firstName, this.lastName);

      this.setFirstLastNameValidation(false);
      return;
    } else this.formHelpSvc.disableFormControls([this.expectedDueDate]);

    if (this.isAide) {
      this.formHelpSvc.disableFormControlsExcept(this.localFormGroup, ['relationshipTypeId', 'firstName', 'lastName', 'dateOfBirth', 'ssn', 'tin']);
      this.formHelpSvc.enableFormControls([this.relationshipTypeId, this.dateOfBirth, this.ssn, this.tin]);
      return;
    }

    this.formHelpSvc.enableFormControlsTarget(this.localFormGroup, this.minRequirementList);
    this.cdRef.detectChanges();
  }

  ngOnDestroy(): void {
    this.unsubscribed.next();
    this.unsubscribed.complete();
  }

  onCurrentStudentChanged(value) {
    if (this.isUnbornChild) return;

    if (value) {
      this.formHelpSvc.enableFormControls([this.enrollmentStatus]);
      this.formHelpSvc.setControlsPristine(this.enrollmentStatus);
    } else {
      this.formHelpSvc.disableFormControls([this.expectedGraduationDate, this.enrollmentStatus]);
    }
  }

  isRaceCNA: boolean = false;
  onDisabledMemberChange(value) {
    if (value) {
      this.formHelpSvc.enableFormControls([this.isMobilityDisabled, this.isVisionHearingDisabled, this.isAccommodationNeeded]);
    } else {
      this.formHelpSvc.disableFormControls([this.isMobilityDisabled, this.isVisionHearingDisabled, this.isAccommodationNeeded]);
    }
  }

  onChangeUserRace(raceValues: number[]) {
    this.cnaOptionRace = this.raceList.find(r => r.name.trim().toLowerCase() === 'choose not to answer');
    this.isRaceCNA = raceValues.includes(this.cnaOptionRace.configTypeId);
    if (this.isRaceCNA) {
      this.userRace.setValue([this.cnaOptionRace.configTypeId]);
    }
  }

  onGenderChange(value) {
    if (value && value !== '') {
      let selectedGender = this.genderList.find(r => r.configTypeId === parseInt(value));

      let showGenderOther = selectedGender.name.toLowerCase() === 'other';
      if (showGenderOther) {
        this.formHelpSvc.enableFormControls([this.otherGender]);
        this.formHelpSvc.markControlsDirtyTouchedTarget(this.otherGender);
      } else {
        this.formHelpSvc.disableFormControls([this.otherGender]);
      }
    }
  }

  onAccomodationChange(newVal) {
    if (newVal) {
      this.formHelpSvc.enableFormControls([this.isExtraBedroomNeeded]);
    } else {
      this.formHelpSvc.disableFormControls([this.isExtraBedroomNeeded]);
    }
  }

  onExtraBedroomNeededChange(newVal) {
    if (newVal != null && !newVal) {
      this.formHelpSvc.enableFormControls([this.otherAccommodation]);
      this.formHelpSvc.markControlsDirtyTouchedTarget(this.otherAccommodation);
    } else {
      this.formHelpSvc.disableFormControls([this.otherAccommodation]);
    }
  }

  onPrefPronounChange(value) {
    if (value && value !== '') {
      let selectedOption = this.pronounList.find(r => r.configTypeId === parseInt(value));

      let showPronounOther = selectedOption.name.toLowerCase() === 'other';
      if (showPronounOther) {
        this.formHelpSvc.enableFormControls([this.otherPrefPronoun]);
        this.formHelpSvc.markControlsDirtyTouchedTarget(this.otherPrefPronoun);
      } else {
        this.formHelpSvc.disableFormControls([this.otherPrefPronoun]);
      }
    }
  }

  onEnrollmentStatusChange(value) {
    if (value && value !== '') {
      let selectedEnrollment = this.enrollmentStatusList.find(r => r.configTypeId === parseInt(value));
      let showEnrollmentStatusFollowUp = selectedEnrollment.name.toLowerCase() === 'full time';
      if (showEnrollmentStatusFollowUp) {
        this.formHelpSvc.enableFormControls([this.expectedGraduationDate]);
      } else {
        this.formHelpSvc.disableFormControls([this.expectedGraduationDate]);
      }
    }
  }
}
