import { Component, OnInit, ChangeDetectorRef } from '@angular/core';
import { FaqService } from './services/faq.servises';
import * as _ from 'lodash';
import { OAuthService } from 'angular-oauth2-oidc';

interface FAQInterface {
  isActive: boolean;
  answer: string;
  categoryId: number;
  displayOrder: number;
  faqId: number;
  question: string;
}

@Component({
  selector: 'app-faq',
  templateUrl: './faq.component.html',
  styleUrls: ['./faq.component.scss']
})
export class FaqComponent implements OnInit {
  sticky: boolean = false;
  isFiltersVisible: boolean = false;
  categoryIndexMap: Map<any, any>;
  faqs = {
    data: [],
    filteredData: []
  };
  categories: any[] = [];
  activeCategoryId: number;
  isLoadingData: boolean = true;

  constructor(
    private faqService: FaqService,
    private oauthService: OAuthService,
    private cdRef: ChangeDetectorRef
  ) {}

  ngOnInit() {
    this.getFaqCategories();
    this.getFaqsSource();
  }

  getFaqCategories() {
    this.faqService.getCategories().subscribe(data => {
      this.categories = data;
      this.activeCategoryId = this.categories[0].id;
    });
  }

  getFaqsSource() {
    this.faqService.getAllFaqs().subscribe(data => {
      const faqsData = this.mapFAQDataByCategory(data);
      this.faqs['data'] = this.sortingFAQDataByDisplayOrder(faqsData);
      this.faqs.filteredData = this.faqs.data;
      this.isLoadingData = false;
      if (!this.cdRef['destroyed']) this.cdRef.detectChanges();
    });
  }

  mapFAQDataByCategory(data) {
    // ToDo need Refactor this piece
    this.categoryIndexMap = new Map();

    return data.reduce((source, item) => {
      let arrayIndex = this.categoryIndexMap.get(item.faqCategoryId);

      if (arrayIndex === undefined) {
        arrayIndex = source.length;
        source[arrayIndex] = { categoryId: item.faqCategoryId, list: [] };
        this.categoryIndexMap.set(item.faqCategoryId, arrayIndex);
      }

      source[arrayIndex].list.push(item);
      return source;
    }, []);
  }

  sortingFAQDataByDisplayOrder(data) {
    _.forEach(data, FAQGroup => {
      FAQGroup.list = _.sortBy(FAQGroup.list, item => item.displayOrder);
    });
    return data;
  }

  switchToCategory(id): void {
    this.activeCategoryId = id;
  }

  toggleFilters() {
    this.isFiltersVisible = !this.isFiltersVisible;
  }

  applyFilter(filterValue: string) {
    filterValue = filterValue.trim(); // Remove whitespace
    filterValue = filterValue.toLowerCase(); // MatTableDataSource defaults to lowercase matches
    //this.dataSource.filter = filterValue;
    this.faqs.filteredData = _.cloneDeep(this.faqs.data);
    this.faqs.filteredData = this.faqs.filteredData.map(entry => {
      entry.list = entry.list.filter(faq => {
        if (faq.title.toLowerCase().includes(filterValue)) return true;
        if (faq.text.toLowerCase().includes(filterValue)) return true;
        return false;
      });
      return entry;
    });
  }

  // get isUserLogin() {
  //   return //this.oauthService.getAccessToken();
  // }
}
