import { Component, OnInit, Input, EventEmitter, Output, ChangeDetectorRef } from '@angular/core';
import { Router } from '@angular/router';
import { ApplicationsApiService } from '../../services/applications-api.service';
import { addDays } from 'date-fns';
import { FormControl } from '@angular/forms';
import { NoteModel } from 'projects/web/src/app/shared/components/note/note.model';
import { DocService } from 'projects/web/src/app/shared/components/doc/doc.service';

@Component({
  selector: 'messages',
  templateUrl: './messages.component.html',
  styleUrls: ['./messages.component.scss']
})
export class MessagesComponent implements OnInit {

  constructor(private router: Router, private applicationsApiService: ApplicationsApiService, private docService: DocService, private cdRef: ChangeDetectorRef) { }
  @Input() messages: any[];
  @Output() posted: EventEmitter<any> = new EventEmitter();

  newMessage: FormControl = new FormControl("");
  isSaving: boolean;
  replyingMessage: any = null;

  ngOnInit() {
  }

  replyMessage(index){
    this.replyingMessage = (this.replyingMessage == index)? null : index;
    this.cdRef.detectChanges();
    this.scroll('addMessage');
  }

  postMessage() {
    this.isSaving = true;
    let message = this.newMessage.value;
    let conversation = this.messages[this.replyingMessage][0];
    console.log(conversation);
    let notedoc = new NoteModel();
    notedoc.noteDocGroupId = "2";
    notedoc.lotteryId = conversation.lotteryId;
    notedoc.lotteryName = conversation.lotteryName;
    notedoc.lotteryApplicationId = conversation.lotteryApplicationId;
    notedoc.isAdmin = false;
    notedoc.isActive = true;
    notedoc.isRead = false;
    notedoc.noteType = conversation.noteType;
    notedoc.noteId = "0";
    notedoc.noteText = message;
    this.docService.saveNote(notedoc).subscribe(
      result => {
        this.isSaving = false;
        console.log(result);
        if (result.success) {
          this.posted.emit(result.message);
        }
      }
    )
  }

  
  scroll(id) {
    let el = document.getElementById(id);
//    el.scrollIntoView();
    el.scrollTo();
    el.focus();
  }


}
