import { isString } from 'util';
import { CommonProperties } from "../../../entities/common-fields.model";
import { HouseholdMemberProfile } from "./hh-member-profile.model";
import { HouseholdRentalAssist } from './hh-rental-assist.model';
import { HouseholdPrefLayoutTypeModel } from './hh-pref-layout-type.model';
import { HouseholdPrefNeighborhoodModel } from './hh-pref-neighborhood.model';
import { HouseholdPrefBoroughModel } from './hh-pref-borough.model';


export class Household extends CommonProperties {

  constructor(){
    super();
  }

 householdId :      number = 0;
 address :          string = '';
 apartmentNumber :  string = '';
 city :             string = '';
 state :            number = null;
 zip :              string = '';
 language :         number = null;

 //bit fields
 isActive :                 boolean = true;
 isHomeless :               boolean = false;
 isNychaProperty :          boolean = null;
 isNameOnNychaForm :        boolean = null;
 isMailingAddressSame :     boolean = null;
 isMailingPoBox :           boolean = null;
 isRentalAssisted :         boolean = null;
 isRerentalInterest :       boolean = null;
 isPrefSmokeFree:           boolean = null;
 isPrefPetFriendly:         boolean = null;
 isPrefUnitAccessible:      boolean = null;
 isPrefEntranceAccessible:  boolean = null;
 isPrefNotifications:       boolean = null;
 //isPhoneAuth:               boolean = null;
 noCurrentAddress:          boolean = true;

 //mailing info - determined by isMailingAddressSame
 mailingAddress:      string = null;
 mailingCity:         string = null;
 mailingState:        number = null;
 mailingZip:          string = null;
 mailingPoBoxNumber:  string = null;

  //permanent address info - determined by currentAddress isHomeless
  permanentAddress:         string = null;
  permanentApartmentNumber: string = null;
  permanentCity:            string = null;
  permanentState:           number = null;
  permanentZip:             string = null;
  permanentCommunityBoard:  string = null;

 //Need to update models here instead of using any
 householdMemberProfile:      HouseholdMemberProfile[] = [];
 householdPrefBorough:        HouseholdPrefBoroughModel [] = [];
 householdPrefLayoutType:     HouseholdPrefLayoutTypeModel [] = [];
 householdPrefNeighborhood:   HouseholdPrefNeighborhoodModel [] = [];
 householdPrefOwnerType:      any [] = [];
 householdPrefPropertyType:   any [] = [];
 householdRentalAssist:       HouseholdRentalAssist [] = [];
 lotteryApplication:          any [] = [];

 communityBoard:  string = null;
 hpdUserId:       number = null;
 x_COORDINATE:    number = null;
 y_COORDINATE:    number = null;
}
