import { AlphaNumericValidator } from 'projects/web/src/app/core/modules/form/validators/alpha-numeric.validator';
import { ConfigValueService } from './../../../../shared/services/config-value/config-value.service';
import { Component, OnInit, AfterContentInit, Output, EventEmitter, Input } from '@angular/core';
import { FormControl, Validators, FormGroupDirective, FormGroup, ControlContainer } from '@angular/forms';
import { UtilityService } from '../../services/utility/utility.service';
import { Household } from 'projects/web/src/app/entities';
import { formControlBinding } from '@angular/forms/src/directives/reactive_directives/form_control_directive';

@Component({
  selector: 'permanent-address-form',
  templateUrl: './permanent-address-form.component.html',
  styleUrls: ['./permanent-address-form.component.scss'],
  viewProviders: [{
    provide: ControlContainer,
    useExisting: FormGroupDirective
  }]
})
export class PermanentAddressFormComponent implements OnInit, AfterContentInit {

  @Output() nextStep: EventEmitter<any> = new EventEmitter();
  @Input() household:Household = new Household();

  mainForm: FormGroup;
  localForm: FormGroup;
  addressForm: FormGroup;

  // FormControls - Component References
  address: FormControl;
  city: FormControl;
  state: FormControl;
  zip: FormControl;
  unitNumber: FormControl;

  stateOptions:any[];
  stateValueText:string = '';

  //Settings for ng4geovalidation
  userSettings = {
    'showCurrentLocation': false,
    'showSearchButton': false,
    'inputPlaceholderText':'Enter address here'
  };

  constructor(private parentFormDirective: FormGroupDirective,
              private configValSvc: ConfigValueService,
              private utilSvc:UtilityService) { }

  ngOnInit() {
    //attach to mainForm parent FormGroup directive
    this.mainForm = this.parentFormDirective.form;

    this.configValSvc.getAllConfigTypes('State').subscribe(options => {
      this.stateOptions = options.State;

      if(this.household){
        let optionValue = this.stateOptions.find(r => r.configTypeId === this.household.permanentState);
        this.stateValueText = optionValue ? optionValue.name : '';
      }

    });

    this.createLocalForm();

    if(this.household)
      this.setFormValues();
  }

  //builds out local form and attaches to parent mainForm
  createLocalForm(){
    const address = this.address = new FormControl("", [Validators.required]);
    const unitNumber = this.unitNumber = new FormControl("", [AlphaNumericValidator]);
    const city = this.city = new FormControl("", [Validators.required]);
    const state = this.state = new FormControl("", [Validators.required]);
    const zip = this.zip = new FormControl("", [Validators.required]);

    this.localForm = new FormGroup({ address, city, state, zip, unitNumber });

    this.mainForm.addControl('permanentAddressForm', this.localForm);
  }

  setFormValues(){
    this.address.setValue(this.household.permanentAddress);
    this.unitNumber.setValue(this.household.permanentApartmentNumber || '');
    this.city.setValue(this.household.permanentCity);
    this.state.setValue(this.household.permanentState);
    this.zip.setValue(this.household.permanentZip);
  }

  onNextStep(){
    // Object.keys(this.localForm.controls).forEach(key => {
    //     let frmCtrl = this.localForm.get(key);
    //     frmCtrl.markAsDirty();
    //     frmCtrl.markAsTouched();
    // });

    this.localForm.updateValueAndValidity();
    this.nextStep.emit(true);
  }

  // Here is where we would like to handle subscribing to other form elements
  ngAfterContentInit(): void {
   this.mainForm.get('addressForm').get('noCurrentAddress').valueChanges.subscribe(r => this.onUpdateNoAddress(r))
   };

   onUpdateNoAddress(noAddress){
    let removePermanentAddressValues = !noAddress;

    if(removePermanentAddressValues) {
      this.address.setValue('');
      this.city.setValue('');
      this.state.setValue('');
      this.stateValueText = '';
      this.zip.setValue('');
    }
  }

    isReadOnly:boolean = true;
    isUsAddress:boolean = true;
    autoCompleteCallback(result, index) {
      let data = this.utilSvc.processGeoAutoCompleteResult(result);

      if(data){
        let addr = data.mappedAddress;

        this.isReadOnly = this.isUsAddress = addr.isUSAddress;

        if(addr.isUSAddress){
          this.address.setValue(addr.address);
          this.city.setValue(addr.city);
          this.zip.setValue(addr.zip);
          let stateOption = this.stateOptions.find(r=>r.name === addr.state);
          this.stateValueText = addr.state;
          this.state.setValue(stateOption.configTypeId);
        }
        else{
          this.address.setValue(data.formatted_address);
          this.city.setValue('');
          this.zip.setValue('');
          this.stateValueText = '';
          this.state.setValue('');
        }
      } else {
        this.address.setValue('');
        this.city.setValue('');
        this.state.setValue('');
        this.stateValueText = '';
        this.zip.setValue('');
      }
      this.localForm.updateValueAndValidity();
    }

}
