import {Injectable} from '@angular/core';
import {ActivatedRouteSnapshot, CanActivate, Router} from '@angular/router';
import {OAuthService} from 'angular-oauth2-oidc';
import {NgxPermissionsService} from 'ngx-permissions';
import {Observable} from "rxjs";

@Injectable()
export class AuthGuard implements CanActivate {

  constructor(
    public auth: OAuthService,
    public router: Router,
    private permissionsService: NgxPermissionsService
  ) {
  }

  canActivate(route: ActivatedRouteSnapshot): boolean {
    return this.handleAuth(route);
  }

  canLoad(route: ActivatedRouteSnapshot): boolean {
    return this.handleAuth(route);
  }

  canActivateChild(route: ActivatedRouteSnapshot): boolean {
    return this.handleAuth(route);
  }

  private handleAuth(route: ActivatedRouteSnapshot): boolean {
    if (!this.auth.hasValidAccessToken()) {
      this.router.navigate(['unauthorized']);
      return false;
    } else {
      return true;
    }


    // if (!this.auth.IsValidAdminUserLoggedIn()) {
    //   this.router.navigate(['unauthorized']);
    //   return false;
    // }

    // if (!route.data.expectedRole && !route.data.expectedRole.length) {
    //   return true;
    // }

    // const existingClaims = this.auth.getIdentityClaims() || {};
    //
    // if (!existingClaims.hasOwnProperty('role')) {
    //   this.router.navigate(['unauthorized']);
    //   return false;
    // }
    //
    // var userRoles: string[] = existingClaims['role'].toString().split(',');
    //
    // this.permissionsService.loadPermissions(userRoles);
    //
    // if (this.roleMatch(userRoles, route.data.expectedRole)) {
    //   return true;
    // }
    //
    // this.router.navigate(['unauthorized']);
    // return false;
  }

  private roleMatch(userRoles, allowedRoles): boolean {
    return allowedRoles.some(element => userRoles.includes(element));
  }
}

