import { Component, Input } from '@angular/core';
import { AbstractControlDirective, AbstractControl } from '@angular/forms';

@Component({
  selector: 'form-control-error',
  templateUrl: './show-form-control-error.component.html',
  styleUrls: ['./show-form-control-error.component.css']
})
export class ShowFormControlErrorComponent {

  private static readonly errorMessages = {
    'required': () => 'This field is required',
    'minlength': (params) => 'The min number of characters is ' + params.requiredLength,
    'maxlength': (params) => 'The max allowed number of characters is ' + params.requiredLength,
    'pattern': (params) => 'The required pattern is: ' + params.requiredPattern,
    'currency': () => 'Invalid: allowed range is $0 - $9,999,999.99',
    'years': (params) => params.message,
    'name': () => 'Invalid name',
    'alphaNumeric': () => 'Only alpha numberic characters',
    'ngbDate': (params) => {
      if(params.requiredBefore){
        let reqDate = params.requiredBefore;
        return `Invalid: Date must greater than or equal to ${reqDate.month}/${reqDate.day}/${reqDate.year}`;
      }

      if(params.requiredAfter){
        let reqDate = params.requiredAfter;
        return `Invalid: Date must less than or equal to ${reqDate.month}/${reqDate.day}/${reqDate.year}`;
      }

      return 'Invalid Date';

    },
    'bsDate': (params) => {
      if(params.minDate){
        let reqDate = params.minDate;
        return `Invalid: Date must greater than or equal to ${reqDate.getMonth()+1}/${reqDate.getDate()}/${reqDate.getFullYear()}`;
      }

      if(params.maxDate){
        let reqDate = params.maxDate;
        return `Invalid: Date must less than or equal to ${reqDate.getMonth()+1}/${reqDate.getDate()}/${reqDate.getFullYear()}`;
      }

      return 'Invalid Date';

    }
  };

  @Input()
  private control: AbstractControlDirective | AbstractControl;

  shouldShowErrors(): boolean {
    return this.control &&
           this.control.errors &&
           (this.control.touched);
  }

  listOfErrors(): string[] {
    return Object.keys(this.control.errors)
      .map(field => this.getMessage(field, this.control.errors[field]));
  }

  private getMessage(type: string, params: any) {
    return ShowFormControlErrorComponent.errorMessages[type](params);
  }

}
