import { Injectable, ElementRef, Renderer } from '@angular/core';
import { Subject } from 'rxjs/Subject';
import { Observable } from 'rxjs/Observable';

@Injectable()
export class NgxGalleryPreviewService {

    private previewOpen = new Subject<void>();
    PreviewOpen$ = this.previewOpen.asObservable() as Observable<any>;

    private previewClose = new Subject<void>();
    PreviewClose$ = this.previewClose.asObservable();

    private previewChange =  new Subject<number>();
    PreviewSelect$ = this.previewChange.asObservable();


    onPreviewOpen(): void {
        this.previewOpen.next();
    }

    onPreviewClose(): void {
        this.previewClose.next();
    }

    previewSelect(index: number) {
        this.previewChange.next(index);
    }
}
