import { AbstractControl } from '@angular/forms';

export function getCustomPatternValidator(validatorName: string, regExp: any) {
  return function(control: AbstractControl) {
    const value = control.value;
    const result = regExp.test(value);

    const isValueValid = result || !value || value.length === 0;

    if (isValueValid || (typeof value === 'object' && Object.keys(value).length > 0)) {
      return null;
    } else {
      const validationResult = {};
      validationResult[validatorName] = value;
      return validationResult;
    }
  };
}
