import { Directive, HostListener } from '@angular/core';

@Directive({
  selector: 'input[noSpaces]'
})
export class NoSpacesDirective {

  constructor() { }

  @HostListener('keydown', ['$event']) onKeyDown(event: KeyboardEvent) {
    if (event.key.match(/\s/)) {
      event.preventDefault();
    }
  }
}
