import { Component, OnInit, Input, EventEmitter, Output } from '@angular/core';
import { Router } from '@angular/router';
import { ApplicationsApiService } from '../../services/applications-api.service';
import { addDays } from 'date-fns';

@Component({
  selector: 'app-appointment-block',
  templateUrl: './appointment-block.component.html',
  styleUrls: ['./appointment-block.component.scss']
})
export class AppointmentBlockComponent implements OnInit {

  constructor(private router: Router, private applicationsApiService: ApplicationsApiService) { }

  @Input() appointment: any;
  @Input() addressText: string = '';
  @Output() rescheduleClicked: EventEmitter<any> = new EventEmitter();
  ngOnInit() {
  }

  
  openMapsLink(address) {
    let addressArray = encodeURIComponent(address);
    console.log(addressArray);
    window.open('https://www.google.com/maps/search/?api=1&query=' + addressArray)
  }

  scheduleAppointment(id) {
    this.router.navigate(['/dashboard/appointment-setup/' + id]);
    this.rescheduleClicked.emit();
  }

  getDate(app) {
    return new Date(app);
  }

  isAppointmentPassed(date: Date) {
    let now = new Date;
    return date <= now;
  }

  canReschedule(date: Date) {
    let now = new Date;
    let dayBefore = addDays(date, -1);
    return dayBefore >= now && !this.appointment.isReschedule;
  }

}
