import {locale as enLang} from '../assets/metronic/app/config/i18n/en';
import {locale as chLang} from '../assets/metronic/app/config/i18n/ch';
import {locale as esLang} from '../assets/metronic/app/config/i18n/es';
import {locale as jpLang} from '../assets/metronic/app/config/i18n/jp';
import {locale as deLang} from '../assets/metronic/app/config/i18n/de';
