@Component({
  // tslint:disable-next-line:component-selector
  selector: 'body[m-root]',
  styleUrls: ['./app.component.css'],
  templateUrl: './app.component.html',
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class AppComponent implements AfterViewInit, OnInit {
  title = 'HPD Connect';
  @Input() attributes: any;

  @HostBinding('style') style: any;
  @HostBinding('class') classes: any = '';

  @ViewChild('splashScreen', {read: ElementRef})
  splashScreen: ElementRef;

  showFooter: boolean = true;

  constructor(
    private router: Router,
    private oauthService: OAuthService,
    private http: HttpClient,
    private permissionsService: NgxPermissionsService,
    private config: ConfigurationService,
    // Metronic Native
    private layoutConfigService: LayoutConfigService,
    private classInitService: ClassInitService,
    private sanitizer: DomSanitizer,
    private translationService: TranslationService,
    private pageConfigService: PageConfigService,
    private splashScreenService: SplashScreenService,
    private activeRoute: ActivatedRoute
  ) {

    // Tweak config for implicit flow.
    // This is just needed b/c this demo uses both,
    // implicit flow as well as password flow
    this.configureAuth(this.config);
    // subscribe to class update event
    this.classInitService.onClassesUpdated$.subscribe(classes => {
      // get body class array, join as string classes and pass to host binding class
      setTimeout(() => (this.classes = classes.body.join(' ')));
    });

    // // subscribe to atrribute update event
    // this.classInitService.onAttributeUpdated$.subscribe(attributes => {
    //   this.attributes = attributes.body;
    //   // TODO: print attribute to body
    // });

    // this.layoutConfigService.onLayoutConfigUpdated$.subscribe(model => {
    //   this.classInitService.setConfig(model);
    //
    //   this.style = '';
    //   if (objectPath.get(model.config, 'self.layout') === 'boxed') {
    //     const backgroundImage = objectPath.get(model.config, 'self.background');
    //     if (backgroundImage) {
    //       this.style = this.sanitizer.bypassSecurityTrustStyle(`background-image: url(${backgroundImage})`);
    //     }
    //   }
    // });

    // register translations
    // this.translationService.loadTranslations(enLang, chLang, esLang, jpLang, deLang, frLang);

    // override config by router change from pages config
    this.router.events.pipe(
      filter(event => event instanceof NavigationEnd)).subscribe(() => {
      const pageConf = objectPath.get(this.pageConfigService.getCurrentPageConfig(), 'config');
      this.layoutConfigService.setModel({page: pageConf}, true);

      this.showHideFooter(this.activeRoute.root);

    });
  }

  //This will hide or show the footer component
  //Needed to make it like this in order to not disrupt the current routes and templates
  showHideFooter(route: ActivatedRoute) {

    let children = route.children;

    //If footer was hidden prior change to show and return;
    if (children.length === 0) {

      if (!this.showFooter) this.showFooter = true;

      return;
    }
    ;

    //iterate over each child
    for (let child of children) {

      if (!child.snapshot.data.hasOwnProperty('showFooter')) {
        return this.showHideFooter(child);
      }

      //We only get to this point if there is showFooter attached to the route data
      let hideShowFooter = route.snapshot.data['showFooter'];
      this.showFooter = hideShowFooter;

    }

  }

  ngOnInit(): void {
  }

  ngAfterViewInit(): void {
    if (this.splashScreen) {
      this.splashScreenService.init(this.splashScreen.nativeElement);
    }

    //if(window.location.hostname == 'uat.hpdweb.prutechlab.com' || window.location.hostname == 'mspwva-hpdhc202' || window.location.hostname == 'mspwva-hpdhc202.hpdnyc.org')
    //{
    //    alert('xp');
    //    var dynamicScript = "(function(h,o,t,j,a,r){ h.hj=h.hj||function(){(h.hj.q=h.hj.q||[]).push(arguments)}; h._hjSettings={hjid:1463014,hjsv:6}; a=o.getElementsByTagName('head')[0]; r=o.createElement('script');r.async=1; r.src=t+h._hjSettings.hjid+j+h._hjSettings.hjsv; a.appendChild(r);})(window,document,'https://static.hotjar.com/c/hotjar-','.js?sv=');";      
    //    let node = document.createElement('script');
    //    //node.src = dynamicScript;
    //    node.type = 'text/javascript';
    //    node.text = dynamicScript;
    //    node.async = false;
    //    node.charset = 'utf-8';
    //    document.getElementsByTagName('head')[0].appendChild(node);
    //}
  }

  private configureAuth(settings: ConfigurationService) {
    this.oauthService.configure({
      issuer: settings.issuer,
      logoutUrl: settings.logoutUrl,
      postLogoutRedirectUri: settings.postLogoutRedirectUri,
      redirectUri: settings.redirectUri,
      clientId: settings.clientId,
      requireHttps: settings.requireHttps,
      scope: settings.scope,
      // rajiv
      getCustomRolesUri: settings.getCustomRolesUri,
      getIdentityManagerUri: settings.getIdentityManagerUri
    });

    // @ts-ignore
    this.oauthService.tokenValidationHandler = new NullValidationHandler();

    this.oauthService.events.subscribe(e => {
      // tslint:disable-next-line:no-console
      console.debug('oauth/oidc event', e);
    });

    this.oauthService.events.pipe(filter(e => e.type === 'token_expires')).subscribe(e => {
      // tslint:disable-next-line:no-console
      console.debug('received token_expires event', e);
      this.oauthService.silentRefresh();
    });

    this.oauthService.loadDiscoveryDocument().then(doc => {
      this.oauthService.tryLogin();
    });
  }

  get access_token() {
    return this.oauthService.getAccessToken();
  }

  private configurePasswordFlow() {
    // Set a dummy secret
    // Please note that the auth-server used here demand the client to transmit a client secret, although
    // the standard explicitly cites that the password flow can also be used without it. Using a client secret
    // does not make sense for a SPA that runs in the browser. That's why the property is called dummyClientSecret
    // Using such a dummy secreat is as safe as using no secret.
    this.oauthService.dummyClientSecret = 'geheim';
  }
}
