  searchLotteries(lotterySearchModel) {
    //return of(lotteryDataMock)
    return this.publicApiService.postAnonymous('Lottery/SearchLotteries', lotterySearchModel);

  }

  getLatestLotteries() {
    return this.publicApiService.getAnonymous('Lottery/GetLatestLotteries');
  }

  getActiveAmenities() {
    return this.publicApiService.getAnonymous('Lottery/GetActiveAmenities');
  }

