import { Directive, HostListener, Input, ChangeDetectorRef, OnInit } from '@angular/core';
import { NgControl, AbstractControl } from '@angular/forms';
import { AuthService } from '../../modules/auth/services/auth.service';
import { emailValidator } from '../../core/modules/form/validators/email.validator';
import { phoneValidator } from '../../core/modules/form/validators/phone.validator';

@Directive({
  selector: 'input[validateDistinct]'
})
export class ValidateDistinctDirective implements OnInit {

  @Input('validateDistinct') argIndex: number;

  private readonly parameterIndexMap = {
    username: 0,
    email: 1,
    phoneNumber: 2,
    alternateEmail: 3
  }

  _control: NgControl;
  authService: AuthService;
  cdRef: ChangeDetectorRef;

  constructor(control: NgControl, authService: AuthService, cdRef: ChangeDetectorRef) {
    this._control = control;
    this.authService = authService;
    this.cdRef = cdRef;
  }

  ngOnInit() {
    this.argIndex = parseInt(this.argIndex.toString());
  }

  @HostListener('blur', ['$event']) onBlur() {
    if (isNaN(this.argIndex) || this.argIndex < 0 || this.argIndex > 3) {
      return;
    }

    let value = this._control.value;

    // do not perform server-side validation on empty string
    if (!value) return;

    // do not perform server-side validation if client-side does not pass
    if (this.argIndex === 1 || this.argIndex === 3) {
      if (emailValidator(this._control.control) !== null)
        return;
    }
    else if (this.argIndex === 2) {
      if (phoneValidator(this._control.control) !== null)
        return;
      value = value.replace(/(\-|\(|\)|\s)/g, '');
    }

    let args = ['', '', '', ''];
    args[this.argIndex] = value; 

    let serviceCall = this.authService.userExists.apply(this.authService, args);

    serviceCall.subscribe((result: any) => {
      if (!result.success) {
        this._control.control.setErrors({ duplicate: true });
      } else {
        if (this._control.hasError('duplicate')) delete this._control.errors['duplicate'];
      }
      if (!this.cdRef['destroyed']) this.cdRef.detectChanges();
    });
  }
}
