import { HouseholdSetupGuard } from './../../core/guards/household-setup.guard';
import {NgModule} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';
import {HouseholdComponent} from "./household.component";
import { HouseholdSetupComponent } from './pages/household-setup/household-setup.component';
import { HouseholdSummaryComponent } from './pages/household-summary/household-summary.component';
import { HouseholdDetailsComponent } from './pages/household-details/household-details.component';
import {MemberProfileComponent} from "./pages/member-profile/member-profile.component";
import { AuthGuard } from '../../shared/auth/auth.guard';
import { BusinessSummaryComponent } from './pages/business-summary/business-summary.component';

const routes: Routes =  [
  {
    path: '',
    component: HouseholdComponent,
    children: [
      {
        //eventually we will be passing in an id :householdId
        path: 'setup',
        component: HouseholdSetupComponent,
        data:{
          // animate: 'isLeft',
          showFooter: false,
          breadcrumb:{
            label: 'Household Setup'
          }
        },
        // canActivate: [AuthGuard, HouseholdSetupGuard]
      },
      {
        path: 'summary',
        component: HouseholdSummaryComponent,
        data:{
          showFooter: false,
          breadcrumb:{
            label: 'Household Summary'
          }
        },
        // canActivate: [AuthGuard, HouseholdSetupGuard]
      },
      {
        path: 'business-details',
        component: BusinessSummaryComponent,
        data:{
          showFooter: false,
          breadcrumb:{
            label: 'Business Summary'
          }
        },
        // canActivate: [AuthGuard, HouseholdSetupGuard]
      },
      {
        path: 'details',
        component: HouseholdDetailsComponent,
        data:{
          // animate: 'isRight',
          showFooter: false,
          breadcrumb:{
            label: 'Household Details'
          }
        },
        // canActivate: [AuthGuard, HouseholdSetupGuard]
      },
      {
        path: 'member-profile',
        component: MemberProfileComponent,
        data:{
          // animate: 'isRight',
          showFooter: false,
          breadcrumb:{
            label: 'Member Profile'
          }
        },
        // canActivate: [AuthGuard, HouseholdSetupGuard]
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class HouseholdRoutingModule {}
