import { Injectable} from '@angular/core';
import 'rxjs/add/operator/map';
import {PublicApiService} from "../../../core/services/public-api.service";
import {NoteModel} from "./note.model";

@Injectable()
export class NoteService{
  response: string;

  constructor(
    private publicApiService: PublicApiService
  ) { }

  getNotes(note) {
    return this.publicApiService.get('Applicant/GetCommentList', {params: note});
  }

  getCommentList(lotteryApplicationId) {
    return this.publicApiService.get(`Applicant/GetCommentList?lotteryApplicationId=${lotteryApplicationId}`);
  }

  saveNote(note: NoteModel) {
    return this.publicApiService.post('Applicant/AddComment', note);
  }
}
