/// <reference types='@types/googlemaps' />
import {Component, OnInit, ViewChild, Input, OnDestroy,} from '@angular/core';
import {MapMarker} from './mapMarker.class';
import {Observable, Subscription} from 'rxjs';

import { loadGoogleScript } from '../../helpers/load-google-script';

@Component({
  selector: 'app-map-old',
  templateUrl: './map-old.component.html',
  styleUrls: ['./map.component.scss'],
})
export class MapOldComponent implements OnInit, OnDestroy {
  @Input() markers?: Array<any>;
  @Input() events?: Observable<any>;
  @Input() isDetailsView?: boolean;
  @Input() isGridView? :boolean;

  @ViewChild('gmap') gmapElement: any;

  eventsSubscription: Subscription;

  mapInstance: google.maps.Map;
  mapListeners: google.maps.MapsEventListener[] = [];
  mapMarkers: Array<any> = [];
  mapBounds: google.maps.LatLngBounds = new google.maps.LatLngBounds();
  transitLayer: google.maps.TransitLayer;
  poiMap: google.maps.StyledMapType;

  constructor() {
  }

  ngOnInit() {
    if (this.events) {
      this.eventsSubscription = this.events.subscribe({
        next: (arg) => {
          console.log('fromchild', arg);
          this.applyPoiStyles(this.mapInstance, arg);
        },
        error: () => {
        },
        complete: () => {
        },
      })
    }

    const mapConfig = {
      center: new google.maps.LatLng(40.782248, -73.965651),
      zoom: 20,
      mapTypeId: google.maps.MapTypeId.ROADMAP,
      styles: [
        {
          featureType: 'administrative.land_parcel',
          elementType: 'labels',
          stylers: [
            {
              visibility: 'off'
            }
          ]
        },
        {
          featureType: 'poi',
          elementType: 'labels.text',
          stylers: [
            {
              visibility: 'off'
            }
          ]
        },
        {
          featureType: 'poi.business',
          stylers: [
            {
              visibility: 'off'
            }
          ]
        },
        {
          featureType: 'road',
          elementType: 'labels.icon',
          stylers: [
            {
              visibility: 'off'
            }
          ]
        },
      ].map(styleItem => styleItem as google.maps.MapTypeStyle),
      zoomControl: (this.isDetailsView) ? true : false,
      mapTypeControl: false,
      scaleControl: false,
      streetViewControl: false,
      rotateControl: false,
      fullscreenControl: false,
    };

    this.mapInstance = new google.maps.Map(this.gmapElement.nativeElement, mapConfig);

    const mockData = [
      {
        latlng: new google.maps.LatLng(40.782248, -73.965651),
        details: {
          id: '123',
          images: [
            '/assets/images/sample-photos/1.png',
            '/assets/images/sample-photos/2.png',
          ],
          name: 'Bushwick Corner',
          daysToLive: 20,
          location: 'Bushwick | Brooklyn',
          transport: ['a', 'c', 'e', 'n', 'r'],
          isFavorite: false,
        }
      },
      {
        latlng: new google.maps.LatLng(40.578441, -73.942409),
        details: {
          id: '345',
          images: [
            '/assets/images/sample-photos/3.png',
            '/assets/images/sample-photos/4.png'
          ],
          name: 'Bushwick Something Else',
          daysToLive: 35,
          location: 'Bushwick | Brooklyn',
          transport: ['a', 'c', 'e', 'n', 'r'],
          isFavorite: true,
        }
      },
      {
        latlng: new google.maps.LatLng(40.728225, -73.799986),
        details: {
          id: '678',
          images: [
            '/assets/images/sample-photos/5.png',
            '/assets/images/sample-photos/6.png'
          ],
          name: 'Somewhere in Queens',
          daysToLive: 12,
          location: 'Bushwick | Brooklyn',
          transport: ['a', 'c', 'e', 'n', 'r'],
          isFavorite: true,
        }
      },
    ];

    if (!this.markers) {
      this.markers = mockData;
    }
    this.addMarkers(this.markers);
    this.showMarkers(this.mapInstance);
    if (this.isDetailsView) {
      this.addTransitLayer(this.mapInstance);
    }
    if(this.markers.length > 1 ){
      this.fitMapToBounds(this.mapInstance, this.mapBounds);
    } else {
      this.mapInstance.setCenter(this.markers[0].latlng);
      this.mapInstance.setZoom(16);
    }

    const addToFavsListener = google.maps.event.addListener(this.mapInstance, 'addToFavorites', lotteryId => {
      console.log('adding to favorites in component', lotteryId);
      // some magic will happen here
      google.maps.event.trigger(this.mapInstance, 'lotteryToggleComplete', {id: lotteryId});
    });

    const closePopupListener = google.maps.event.addDomListener(this.mapInstance, 'click', () => {
      google.maps.event.trigger(this.mapInstance, 'openPopup', null);
    });

    this.mapListeners = [addToFavsListener, closePopupListener];

  }

  ngOnDestroy() {
    this.mapListeners.forEach(google.maps.event.removeListener);
  }

  public onGridItemHover(markers) {
    this.resetMarkersAndBounds();
    this.addMarkers(markers);
    this.showMarkers(this.mapInstance);
    this.fitMapToBounds(this.mapInstance, this.mapBounds);
    if (this.mapInstance.getZoom() > 14) {
      this.mapInstance.setZoom(14);
    }
  }

  private addMarkers(markers): void {
    markers.sort((a, b) => b.latlng.lat() - a.latlng.lat())
      .forEach(marker => {
        if (!marker.latlng || (!this.isGridView && !marker.details)) {
          console.log('missed required field');
          return;
        }

        const mapMarker = new MapMarker(
          marker.latlng,
          marker.details,
          this.mapInstance,
          this.isGridView,
        );

        this.mapMarkers.push(mapMarker);
        this.mapBounds.extend(marker.latlng);

      })

    //console.log(markers.length);
  }


  private setMarkersToMap(map: google.maps.Map): void {
    this.mapMarkers.forEach((marker) => {
      marker.setMap(map);
    });
  }

  private showMarkers(map: google.maps.Map): void {
    this.setMarkersToMap(map);
  }

  private hideMarkers(): void {
    this.setMarkersToMap(null);
  }

  private addTransitLayer(map): void {
    this.transitLayer = new google.maps.TransitLayer();
    this.transitLayer.setMap(map);
  }

  private fitMapToBounds(map, bounds): void {
    map.fitBounds(bounds);
  }

  private applyPoiStyles(map, formValues) {
    const resultingStyles = [
      {
        featureType: 'administrative.land_parcel',
        elementType: 'labels',
        stylers: [
          {
            visibility: 'off'
          }
        ]
      },
      {
        featureType: 'poi',

        stylers: [
          {
            visibility: 'off'
          }
        ]
      },
      {
        featureType: 'poi.business',
        stylers: [
          {
            visibility: 'off'
          }
        ]
      },
      {
        featureType: 'road',
        elementType: 'labels.icon',
        stylers: [
          {
            visibility: 'off'
          }
        ]
      },
    ].map(styleItem => styleItem as google.maps.MapTypeStyle);
    // Types of poi 
    // https://developers.google.com/places/supported_types
    const styles = {
      park: {
        "featureType": "poi.park",
        "stylers": [
          {
            "visibility": "on",
          },
        ],
      },
      hospital: {
        "featureType": "poi.medical",
        "stylers": [
          {
            "visibility": "on",
          },
        ],
      },
      school: {
        "featureType": "poi.school",
        "stylers": [
          {
            "visibility": "on",
          },
        ],
      },
      police: {
        "featureType": "poi.police",
        "stylers": [
          {
            "visibility": "on",
          }
        ],
      },
    };

    Object.keys(formValues).forEach(key => {
      if (formValues[key]) {
        resultingStyles.push(styles[key] as google.maps.MapTypeStyle);
      }
    });
    console.log(resultingStyles);

    if (resultingStyles.length) {
      const styledMapType = new google.maps.StyledMapType(resultingStyles);
      map.mapTypes.set('poiMap', styledMapType);
      map.setMapTypeId('poiMap');
    }
  }

  private resetMarkersAndBounds(): void {
    this.hideMarkers();
    this.mapMarkers = [];
    this.mapBounds = new google.maps.LatLngBounds();
  }
}
