import { ChangeDetectorRef, OnDestroy } from '@angular/core';
import { Household } from 'projects/web/src/app/entities';
import { UtilityService } from './../../services/utility/utility.service';
import { DynamicScriptLoaderService } from './../../../../core/services/dynamic-script-loader.service';
import { Component, OnInit, Output, EventEmitter, Input } from '@angular/core';
import { ControlContainer, FormGroupDirective, FormGroup, FormControl, Validators } from '@angular/forms';
import { ConfigValueService } from 'projects/web/src/app/shared/services/config-value/config-value.service';
import { takeUntil } from 'rxjs/operators';
import { Subject } from 'rxjs';
import { AlphaNumericValidator } from 'projects/web/src/app/core/modules/form/validators/alpha-numeric.validator';

@Component({
  selector: 'address-form',
  templateUrl: './address-form.component.html',
  styleUrls: ['./address-form.component.scss'],
  viewProviders: [{
    provide: ControlContainer,
    useExisting: FormGroupDirective
  }]
})
export class AddressFormComponent implements OnInit, OnDestroy {

  @Output() nextStep:EventEmitter<any> = new EventEmitter();
  @Input() disabled:boolean = false;
  @Input() household:Household = new Household();
  @Input() inputAddress = null;

  mainForm: FormGroup;
  addressForm: FormGroup;

  // FormControls - Component References
  address: FormControl;
  unitNumber:FormControl;
  city: FormControl;
  state: FormControl;
  zip: FormControl;
  noCurrentAddress: FormControl;

  stateOptions:any;
  disableForm:boolean;
  unsubscribe: Subject<void> = new Subject();
  stateValueText:string = '';
  isReadOnly:boolean = true;
  isUsAddress:boolean = true;

  //Settings for ng4geovalidation
  userSettings = {
    'showCurrentLocation': false,
    'showSearchButton': false,
    'inputPlaceholderText':'Enter address here',
    'inputString':this.inputAddress? this.inputAddress : '',
    'resOnSearchButtonClickOnly':false
  };

  constructor(private parentFormDirective: FormGroupDirective,
              private configValSvc:ConfigValueService,
              private scriptLoaderSvc: DynamicScriptLoaderService,
              private utilSvc: UtilityService, private cdRef:ChangeDetectorRef) { }

  ngOnInit() {
    this.scriptLoaderSvc.load('googleApi');
    
    if(this.inputAddress)
      this.setFormValues();

    this.configValSvc.getAllConfigTypes('State').subscribe(options => {
      this.stateOptions = options.State;

      if(this.household){
        let optionValue = this.stateOptions.find(r => r.configTypeId === this.household.state);
        this.stateValueText = optionValue ? optionValue.name : '';
      }

    });

    //attach to mainForm parent FormGroup directive
    this.mainForm = this.parentFormDirective.form;

    this.createLocalForm();

  }

  ngOnChanges(): void {
    //Called before any other lifecycle hook. Use it to inject dependencies, but avoid any serious work here.
    //Add '${implements OnChanges}' to the class.
    
    if(this.inputAddress)
      this.setFormValues();

      
  this.userSettings = {
    'showCurrentLocation': false,
    'showSearchButton': false,
    'inputPlaceholderText':'Enter address here',
    'inputString':this.inputAddress? this.inputAddress : '',
    'resOnSearchButtonClickOnly':false
  };
  this.userSettings = Object.assign({},this.userSettings)
  }

  //builds out local form and attaches to parent mainForm
  createLocalForm(){
    const address = this.address = new FormControl("",[Validators.required]);
    const unitNumber = this.unitNumber = new FormControl("", [AlphaNumericValidator]);
    const city = this.city = new FormControl("",[Validators.required]);
    const state = this.state = new FormControl("", [Validators.required]);
    const zip = this.zip = new FormControl("", [Validators.required]);
    const noCurrentAddress = this.noCurrentAddress = new FormControl(false,[Validators.required]);
    this.addressForm = new FormGroup({ address, unitNumber, city, state, zip, noCurrentAddress });

    this.mainForm.addControl('addressForm', this.addressForm);

    this.noCurrentAddress.valueChanges.pipe(takeUntil(this.unsubscribe))
    .subscribe(ishomeless => this.onNoCurrentAddressChange(ishomeless));

    if(this.inputAddress)
      this.setFormValues();

  }

  setFormValues(){
    this.address.setValue(this.inputAddress);
    this.unitNumber.setValue(null);
    this.city.setValue('BROOKLYN');
    this.state.setValue('NY');
    this.zip.setValue('11205');
    this.noCurrentAddress.setValue( false);
    this.cdRef.detectChanges();
  }

  onNextStep(){
    this.addressForm.updateValueAndValidity();
    this.nextStep.emit(true);
  }

  private onNoCurrentAddressChange(noAddress: boolean) {

    if (noAddress){
        Object.keys(this.addressForm.controls).forEach(key => {
          if(!["noCurrentAddress"].includes(key)){
            let frmCtrl = this.addressForm.get(key);
            frmCtrl.setValidators([]);
            frmCtrl.setValue('');
            frmCtrl.markAsDirty();
            frmCtrl.markAsTouched();
          }
        });
        this.isReadOnly = true;
        this.isUsAddress = true; //Controls the address input readonly attr
        this.stateValueText = ''; //need to update the state text
        this.onNextStep();
      } else {
        this.addressForm.setValidators([Validators.required]);
        this.mainForm.removeControl("permanentAddressForm");
        Object.keys(this.addressForm.controls).forEach(key => {
          let frmCtrl = this.addressForm.get(key);

          if(!["noCurrentAddress","unitNumber"].includes(key)){
            frmCtrl.setValidators([Validators.required]);
            if(!this.household) frmCtrl.setValue(''); //Do not want to clear out the existing values on edit
            frmCtrl.enable();
          }
        });

        this.unitNumber.setValidators([AlphaNumericValidator]);
        this.unitNumber.updateValueAndValidity();
      }

      this.addressForm.updateValueAndValidity();
    }

    autoCompleteCallback(result, index) {
      let data = this.utilSvc.processGeoAutoCompleteResult(result);

      if(data){
        let addr = data.mappedAddress;

        this.isReadOnly = this.isUsAddress = addr.isUSAddress;

        if(addr.isUSAddress){
          this.address.setValue(addr.address);
          this.city.setValue(addr.city);
          this.zip.setValue(addr.zip);
          let stateOption = this.stateOptions.find(r=>r.name === addr.state);
          this.stateValueText = addr.state;
          this.state.setValue(stateOption.configTypeId);
        }
        else{
          this.address.setValue(data.formatted_address);
          Object.keys(this.addressForm.controls).forEach(key => {
            let frmCtrl = this.addressForm.get(key);

            if(["city","state",'zip'].includes(key)){
              frmCtrl.setValue('');
              frmCtrl.setValidators([]);
              frmCtrl.updateValueAndValidity();
            }
          });
        }

      } else {
        this.address.setValue('');
        this.city.setValue('');
        this.state.setValue('');
        this.stateValueText = '';
        this.zip.setValue('');
        this.isReadOnly = true;
      }
      this.unitNumber.setValue('');
      this.setDirtyTouchedFrmCtrl();
      this.cdRef.detectChanges();
    }

    //Set controls touched or dirty
    private setDirtyTouchedFrmCtrl(){
      Object.keys(this.addressForm.controls).forEach(key => {
        let frmCtrl = this.addressForm.get(key);
        frmCtrl.markAsTouched();
        frmCtrl.markAsDirty();
      });
    }

    ngOnDestroy(): void {
      this.unsubscribe.next();
      this.unsubscribe.complete();
    }
}
