import { Component, OnInit, OnChanges, Input, SimpleChanges, Output, EventEmitter } from '@angular/core';
import { ControlContainer, FormGroupDirective, FormGroup, FormControl, Validators, AbstractControl } from '@angular/forms';
import { ApplicationUser, ConfigValue } from 'projects/web/src/app/shared/models';
import { OAuthService } from 'angular-oauth2-oidc';
import { ConfigValueService } from 'projects/web/src/app/shared/services/config-value/config-value.service';
import { flNameValidator } from 'projects/web/src/app/core/modules/form/validators/name.validator';
import {conformToMask} from 'angular2-text-mask';

@Component({
  selector: 'initial-primary-user-form',
  templateUrl: './initial-primary-user-form.component.html',
  styleUrls: ['./initial-primary-user-form.component.scss'],
  viewProviders: [{
    provide: ControlContainer,
    useExisting: FormGroupDirective
  }]
})
export class InitialPrimaryUserFormComponent implements OnInit, OnChanges {
  @Input() myname:any;
  @Output() nextStep:EventEmitter<any> = new EventEmitter();
  public phoneNumberMask =  ['(',/\d/, /\d/, /\d/, ')', ' ',/\d/, /\d/, /\d/,'-', /\d/, /\d/, /\d/, /\d/];

  ngOnChanges(changes: SimpleChanges): void {
    if(changes.myname.currentValue){

    }
  }

  mainForm:FormGroup;
  authUserIdInfo:ApplicationUser;
  initialUserGroup: FormGroup;
  firstname: FormControl;
  lastname:  FormControl;
  language:  FormControl;
  languageOptions :ConfigValue [];

  constructor(private oAuthSvc:OAuthService,private parentFormDirective: FormGroupDirective, private configValSvc:ConfigValueService) { }


  ngOnInit() {
    this.oAuthSvc.getIdentityProfile().subscribe(profile=>{
      this.authUserIdInfo = profile;
    });

    this.configValSvc.getLanguage().subscribe(results => this.languageOptions = results);

    this.mainForm = this.parentFormDirective.form;
    this.createLocalform();
  }

  // create localform
  createLocalform():void{
    const firstName = this.firstname =new FormControl('', [Validators.required, flNameValidator]);
    // lastname
    const lastName = this.lastname = new FormControl('', [Validators.required, flNameValidator]);
    // language
    const language = this.language = new FormControl('', Validators.required);

    this.initialUserGroup = new FormGroup({firstName, lastName, language});

    this.mainForm.addControl('initialUserGroup',this.initialUserGroup);
  }

  conformPhoneNumber(number){
    let conformedNumber = conformToMask(number,this.phoneNumberMask, {guide:false});

    return conformedNumber.conformedValue;
  }

  onToTheNext(){
    this.nextStep.emit(true);
  }
}
