export class CommonFormConstructor {
  setData(data?: any) {
    if (!data) {
      return false;
    }

    Object.keys(data).forEach(name => {
      if (data[name] != null && typeof data[name] !== 'undefined' && this.hasOwnProperty(name)) {
        this[name] = data[name];
      }
    });
  }
}
