import { Component, Input, OnInit } from '@angular/core';
import { FormControl, Validator } from '@angular/forms';

interface ConfigInterface {
  condition: any | Array<Validator>;
  validatorText: string;
}

@Component({
  selector: 'app-condition-input',
  templateUrl: './condition-input.component.html',
  styleUrls: ['./condition-input.component.scss']
})
export class ConditionInputComponent implements OnInit {
  @Input() config: Array<ConfigInterface> = [<ConfigInterface>{}];
  @Input() input: FormControl | null;
  @Input() display: boolean = true;

  conditionData: any = {};

  constructor() {}

  ngOnInit() {}

  updateCondition() {
    const validateResult = this.config.filter((rule: ConfigInterface) => {
      if (!Array.isArray(rule.condition)) {
        return rule.condition === this.input;
      }

      return rule.condition.reduce((pre, fn) => (pre ? pre : fn(this.input)), false);
    });

    const resultValue = validateResult[validateResult.length - 1];

    this.conditionData = resultValue
      ? { text: resultValue.validatorText, isValid: resultValue.condition }
      : {};
    return true;
  }
}
