import { Directive, ElementRef, EventEmitter, HostListener, Input, Output } from '@angular/core';

@Directive({
  selector: '[changeVisible]'
})
export class ChangeVisibleDirective {
  @Output('handleChangingVisible') handleChangingVisible: EventEmitter<any> = new EventEmitter<any>();

  previousValue: boolean;

  constructor(private _el: ElementRef) {
  }

  @HostListener('window:scroll', ['$event'])
  handleScroll() {
    const rect = this._el.nativeElement.getBoundingClientRect();
    const isElementVisible = rect.top >= 0;

    if (isElementVisible !== this.previousValue) {
      this.previousValue = isElementVisible;
      this.handleChangingVisible.emit({ isVisible: isElementVisible });
    }
  }
}
