import { AfterViewInit, Directive, ElementRef, Input } from '@angular/core';
import shave from 'shave';

@Directive({
  selector: '[shave]'
})
export class ShaveDirective implements AfterViewInit {
  _shaveOptions: any;
  _height = 100;

  @Input('shave')
  get shave() {
    return this._shaveOptions;
  }

  set shave(val) {
    if (val) {
      this._shaveOptions = val;
    }
    this.runShave();
  }

  @Input('shaveHeight')
  get shaveHeight() {
    return this._height;
  }

  set shaveHeight(val) {
    this._height = val;
    this.runShave();
  }

  constructor(private el: ElementRef) {
  }

  runShave() {
    if (typeof this._height !== 'number') {
      throw new Error('Height must be a number');
    }
    shave(this.el.nativeElement, this.shaveHeight, this.shave);
  }

  ngAfterViewInit() {
    this.runShave();
  }
}

