import {Component, forwardRef, AfterViewInit, Input, OnInit, ContentChild} from '@angular/core';
import {FormControl} from '@angular/forms';

interface ISelectedItems {
  label: string;
  value: number
}

@Component({
  selector: 'dropdown-selector',
  templateUrl: './dropdown-selector.component.html',
  styleUrls: ['dropdown-selector.component.scss'],
  providers: [{
    provide: DropdownSelectorComponent,
    useClass: forwardRef(() => DropdownSelectorComponent)
  }]
})
export class DropdownSelectorComponent implements AfterViewInit {
  @Input() control: FormControl;
  // <dropdown-selector multiple></dropdown-selector'>is multiple exist it would be true.. if this attr not exist it would be undefined
  @Input() multiple: string;
  @Input() placeholderText = "Any";
  @Input() placement: string = "bottom-left"; // Options: "top", "top-left", "top-right", "bottom", "bottom-left", "bottom-right", "left", "left-top", "left-bottom", "right", "right-top", "right-bottom"
  @Input() autoClose?: any; // Options: true, false, "inside", "outside"
  @Input() maxHeight: string = "16rem"; // max size of the dropdown menu

  selectedItemsCheckbox: ISelectedItems[] = [];
  selectedItemsRadioBtn: ISelectedItems = <ISelectedItems>{};
  childrenItems = [];
  isSelectorMultiple: boolean;

  get uiLabel(): string {
    return this.isSelectorMultiple
      ? this.selectedItemsCheckbox.map(i => i.label).join(', ') || this.placeholderText
      : this.selectedItemsRadioBtn.label || this.placeholderText
  }

  constructor() {
  }

  ngAfterViewInit() {
    setTimeout(() => {  //fixes ExpressionChangedAfterItHasBeenCheckedError error

      this.autoClose = (this.isSelectorMultiple ? "outside" : true); //defaults to outside if multiple select and true if single select

      if(!this.control.value){
        return;
      }

      const selectedItems = this.childrenItems.filter(childItem => {
        return this.isSelectorMultiple
          ? this.control.value.indexOf(childItem.value) >= 0
          : this.control.value == childItem.value
      });

      this.isSelectorMultiple
        ? this.selectedItemsCheckbox = selectedItems
        : this.selectedItemsRadioBtn = selectedItems[0] || {};
    });
  }

  toggleItem(selectedData) {
    this.isSelectorMultiple
      ? this.checkboxFn(selectedData)
      : this.radiobuttonFn(selectedData);
  }

  checkboxFn(selectedData) {
    const elementIndex = this.selectedItemsCheckbox.map(e => e.value).indexOf(selectedData.value);

    elementIndex >= 0
      ? this.selectedItemsCheckbox.splice(elementIndex, 1)
      : this.selectedItemsCheckbox.push(selectedData);

    const formControlValue = this.selectedItemsCheckbox.map(e => e.value);

    this.control.setValue(formControlValue);
  }

  radiobuttonFn(selectedData) {
    this.selectedItemsRadioBtn = selectedData;
    this.control.setValue(selectedData.value + '');
  }

  bootstrapChild(value) {
    this.childrenItems.push(value);
  }

  isChildSelected(childValue) {
    return this.isSelectorMultiple
      ? this.isChildSelectedAsCheckBox(childValue)
      : this.isChildSelectedAsRadioBtn(childValue)
  }

  isChildSelectedAsRadioBtn(childValue) {
    return this.selectedItemsRadioBtn.value == childValue;
  }

  isChildSelectedAsCheckBox(childValue) {
    if (!this.selectedItemsCheckbox) {
      return false;
    }

    return !!(this.selectedItemsCheckbox).find(i => i.value == childValue)
  }


  setTypeOfOption(childType){
    if(childType.type === 'checkbox'){
      this.isSelectorMultiple = true;
    }

    if(childType.type === 'radiobtn'){
      this.isSelectorMultiple = false;
    }
  }
}

