import { Component, EventEmitter, Input, OnInit, Output, ViewChild, ChangeDetectorRef } from '@angular/core';

@Component({
  selector: 'app-hpd-accordion',
  templateUrl: './hpd-accordion.component.html',
  styleUrls: ['./hpd-accordion.component.scss']
})

export class HpdAccordionComponent implements OnInit {
  @Input() title: string;
  @Input() itemsCount: number;
  @Input('hpdAccordionBody') hpdAccordionBody;

  @Output() onDeleteItem: EventEmitter<number> = new EventEmitter();
  @Output() onAddItem: EventEmitter<void> = new EventEmitter<void>();

  _itemsArray: Array<any> = [];
  _itemCollapsed: Array<boolean> = [];

  labels: string[] = [];

  get itemsArray() {
    if (this._itemsArray.length !== this.itemsCount) {
      this._itemsArray = new Array(this.itemsCount);
    }

    return this._itemsArray;
  }

  get itemCollapsed() {
    if (this._itemCollapsed.length !== this.itemsCount) {
      let _items = new Array(this.itemsCount).fill(true);
      _items[this.itemsCount - 1] = false
      this._itemCollapsed = _items;
    }

    return this._itemCollapsed;
  }

  constructor(private cdRef: ChangeDetectorRef) {
  }

  ngOnInit() {
    if (!this.itemsArray.length) {
      setTimeout(() => {
        this.onAddItem.emit();
      });
    }

    this._itemCollapsed = new Array(this.itemsArray.length);
    this._itemCollapsed.fill(true);
  }

  addItem(): void {
    this.onAddItem.emit();
  }

  deleteItem(event, index): void {
    event.preventDefault();
    event.stopPropagation();
    this.onDeleteItem.emit(index);
  }

  setLabel(label, index) {
    this.labels[index] = label;
    this.cdRef.detectChanges();
  }

  selectItem(index) {
    let itemState = this._itemCollapsed[index];
    this._itemCollapsed.fill(true);
    this._itemCollapsed[index] = !itemState;
  }
}
