import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

const routes: Routes = [
  {
    path: '',
    // loadChildren: '../../assets/metronic-angular/content/pages/pages.module#PagesModule'
    loadChildren: '../../assets/metronic/app/content/pages/pages.module#PagesModule'
  },
  {
    path: '**',
    redirectTo: '404',
    pathMatch: 'full'
  }
];

@NgModule({
  imports: [
    RouterModule.forRoot(routes, {
      onSameUrlNavigation: 'reload', // rajiv - I changed this from >>RouterModule.forRoot(routes)
      // anchorScrolling: 'enabled',
      scrollPositionRestoration: 'enabled'
    })
  ],
  exports: [RouterModule]
})
export class RootModule {}
