import {
  Component, OnInit, Input, OnChanges, ChangeDetectorRef, SimpleChanges, SimpleChange, Output, EventEmitter
} from '@angular/core';
import { OAuthService } from 'angular-oauth2-oidc';
import { ToastrService } from 'ngx-toastr';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { FileUploader, FileItem, ParsedResponseHeaders } from 'ng2-file-upload';
import { Subscription } from 'rxjs';
import 'rxjs/add/operator/map';

import { environment } from '../../../../environments/environment';
import { AppSettings } from "../../../../environments/appsettings";
import { DocModel } from "./doc.model";
import { DocService } from "./doc.service";
import { ApplicationsApiService } from '../../../modules/dashboard/services/applications-api.service';

@Component({
  selector: 'app-doc',
  templateUrl: './doc.component.html'
})
export class DocComponent implements OnInit {
  // @Input() inputassociateid: string;
  @Input() inputnotegroupid;
  // @Input() inputdevelopmentid: string;
  @Input() lotteryId: string;
  @Input() associateId:any; 
  // @Input() profileid: string;
  @Input() showAddButton : boolean = false;
  @Input() isPortlet: boolean = false;
  @Input() displayProfileDocs : boolean = false;
  @Input() lotteryapplicationid: string;
  @Input() hasEffectiveDate: boolean = false; // if not need delete
  @Input() reason: string;  
  @Input() complaintReason: string;
  

  // @Input() isPortlet: boolean = false;
  // @Input() portletTitle: string = "";
  // @Input() isUploadMode: boolean = false;
  @Input() isPhoto: boolean = null;
  pastDueDate: Date = null;
  subdoctypes: any[];
  associatedProjects: any[];
  isRegulatoryAgreement: boolean = false;
  complexForm: FormGroup;
  public docs: Array<DocModel>;
  public profiledocs: Array<DocModel>;
  doctypes: any;
  // selecteddoctype: string = '';
  docmodel = new DocModel();

  uploader: FileUploader;
  hasBaseDropZoneOver: boolean;
  hasAnotherDropZoneOver: boolean;
  response: string;
  isAuthenticated: boolean;
  projectAddedSubscription: Subscription;
  loadingPage: boolean = true;
  isUploading: boolean = false;
  uploaderProgress: number = 0;
  isUploadMode: boolean = false;
  selectedDocIds: Array<DocModel>;
  copytoProfileDocs : boolean = false;
  @Output() uploadDoc : EventEmitter<any> = new EventEmitter<any>(); 
  isConsent : boolean = false;

  public fileOverBase(e: any): void {
    this.hasBaseDropZoneOver = e;
  }

  public fileOverAnother(e: any): void {
    this.hasAnotherDropZoneOver = e;
  }

  constructor(
    private docService: DocService,
    private toastr: ToastrService,
    private fb: FormBuilder,
    private cdRef: ChangeDetectorRef,
    private oauthService: OAuthService,
    private applicationsApiService: ApplicationsApiService,

  ) {
    this.complexForm = this.fb.group({
      'txtDocDescription': ["", Validators.required],
      'effectivedate': [""],
      'subdoctype': [null],
      'devProject': [null]
    })
  }

  get today() {
    return new Date();
  }

  onChangeDate(isOpening) {
    if (isOpening)
      this.docmodel.effectivedate = this.today;
    else
      this.docmodel.effectivedate = (this.pastDueDate > this.today) ? this.pastDueDate : this.today;

    if (!this.cdRef['destroyed'])
      this.cdRef.detectChanges();
  }

  ngOnInit() {
    // this.projectAddedSubscription = this.developmentService.projectAssociated$.subscribe(result => {
    // this.getDocTypes();
    this.getDocs(true);
    // })
    // Load Profile Docs
    if (this.displayProfileDocs) {
      this.getProfileDocs();
    }

    if (this.lotteryId || this.inputnotegroupid) {
      // this.docmodel.profileid = this.profileid;
      this.docmodel.notedocgroupid = this.inputnotegroupid;
      this.docmodel.lotteryapplicationid = this.lotteryapplicationid;
      this.docmodel.lotteryid = this.lotteryId;
      this.docmodel.isPhoto = this.isPhoto;

      this.docmodel.developmentid = null;
      this.docmodel.associateid = this.associateId;
      this.docmodel.appeal = true;

      //this.docService.getSubDocTypes().subscribe((data: any) => {
      //   if (data.length > 0) this.subdoctypes = data;
      //});

      this.buildUploader();
    }

      
  }

  ngAfterViewInit() {
    this.cdRef.detectChanges();
  }

  buildUploader() {
    this.uploader = new FileUploader({
      // url: environment.publicApiUri + ((this.hasEffectiveDate) ? 'Comment/AddHPDDocument' : 'Comment/AddDocument'),
      url: environment.publicApiUri + 'Applicant/AddDocument',
      authToken: 'Bearer ' + this.oauthService.getAccessToken(),
      isHTML5: true,
      //allowedFileType: ['image', 'pdf', 'docx'],
      removeAfterUpload: true,
      autoUpload: false,
      maxFileSize: 10 * 1024 * 1024,
    });


    this.uploader.onBuildItemForm = (fileItem, form) => {
      this.docmodel.docdesc = this.complexForm.controls["txtDocDescription"].value;
      //if (this.inputnotegroupid === AppSettings.profileApplicationNoteGroupdId) {
      //   this.docmodel.doctype = this.complexForm.controls["subdoctype"].value;
      //}

      // if (this.hasEffectiveDate) {
      //   this.docmodel.effectivedate = new Date(this.complexForm.controls["effectivedate"].value);
      //   this.docmodel.associateid = this.complexForm.controls["devProject"].value;
      // }

      // if (this.hasEffectiveDate) {
      //   this.docmodel.doctype = this.complexForm.controls["subdoctype"].value;
      // } else {
      //   this.docmodel.doctype = this.selecteddoctype;
      this.docmodel.doctype = '';
      // }

      this.docmodel.filename = fileItem.file.name;
      this.docmodel.username = this.givenName;

      if(this.lotteryapplicationid)
        this.docmodel.copy = (!this.copytoProfileDocs && this.lotteryapplicationid != "" && this.inputnotegroupid == 2);

      form.append("frm", JSON.stringify(this.docmodel));
      return { fileItem, form }

    };

    this.uploader.onErrorItem = (item, response, status, headers) => this.onErrorItem(item, response, status, headers);
    this.uploader.onSuccessItem = (item, response, status, headers) => this.onSuccessItem(item, response, status, headers);
    this.uploader.onCompleteAll = () => this.onCompleteAll();
    this.uploader.onProgressAll = progress => this.onProgress(progress);

    this.hasBaseDropZoneOver = false;
    this.hasAnotherDropZoneOver = false;
  }

  onSuccessItem(item: FileItem, response: string, status: number, headers: ParsedResponseHeaders): any {
    let data = JSON.parse(response); //success server response
    if (data.item1.success == true) {
      this.toastr.success(data.item1.message);
      this.docs = data.item2;
      // appeal

      this.uploadDoc.emit(true);

      if (this.inputnotegroupid == AppSettings.appealNoteGroupdId && this.reason) {
      }
      else if (this.inputnotegroupid == AppSettings.complaintNoteGroupID && this.complaintReason)
      {
      } else {
        this.toastr.error(data.item1.message);
      }
    }
    if (data.item1.success == false)
    {
      this.toastr.error(data.item1.message);
    }

    if (!this.cdRef['destroyed']) this.cdRef.detectChanges();
    // this.selecteddoctype = '';
    //this.complexForm.reset();
    
    this.getDocs(true);
    this.isUploadMode = false;
  }

  onErrorItem(item: FileItem, response: string, status: number, headers: ParsedResponseHeaders): any {
    
    this.uploadDoc.emit(true);
    // let error = JSON.parse(response); //error server response
    console.log(response);
    let newDoc: any = {};
    newDoc.fileName = item.file.name;
    newDoc.description = this.complexForm.get('txtDocDescription').value;
    newDoc.addedDate = new Date();
    this.docs.push(newDoc);
    this.toastr.success('Document Uploaded Successfully');
  }

  onCompleteAll(): any {
    this.reset();
    
    this.isUploadMode = false;
    
  }

  onProgress(progress: any): void {
    this.uploaderProgress = progress;
    this.cdRef.detectChanges();
  }

  download(doc): void {
    if (this.hasEffectiveDate) {
      window.open(doc.notetext);
    } else {
      this.docService.getFile(doc);
    }
  }

  deldoc(doc) {
    const reqData = { ...doc };
    if (reqData.profileId && !reqData.createdby) {
      reqData.createdby = reqData.profileId;
      reqData.fileid = reqData.docId;
    }

    this.docService.deleteDoc(doc.hpdDocId).subscribe(data => {
      this.loadingPage = false;
      if (data.success == true) {
        this.toastr.success(data.message);
        this.getDocs(true);        
      } else
        this.toastr.error(data.message);
    });


    //this.docService.deleteDoc(reqData).subscribe(data => {
    //  if (data.success == true)
    //  {
    //    this.toastr.success(data.message);
    //    this.getDocs(true);
    //  }
    //  else
    //  {
    //    this.toastr.error("Error");
    //  }      
    //});
  };

  downloadFile(data: object, mediatype: string) {
    const blob = new Blob([data], { type: mediatype });
    const url = window.URL.createObjectURL(blob);
    window.open(url);
  }

  get givenName() {
    const claims = this.oauthService.getIdentityClaims();
    if (!claims) return null;
    return claims['name'];
  }

  uploadDocument() { 
    
    var description = this.complexForm.controls['txtDocDescription'].value;
     if(description == "")
     {
       this.toastr.error("Please provide the description");
       return;
     }     
    this.isUploading = true;

    //if(this.inputnotegroupid == AppSettings.appealNoteGroupdId || this.inputnotegroupid == AppSettings.complaintNoteGroupID)
    //{
    //    if(this.complexForm.controls["txtDocDescription"].value.toString().trim() === '')
    //    {
    //      this.toastr.error('Please, provide reason.');
    //      return;
    //    }
    //}
     
    this.uploader.uploadAll();
  }

  reset() {
    // this.selecteddoctype = '';
    this.uploader.clearQueue();
    this.complexForm.reset();
    // this.isUploadMode = false;
    this.isUploading = false;
    this.uploaderProgress = 0;
    this.isUploadMode = false;
    this.cdRef.detectChanges();
  }
  
  assignApplication()
  {
    this.docService.assignApplication(this.selectedDocIds).subscribe(data => {
      this.loadingPage = false;
      if (data.success == true) {
        this.toastr.success(data.message);
        this.getDocs(true);
        if (this.displayProfileDocs) {
          this.getProfileDocs();

          if (this.lotteryId || this.inputnotegroupid) {
            // this.docmodel.profileid = this.profileid;
            this.docmodel.notedocgroupid = this.inputnotegroupid;
            this.docmodel.lotteryapplicationid = this.lotteryapplicationid;
            this.docmodel.lotteryid = this.lotteryId;
            this.docmodel.isPhoto = this.isPhoto;

            this.docmodel.developmentid = null;
            this.docmodel.associateid = null;
            this.docmodel.appeal = true;
          }
        }

        this.isUploadMode = false;

      } else
        this.toastr.error(data.message);
    });
  }

  toggleAssign2Application(doc)
  {
    console.log("selected doc", doc);
    let app = new DocModel();
    app.fileid = doc.docId;
    app.notedocgroupid = this.inputnotegroupid;
    app.lotteryid = this.lotteryId;
    app.lotteryapplicationid = this.lotteryapplicationid;

    if (this.selectedDocIds) {
      let selected = this.selectedDocIds.find(ob => ob.fileid === doc.docId);
      if (selected === null || selected == undefined) {
        this.selectedDocIds.push(app);
      } else {
        const index = this.selectedDocIds.findIndex(ob => ob.fileid === doc.docId)
        this.selectedDocIds.splice(index, 1);
      }
    } else {
      this.selectedDocIds = new Array<DocModel>();
      this.selectedDocIds.push(app);
    }    
  }

  isSelectedDoc(doc) {
    if (this.selectedDocIds) {
      let selected = this.selectedDocIds.find(ob => ob.fileid === doc.docId);
      return !(selected === null || selected == undefined);
    } else {
      return false;
    }    
  }

  copyToProfileDocuments(value)
  {
      this.copytoProfileDocs = false;
      if(value.checked)      
        this.copytoProfileDocs = true;   
  }

  // getDocTypes() {
  //   this.docService.getDoctypes(this.docmodel.notedocgroupid).subscribe(data => {
  //       this.doctypes = data;
  //       if (this.doctypes[0].doctype === "Regulatory Agreement") this.isRegulatoryAgreement = true;
  //       console.log(this.doctypes);
  //     }
  //   );
  // }


  getProfileDocs() {
    this.loadingPage = true;
    this.docmodel.lotteryapplicationid = '';
    this.docmodel.lotteryid = '';
    this.docService.getDocs(this.docmodel, true).subscribe(profiledata => {
      this.loadingPage = false;

      //const array3 = data.filter((item) => this.docs.findIndex(ob => ob.fileid === item.fileid) < 0);
      //const arr3 = data.filter(({ fileid }) => !this.docs.has(fileid))
      
      this.docService.getDocs(this.docmodel, true).subscribe(data => {
        this.loadingPage = false;        
        var appdata = data;
        var newprofiledata = profiledata;
        for(var doc of appdata)
        {
          const index = profiledata.findIndex(ob => ob.docId === doc.docId);
          if(index >= 0)
            newprofiledata.splice(index, 1);
        }

        this.profiledocs = newprofiledata;

      });
    });    
  }

  filterUnassociatedProfileDocs() {
  
}

  getDocs(isCached) {
    this.loadingPage = true; 
    this.docService.getDocs(this.docmodel, isCached).subscribe(data => {
      this.loadingPage = false;
      console.log("Data with Lottery Id",data);
      this.docs = data;
      this.docs = this.docs.slice(0,1); 
      console.log("this.docs", this.docs);
      if (!this.cdRef['destroyed']) this.cdRef.detectChanges();
      console.log('DOCS: ', data);
    }, error =>{
      this.docs = [];
      this.loadingPage = false;
    });
  }


  // setdoctype(doctype) {
  //   this.selecteddoctype = doctype;
  //   this.isUploadMode = true;
  // }

  submitForm(formvalue: any) {
    this.docService.saveNote(formvalue).subscribe((data: any) => {
      if (data.success == true) {
        this.toastr.success(data.message);
        // this.isUploadMode = false;
      } else
        this.toastr.error(data.message);
    });
  }

  uploadDocs() {
    this.isUploadMode = true;
  }
}
