import { Pipe, PipeTransform } from '@angular/core';
import { ApplicationStatusEnum } from '../../../shared/enums/applicationStatusEnum';

@Pipe({name: 'appStatusFilter'})
export class ApplicationStatusFilter implements PipeTransform {
  transform(applications: any[], filter: string): any[] {

    return (filter)
      ? applications.filter(app => this.applyFilter(filter, app.details.lotteryApplication.applicationStatus.applicationStatusId))
      : applications;
  }

  applyFilter(filter, id) {
    switch (true) {
      case filter == 'In Process':
        return this.isInProcess(id);
      case filter == "Action Needed":
        return this.isActionNeeded(id);
      case filter == "In Review":
        return this.isInReview(id);
      case filter == "Accepted":
        return this.isAccepted(id);
      case filter == "Rejected":
        return this.isRejected(id);
      default: return false;
    }
  }

  isInProcess(statusId) {
    switch (statusId) {
      case statusId == ApplicationStatusEnum.SetAsPending:
      case statusId == ApplicationStatusEnum.BackgroundCheckPassed:
      case statusId == ApplicationStatusEnum.BackgroundCheckFailed:
      case statusId == ApplicationStatusEnum.InBatch:
      case statusId == ApplicationStatusEnum.AppointmentScheduled:
      case null:
        return true;
      default: return false;
    }
  }
  isActionNeeded(statusId) {
    switch (statusId) {
      case ApplicationStatusEnum.RequestForDocuments:
      case ApplicationStatusEnum.AppointmentRequested:
        return true;
      default: return false;
    }
  }
  isAccepted(statusId) {
    switch (statusId) {
      case ApplicationStatusEnum.PriorityWaitlist:
      case ApplicationStatusEnum.LeaseSigned:
        return true;
      default: return false;
    }
  }
  isRejected(statusId) {
    switch (statusId) {
      case ApplicationStatusEnum.Ineligible:
      case ApplicationStatusEnum.Rejected:
      case ApplicationStatusEnum.AppealRejected:
      case ApplicationStatusEnum.ComplaintRejected:
      case ApplicationStatusEnum.Skipped:
        return true;
      default: return false;
    }
  }
  isInReview(statusId) {
    switch (statusId) {
      case ApplicationStatusEnum.AppealFiled:
      case ApplicationStatusEnum.AppealAccepted:
      case ApplicationStatusEnum.ComplaintFiled:
      case ApplicationStatusEnum.ComplaintAccepted:
        return true;
      default: return false;
    }
  }
}
