import { Injectable } from '@angular/core';
import { Observable, BehaviorSubject } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class ToggleLoginService {
  isLoggedIn = new BehaviorSubject(false);

  constructor() {}

  toggleLogin() {
    this.isLoggedIn.next(!this.isLoggedIn.value);
  }
}
