import { Injectable } from '@angular/core';
import { of } from 'rxjs';
// @ts-ignore
import * as mockData from './mockData.json';

let categories = (<any>mockData).categories;
let faqs = (<any>mockData).faqs;

@Injectable()
export class FaqService {
  constructor() {}

  getCategories() {
    return of(categories);
  }

  getAllFaqs() {
    return of(faqs);
  }
}
