import {HouseholdModule} from './modules/household/household.module';
import {APP_INITIALIZER, APP_BOOTSTRAP_LISTENER, NgModule} from '@angular/core';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {GestureConfig, MatProgressSpinnerModule, MatStepper} from '@angular/material';
import {OverlayModule} from '@angular/cdk/overlay';
import {DatePipe, LocationStrategy, PathLocationStrategy} from '@angular/common';
import {HttpClientModule} from '@angular/common/http';
import {HttpModule} from '@angular/http';
import {HAMMER_GESTURE_CONFIG} from '@angular/platform-browser';
import {BrowserAnimationsModule} from '@angular/platform-browser/animations';
import {JwtHelper} from 'angular2-jwt';
import {PERFECT_SCROLLBAR_CONFIG, PerfectScrollbarConfigInterface} from 'ngx-perfect-scrollbar';
import {OAuthModule} from 'angular-oauth2-oidc';
import {TranslateModule} from '@ngx-translate/core';
import {NgxPermissionsModule} from 'ngx-permissions';
import {ToastrModule} from 'ngx-toastr';
import {NgbActiveModal, NgbModule, NgbDropdown} from '@ng-bootstrap/ng-bootstrap';
import {FileUploadModule} from 'ng2-file-upload';
import {SortablejsModule} from 'angular-sortablejs';
import 'hammerjs';

import {AuthenticationModule} from '../assets/metronic/app/core/auth/authentication.module';
import {LayoutModule} from '../assets/metronic/app/content/layout/layout.module';
import {PartialsModule} from '../assets/metronic/app/content/partials/partials.module';
import {CoreModule as MetronicAngularCoreModule} from '../assets/metronic/app/core/core.module';
import {AclService} from '../assets/metronic/app/core/services/acl.service';
import {LayoutConfigService} from '../assets/metronic/app/core/services/layout-config.service';
import {MenuConfigService} from '../assets/metronic/app/core/services/menu-config.service';
import {PageConfigService} from '../assets/metronic/app/core/services/page-config.service';
import {UtilsService} from '../assets/metronic/app/core/services/utils.service';
import {ClassInitService} from '../assets/metronic/app/core/services/class-init.service';
import {MessengerService} from '../assets/metronic/app/core/services/messenger.service';
import {ClipboardService} from '../assets/metronic/app/core/services/clipboard.sevice';
import {LayoutConfigStorageService} from '../assets/metronic/app/core/services/layout-config-storage.service';
import {LogsService} from '../assets/metronic/app/core/services/logs.service';
import {QuickSearchService} from '../assets/metronic/app/core/services/quick-search.service';
import {SubheaderService} from '../assets/metronic/app/core/services/layout/subheader.service';
import {HeaderService} from '../assets/metronic/app/core/services/layout/header.service';
import {MenuHorizontalService} from '../assets/metronic/app/core/services/layout/menu-horizontal.service';
import {MenuAsideService} from '../assets/metronic/app/core/services/layout/menu-aside.service';
import {LayoutRefService} from '../assets/metronic/app/core/services/layout/layout-ref.service';
import {SplashScreenService} from '../assets/metronic/app/core/services/splash-screen.service';
import {DataTableService} from '../assets/metronic/app/core/services/datatable.service';
import {PagesModule} from '../assets/metronic/app/content/pages/pages.module';

import {SharedModule} from './shared/shared.module';
import {AuthService} from './core/services/auth.service';
import {AppComponent} from './app.component';
import {UnauthorizedModule} from './modules/unauthorized/unauthorized.module';
import {HomeModule} from './modules/home/home.module';
import {CoreModule} from './core/core.module';
import {RootModule} from './routing/root-routing.module';
import {FaqModule} from './modules/faq/faq.module';
import {AboutUsModule} from './modules/about-us/about-us.module';
import {DashboardModule} from './modules/dashboard/dashboard.module';
import {configurationFactory, ConfigurationService} from "./core/services/configuration.service";
import { BsDatepickerModule } from 'ngx-bootstrap/datepicker';
import { BsDropdownModule } from 'ngx-bootstrap/dropdown';

const DEFAULT_PERFECT_SCROLLBAR_CONFIG: PerfectScrollbarConfigInterface = {
  // suppressScrollX: true
};

// @ts-ignore
// @ts-ignore
@NgModule({
  imports: [
    HttpClientModule,
    BrowserAnimationsModule,
    FormsModule,
    ReactiveFormsModule,
    OverlayModule,
    FileUploadModule,
    NgbModule,
    NgxPermissionsModule.forRoot(),
    OAuthModule
      .forRoot
      //   {
      //   resourceServer: {
      //     allowedUrls: ['http://www.angular.at/api'],
      //     sendAccessToken: true
      //   }
      // }
      (),
    ToastrModule.forRoot({
      preventDuplicates: true,
      timeOut: 5000,
      extendedTimeOut: 5000,
      enableHtml: true,
      positionClass: 'toast-top-center'
    }),
    TranslateModule.forRoot(),
    SortablejsModule.forRoot({animation: 150}),

    MatProgressSpinnerModule,
    // MetronicAngularCoreModule.forRoot(),
    MetronicAngularCoreModule,
    LayoutModule,
    PartialsModule,
    AuthenticationModule,
    PagesModule,
    SharedModule,
    CoreModule,
    RootModule,
    UnauthorizedModule,
    HomeModule,
    FaqModule,
    AboutUsModule,
    DashboardModule,
    HouseholdModule,
    BsDatepickerModule.forRoot(),
    BsDropdownModule.forRoot()
  ],
  declarations: [AppComponent],
  providers: [
    DatePipe,
    NgbActiveModal,
    NgbDropdown,
    {provide: LocationStrategy, useClass: PathLocationStrategy},
    AuthService,
    JwtHelper,
    HttpModule,
    ConfigurationService,
    {
      provide: APP_INITIALIZER,
      useFactory: configurationFactory,
      multi: true,
      deps: [ConfigurationService]
    },
    // Metronic - Native
    AclService,
    LayoutConfigService,
    LayoutConfigStorageService,
    LayoutRefService,
    MenuConfigService,
    PageConfigService,
    UtilsService,
    ClassInitService,
    MessengerService,
    ClipboardService,
    LogsService,
    QuickSearchService,
    DataTableService,
    SplashScreenService,
    {
      provide: PERFECT_SCROLLBAR_CONFIG,
      useValue: DEFAULT_PERFECT_SCROLLBAR_CONFIG
    },
    // template services
    SubheaderService,
    HeaderService,
    MenuHorizontalService,
    MenuAsideService,
    {
      provide: HAMMER_GESTURE_CONFIG,
      useClass: GestureConfig
    }
  ],
  entryComponents: [],
  bootstrap: [AppComponent]
})
export class AppModule {
}
