import { Component, OnInit, Input, Output, EventEmitter, OnChanges} from '@angular/core';
import { MarkerModel } from 'projects/web/src/app/shared/models/marker.model';

@Component({
  selector: 'app-appointment-map',
  templateUrl: './appointment-map.component.html',
  styleUrls: ['./appointment-map.component.scss']
})
export class AppointmentMapComponent implements OnChanges {

  constructor() { }
  @Input() locationList: any[];
  @Input() selectedLocationId = -1;
  @Output() setLocation: EventEmitter<any> = new EventEmitter();
  markers: MarkerModel[] = [];

  ngOnChanges() {
    if (this.locationList) {
      this.markers = [];
      this.locationList.forEach((location, i) => {
        if (location) {
          let m = new MarkerModel();
          m.id = location.locationId;
          m.index = i + 1;
          m.lat = location.latitude;
          m.lng = location.longitude;
          m.label = location.label;
          m.address = location.address;
          //m.city = b.building.city;
          //m.state = b.building.state;
          //m.zip = b.building.zip;
          this.markers.push(m);
        }
      });
    }
    //if (this.selectedDate || this.selectedTime !== "") {
     // this.markers = this.locationMarkers.filter(marker => {
     //   return (this.availableLocations().map(loc => loc.locationId).indexOf(marker.id) !== -1) ? true : false;
     // })
  }
  onClicked = (event) => {
    console.log(event);
  }

}
