import { Component, Input, ViewChild, ElementRef, AfterViewInit, OnInit } from '@angular/core';
import Player from '@vimeo/player';

@Component({
  selector: 'app-video',
  templateUrl: './video.component.html',
  styleUrls: ['./video.component.scss']
})
export class VideoComponent implements OnInit, AfterViewInit {
  @Input() videoId?: string;
  @ViewChild('vimeoPlayer') videoFrame: ElementRef;
  player: Player;
  playerOptions: any;

  constructor() {}

  ngOnInit() {
    this.playerOptions = {
      id: this.videoId
    };
  }

  ngAfterViewInit() {
    this.player = new Player(this.videoFrame.nativeElement, this.playerOptions);
  }

  playVideo() {
    this.player.play();
  }
}
