import { distinctUntilChanged, debounce, takeUntil } from 'rxjs/operators';
import { ChangeDetectorRef, ChangeDetectionStrategy, OnDestroy, OnChanges, SimpleChanges } from '@angular/core';
import { Component, OnInit, Input } from '@angular/core';
import { FormGroup, AbstractControl, FormControl, FormArray } from '@angular/forms';
import { timer, Subject, Subscription } from 'rxjs';

@Component({
  selector: 'app-form-footer',
  templateUrl: './form-footer.component.html',
  styleUrls: ['./form-footer.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class FormFooterComponent implements OnInit, OnDestroy, OnChanges {

  ngOnChanges(changes: SimpleChanges): void {
    if(changes.mainForm.currentValue && !changes.mainForm.firstChange){
      this.subscribeToFormChanges();
      this.updateFormCount();
    }
  }

  unsubscribe: Subject<void> = new Subject();
  @Input() mainForm: FormGroup;
  @Input() isEditForm: boolean = false;


  totalControlsCount: number;
  validControlsCount: number;
  percentageComplete: number = 0;

  formChangesSub: Subscription;
  constructor(private cdRef: ChangeDetectorRef) { }

  ngOnInit() {
    // this.updateFormCount();
    this.subscribeToFormChanges();
  }


  private subscribeToFormChanges(){
    //Need to unsubscribe if input mainForm changes in order detect changes
    if(this.formChangesSub) this.formChangesSub.unsubscribe();

    this.formChangesSub = this.mainForm.valueChanges.pipe(
      takeUntil(this.unsubscribe),
      distinctUntilChanged(),
      debounce(() => timer(1000))
    ).subscribe(() => {
      this.updateFormCount();
      this.cdRef.detectChanges();
    });
  }

  // Calculate form controls total count and pecentage complete
  updateFormCount() {
    this.totalControlsCount = this.countControls(this.mainForm, false);
    this.validControlsCount = this.countControls(this.mainForm, true);

    this.percentageComplete = this.calcPercentage(this.totalControlsCount, this.validControlsCount);
  }

  private calcPercentage(total: number, actual: number): number {
    return Math.round((actual / total) * 100);
  }

  private countControls(control: AbstractControl, isValid: boolean = false): number {
    if (control instanceof FormControl) {
      // only count required controls
      if (control.validator) {
        const validator = control.validator({} as AbstractControl);

        if (isValid) {
          //If this is not an editform none of the controls will be pre-populated
          if (!this.isEditForm) {
            if ((control.dirty || control.touched) && !control.invalid) return 1;
          } else if (control.valid && validator && validator.required) {
            return 1;
          }

          return 0;
        }

        if (validator && validator.required && control.enabled) {
          return 1;
        }
      }
      return 0
    }

    if (control instanceof FormArray) {
      return control.controls.reduce((acc, curr) => acc + this.countControls(curr, isValid), 0)
    }

    if (control instanceof FormGroup) {
      return Object.keys(control.controls)
        .map(key => control.controls[key])
        .reduce((acc, curr) => acc + this.countControls(curr, isValid), 0);
    }
  }

  ngOnDestroy(): void {
    this.unsubscribe.next();
    this.unsubscribe.complete();
  }
}
