import { UtilityService } from './../../../services/utility/utility.service';
import { HouseholdMemberProfile } from '../../../models/hh-member-profile.model';
import { Component, OnInit, Input, Output, EventEmitter, OnChanges, SimpleChanges, ChangeDetectorRef, TemplateRef, ViewChild } from '@angular/core';
import { MemberEtiquette } from '../../../models/member-etiquette.model';
import { MatDialog, MatDialogRef } from '@angular/material';
import { FormHelpers } from '../../../../../shared/helpers/form-helpers';
import { HouseholdService } from '../../../services/household/household.service';
import { ToastrService } from 'ngx-toastr';
import { HouseholdMemberAsset, HouseholdMemberIncome } from '../../../models';
import { forkJoin } from 'rxjs';
import { ConfigValueService } from '../../../../../shared/services/config-value/config-value.service';
import { RelationshipTypeEnum } from 'projects/web/src/app/shared/enums/applicantEnums';
import { AppSettings } from 'projects/web/src/environments/appsettings';
import * as _ from 'lodash';


@Component({
  selector: 'member-profile-summary',
  templateUrl: './member-profile-summary.component.html',
  styleUrls: ['./member-profile-summary.component.scss']
})
export class MemberProfileSummaryComponent implements OnInit, OnChanges {
  servicesData: any[];
  schoolDetails: Array<any>;
  collapseArray: any[];
  ngOnChanges(changes: SimpleChanges): void {
    if (changes.viewModel.currentValue) {
      this.memberEttiquette = this.utilSvc.getQuestionairreEtiquette(this.viewModel, this.relationshipOptions);
    }
  }

  @Output() EditProfileEvent: EventEmitter<HouseholdMemberProfile> = new EventEmitter();
  @Input('householdMember') viewModel: HouseholdMemberProfile;
  @Input() relationshipOptions: any = [];
  @Input() household: any;
  isCollapsed: boolean = true;
  memberEttiquette: MemberEtiquette;
  inputnotegroupid: any = AppSettings.profileApplicationNoteGroupdId;

  assetTypeOptions: any[] = [];
  incomeTypeOptions: any[] = [];
  incomeSubTypeOptions: any[] = [];
  stateOptions: any[];


  activeModalRef: MatDialogRef<any>;
  isConsentProvided: boolean;
  isDocUpload: boolean;
  studentData = null;

  @ViewChild('removeMemberDialog') removeMemberDialog: TemplateRef<any>;
  @ViewChild('initiateReferralDialog') initiateReferralDialog: TemplateRef<any>;

  constructor(private utilSvc: UtilityService,
    public matDialog: MatDialog,
    private configValSvc: ConfigValueService,
    private householdSvc: HouseholdService,
    private toastr: ToastrService,
    private cdRef: ChangeDetectorRef) { }

  ngOnInit() {

    let configvalSub = this.configValSvc.getAllConfigTypes('AssetType', 'IncomeType', 'IncomeSubType', 'State');

    forkJoin(configvalSub).subscribe(([configValOptions]) => {
      this.assetTypeOptions = configValOptions.AssetType;
      this.incomeTypeOptions = configValOptions.IncomeType;
      this.incomeSubTypeOptions = configValOptions.IncomeSubTypes;
      this.stateOptions = configValOptions.State;
    })

    this.householdSvc.memberAssetRemoved$.subscribe(removedAsset => this.onAssetRemoved(removedAsset));
    this.householdSvc.memberIncomeRemoved$.subscribe(removedIncome => this.onIncomeRemoved(removedIncome));
    console.log("View Model", this.viewModel);
    this.getIEPData();

  }

  editProfile(): void {
    this.utilSvc.setCurrentMemberQuestionairreEtiquette(this.viewModel, this.relationshipOptions);
    this.EditProfileEvent.emit(this.viewModel);
  }

  getIEPData() {
    let id = this.viewModel.householdId;
    this.householdSvc.getIEPData(this.viewModel.logNumber).subscribe(data => {
      if (data) {
        console.log("Exisiting IEP data of the student", data);
        this.studentData = data;
        if (this.studentData.iepId) this.householdSvc.getSchoolPlacementsData(this.studentData.iepId, 0, false).subscribe(data => {
          if (data) {
            let dataArray: any = Array.from(new Set(data.map(x => x.schooL_LOCATION_ID)))
              .map(id => {
                return { schooL_LOCATION_ID: id }
              });

            forkJoin(dataArray.map(x => this.householdSvc.getSchoolPlacementsData(this.studentData.iepId, x.schooL_LOCATION_ID, true))).subscribe(arr => {
              this.servicesData = arr;
              this.servicesData.forEach(schoolService => {
                schoolService.forEach(service => {
                  this.householdSvc.getAssignedProvider(service.speciaL_EDUCATION_SERVICE_ID).subscribe(x => { if (x) { service.assignment = x; } });
                })
              })
              console.log('FORK JOIN', arr)
            });
            
            this.schoolDetails = _.uniqBy(data, 'schooL_LOCATION_ID');

            this.collapseArray = new Array((this.schoolDetails) ? this.schoolDetails.length : 0);
            this.collapseArray.fill(0);
            console.log("Data", this.schoolDetails);
          }
        });
      }
      this.cdRef.detectChanges();
    });
  }

  getRelationshipTypeLabel(relationshipTypeId) {
    let optionValue = this.relationshipOptions.find(r => r.configTypeId === relationshipTypeId);
    return optionValue ? optionValue.name : '';
  }

  onAssetRemoved(model) {
    this.viewModel.householdMemberAssets = this.viewModel.householdMemberAssets.filter(r => r.householdMemberAssetId !== model.assetId);
  }

  onIncomeRemoved(model) {
    this.viewModel.householdMemberIncomes = this.viewModel.householdMemberIncomes.filter(r => r.householdMemberIncomeId !== model.incomeId);
  }

  toggleCollapse(index) {
    let newArray = this.collapseArray.fill(0);
    newArray[index] = (1 - this.collapseArray[index]);
    this.collapseArray = newArray;
  }

  openRemoveMemberDialog(profile: HouseholdMemberProfile) {
    this.activeModalRef = this.matDialog.open(this.removeMemberDialog, {
      hasBackdrop: true,
      panelClass: 'mat-dialog'
    });
  }

  openInitiateReferralDialog(profile: HouseholdMemberProfile) {
    this.activeModalRef = this.matDialog.open(this.initiateReferralDialog, {
      hasBackdrop: true,
      panelClass: 'mat-dialog'
    });

    //this.activeModalRef.beforeClose().subscribe(filterModel => {
    //  console.log("Return from modal: ", filterModel);
    //  if (filterModel) {
    //    this.filtersModel = filterModel;

    //    this.updateUrlParamsByFilterModel(this.filtersModel);
    //    this.searchLotteries(this.filtersModel);
    //  }
    //});
  }

  onRemoveMember() {
    this.householdSvc.removeHouseholdMember(this.viewModel.householdMemberProfileId).subscribe(result => {
      if (result.success) {
        this.toastr.success("Household member has been removed");
        this.activeModalRef.close();
      }
      else {
        this.toastr.error("Unable to remove this household member at this time.");
      }

    });
  }

  getAssetTypeName(type) {
    if (this.assetTypeOptions.length) {
      let filter = this.assetTypeOptions.find(option => option.configTypeId === type);
      return filter ? filter.name : "";
    } else return "";
  }

  getIncomeTypeName(type) {
    if (this.incomeTypeOptions.length) {
      let filter = this.incomeTypeOptions.find(option => option.configTypeId === type);
      return filter ? filter.name : "";
    } else return "";
  }

  getIncomeSubTypeName(type) {
    if (this.incomeTypeOptions.length) {
      let filter = this.incomeSubTypeOptions.find(option => option.configTypeId === type);
      return filter ? filter.name : "";
    } else return "";
  }

  getStateLabel(stateId) {
    let optionValue = this.stateOptions.find(r => r.configTypeId === stateId);
    return optionValue ? optionValue.name : '';
  }

  isUpload(event) {
    console.log("Upload doc event emitter  value", event);
    if (event) {
      this.isDocUpload = true;
    }

  }

  updateFlag(value) {
    this.viewModel.isExtraBedroomNeeded = value;
    this.householdSvc.updateHouseholdMember(this.viewModel).subscribe(data => {
      console.log("Response Data after member Update", data);
      if (data.isExtraBedroomNeeded) {
        this.isConsentProvided = true;
      }
    });
  }

  showIepTab() {
    return this.studentData;
  }

  showIncomeAssetsTab() {
    return false;
    //let relationshipType = this.viewModel.relationshipTypeId;
    //if (relationshipType == RelationshipTypeEnum.UnbornChild || relationshipType == RelationshipTypeEnum.LineInAide) {
    //  return false;
    //}
    //if (!this.viewModel.isEmancipatedMinor) {
    //  if (this.viewModel.dateOfBirth) {
    //    var today = new Date();
    //    var birthDate = new Date(this.viewModel.dateOfBirth);
    //    var age = today.getFullYear() - birthDate.getFullYear();
    //    var m = today.getMonth() - birthDate.getMonth();
    //    if (m < 0 || (m === 0 && today.getDate() < birthDate.getDate())) {
    //      age = age - 1;
    //    }

    //    if (age < 18) return false;
    //    else return true;
    //  } else return true;
    //}
    //else return true
  }

  log(e) {
    console.log(e);
  }
}
