/// <reference types='@types/googlemaps' />
import { MapPopup } from './mapPopup.class';
import { EventEmitter } from '@angular/core';
export function MapMarker(latlng, args, map, isGridView) {

  if (!window['google']) {
    return;
  }
  this.emitter = new EventEmitter<any>();
  this.complaintEmitter = new EventEmitter<any>();

  // constructor(latlng, args, map, isGridView) {
  google.maps.OverlayView.call(this);
  this.latlng = latlng;
  this.args = args;
  this.map = map;
  this.isPopupOpen = false;
  this.isGridView = isGridView;
  this.mapPopup = new MapPopup(latlng, args, map);
  this.mapPopup.emitter.subscribe(x => this.complaintEmitter.emit(x));
  // }

  this.draw = function () {
    const self = this;
    let div = this.div;
    if (!div) {
      const styles = document.createElement('style');
      styles.innerHTML = `
        .marker {
          position: absolute;
          cursor: pointer;
          width: 20px;
          height: 20px;
        }

        .marker img {
          width: 36px;
          height: 36px;
          position: absolute;
          top: -36px;
          left: -16px;
        }
      `;
      div = this.div = document.createElement('div');
      div.className = 'marker';
      div.appendChild(styles);
      const markerIcon = document.createElement('img');
      markerIcon.src = '/assets/images/map-marker.svg';
      if (this.args['iconUrl']) markerIcon.src = this.args['iconUrl'];
      div.appendChild(markerIcon);

      if (!this.isGridView) {
        if (self.args && self.args.lottery_id) {
          div.dataset.lottery_id = self.args.id;
        }

        if (!self.args.id) {
          google.maps.event.addDomListener(div, 'click', event => {
            event.stopPropagation();
            google.maps.event.trigger(self.map, 'openPopup', div);
            if (!self.isPopupOpen) {
              self.openPopup();
              // self.setActive();
            } else {
              self.closePopup();
              // self.setInactive();
            }
          });

          google.maps.event.addListener(self.map, 'openPopup', target => {
            if (self.div !== target) {
              self.closePopup();
              // self.setInactive();
            }
          });
        }
        else {
          google.maps.event.addDomListener(div, 'click', event => {
            console.log(self.args);
            self.selectMarker(self.args.id);
            event.stopPropagation();
          });
        }
      }

      let panes = this.getPanes();
      panes.overlayImage.appendChild(div);
    }

    let point = this.getProjection().fromLatLngToDivPixel(this.latlng);

    if (point) {
      div.style.left = point.x - 10 + 'px';
      div.style.top = point.y - 10 + 'px';
    }
  }

  this.onRemove = function () {
    if (this.div) {
      this.div.parentNode.removeChild(this.div);
      this.div = null;
    }
  }

  this.getPosition = function () {
    return this.latlng;
  }

  this.setActive = function () {
    this.div.className = 'marker active';
  }

  this.setInactive = function () {
    this.div.className = 'marker';
  }

  this.openPopup = function () {
    this.mapPopup.setMap(this.map);
    this.isPopupOpen = true;
  }

  this.closePopup = function () {
    this.mapPopup.setMap(null);
    this.isPopupOpen = false;
  }

  this.selectMarker = function (locationId) {
    this.emitter.emit(locationId);
    console.log(locationId);
    console.log(this);
  }
}

const intervalCounter = setInterval(() => {
  if (window['google']) {
    MapMarker.prototype = Object.create(google.maps.OverlayView.prototype);
    clearInterval(intervalCounter);
  }
}, 1000);
