import { Component, Input } from '@angular/core';
import { Router } from '@angular/router';

@Component({
  selector: 'app-lotteries-by-neighborhood',
  templateUrl: './lotteries-by-neighborhood.component.html',
  styleUrls: ['./lotteries-by-neighborhood.component.scss']
})
export class LotteriesByNeighborhoodComponent {
  @Input() data: any;

  constructor(private router: Router) {}

  openBoroughInSearchPage(item) {
    console.log(item);
    this.router.navigate(['/search-businesses'],
      {
        queryParams: {
          neighborhoods: item.title
        }
      });
  }
}
